import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import Resize from "@modules/resize/Resize";
import {IResize} from "@modules/resize/model";
import {IVisibility} from "@modules/visibility/model";
import Visibility from "@modules/visibility/Visibility";
import ScrollTriggerInstance = gsap.plugins.ScrollTriggerInstance;

gsap.registerPlugin(ScrollTrigger);

export default class StickyCostLine {
	private CLASS_HANDLER: string = 'js-stickyCostLineHandler';
	private CLASS_FIXED_DUPLICATE: string = 'js-stickyCostLineFixedDuplicate';

	private handlerEl: HTMLElement;
	private fixedDuplicateEl: HTMLElement;
	private resize: IResize;
	private trigger: ScrollTriggerInstance;
	private visibility: IVisibility;

	private handlerHeight: number;

	public init(): void {
		this.handlerEl = document.querySelector(`.${this.CLASS_HANDLER}`);
		this.fixedDuplicateEl = document.querySelector(`.${this.CLASS_FIXED_DUPLICATE}`);
		if (!this.handlerEl || !this.fixedDuplicateEl) {
			return;
		}
		this.visibility = new Visibility();
		this.resize = new Resize(document.body);
		this.handlerHeight = this.handlerEl.offsetHeight;
		this.initTrigger();
		this.attachEvent();
	}

	private initTrigger(): void {
		this.trigger = ScrollTrigger.create({
			trigger: this.handlerEl,
			start: "bottom bottom",
			endTrigger: this.handlerEl,
			end: "bottom bottom",
			markers: false, //for debug,
			onUpdate: self => this.updateFixedDuplicate(self.progress)
		});
	}

	private attachEvent(): void {
		this.resize
			.getStream()
			.subscribe(() => {
				ScrollTrigger.refresh();
			});
	}

	private updateFixedDuplicate(progress: number): void {
		if (progress > 0) {
			this.visibility.hide(this.fixedDuplicateEl);
		} else {
			this.visibility.show(this.fixedDuplicateEl);
		}
	}

}
