import {EBtnSize, EBtnType} from "@components/btn/model";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {
	ECartEditTypes,
	TCartAddRequest,
	TCartAddResponse,
	TCartEditRequest,
	TCartEditResponse,
	TCartItem,
	TCartRemoveRequest,
	TCartRemoveResponse,
	TCartViewRequest,
	TCartViewResponse
} from "@models/Cart";
import CartQuantity from "@modules/cart/CartQuantity";
import HttpRequest from "@modules/http-request/HttpRequest";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosRequestConfig} from "axios";
import {customElement, html, property, PropertyValues, state, TemplateResult} from "lit-element";
import CartCostLine from "@modules/cart/CartCostLine";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {BehaviorSubject} from "rxjs";
import {EComponentStatuses} from "@models/Status";
import {TEditProductsCartStatusChangeEventDetail} from "@components/edit-products-cart/model";
import {ECartCostLineNames} from "@modules/cart/model";

@customElement('c-edit-products-cart')
export default class EditProductsCartComponent extends WebComponentLite {
	@property({
		type: Object,
		attribute: 'get-request-config',
	})
	public getRequestConfig: AxiosRequestConfig = {
		url: '/cart/',
		method: "GET",
	}

	@property({
		type: Object,
		attribute: 'remove-request-config',
	})
	public removeRequestConfig: AxiosRequestConfig = {
		url: '/cart/remove/',
		method: "POST",
	}

	@property({
		type: Object,
		attribute: 'edit-request-config',
	})
	public editRequestConfig: AxiosRequestConfig = {
		url: '/cart/edit/',
		method: "POST",
	}

	@property({
		type: Object,
		attribute: 'add-request-config',
	})
	public addRequestConfig: AxiosRequestConfig = {
		url: '/cart/add/',
		method: "POST",
	}

	@state()
	private items: Array<TCartItem> = [];

	@state()
	private accessories: Array<TCartItem> = [];

	@state()
	private isLoading: boolean = false;

	@state()
	private isGlobalLoading: boolean = false;

	@state()
	private error: unknown;

	private httpService: HttpRequest = new HttpRequest();
	private cartQuantity: CartQuantity = CartQuantity.getInstance();
	private cartCostLine: CartCostLine;
	public readonly status: BehaviorSubject<EComponentStatuses> = new BehaviorSubject<EComponentStatuses>(EComponentStatuses.LOADING);

	constructor() {
		super();
		const CartCostLineEl: HTMLElement = document.querySelector('.js-cartCostLine');
		this.cartCostLine = new CartCostLine(CartCostLineEl);
	}

	public updateData(): void {
		this.getData({
			withGlobalLoading: true,
		});
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
		this.getData();
	}

	protected render(): TemplateResult {
		let template: TemplateResult;
		if (this.isLoading) {
			template = this.getLoaderTemplate();
		} else if (this.error) {
			template = this.getErrorTemplate();
		} else {
			template = this.getContentTemplate();
		}

		return html`
			<div class="c-edit-products-cart">
				${template}

				<c-loader
					class="c-edit-products-cart__global-loader ${this.isGlobalLoading ? 'c-edit-products-cart__global-loader_active' : ''}"
					.active="${this.isGlobalLoading}"
					.type="${ELoaderType.PRIMARY}"
					.size="${ELoaderSize.MD}">
			</div>
		`;
	}

	private getContentTemplate(): TemplateResult {
		if (this.items.length) {
			return html`
				${this.getItemsTemplate()}
				${this.getAccessoriesTemplate()}
			`
		}
		return this.getEmptyTemplate()
	}

	private getItemsTemplate(): TemplateResult {
		const accessoriesSkus = this.accessories.map(item => item.product.sku);
		const filteredItems = this.items.filter(item => !accessoriesSkus.includes(item.product.sku));

		if (!filteredItems.length) {
			return this.getEmptyTemplate();
		}

		return html`
			<ul class="c-edit-products-cart__product-list">
				${filteredItems.map(item => {
					return html`
						<li class="c-edit-products-cart__product-item">
							<c-edit-products-cart-item .data="${item}"
													   .onRemove="${() => this.removeItem(item)}"
													   .onDecrement="${() => this.editItem(item, ECartEditTypes.DECREMENT)}"
													   .onIncrement="${() => this.editItem(item, ECartEditTypes.INCREMENT)}">
						</li>
					`;
				})}
			</ul>
		`;
	}

	private getAccessoriesTemplate(): TemplateResult {
		if (!this.accessories.length) {
			return null;
		}

		const accessoriesSkus = this.accessories.map(item => item.product.sku);
		const selectedSkus = this.items.map(item => item.product.sku).filter(sku => accessoriesSkus.includes(sku));

		return html`
			<div class="c-edit-products-cart__accessories">
				<c-edit-products-cart-carousel-accessories .data="${this.accessories}"
														   .selectedSkus="${selectedSkus}"
														   .onChange="${this.handleChangeAccessories.bind(this)}">
			</div>
		`;
	}

	private getEmptyTemplate(): TemplateResult {
		return html`
			<div class="c-result c-result_404">
				<div class="c-result__icon">
					<i class="o-icon o-icon_cart"></i>
				</div>
				<h5 class="c-result__title">
					Ваша корзина пуста
				</h5>
			</div>
		`;
	}

	private getLoaderTemplate(): TemplateResult {
		return html`
			<c-loader active
					  class="c-edit-products-cart__loader"
					  type="${ELoaderType.PRIMARY}"
					  size="${ELoaderSize.MD}">
			</c-loader>
		`;
	}

	private getErrorTemplate(): TemplateResult {
		return html`
			<div class="u-text-center u-py-30 u-px-20">
				<h4>При загрузке данных произошла ошибка !</h4>
				<p class="u-mb-20">Проверьте подключение к интернету</p>
				<c-btn type="${EBtnType.PRIMARY}"
					   size="${EBtnSize.MD}"
					   @click="${this.handleClickBtnReload}">
					Повторить
				</c-btn>
			</div>
		`;
	}

	private handleClickBtnReload(event: Event): void {
		event.preventDefault();
		this.getData();
	}

	private handleChangeAccessories(item: TCartItem, isAdd: boolean): void {
		if (isAdd) {
			this.addItem(item);
		} else {
			this.removeItem(item);
		}
	}

	private getData(options: { withGlobalLoading?: boolean, reloadOnEmpty?: boolean } = {}): void {
		if (this.isLoading || this.isGlobalLoading) {
			return
		}

		this.status.next(EComponentStatuses.LOADING);
		if (options.withGlobalLoading) {
			this.isGlobalLoading = true;
		} else {
			this.isLoading = true;
		}
		const {url, method} = this.getRequestConfig;
		const data: TCartViewRequest = {};
		this.error = null;
		this.httpService
			.request<TCartViewResponse>({
				url: url,
				method: method,
				data: data,
			})
			.then((response) => {
				this.items = response?.data?.data?.items || [];
				this.accessories = response?.data?.data?.accessories || [];
				if (options.withGlobalLoading) {
					this.isGlobalLoading = false;
				} else {
					this.isLoading = false;
				}
				if (options.reloadOnEmpty) {
					this.reloadOnEmpty();
				}
				this.updatePriceInCostLine();
				this.status.next(EComponentStatuses.READY);
			})
			.catch((reject) => {
				if (options.withGlobalLoading) {
					this.isGlobalLoading = false;
				} else {
					this.isLoading = false;
				}
				this.error = reject;
				this.status.next(EComponentStatuses.ERROR);
			})
			.finally(() => {
				if (options.withGlobalLoading) {
					this.isGlobalLoading = false;
				} else {
					this.isLoading = false;
				}
			});
	}

	private updatePriceInCostLine(): void {
		const totalPrice = MoneyFormatter.format(
			this.getTotalPrice().toString(),
			EMoneyFormatterTypes.THOUSAND_SUPERSCRIPT,
		)
		this.cartCostLine.updateItemValue(ECartCostLineNames.FULL_PRICE, totalPrice);
	}

	private getTotalPrice(): number {
		return this.items.reduce((previousValue, currentValue) => {
			const {quantity, product: {price}} = currentValue;
			return previousValue + (quantity * parseFloat(price))
		}, 0);
	}

	private reloadOnEmpty(): void {
		const accessoriesSkus = this.accessories.map(item => item.product.sku);
		const filteredItems = this.items.filter(item => !accessoriesSkus.includes(item.product.sku));
		if (filteredItems.length) {
			return;
		}
		window.location.reload();
	}

	private removeItem(item: TCartItem): void {
		const {url, method} = this.removeRequestConfig;
		const data: TCartRemoveRequest = {
			sku: item.product.sku,
		}
		this.isGlobalLoading = true;
		this.httpService
			.request<TCartRemoveResponse>({
				url: url,
				method: method,
				data: data,
			})
			.then((response) => {
				this.isGlobalLoading = false;
				this.cartQuantity.update(response.data.data.cartItemCount);
				this.getData({
					withGlobalLoading: true,
					reloadOnEmpty: true,
				});
				this.emitEventChange();
			})
			.catch(() => {
				this.isGlobalLoading = false;
			})
			.finally(() => {
				this.isGlobalLoading = false;
			});
	}

	private editItem(item: TCartItem, type: ECartEditTypes): void {
		const {url, method} = this.editRequestConfig;
		const data: TCartEditRequest = {
			sku: item.product.sku,
			type: type,
		};

		this.isGlobalLoading = true;
		this.httpService
			.request<TCartEditResponse>({
				url: url,
				method: method,
				data: data,
			})
			.then((response) => {
				this.isGlobalLoading = false;
				this.cartQuantity.update(response.data.data.cartItemCount);
				this.getData({
					withGlobalLoading: true,
					reloadOnEmpty: true,
				});
				this.emitEventChange();
			})
			.catch(() => {
				this.isGlobalLoading = false;
			})
			.finally(() => {
				this.isGlobalLoading = false;
			});
	}

	private addItem(item: TCartItem): void {
		const {url, method} = this.addRequestConfig;
		const data: TCartAddRequest = {
			sku: item.product.sku,
		}
		this.isGlobalLoading = true;
		this.httpService
			.request<TCartAddResponse>({
				url: url,
				method: method,
				data: data,
			})
			.then((response) => {
				this.isGlobalLoading = false;
				this.cartQuantity.update(response.data.data.cartItemCount);
				this.getData({
					withGlobalLoading: true,
					reloadOnEmpty: true,
				});
				this.emitEventChange();
			})
			.catch(() => {
				this.isGlobalLoading = false;
			})
			.finally(() => {
				this.isGlobalLoading = false;
			});
	}

	private emitEventChange(): void {
		const event: CustomEvent = new CustomEvent('on-change');
		this.dispatchEvent(event);
	}

	private attachEvent(): void {
		this.status
			.asObservable()
			.subscribe(status => {
				this.emitEventOnStatusChange(status);
			});
	}

	private emitEventOnStatusChange(status: EComponentStatuses): void {
		const event: CustomEvent<TEditProductsCartStatusChangeEventDetail> = new CustomEvent('on-status-change', {
			detail: {
				status: status,
			}
		});
		this.dispatchEvent(event);
	}
}
