import Badge from "@components/badge/Badge";
import {getDefinitionElement, isDefinitionElementEvent, strToBoolean} from "@helpers/index";
import {EWishlistStatus, TWishlistResponse} from "@models/Wishlist";
import HttpRequest from "@modules/http-request/HttpRequest";
import {Method} from "axios";
import {fromEvent} from "rxjs";
import {filter} from "rxjs/operators";

export default class Wishlist {
	private CLASS_WISHLIST: string = 'js-wishlist';
	private CLASS_BLOCK: string = 'js-wishlistBlock';
	private CLASS_HANDLER: string = 'js-wishlistHandler';
	private CLASS_FORM: string = 'js-wishlistForm';
	private CLASS_BADGE: string = 'js-wishlistBudge';

	private CLASS_ACTIVE: string = 'is-active';
	private CLASS_LOADING: string = 'is-loading';


	private DATA_ATTR_REMOVE_UNACTIVE: string = 'data-remove-unactive';

	private badges: Array<Badge> = [];
	private httpRequest: HttpRequest = new HttpRequest();

	public init(): void {
		const badgeEls: Array<HTMLElement> = Array.from(document.getElementsByClassName(this.CLASS_BADGE)) as Array<HTMLElement>;
		this.badges = badgeEls.map(badgeEl => new Badge(badgeEl));
		this.attachEvent();
	}

	private request(handler: HTMLButtonElement): void {
		if (handler.disabled) {
			return;
		}
		handler.disabled = true;
		handler.classList.add(this.CLASS_LOADING);
		const form: HTMLFormElement = handler.querySelector(`.${this.CLASS_FORM}`);
		this.httpRequest.request<TWishlistResponse, object>({
			method: form.method as Method,
			url: form.action,
			data: new FormData(form)
		})
			.then(response => {
				const status: EWishlistStatus = response.data.data.status;
				this.updateStatus(handler, status);
				this.updateBadges(response.data.data.quantity);
				this.updateIdenticalHandlerStatus(handler, status);
				this.handleRemoveUnactive(handler, status);
				if (status === EWishlistStatus.ADDED) {
					this.updateContent(handler);
				}
			})
			.catch(() => {
				//TODO: handle error
			})
			.finally(() => {
				handler.classList.remove(this.CLASS_LOADING);
				handler.disabled = false;
			})
	}

	private updateStatus(handler: HTMLElement, status: EWishlistStatus): void {
		switch (status) {
			case EWishlistStatus.ADDED:
				handler.classList.add(this.CLASS_ACTIVE);
				return;
			case EWishlistStatus.REMOVED:
				handler.classList.remove(this.CLASS_ACTIVE);
				return;
		}
	}

	private updateBadges(quantity: number): void {
		this.badges.forEach(badge => {
			badge.setValue(quantity == 0 ? '' : quantity.toString())
		});
	}

	private updateIdenticalHandlerStatus(handler: HTMLElement, status: EWishlistStatus): void {
		this.getIdenticalHandlers(handler)
			.forEach(identityHandler => this.updateStatus(identityHandler, status));
	}

	private getIdenticalHandlers(handler: HTMLElement): Array<HTMLElement> {
		const skuIdInput: HTMLInputElement = handler.querySelector(`input[name="skuId"]`);
		if (!skuIdInput) {
			return [];
		}
		const skuId: string = skuIdInput.value;
		const inputs: Array<HTMLInputElement> = Array.from(
			document.querySelectorAll(`.${this.CLASS_HANDLER} input[name="skuId"][value="${skuId}"]`)
		);
		return inputs
			.map(input => input.closest(`.${this.CLASS_HANDLER}`))
			.filter(el => el !== handler) as Array<HTMLElement>;
	}

	private handleRemoveUnactive(handler: HTMLElement, status: EWishlistStatus): void {
		if (
			status !== EWishlistStatus.REMOVED ||
			!strToBoolean(handler.getAttribute(this.DATA_ATTR_REMOVE_UNACTIVE))
		) {
			return;
		}

		const block: HTMLElement = handler.closest(`.${this.CLASS_BLOCK}`);
		if (block) {
			const parent = block.parentElement;
			block.remove();
			if (!parent.childElementCount) {
				const url: URL = new URL(window.location.href);
				url.searchParams.delete('page');
				window.location.href = url.href;
			}
		}
	}

	private attachEvent(): void {
		const selectorHandler: string = `.${this.CLASS_HANDLER}`;
		fromEvent(document, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, selectorHandler)),
			)
			.subscribe(event => {
				event.preventDefault();
				const handler: HTMLButtonElement = getDefinitionElement<HTMLButtonElement>(event.target as HTMLElement, selectorHandler);
				this.request(handler);
			})
	}

	private updateContent(handler: HTMLButtonElement): void {
		const cart: HTMLElement = document.querySelector(`.${this.CLASS_WISHLIST}`);
		if (!cart) {
			return;
		}
		window.location.reload();
	}
}
