import {EAlertMods, EAlertTypes} from "@components/alert/model";
import {
	EContractTariffsBasketBundlerPaymentMethods,
	TContractTariffsBasketBundlerAddToCardRequest,
	TContractTariffsBasketBundlerAddToCardResponse,
	TContractTariffsBasketBundlerPaymentMethod,
	TContractTariffsBasketBundlerResponse
} from "@components/contract-tariffs/contract-tariffs-basket-bundler/model";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {EOperatorCardDirection, TOperatorCard} from "@components/operator-card/model";
import ProductCostLineComponent from "@components/product-cost-line/ProductCostLineComponent";
import {TSelectTariffFormattedData, TSelectTariffPeriod} from "@components/select-tariff/model";
import {TTariffCard} from "@components/tariff-card/model";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {TOperator} from "@models/Operator";
import {TTariff, TTariffPeriod, TTariffTree} from "@models/Tariff";
import {TariffFormatter} from "@modules/formatter/TariffFormatter";
import HttpRequest from "@modules/http-request/HttpRequest";
import Mask, {EMaskTypes} from "@modules/mask/Mask";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosError} from "axios";
import {customElement, html, state, property, PropertyValues, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";
import {fromEvent, Subject} from "rxjs";

@customElement('c-contract-tariffs-basket-bundler')
export default class ContractTariffsBasketBundlerComponent extends WebComponentLite {
	@property({
		type: String,
		attribute: 'link-to-change-phone-number',
	})
	public linkToChangePhoneNumber: string;

	@property({
		type: Object,
		attribute: 'add-request-data',
	})
	private addRequestData: Object = {};

	@property({
		type: String,
		attribute: 'link-to-bundler-items',
	})
	public linkToBundlerItems: string;

	@property({
		type: String,
		attribute: 'add-to-cart-link',
	})
	public addToCartLink: string;

	@state()
	private data: TContractTariffsBasketBundlerResponse;

	@state()
	private isNetworkError: boolean = false;

	@state()
	private isLoading: boolean = false;

	@state()
	private isAddToCartRequesting: boolean = false;

	@state()
	private selectedPeriodValue: number;

	@state()
	private selectedPaymentMethod: TContractTariffsBasketBundlerPaymentMethod;

	@state()
	private selectedTariff: TTariff;

	@state()
	private formattedTariffTree: TSelectTariffFormattedData;

	private readonly productCostLine: ProductCostLineComponent;

	private httpService: HttpRequest = new HttpRequest();

	private onOptionChangeStream: Subject<void> = new Subject();

	constructor() {
		super();
		this.productCostLine = document.querySelector(`c-product-cost-line`);
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
		this.getData();
	}

	protected render(): unknown {
		if (this.isLoading) {
			return this.getLoadingTemplate();
		}

		if (this.isNetworkError) {
			return this.getNetworkErrorTemplate();
		}

		if (!this.data) {
			return this.getNoDataTemplate();
		}

		return this.getContentTemplate();
	}

	private getContentTemplate(): TemplateResult {
		return html`
			<div class="c-contract-tariffs-basket-bundler">
				<h2 class="c-contract-tariffs-basket-bundler__title">ВЫБЕРИТЕ КОНТРАКТНЫЙ ТАРИФ</h2>
				<div class="c-contract-tariffs-basket-bundler__content">
					<div class="c-contract-tariffs-basket-bundler__left">
						<div class="c-column c-column_gap-30">
							<div class="c-column__container">
								${this.getPhoneNumberTemplate()}
								${this.getPaymentTypesTemplate()}
								${this.formattedTariffTree?.periods?.length ? this.getPeriodTemplate() : null}
							</div>
						</div>
					</div>

					<div class="c-contract-tariffs-basket-bundler__right">
						${!this.formattedTariffTree?.periods?.length
							? this.getTariffConnectionResultMessageTemplate()
							: this.getTariffsTemplate()
						}
					</div>
				</div>
			</div>
		`;
	}

	private getPhoneNumberTemplate(): TemplateResult {
		const phoneNumber: string = Mask.getFormattedValue(
			this.data.phoneNumber,
			EMaskTypes.PHONE
		);

		return html`
			<section class="c-section">
				<header class="c-section__header u-d-flex u-flex-row u-justify-content-between u-align-items-center">
					<h4 class="c-section__title">Ваш номер телефона</h4>
					<a href="${this.linkToChangePhoneNumber}"
					   class="c-btn c-btn_link">
						<span class="c-btn__text">Изменить</span>
					</a>
				</header>
				<div class="c-section__body">
					<p class="c-contract-tariffs-basket-bundler__phone-number">
						${phoneNumber}
					</p>
				</div>
			</section>
		`;
	}

	private getOperatorTemplate(): TemplateResult {
		const operator: TOperator = this.data.tariffTree?.operator;
		const operatorCard: TOperatorCard = {
			img: {
				url: operator?.logo,
				altText: operator?.name,
			},
			title: operator?.name
		};

		return html`
			<section class="c-section">
				<header class="c-section__header">
					<h4 class="c-section__title">Ваш оператор</h4>
				</header>
				<div class="c-section__body">
					<c-operator-card .direction="${EOperatorCardDirection.HORIZONTAL}"
									 .data="${operatorCard}">
				</div>
			</section>
		`;
	}

	private getPaymentTypesTemplate(): TemplateResult {
		const paymentMethods: Array<TContractTariffsBasketBundlerPaymentMethod> = this.data.paymentMethods || [];

		return html`
			<section class="c-section">
				<header class="c-section__header">
					<h4 class="c-section__title">Способ оплаты</h4>
				</header>
				<div class="c-section__body">
					<div class="c-column c-column_gap-16">
						<div class="c-column__container">
							${paymentMethods.map(method => {
								return html`
									<c-toggle-card marker
												   type="radio"
												   name="payment-type"
												   .value="${method.id}"
												   ?checked="${method.id === this.selectedPaymentMethod?.id}"
												   @on-change="${this.handleClickPaymentMethod}">
										<div
											class="u-d-flex u-flex-row u-justify-content-between u-align-items-center u-h-100">
											<p>${method.label}</p>
										</div>
									</c-toggle-card>
								`;
							})}
						</div>
					</div>
				</div>
			</section>
		`;
	}

	private getPeriodTemplate(): TemplateResult {
		const periods = this.formattedTariffTree?.periods || [];

		return html`
			<section class="c-section">
				<header class="c-section__header">
					<h4 class="c-section__title">Срок Контракта</h4>
				</header>
				<div class="c-section__body">
					<ul class="c-operator-card-list">
						${periods.map(period =>
							html`
								<li class="c-operator-card-list__item">
									<c-toggle-card type="radio"
												   name="period"
												   class="u-w-100 u-h-100"
												   .value="${period.value}"
												   ?checked="${period.value == this.selectedPeriodValue}"
												   @on-change="${this.handleClickPeriod}">
										<div
											class="u-w-100 u-h-100 u-d-flex u-align-items-center u-justify-content-center">
											<c-term-card .data="${period}">
										</div>
									</c-toggle-card>
								</li>
							`
						)}
					</ul>
				</div>
			</section>
		`;
	}

	private getTariffsTemplate(): TemplateResult {
		const tariffs = this.formattedTariffTree.periods.find(period => period.value === this.selectedPeriodValue)?.tariffs || [];
		let show: boolean = true;

		if (!tariffs.length && !this.data.tariffTree.reasonAbsenceTariffs) {
			show = false;
		}

		if (!this.selectedPeriodValue || !show) {
			return null;
		}

		return html`
			<div class="c-column c-column_gap-16">
				<div class="c-column__container">
					${tariffs.length ?
						html`
							${tariffs.map(tariff =>
								html`
									<c-toggle-card marker
												   type="radio"
												   name="tariff"
												   .value="${tariff.id}"
												   ?checked="${tariff.id == this.selectedTariff?.id}"
												   @on-change="${this.handleClickTariff}">
										<c-tariff-card class="u-min-w-100"
													   .data="${tariff}">
									</c-toggle-card>
								`
							)}
						` :
						this.reasonAbsenceTariffsTemplate()
					}
				</div>
			</div>
		`
	}

	private reasonAbsenceTariffsTemplate(): TemplateResult {
		return html`
			<c-alert type="${EAlertTypes.WARNING}"
					 .mods="${[EAlertMods.SM]}">
				${this.data.tariffTree.reasonAbsenceTariffs}
			</c-alert>
		`
	}

	private getTariffConnectionResultMessageTemplate(): TemplateResult {
		return html`
			<p class="c-contract-tariffs-basket-bundler__tariff-connection-result-message">
				${unsafeHTML(
					this.data.tariffTree.tariffConnection?.message
				)}
			</p>
		`;
	}

	private getNetworkErrorTemplate(): TemplateResult {
		return html`
			<div class="c-contract-tariffs-basket-bundler">
				<c-network-error @on-click-reload="${this.handleBtnClickReload}">
			</div>
		`;
	}

	private getLoadingTemplate(): TemplateResult {
		return html`
			<div class="c-contract-tariffs-basket-bundler c-contract-tariffs-basket-bundler_loading">
				<c-loader active
						  type="${ELoaderType.GHOST}"
						  size="${ELoaderSize.LG}"
						  class="c-contract-tariffs-basket-bundler__loader">
			</div>
		`;
	}

	private getNoDataTemplate(): TemplateResult {
		return html`
			<div class="c-contract-tariffs-basket-bundler c-contract-tariffs-basket-bundler_loading">
				<p>Данных нет !</p>
			</div>
		`;
	}

	private getData(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.isNetworkError = false;
		const data: Object = {
			...this.addRequestData || {},
		};
		this.httpService
			.request<TContractTariffsBasketBundlerResponse>({
				url: this.linkToBundlerItems,
				method: 'GET',
				data: data,
			})
			.then((response) => {
				this.data = response.data.data;
				this.formattedTariffTree = this.getFormattedTariffTree(this.data.tariffTree);
				this.setFirstSelected();
			})
			.catch((reject: AxiosError) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
			})
			.finally(() => {
				this.isLoading = false;
			})
	}

	private addToCart(): void {
		if (this.isAddToCartRequesting) {
			return;
		}
		this.isAddToCartRequesting = true;
		this.productCostLine.isLoadingBtn = true;
		this.productCostLine.isDisableBtn = true;
		const data: TContractTariffsBasketBundlerAddToCardRequest = {
			phoneNumber: this.data.phoneNumber,
			tariff: this.selectedTariff,
			period: this.selectedPeriodValue,
			paymentMethod: this.selectedPaymentMethod,
		};
		this.httpService
			.request<TContractTariffsBasketBundlerAddToCardResponse>({
				url: this.addToCartLink,
				method: 'POST',
				data: data,
			})
			.then((response) => {
				//TODO: handle response
			})
			.catch((reject: AxiosError) => {
				//TODO: handle response
			})
			.finally(() => {
				this.isAddToCartRequesting = false;
				this.productCostLine.isLoadingBtn = false;
				this.productCostLine.isDisableBtn = false;
			})
	}

	private attachEvent(): void {
		this.onOptionChangeStream
			.asObservable()
			.subscribe(() => {
				this.updateCostInProductCostLine();
				this.updateBtnAvailableInProductCostLine();
			});

		fromEvent(this.productCostLine, 'on-btn-click')
			.subscribe(() => {
				this.addToCart();
			});
	}

	private getFormattedTariffTree(data: TTariffTree): TSelectTariffFormattedData {
		return {
			...data,
			periods: this.getFormattedPeriods(data.periods || [])
		}
	}

	private getFormattedPeriods(periods: Array<TTariffPeriod>): Array<TSelectTariffPeriod> {
		return periods?.map(item => {
			return {
				title: 'месяцев',
				value: item.value,
				tariffs: this.getFormattedTariffs(item.tariffs || [])
			}
		}) || [];
	}

	private getFormattedTariffs(tariffs: Array<TTariff>): Array<TTariffCard> {
		return tariffs?.map(item => TariffFormatter.toTariffCard(item, this.data.tariffTree.operator)) || [];
	}

	private setFirstSelected(): void {
		this.selectedPaymentMethod = this.data.paymentMethods[0];
		this.selectedPeriodValue = this.data.tariffTree.periods?.[0]?.value;
		this.selectedTariff = this.data.tariffTree.periods?.[0]?.tariffs?.[0];
		this.onOptionChangeStream.next();
	}

	private handleBtnClickReload(event: Event): void {
		event.preventDefault();
		this.getData();
	}

	private handleClickPaymentMethod(event: CustomEvent): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		this.selectedPaymentMethod = this.data.paymentMethods.find(item => item.id == toggle.value);
		this.onOptionChangeStream.next();
	}

	private handleClickTariff(event: CustomEvent): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		const tariffId: number = parseFloat(toggle.value);
		const period: TTariffPeriod = this.data.tariffTree.periods.find(item => item.value == this.selectedPeriodValue);
		const tariff: TTariff = period.tariffs.find(item => item.id == tariffId);
		this.selectedTariff = tariff;
		this.onOptionChangeStream.next();
	}

	private handleClickPeriod(event: CustomEvent): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		const periodValue: number = parseFloat(toggle.value);
		const period: TTariffPeriod = this.data.tariffTree.periods.find(item => item.value == periodValue);
		const firstTariff: TTariff = period.tariffs?.[0];

		this.selectedPeriodValue = periodValue;
		this.selectedTariff = firstTariff;
		this.onOptionChangeStream.next();
	}

	private getTariffPrice(paymentType: EContractTariffsBasketBundlerPaymentMethods): number {
		const tariff: TTariff = this.selectedTariff;
		if (!tariff) {
			return 0;
		}
		switch (paymentType) {
			case EContractTariffsBasketBundlerPaymentMethods.INSTALLMENT:
				return parseFloat(tariff?.pricePerMonth) ?? 0;
			case EContractTariffsBasketBundlerPaymentMethods.FULL_PRICE:
				return parseFloat(tariff?.price) ?? 0;
			default:
				return 0;
		}
	}

	private updateCostInProductCostLine(): void {
		let price: number = 0;
		let pricePerMonth: number = 0;
		const paymentMethod: TContractTariffsBasketBundlerPaymentMethod = this.selectedPaymentMethod;
		if (paymentMethod) {
			const tariffPrice = this.getTariffPrice(paymentMethod.type);

			if (paymentMethod.type === EContractTariffsBasketBundlerPaymentMethods.INSTALLMENT) {
				price = 0;
				pricePerMonth = tariffPrice;
			} else if (paymentMethod.type === EContractTariffsBasketBundlerPaymentMethods.FULL_PRICE) {
				price = tariffPrice;
				pricePerMonth = 0;
			}
		}

		this.productCostLine.price = price.toString();
		this.productCostLine.pricePerMonth = pricePerMonth.toString();
	}

	private updateBtnAvailableInProductCostLine(): void {
		const isAllSelected: boolean = Boolean(this.selectedTariff && this.selectedPaymentMethod)
		this.productCostLine.isDisableBtn = !isAllSelected;
	}
}
