import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property} from "lit-element";
import {TProduct} from "@models/Product";

@customElement('c-insurance-device-product-card')
export default class InsuranceDeviceProductCardComponent extends WebComponentLite {
	@property({
		type: Object,
	})
	private data: TProduct;

	protected render(): unknown {
		const {img, name} = this.data;

		return html`
			<div class="c-insurance-device-product-card">
				<div class="c-insurance-device-product-card__img-box">
					<img data-src="${img}"
						 alt="pic"
						 class="c-insurance-device-product-card__img u-shimmer js-lazyLoadImage">
				</div>
				<h4 class="c-insurance-device-product-card__name">${name}</h4>
			</div>
		`;
	}
}
