import {ICollapse, ICollapseFactory, TCollapseOption} from "@modules/collapse/model";
import Collapse from "@modules/collapse/Collapse";
import CollapseAnimated from "@modules/collapse/CollapseAnimated";

export default class CollapseFactory implements ICollapseFactory {
	public createCollapse(blockEl: HTMLElement, option: TCollapseOption): ICollapse {
		return new Collapse(blockEl, option)
	}

	public createCollapseAnimated(blockEl: HTMLElement, option: TCollapseOption): ICollapse {
		return new CollapseAnimated(blockEl, option);
	}
}
