import {ECollapseState, ICollapse} from "@modules/collapse/model";
import {IBtnSwitch} from "@modules/btn/model";
import CollapseAnimated from "@modules/collapse/CollapseAnimated";
import BtnSwitch from "@modules/btn/BtnSwitch";
import {fromEvent} from "rxjs";

export default class CollapseRemoteWithBtnSwitch {
	private DATA_ATTR_BTN_HANDLER_CLASS: string = 'data-collapse-remote-with-btn-switch-handler';

	private collapseEl: HTMLElement;
	private collapse: ICollapse;
	private btnSwitch: IBtnSwitch;

	private btnHandlerEl: HTMLButtonElement;

	constructor(collapseEl: HTMLElement) {
		this.collapseEl = collapseEl;
		this.collapse = new CollapseAnimated(collapseEl);
		const classBtnHandler: string = collapseEl.getAttribute(this.DATA_ATTR_BTN_HANDLER_CLASS);
		this.btnHandlerEl = document.querySelector(`.${classBtnHandler}`);
		this.btnSwitch = new BtnSwitch(this.btnHandlerEl);
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.btnHandlerEl, 'click')
			.subscribe(event => {
				event.preventDefault();
				this.toggle();
			});
	}

	private toggle(): void {
		if (this.collapse.getState() === ECollapseState.OPEN) {
			this.close();
		} else {
			this.open();
		}
	}

	private open(): void {
		this.collapse.open();
		this.btnSwitch.switch(false);
	}

	private close(): void {
		this.collapse.close();
		this.btnSwitch.switch(true);
	}
}
