import {Observable, Subject} from "rxjs";
import {IResize} from "@modules/resize/model";

export default class Resize implements IResize {
	private $stream: Subject<ResizeObserverEntry> = new Subject<ResizeObserverEntry>();

	private resizeObserver: ResizeObserver;

	constructor(element: Element) {
		this.resizeObserver = new ResizeObserver(entries => {
			for (let entry of entries) {
				this.$stream.next(entry);
			}
		});
		this.resizeObserver.observe(element);
	}

	public getStream(): Observable<ResizeObserverEntry> {
		return this.$stream.asObservable();
	}
}
