import {IVisibility} from "@modules/visibility/model";

export default class Visibility implements IVisibility {
	private CLASS_HIDE: string = 'u-d-none';

	public show(el: HTMLElement | Array<HTMLElement>) {
		if (Array.isArray(el)) {
			el.forEach(item => item.classList.remove(this.CLASS_HIDE))
		} else {
			el.classList.remove(this.CLASS_HIDE);
		}
	}

	public hide(el: HTMLElement | Array<HTMLElement>) {
		if (Array.isArray(el)) {
			el.forEach(item => item.classList.add(this.CLASS_HIDE))
		} else {
			el.classList.add(this.CLASS_HIDE);
		}
	}

}
