import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, PropertyValues, state, TemplateResult} from "lit-element";
import {TInsuranceDevice, TInsurancePeriod} from "@models/Insurance";
import {ETermCardSizes} from "@components/term-card/model";
import {isFunction, pluralize} from "@helpers/index";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {TInsuranceDeviceTypeComponentInitSelected} from "@components/select-insurance/device/model";
import {TProduct} from "@models/Product";
import {TSelectInsuranceSelectedDevice} from "@components/select-insurance/model";

@customElement('c-insurance-device-type')
export default class InsuranceDeviceTypeComponent extends WebComponentLite {
	@property({
		type: Object,
	})
	public data: TInsuranceDevice;

	@property({
		type: Object,
		attribute: 'init-selected',
	})
	public initSelected: TInsuranceDeviceTypeComponentInitSelected;

	@property({
		type: Object,
		reflect: true,
	})
	public syncSelectedData: TSelectInsuranceSelectedDevice;

	@property({
		type: Object,
		reflect: true,
	})
	public emitEventChange: () => void;

	@state()
	private selectedPeriod: TInsurancePeriod;

	@state()
	private selectedProducts: Array<TProduct> = [];

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
	}

	protected beforeFirstRendered() {
		super.beforeFirstRendered();
		this.setInitSelected();
	}

	private setFirstSelectedPeriod(): void {
		this.selectedPeriod = this.data.periods?.[0];
		this.syncSelectedData.period = this.selectedPeriod.value;
	}

	private setInitSelected(): void {
		const initSelected = this.initSelected;
		if (!initSelected || !Object.entries(initSelected).length) {
			this.setFirstSelectedPeriod();
			return;
		}
		this.selectedPeriod = this.data.periods.find(item => item.value === initSelected.period);
		this.selectedProducts = this.data.products.length
			? initSelected.productSkus?.map(sku => {
				return this.data.products.find(product => product.sku === sku);
			}).filter(Boolean)
			: [];
		this.syncSelectedData.period = this.selectedPeriod.value;
		this.syncSelectedData.products = this.selectedProducts;
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-insurance-device-type">
				${this.getProductsSectionTemplate()}
				${this.getPeriodSectionTemplate()}
			</div>
		`;
	}

	private getProductsSectionTemplate(): TemplateResult {
		const selectedProductSkus = this.selectedProducts.map(item => item.sku);

		return html`
			<section class="c-insurance-device-type__section">
				<h4 class="c-insurance-device-type__section-title">Выберите устройства, которые хотите застраховать</h4>

				<div class="c-insurance-device-type__section-content">
					<c-insurance-device-product-carousel .data="${this.data.products}"
														 .insurance="${this.data}"
														 .selectedSkus="${selectedProductSkus}"
														 .onChange="${this.handleChangeSelectedProduct.bind(this)}">
				</div>
			</section>
		`;
	}

	private handleChangeSelectedProduct(product: TProduct, checked: boolean): boolean {
		if (checked) {
			this.selectedProducts = [
				...this.selectedProducts,
				product,
			];
		} else {
			const existIndex = this.selectedProducts.findIndex(item => item.sku == product.sku);
			if (existIndex != -1) {
				this.selectedProducts.splice(existIndex, 1);
				this.selectedProducts = [...this.selectedProducts];
			}
		}
		this.syncSelectedData.products = this.selectedProducts;
		this.dispatchEmitEventChange();
		return !checked;
	}

	private getPeriodSectionTemplate(): TemplateResult {
		return html`
			<section class="c-insurance-device-type__section">
				<h4 class="c-insurance-device-type__section-title">Срок страхования от повреждений</h4>
				<p class="c-insurance-device-type__section-sub-title">
					Внимание! Страховка от повреждений
					распространяется только на смартфоны, на аксессуары не распространяется.
				</p>

				<div class="c-insurance-device-type__section-content">
					<ul class="c-insurance-device-type__periods">
						${this.data.periods.map(period =>
								html`
									<li class="c-insurance-device-type__periods-item">
										<c-toggle-card type="radio"
													   name="${`insurance-${this.data.id}-device-period`}"
													   class="u-w-100 u-h-100"
													   .value="${period.value}"
													   ?checked="${this.selectedPeriod?.value == period.value}"
													   @on-change="${(event) => this.handleClickPeriod(event, period)}">
											<div
													class="u-w-100 u-h-100 u-d-flex u-align-items-center u-justify-content-center">
												<c-term-card .size="${ETermCardSizes.MEDIUM}"
															 .data="${{
																 value: period.value,
																 title: pluralize(period.value, 'месяц', 'месяца', 'месяцев'),
															 }}">
											</div>
										</c-toggle-card>
									</li>
								`
						)}
					</ul>
				</div>
			</section>
		`;
	}

	private handleClickPeriod(event: Event, period: TInsurancePeriod): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		this.selectedPeriod = period;
		this.syncSelectedData.period = period.value;
		this.dispatchEmitEventChange();
	}

	private dispatchEmitEventChange(): void {
		if (this.emitEventChange && isFunction(this.emitEventChange)) {
			this.emitEventChange()
		}
	}
}
