import ProductCostLineComponent from "@components/product-cost-line/ProductCostLineComponent";
import {getDefinitionElement, isDefinitionElementEvent} from "@helpers/index";
import {TCartAddResponse} from "@models/Cart";
import CartQuantity from "@modules/cart/CartQuantity";
import ProductCartHandler from "@modules/cart/ProductCartHandler";
import HttpRequest from "@modules/http-request/HttpRequest";
import {Method} from "axios";
import {fromEvent} from "rxjs";
import {filter} from "rxjs/operators";

export default class AddToCart {
	private CLASS_HANDLER: string = 'js-addToCartHandler';
	private CLASS_HANDLER_ON_PRODUCT_PAGE: string = 'js-addToCartHandlerOnProductPage'
	private CLASS_FORM: string = 'js-addToCartForm';

	private productCostLine: ProductCostLineComponent;
	private httpRequest: HttpRequest = new HttpRequest();
	private handlerOnProductPage: HTMLButtonElement;
	private cartQuantity: CartQuantity = CartQuantity.getInstance();
	private productCartHandler: ProductCartHandler = ProductCartHandler.getInstance();

	public init(): void {
		this.productCostLine = document.querySelector(`c-product-cost-line`);
		this.handlerOnProductPage = document.querySelector(`.${this.CLASS_HANDLER_ON_PRODUCT_PAGE}`);
		this.attachEvent();
	}

	private request(handler: HTMLButtonElement): void {
		if (handler.disabled) {
			return;
		}
		this.productCartHandler.setLoader(handler);
		this.productCartHandler.setDisabled(handler);
		const form: HTMLFormElement = handler.querySelector(`.${this.CLASS_FORM}`);

		this.httpRequest.request<TCartAddResponse, object>({
			method: form.method as Method,
			url: form.action,
			data: new FormData(form)
		})
			.then(response => {
				this.cartQuantity.update(response.data.data.cartItemCount);
				this.productCartHandler.setActive(this.getSkuByHandler(handler), handler);
			})
			.catch(() => {
				//TODO: handle error
			})
			.finally(() => {
				this.productCartHandler.removeLoader(handler);
				this.productCartHandler.removeDisabled(handler);
			});
	}

	private getSkuByHandler(handler: HTMLElement): string {
		const skuInput: HTMLInputElement = handler.querySelector(`input[name="sku"]`);
		if (!skuInput) {
			return '';
		}
		return skuInput.value;
	}

	private attachEvent(): void {
		const selectorHandler: string = `.${this.CLASS_HANDLER}`;
		fromEvent(document, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, selectorHandler)),
			)
			.subscribe(event => {
				event.preventDefault();
				const handler: HTMLButtonElement = getDefinitionElement<HTMLButtonElement>(event.target as HTMLElement, selectorHandler);
				this.request(handler);
			});

		if (this.productCostLine) {
			fromEvent(this.productCostLine, 'on-btn-click')
				.subscribe(() => {
					const handler: HTMLButtonElement = document.querySelector(`.${this.CLASS_HANDLER_ON_PRODUCT_PAGE}`);
					this.request(handler);
				});
		}
	}
}
