import ToggleChange from "@modules/toggle/ToggleChange";
import {TToggleChangeStreamOnChange} from "@modules/toggle/model";

export enum EToggleGridViewStyle {
	LIST = 'LIST',
	TILE = 'TILE',
}

export default class ToggleGridView extends ToggleChange {
	private CLASS_GRID: string = 'js-gridViewSwitch';

	private CLASS_STYLE_TILE: string = 'c-product-card-grid_tile';
	private CLASS_STYLE_LIST: string = 'c-product-card-grid_list';

	constructor() {
		super({
			classHandler: 'js-gridViewSwitchHandler',
			hasDynamicActivated: true
		});
		this.subscribeChanges();
	}

	private subscribeChanges(): void {
		this.getStreamOnChange()
			.subscribe(streamOnChange => {
				this.onChange(streamOnChange);
			})
	}

	private onChange(streamOnChange: TToggleChangeStreamOnChange) {
		const grid: HTMLElement = document.querySelector(`.${this.CLASS_GRID}`);
		const value: string = streamOnChange.input.value || '';
		const style: EToggleGridViewStyle = this.getStyle(value);
		if (streamOnChange.state) {
			this.switchStyle(grid, style);
		}
	}

	private switchStyle(grid: HTMLElement, style: EToggleGridViewStyle): void {
		this.cleatStyles(grid);
		grid?.classList.add(this.getClassStyle(style));
	}

	private cleatStyles(grid: HTMLElement): void {
		grid?.classList.remove(this.CLASS_STYLE_TILE);
		grid?.classList.remove(this.CLASS_STYLE_LIST);
	}

	private getClassStyle(style: EToggleGridViewStyle): string {
		switch (style) {
			case EToggleGridViewStyle.LIST:
				return this.CLASS_STYLE_LIST;
			case EToggleGridViewStyle.TILE:
				return this.CLASS_STYLE_TILE;
			default:
				return this.CLASS_STYLE_TILE;
		}
	}

	private getStyle(value: string): EToggleGridViewStyle {
		value = value.toUpperCase();
		if (value in EToggleGridViewStyle) {
			return value as EToggleGridViewStyle;
		}
		return EToggleGridViewStyle.TILE;
	}
}
