import {EBtnSize, EBtnType} from "@components/btn/model";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {
	TPaymentMethodLoanPeriod,
	TPaymentMethodLoanPeriodCredit,
	TPaymentMethodLoanResponse
} from "@components/payment-method/loan/model";
import PaymentMethodComponent from "@components/payment-method/PaymentMethodComponent";
import {ETermCardSizes} from "@components/term-card/model";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {pluralize} from "@helpers/index";
import {ELoanTypes} from "@models/Loan";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import HttpRequest from "@modules/http-request/HttpRequest";
import {AxiosRequestConfig} from "axios";
import {customElement, html, property, PropertyValues, state, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";
import {EComponentStatuses} from "@models/Status";

@customElement('c-payment-method-loan')
export default class PaymentMethodLoanComponent extends PaymentMethodComponent {
	@property({
		type: String,
	})
	public type: ELoanTypes;

	@property({
		type: Object,
		attribute: 'get-request-config',
	})
	public getRequestConfig: AxiosRequestConfig = {
		url: '/cart/payment-method/loan',
		method: "GET",
	}

	@property({
		type: Number,
		attribute: 'init-selected-period',
	})
	public initSelectedPeriod: number;

	@state()
	private isLoading: boolean = false;

	@state()
	private error: unknown;

	@state()
	private periods: Array<TPaymentMethodLoanPeriod> = [];

	@state()
	private selectedPeriod: TPaymentMethodLoanPeriod;

	private httpService: HttpRequest = new HttpRequest();

	private isFirstLoaded: boolean;

	public updateData(): void {
		this.getData();
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.getData();
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);

		if (name === 'type') {
			this.getData();
		}
	}

	private getData(): void {
		if (this.isLoading) {
			return;
		}
		this.status.next(EComponentStatuses.LOADING);
		this.isLoading = true;
		this.error = null;
		const {url, method} = this.getRequestConfig;
		this.httpService
			.request<TPaymentMethodLoanResponse>({
				url: url,
				method: method,
				params: {
					type: this.type
				}
			})
			.then(response => {
				this.periods = response.data.data.periods || [];
				this.setFirstSelectedPeriod();
				this.isFirstLoaded = true;
				this.status.next(EComponentStatuses.READY);
			})
			.catch(reject => {
				this.error = reject;
				this.status.next(EComponentStatuses.ERROR);
			})
			.finally(() => {
				this.isLoading = false;
			});
	}

	private setFirstSelectedPeriod(): void {
		let period;
		const firstPeriod = this.periods?.[0];
		const updatedCurrentSelectedPeriod = this.periods.find(item => item.value === this.selectedPeriod?.value);
		if (!this.isFirstLoaded) {
			period = this.initSelectedPeriod ? this.periods.find(item => item.value == this.initSelectedPeriod) : firstPeriod;
		} else {
			period = this.selectedPeriod ? updatedCurrentSelectedPeriod : firstPeriod;
		}
		this.selectedPeriod = period;
	}

	protected render(): unknown {
		let template: TemplateResult;

		if (this.isLoading) {
			template = this.getLoaderTemplate();
		} else if (this.error) {
			template = this.getErrorTemplate();
		} else {
			template = this.getContentTemplate();
		}

		return html`
			<div class="c-payment-method-loan">
				${template}
			</div>
		`;
	}

	private getContentTemplate(): TemplateResult {
		const pricePerMonth = MoneyFormatter.format(
			this.selectedPeriod?.pricePerMonth || '0',
			EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);
		const price = MoneyFormatter.format(
			'0',
			EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);
		const overpayment = this.type === ELoanTypes.CREDIT
			? (<TPaymentMethodLoanPeriodCredit>this.selectedPeriod)?.overpayment ?? 0
			: 0;
		return html`
			<div class="c-payment-method-loan__periods">
				<h4 class="c-payment-method-loan__periods-title">
					Срок ${this.type === ELoanTypes.CREDIT ? 'кредита' : 'рассрочки'}
				</h4>
				<ul class="c-payment-method-loan__periods-list">
					${this.periods.map(period =>
						html`
							<li class="c-payment-method-loan__periods-item">
								<c-toggle-card type="radio"
											   name="period"
											   class="u-w-100 u-h-100"
											   .value="${period.value}"
											   .pricePerMonth="${period.pricePerMonth}"
											   ?checked="${this.selectedPeriod?.value == period.value}"
											   @on-change="${(event) => this.handleClickPeriod(event, period)}">
									<div
										class="u-w-100 u-h-100 u-d-flex u-align-items-center u-justify-content-center">
										<c-term-card .size="${ETermCardSizes.MEDIUM}"
													 .data="${{
														 value: period.value,
														 title: pluralize(period.value, 'месяц', 'месяца', 'месяцев'),
													 }}">
									</div>
								</c-toggle-card>
							</li>
						`
					)}
				</ul>
			</div>
			<div class="c-payment-method-loan__cost">
				<div class="c-payment-method-loan__cost-item">
					<h4 class="c-payment-method-loan__cost-title">Ежемесячный платеж</h4>
					<div class="c-payment-method-loan__cost-value">
						${unsafeHTML(pricePerMonth)}
					</div>
				</div>
				<div class="c-payment-method-loan__cost-item">
					<h4 class="c-payment-method-loan__cost-title">К оплате сейчас</h4>
					<div class="c-payment-method-loan__cost-value">
						${unsafeHTML(price)}
					</div>
				</div>
				${this.type === ELoanTypes.CREDIT
					? html`
						<div class="c-payment-method-loan__cost-item">
							<h4 class="c-payment-method-loan__cost-title">Переплата</h4>
							<div class="c-payment-method-loan__cost-value">
								${overpayment}
								<sup>%</sup>
							</div>
						</div>
					`
					: null
				}
			</div>
		`;
	}

	private getLoaderTemplate(): TemplateResult {
		return html`
			<c-loader class="c-payment-method-loan__loader"
					  active
					  type="${ELoaderType.PRIMARY}"
					  size="${ELoaderSize.MD}">
			</c-loader>
		`;
	}

	private getErrorTemplate(): TemplateResult {
		return html`
			<div class="c-payment-method-loan__retry">
				<h4>При загрузке данных произошла ошибка !</h4>
				<p class="u-mb-20">Проверьте подключение к интернету</p>
				<c-btn type="${EBtnType.PRIMARY}"
					   size="${EBtnSize.MD}"
					   @click="${this.handleClickBtnReload}">
					Повторить
				</c-btn>
			</div>
		`;
	}

	private handleClickBtnReload(event: Event): void {
		event.preventDefault();
		this.getData();
	}

	private handleClickPeriod(event: CustomEvent, period: TPaymentMethodLoanPeriod): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		this.selectedPeriod = period;
	}
}
