import DialogComponent from "@components/dialog/DialogComponent";
import {customElement, html, property, PropertyValues, query, TemplateResult} from "lit-element";
import {fromEvent, Subject} from "rxjs";
import {EConfirmDialogActions, TConfirmDialogAction} from "@components/confirm-dialog/model";
import {IBtn} from "@modules/btn/model";
import Btn from "@modules/btn/Btn";

@customElement('c-confirm-dialog')
export default class ConfirmDialogComponent extends DialogComponent {
	@property({
		type: String,
		reflect: true
	})
	public title: string;

	@property({
		type: String,
		attribute: 'btn-cancel-text',
	})
	public btnCancelText: string = 'Cancel';

	@property({
		type: String,
		attribute: 'btn-confirm-text',
	})
	public btnConfirmText: string = 'Confirm';

	@property({
		type: Function,
		attribute: 'on-cancel',
	})
	public onCancel: TConfirmDialogAction

	@property({
		type: Function,
		attribute: 'on-confirm',
	})
	public onConfirm: TConfirmDialogAction

	@query('[confirm-dialog-btn-cancel]')
	private btnCancelEl: HTMLButtonElement;

	@query('[confirm-dialog-btn-confirm]')
	private btnConfirmEl: HTMLButtonElement;

	maskClosable = false;
	baseClassName = 'c-confirm-dialog';
	private isActionLoadingType: EConfirmDialogActions;
	private btnCancel: IBtn;
	private btnConfirm: IBtn;
	private _result: boolean;
	private streamResult: Subject<boolean> = new Subject<boolean>();

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
		this.btnCancel = new Btn(this.btnCancelEl);
		this.btnConfirm = new Btn(this.btnConfirmEl);
	}

	public result(): Promise<unknown> {
		return new Promise((resolve, reject) => {
			const sub = this.streamResult.asObservable().subscribe(value => {
				sub.unsubscribe();
				resolve(value);
			}, (error) => {
				sub.unsubscribe();
				reject(error);
			});
		});
	}

	open() {
		this._result = null;
		super.open();
	}

	protected afterClose() {
		super.afterClose();
		this.streamResult.next(this._result);
	}

	protected getContent(): TemplateResult {
		return html`
			${this.getHeader()}
			${this.getBody()}
			${this.getFooter()}
		`;
	}

	protected getHeader(): TemplateResult {
		return html`
			<header class="c-confirm-dialog__header js-dialogHeader">
				<h4 class="c-confirm-dialog__title">${this.title}</h4>
			</header>
		`;
	}

	protected getBody(): TemplateResult {
		const body = this.getSlot('body');

		return html`
			<div class="c-confirm-dialog__body js-dialogBody">
				${body}
			</div>
		`;
	}

	protected getFooter(): TemplateResult {
		const btnCancel = this.getSlot('btn-cancel');
		const btnConfirm = this.getSlot('btn-confirm');

		return html`
			<footer class="c-confirm-dialog__footer js-dialogFooter">
				<div class="c-confirm-dialog__action">
					${btnCancel ?? this.getBtnCancel()}
				</div>
				<div class="c-confirm-dialog__action">
					${btnConfirm ?? this.getBtnConfirm()}
				</div>
			</footer>
		`;
	}

	protected getBtnCancel(): TemplateResult {
		return html`
			<button confirm-dialog-btn-cancel
					class="c-btn c-btn_secondary c-btn_md c-btn_block">
				<div class="c-btn__loader">
					<i class="o-icon o-icon_loader"></i>
				</div>
				<span class="c-btn__text">${this.btnCancelText}</span>
			</button>
		`;
	}

	protected getBtnConfirm(): TemplateResult {
		return html`
			<button confirm-dialog-btn-confirm
					class="c-btn c-btn_primary c-btn_md c-btn_block">
				<div class="c-btn__loader">
					<i class="o-icon o-icon_loader"></i>
				</div>
				<span class="c-btn__text">${this.btnConfirmText}</span>
			</button>
		`;
	}

	protected handleClickBtnCancel(event: Event): void {
		if ((event.currentTarget as HTMLElement).tagName.toLowerCase() !== 'a') {
			event.preventDefault();
		}
		this._result = false;
		this.handleBtnAction(EConfirmDialogActions.CANCEL, this.btnCancel, this.onCancel);
	}

	protected handleClickBtnConfirm(event: Event): void {
		if ((event.currentTarget as HTMLElement).tagName.toLowerCase() !== 'a') {
			event.preventDefault();
		}
		this._result = true;
		this.handleBtnAction(EConfirmDialogActions.CONFIRM, this.btnConfirm, this.onConfirm);
	}

	protected handleBtnAction(type: EConfirmDialogActions, btn: IBtn, action: TConfirmDialogAction): void {
		if (this.isActionLoadingType) {
			return;
		}
		if (action && typeof action === 'function') {
			const res = action();
			if (res instanceof Promise) {
				btn.addLoader();
				this.isActionLoadingType = type;
				this.setDisableBtnActionByType(type, true);
				const maskClosable = this.maskClosable;
				if (maskClosable) {
					this.maskClosable = false;
				}
				res
					.then((status) => {
						if (status) {
							this.close();
						}
						btn.removeLoader();
						this.isActionLoadingType = null;
						this.setDisableBtnActionByType(type, false);
						this.maskClosable = maskClosable;
					})
					.catch(() => {
						btn.removeLoader();
						this.isActionLoadingType = null;
						this.setDisableBtnActionByType(type, false);
						this.maskClosable = maskClosable;
					});
			} else {
				if (res) {
					this.close();
				}
			}
		} else {
			this.close();
		}
	}

	private setDisableBtnActionByType(type: EConfirmDialogActions, disabled: boolean): void {
		const btn: IBtn = type == EConfirmDialogActions.CANCEL ? this.btnCancel : this.btnConfirm;
		if (disabled) {
			btn.disable();
		} else {
			btn.enable();
		}
	}

	protected attachEvent(): void {
		fromEvent(this.btnCancelEl, 'click')
			.subscribe((event) => {
				this.handleClickBtnCancel(event);
			});

		fromEvent(this.btnConfirmEl, 'click')
			.subscribe((event) => {
				this.handleClickBtnConfirm(event);
			});
	}
}
