import {TWebComponentLiteSlotMap} from "@modules/web-component/model";
import {LitElement} from "lit-element";

export default class WebComponentLite extends LitElement {
	private slotMap: TWebComponentLiteSlotMap;
	private slotListMap: Record<string, Array<Element>>;
	private elementChildren: Array<ChildNode> = [];
	private isFirstRendered: boolean = false;

	public connectedCallback() {
		super.connectedCallback();
		if (!this.elementChildren.length) {
			this.elementChildren = this.getElementChildren();
		}
		this.slotMap = this.getSlotMap();
		this.slotListMap = this.getSlotListMap();

		if (!this.isFirstRendered) {
			this.beforeFirstRendered();
		}
		this.isFirstRendered = true;
	}

	private getSlotMap(): TWebComponentLiteSlotMap {
		return Array
			.from(this.children)
			.filter(element => element.hasAttribute('slot'))
			.reduce((map, obj) => ({
				...map,
				[obj.getAttribute('slot')]: obj
			}), {});
	}

	private getSlotListMap(): Record<string, Array<Element>> {
		const listMap: Record<string, Array<Element>> = {};
		Array
			.from(this.children)
			.filter(element => element.hasAttribute('slot'))
			.forEach(element => {
				const name = element.getAttribute('slot');
				listMap[name] = [
					...listMap[name] ?? [],
					element,
				];
			});
		return listMap;
	}

	private getElementChildren(): Array<ChildNode> {
		return Array
			.from(this.childNodes)
			.filter(element => {
				if (element instanceof Element || element instanceof HTMLElement) {
					return !element.hasAttribute('slot')
				}
				return true;
			});
	}

	protected getSlot(slotName: string): ChildNode | null {
		return this.slotMap[slotName] || null;
	}

	protected getSlotList(slotName: string): Array<ChildNode> {
		return this.slotListMap[slotName] || [];
	}

	protected getChildren(): Array<ChildNode> {
		return this.elementChildren;
	}

	protected createRenderRoot() {
		return this;
	}

	protected beforeFirstRendered(): void {

	}
}
