import {EBtnSize, EBtnType} from "@components/btn/model";
import {TEnterPhoneNumberActionBtnClickEventDetail} from "@components/enter-phone-number/model";
import InputComponent from "@components/input/InputComponent";
import {EMaskTypes} from "@modules/mask/Mask";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, internalProperty, property, PropertyValues, query, TemplateResult} from "lit-element";

@customElement('c-enter-phone-number-without-button')
export default class EnterPhoneNumberComponent extends WebComponentLite {
	@property({
		type: String,
		attribute: 'phone-number-value',
		reflect: true,
	})
	public phoneNumberValue: string = '';

	@property({
		type: String,
		attribute: 'phone-number-name',
	})
	public phoneNumberName: string = 'phoneNumber';

	@property({
		type: String,
		attribute: 'phone-number-label',
	})
	public phoneNumberLabel: string = 'Номер телефона';

	@property({
		type: String,
		attribute: 'action-btn-text',
	})
	public actionBtnText: string = 'Получить код';

	@property({
		type: Boolean,
		attribute: 'loading-btn',
		reflect: true,
	})
	public loadingBtn: boolean = false;

	@property({
		type: Boolean,
		attribute: 'disable-btn',
		reflect: true,
	})
	public disableBtn: boolean = false;

	@query('[enter-phone-number-input-field]')
	private fieldPhoneNumber: InputComponent;

	@internalProperty()
	private isCompletePhoneNumber: boolean = false;

	public getMaskedPhoneNumber(): string {
		return this.fieldPhoneNumber.getValue();
	}

	public focus(): void {
		this.fieldPhoneNumber
			.updateComplete
			.then(() => {
				this.fieldPhoneNumber.focus();
			});
	}

	protected render(): unknown {
		return html`
			<div class="c-enter-phone-number">
				${this.getPhoneNumberFieldTemplate()}
			
			</div>
		`;
	}

	protected getPhoneNumberFieldTemplate(): TemplateResult {
		return html`
			<c-form-item class="c-enter-phone-number__phone-number-field">
				<c-input enter-phone-number-input-field
						 .label="${this.phoneNumberLabel}"
						 .name="${this.phoneNumberName}"
						 .value="${this.phoneNumberValue}"
						 type="tel"
						 fieldClass=""
						 .maskType="${EMaskTypes.PHONE}"
						 @on-change="${this.handleInputFiledChange}">
				</c-input>
			</c-form-item>
		`
	}

	protected getActionBtn(): TemplateResult {
		return html`
			<c-btn class="c-enter-phone-number__btn"
				   .type="${this.isDisabledActionBtn() ? EBtnType.SECONDARY : EBtnType.PRIMARY}"
				   .size="${EBtnSize.MD}"
				   ?loading="${this.isLoadingActionBtn()}"
				   ?disabled="${this.isDisabledActionBtn()}"
				   @click="${this.handleClickActionBtn}">
				${this.actionBtnText}
			</c-btn>
		`;
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
	}

	private attachEvent(): void {
		this.fieldPhoneNumber
			.updateComplete
			.then(() => {
				this.isCompletePhoneNumber = this.fieldPhoneNumber.getMask()?.masked.isComplete;
				this.fieldPhoneNumber.getMask()?.on('accept', () => {
					this.isCompletePhoneNumber = false;
				});
				this.fieldPhoneNumber.getMask()?.on('complete', () => {
					this.isCompletePhoneNumber = true;
				});
			});
	}

	protected isLoadingActionBtn(): boolean {
		return this.loadingBtn;
	}

	protected isDisabledActionBtn(): boolean {
		if (!this.isCompletePhoneNumber || this.disableBtn) {
			return true;
		}
		return false;
	}

	protected handleClickActionBtn(event: Event): void {
		event.preventDefault();
		if (this.isDisabledActionBtn()) {
			return;
		}
		this.emitEventActionBtnClick();
	}

	protected handleInputFiledChange(event: Event): void {
		this.phoneNumberValue = this.fieldPhoneNumber.value;
	}

	private emitEventActionBtnClick(): void {
		const event = new CustomEvent<TEnterPhoneNumberActionBtnClickEventDetail>('on-action-btn-click', {
			detail: {
				phoneNumber: this.fieldPhoneNumber.getMask().unmaskedValue,
				maskedPhoneNumber: this.phoneNumberValue,
			},
		});
		this.dispatchEvent(event);
	}
}
