import {TToast} from "@modules/toast/model";

export type TRequestResponse<TData = any, TMeta extends TRequestResponseMetadata = TRequestResponseMetadata> = {
	data: TData
	metadata: TMeta
}

export type TRequestResponseMetadata = {
	toasts?: Array<TToast>
	redirectURL?: string
	errorFields?: Array<TErrorField>
	dataType?: 'json' | 'html' | 'string' | 'array';
}

export type TErrorField = {
	fieldNames: Array<string>
	messages: Array<string>
	key?: string
}

export enum ERequestResponseStatus {
	SUCCESS,
	ERROR,
}
