import {IModal, TModalOptions} from "@modules/modal/model";
import Dialog from "@modules/dialog/Dialog";

export default class Modal extends Dialog<TModalOptions> implements IModal {
	private contentEl: HTMLElement;
	private headerEl: HTMLElement;
	private extraTopEl: HTMLElement;
	private bodyEl: HTMLElement;
	private footerEl: HTMLElement;
	private titleEl: HTMLElement;

	constructor(options?: TModalOptions) {
		super({
			element: "create",
			modifications: [],
			maskClosable: true,
			closable: true,
			classes: {
				dialog: 'js-modal',
				view: 'js-modalView',
				content: 'js-modalContent',
				header: 'js-modalHeader',
				extraTop: 'js-modalExtraTop',
				body: 'js-modalBody',
				footer: 'js-modalFooter',
				title: 'js-modalTitle',
			},
			actionClasses: {
				close: 'js-modalClose',
				closeIcon: 'js-modalCloseIcon',
			},
			...options,
		});
		this.findModalElements();
		this.setBackdrop();
	}

	public getContentEl(): HTMLElement {
		return this.contentEl;
	}

	public getHeaderEl(): HTMLElement {
		return this.headerEl;
	}

	public getExtraTopEl(): HTMLElement {
		return this.extraTopEl;
	}

	public getBodyEl(): HTMLElement {
		return this.bodyEl;
	}

	public getFooterEl(): HTMLElement {
		return this.footerEl;
	}

	public getTitleEl(): HTMLElement {
		return this.titleEl;
	}

	public updateContent(): void {
		this.findModalElements();
	}

	protected getTemplate(): string {
		const modifications: string = this.options.modifications.join(' ');
		return `
      <dialog class="c-modal ${modifications} ${this.options.classes.dialog} ${this.options.hasBackdrop ? 'c-modal_backdrop' : ''}">
        <div class="c-modal__view ${this.options.classes.view}">
          <div class="c-modal__content ${this.options.classes.content}">
            <header class="c-modal__header ${this.options.classes.header}">
              <h5 class="c-modal__title ${this.options.classes.title}">${this.options.title}</h5>
              <button class="c-modal__close ${this.options.actionClasses.close} ${this.options.actionClasses.closeIcon}">
                <i class="o-icon o-icon_close-filled"></i>
              </button>
            </header>
            <div class="c-modal__extra-top ${this.options.classes.extraTop}"></div>
            <div class="c-modal__body ${this.options.classes.body} js-customScroll"></div>
            <footer class="c-modal__footer ${this.options.classes.footer}"></footer>
          </div>
        </div>
      </dialog>
    `;
	}

	private findModalElements(): void {
		this.contentEl = this.getViewEl().querySelector(`.${this.options.classes.content}`);
		this.headerEl = this.getViewEl().querySelector(`.${this.options.classes.header}`);
		this.extraTopEl = this.getViewEl().querySelector(`.${this.options.classes.extraTop}`);
		this.bodyEl = this.getViewEl().querySelector(`.${this.options.classes.body}`);
		this.footerEl = this.getViewEl().querySelector(`.${this.options.classes.footer}`);
		this.titleEl = this.getViewEl().querySelector(`.${this.options.classes.title}`);
	}

	private setBackdrop(): void {
		if (this.options.hasBackdrop) {
			this.getDialogEl().classList.add('c-modal_backdrop');
		}
	}

	set closable(state: boolean) {
		this.options.closable = state;
	}

	set maskClosable(state: boolean) {
		this.options.maskClosable = state;
	}
}
