import {TCodeInputChangeEventDetail} from "@components/code-input/model";
import {EMaskTypes, MASK_FORMATS} from "@modules/mask/Mask";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import IMask from "imask";
import {customElement, html, property, PropertyValues, query, TemplateResult} from "lit-element";
import AnyMaskedOptions = IMask.AnyMaskedOptions;
import InputMask = IMask.InputMask;

@customElement('c-code-input')
export default class CodeInputComponent extends WebComponentLite {
	@property({
		reflect: true,
		type: String,
	})
	public name: string = '';

	@property({
		reflect: true,
		type: String,
	})
	public value: string = '';

	@property({
		reflect: true,
		type: Boolean,
	})
	public disabled: boolean = false;

	@property({
		type: String,
		attribute: 'mask-type',
	})
	public maskType: string = EMaskTypes.SMS_CODE;

	@query('[code-input-field]')
	private field: HTMLInputElement;

	private mask: InputMask<AnyMaskedOptions>;

	public getMask(): InputMask<AnyMaskedOptions> {
		return this.mask;
	}

	public focus(): void {
		this.field.focus();
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.initMask();
		this.attachEvent();
	}

	disconnectedCallback() {
		super.disconnectedCallback();
		this.mask.destroy();
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);
		if (this.field && name === 'value') {
			if (this.mask) {
				setTimeout(() => {
					this.mask?.updateValue();
					this.mask?.updateControl();
				}, 0);
			}
		}
	}

	private initMask(): void {
		this.mask = IMask(
			this.field,
			MASK_FORMATS[this.maskType || EMaskTypes.SMS_CODE].option,
		);
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-code-input">
				<input code-input-field
					   class="c-code-input__field"
					   type="text"
					   .name="${this.name}"
					   .value="${this.value}"
					   ?disabled="${this.disabled}"
					   pattern="[0-9]*"
					   inputmode="numeric">
			</div>
		`;
	}

	private attachEvent(): void {
		this.mask.on('accept', () => {
			this.emitEventOnChange({
				value: this.mask.unmaskedValue,
				isComplete: false,
			});
		});
		this.mask.on('complete', () => {
			this.emitEventOnChange({
				value: this.mask.unmaskedValue,
				isComplete: true,
			});
		});
	}

	private emitEventOnChange(detail: TCodeInputChangeEventDetail): void {
		this.value = this.field.value;
		const event = new CustomEvent('on-change', {
			detail: detail,
		});
		this.dispatchEvent(event);
	}
}
