import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";

@customElement('c-example')
class ExampleComponent extends WebComponentLite {
	@property()
	name: string = 'Name';

	constructor() {
		super();
	}

	render(): TemplateResult {
		const header = this.getSlot('header');
		const body = this.getSlot('body');
		const children = this.getChildren();

		return html`
			<div class="c-example">
				<input type="text" value="text value" name="field-in-custom-element">
				${header ?
					html`
						<header class="c-example__header">
							<mark>Header slot start</mark>
							${header}
							<mark>Header slot end</mark>
						</header>
					` :
					null}
				<p>My name ${this.name}</p>
				${body ?
					html`
						<div class="c-example__body">
							<mark>Body slot start</mark>
							${body}
							<mark>Body slot end</mark>
						</div>
					` :
					null}
				${children.length ?
					html`
						<div class="c-example__other">
							<mark>Other slot</mark>
							${children}
							<mark>Other slot end</mark>
						</div>
					` :
					null}
			</div>
		`;
	}
}
