import {TProduct} from "@models/Product";

export type TCartAddRequest = {
	sku: string
}
export type TCartAddResponse = {
	cartItemCount: number
}

export type TCartRemoveRequest = {
	sku: string
}
export type TCartRemoveResponse = {
	cartItemCount: number
}

export type TCartEditRequest = {
	sku: string
	type: ECartEditTypes
}
export type TCartEditResponse = {
	cartItemCount: number
}

export enum ECartEditTypes {
	INCREMENT = 'INCREMENT',
	DECREMENT = 'DECREMENT',
}

export type TCartViewRequest = {}

export type TCartViewResponse = {
	items: Array<TCartItem>
	accessories: Array<TCartItem>
	cartItemCount: number
}

export type TCartItem = {
	product: TProduct
	quantity: number
	min: number
	max: number
	availability: boolean
}
