import {EBtnMods, EBtnSize, EBtnType} from "@components/btn/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";

@customElement('c-btn')
export default class BtnComponent extends WebComponentLite {
	@property({
		reflect: true,
		type: Boolean,
	})
	public disabled: boolean = false;

	@property({
		reflect: true,
		type: Boolean
	})
	public loading: boolean = false;

	@property({
		reflect: true,
		type: String,
	})
	public prependIconClass: string = null;

	@property({
		reflect: true,
		type: String,
	})
	public appendIconClass: string = null;

	@property({
		reflect: true,
		type: EBtnSize,
	})
	public size: EBtnSize = EBtnSize.MD;

	@property({
		reflect: true,
		type: Array,
	})
	public mods: Array<EBtnMods> = [];

	@property({
		reflect: true,
		type: EBtnType,
	})
	public type: EBtnType = EBtnType.PRIMARY;

	@property({
		type: String,
	})
	public link: string;

	protected getMods(): string {
		const mods: Array<string> = [this.size, this.type, ...this.mods];
		return mods
			.filter(Boolean)
			.map(mod => `c-btn_${mod}`)
			.join(' ');
	}

	protected render(): TemplateResult {
		const children = this.getChildren();

		if (this.link) {
			return html`
				<a .href="${this.link}" class="c-btn ${this.getMods()} ${this.loading && 'is-loading'}"
				   ?disabled="${this.disabled}">
					${this.getPrepend()}
					${this.getLoader()}
					<span class="c-btn__text">
						${children}
					</span>
					${this.getAppend()}
			`;
		}
		return html`
			<button class="c-btn ${this.getMods()} ${this.loading && 'is-loading'}"
					?disabled="${this.disabled}">
				${this.getPrepend()}
				${this.getLoader()}
				<span class="c-btn__text">
						${children}
					</span>
				${this.getAppend()}
		`;
	}

	protected getLoader(): TemplateResult {
		return html`
			<div class="c-btn__loader">
				<i class="o-icon o-icon_loader-line-filled"></i>
			</div>
		`;
	}

	protected getPrepend(): TemplateResult {
		if (!this.prependIconClass) {
			return null;
		}
		return html`
			<div class="c-btn__prepend o-icon-box">
				<i class="o-icon ${this.prependIconClass}"></i>
			</div>
		`;
	}

	protected getAppend(): TemplateResult {
		if (!this.appendIconClass) {
			return null;
		}
		return html`
			<div class="c-btn__append o-icon-box">
				<i class="o-icon ${this.appendIconClass}></i>
			</div>
		`;
	}
}

