import {customElement, html, property, PropertyValues, query, TemplateResult} from "lit-element";
import {strToBoolean} from "@helpers/index";
import Dialog from "@modules/dialog/Dialog";
import {IDialog, TDialogAnimation, TDialogHooks} from "@modules/dialog/model";
import {EAnimationType} from "@modules/animation/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";

@customElement('c-dialog')
export default class DialogComponent extends WebComponentLite {
	@property({
		type: Boolean,
		reflect: true
	})
	public backdrop: boolean = true;

	@property({
		type: Boolean,
		reflect: true,
		attribute: 'mask-closable',
	})
	public maskClosable: boolean = true;

	@property({
		type: Boolean,
		reflect: true
	})
	public closable: boolean = true;

	@property({
		type: Boolean,
		reflect: true
	})
	opened: boolean = false;

	@property({
		type: Object,
		reflect: true
	})
	hooks: TDialogHooks = {};

	@query('.js-dialog')
	private modalEl: HTMLDialogElement;

	private dialog: IDialog;

	protected baseClassName: string = 'c-dialog';

	constructor() {
		super();
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.dialog = new Dialog({
			element: this.modalEl,
			classes: {
				dialog: 'js-dialog',
				view: 'js-dialogView',
			},
			maskClosable: this.maskClosable,
			closable: this.closable,
			animation: this.getAnimationOption(),
			hooks: this.getHooksOption(),
		});
	}

	protected getAnimationOption(): TDialogAnimation {
		return {
			dialogOption: {
				name: 'o-animation-fade-in',
				type: EAnimationType.ANIMATION,
			},
			viewOption: {
				name: 'o-animation-zoom-in',
				type: EAnimationType.ANIMATION,
			},
		}
	}

	protected getHooksOption(): TDialogHooks {
		return {
			afterClose: () => {
				this.afterClose();
			},
			afterOpen: () => {
				this.afterOpen();
			},
			beforeClose: () => {
				this.beforeClose();
			},
			beforeOpen: () => {
				this.beforeOpen();
			},
		};
	}

	protected afterClose(): void {
		this.hooks?.afterClose?.();
	}

	protected afterOpen(): void {
		this.hooks?.afterOpen?.();
	}

	protected beforeClose(): void {
		this.hooks?.beforeClose?.();
	}

	protected beforeOpen(): void {
		this.hooks?.beforeOpen?.();
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);
		if (name === 'opened') {
			this.toggle(strToBoolean(value === '' ? true : value));
		}

		if (name === 'mask-closable') {
			if (this.dialog) {
				this.dialog.maskClosable = strToBoolean(value === '' ? true : value)
			}
		}

		if (name === 'closable') {
			if (this.dialog) {
				this.dialog.closable = strToBoolean(value === '' ? true : value)
			}
		}
	}

	public open(): void {
		this.dialog.open();
	}

	public close(): void {
		this.dialog.close();
	}

	private toggle(state: boolean): void {
		if (state) {
			this.open();
		} else {
			this.close();
		}
	}

	protected render(): TemplateResult {
		const cn = this.baseClassName;

		return html`
			<dialog class="${cn} js-dialog ${this.getMainClasses()} ${this.backdrop ? `${cn}_backdrop` : ''}">
				<div class="${cn}__view js-dialogView">
					<div class="${cn}__content js-dialogContent">
						${this.getContent()}
					</div>
				</div>
			</dialog>
		`;
	}

	protected getContent(): TemplateResult {
		const content = this.getContent();
		return html`
			${content}
		`;
	}

	protected getMainClasses(): string {
		return '';
	}
}
