import {EAnimationType} from "@modules/animation/model";
import Modal from "@modules/modal/Modal";
import {IModal} from "@modules/modal/model";
import {fromEvent} from "rxjs";

export default class ModalFilters {
	private CLASS_HANDLER: string = 'js-modalFiltersHandler';
	private DATA_ATTR_FILTER_ID: string = 'data-id-filter';
	private DATA_ATTR_MODAL_ID: string = 'data-id-modal';
	private CLASS_FILTERS_IN_MODAL: string = 's-category-search-page__filters-form_modal-insertion';

	private handlerEl: HTMLElement;
	private filterContentEl: HTMLElement;
	private modalEl: HTMLDialogElement;
	private modal: IModal;
	private parentFiltersEl: HTMLElement;
	private opened: boolean = false;

	public init(): void {
		this.handlerEl = document.querySelector(`.${this.CLASS_HANDLER}`);
		this.filterContentEl = document.getElementById(this.handlerEl?.getAttribute(this.DATA_ATTR_FILTER_ID));
		this.modalEl = document.getElementById(this.handlerEl?.getAttribute(this.DATA_ATTR_MODAL_ID)) as HTMLDialogElement;
		this.parentFiltersEl = this.filterContentEl?.parentElement;
		if (!this.handlerEl || !this.filterContentEl || !this.modalEl || !this.parentFiltersEl) {
			return;
		}
		this.modal = new Modal({
			element: this.modalEl,
			animation: {
				dialogOption: {
					name: 'o-animation-fade-in-right',
					type: EAnimationType.ANIMATION,
				},
				viewOption: null,
			},
			hooks: {
				afterOpen: () => {
					this.opened = true;
				},
				afterClose: this.afterModalClose.bind(this),
			}
		});
		this.attachEvent();
	}

	public open(): void {
		this.setModalContent(this.filterContentEl);
		this.modal.open();
	}

	public close(): void {
		this.modal.close();
	}

	public isOpen(): boolean {
		return this.opened;
	}

	public updateFilterContent(contentEl: HTMLElement): void {
		this.filterContentEl = contentEl;
		this.parentFiltersEl = this.filterContentEl?.parentElement;
		if (this.isOpen()) {
			this.setModalContent(contentEl);
		}
	}

	private setModalContent(content: HTMLElement): void {
		this.modal.getBodyEl().innerHTML = '';
		this.modal.getBodyEl().append(content);
		this.filterContentEl.classList.add(this.CLASS_FILTERS_IN_MODAL);
	}

	private attachEvent(): void {
		fromEvent(this.handlerEl, 'click')
			.subscribe(event => {
				event.preventDefault();
				this.open();
			});
	}

	private afterModalClose(): void {
		this.parentFiltersEl.append(this.filterContentEl);
		this.filterContentEl.classList.remove(this.CLASS_FILTERS_IN_MODAL);
		this.opened = false;
	}

}
