import {fromEvent} from "rxjs";

export default class ToggleConnectedArea {
	private DATA_ATTR_INPUT_CLASS: string = 'data-toggle-connected-area-input-class';

	private handlerEL: HTMLElement;

	constructor(handler: HTMLElement) {
		this.handlerEL = handler;
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.handlerEL, 'click')
			.subscribe(() => {
				this.activateToggle();
			});
	}

	private activateToggle(): void {
		const className: string = this.handlerEL.getAttribute(this.DATA_ATTR_INPUT_CLASS);
		if (!className) {
			throw new Error(`ToggleConnectedArea: an attribute named "${this.DATA_ATTR_INPUT_CLASS}" is required`);
		}
		const toggle: HTMLInputElement = document.querySelector(`.${className}`);
		toggle.checked = true;
		toggle.dispatchEvent(new Event('change'));
	}
}
