import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import {isHTMLElement, strToBoolean} from "@helpers/index";
import FilterList from "@modules/filter-list/FilterList";

export default class FilterListAutoActivation extends DynamicInsertionElements {
	private CLASS_BLOCK: string = 'js-filterListBlock';
	private CLASS_INPUT: string = 'js-filterListInput';
	private CLASS_LIST: string = 'js-filterList';

	private DATA_ACTIVATED: string = 'data-filter-list-activated';

	constructor() {
		super();
	}

	public init(): void {
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		const blocks: Array<HTMLElement> = Array.from(scope.getElementsByClassName(this.CLASS_BLOCK)) as Array<HTMLElement>;
		blocks.forEach(block => {
			if (strToBoolean(block.getAttribute(this.DATA_ACTIVATED))) {
				return
			}
			block.setAttribute(this.DATA_ACTIVATED, 'true');
			const input: HTMLInputElement = block.querySelector(`.${this.CLASS_INPUT}`);
			const list: HTMLInputElement = block.querySelector(`.${this.CLASS_LIST}`);
			new FilterList({
				list,
				input
			});
		});
	}
}
