import { Base64 } from '@modules/base64/Base64';

export default class HiddenLinks {
	private CLASS_HANDLER: string = 'js-hiddenLink';
	private DATA_ATTR_HREF: string = 'data-href';
	private DATA_ATTR_TARGET: string = 'data-target';


	public init(): void {
		this.handleLinks();
	}

	private handleLinks(): void {
		const hiddenLinks = document.getElementsByClassName(this.CLASS_HANDLER);
		Array.from(hiddenLinks).forEach((hl) => this.transformToLink(hl as HTMLSpanElement));
	}

	private createLink(href: string, target: string, className: string): HTMLAnchorElement {
		const anchor: HTMLAnchorElement = document.createElement('a');
		anchor.href = href;
		anchor.target = target || "_blank";
		anchor.className = className;
		return anchor;
	}

	private transformToLink(span: HTMLSpanElement): void {
		const href = this.parseHiddenLink(span.getAttribute(this.DATA_ATTR_HREF));
		const target = span.getAttribute(this.DATA_ATTR_TARGET) ;
		const className = span.className.replace(this.CLASS_HANDLER, "");
		const replacement = this.createLink(href, target, className);
		replacement.innerHTML = span.innerHTML;
		span.replaceWith(replacement);
	}

	private parseHiddenLink(data = ""): string {
		return Base64.decodeUnicode(data);
	}
}
