import CartCostLine from "@modules/cart/CartCostLine";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, property, state} from "lit-element";
import {AxiosRequestConfig} from "axios";
import HttpRequest from "@modules/http-request/HttpRequest";
import {BehaviorSubject} from "rxjs";
import {EComponentStatuses} from "@models/Status";
import {EPriceRequestTypes, TPricesResponse, TTariffPriceRequest} from "@models/Tariff";
import {ECartCostLineNames} from "@modules/cart/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import {EMoneyFormatterTypes} from "@modules/formatter/model";


@customElement('prices-calculator')
export default class LoanCalculator extends WebComponentLite {
	private CLASS_COST_LINE: string = 'js-cartCostLine';
	private CLASS_LOAN_TOTAL: string = 'js-calculateLoanTotal';
	private DATA_ATTR_REQUEST_CONFIG = 'calculate-request-config';
	private costLineEl: HTMLElement;
	private cartCostLine: CartCostLine;
	private loanTotalCalculatorEl: HTMLElement;

	@property({
		type: Object,
		attribute: 'calculate-request-config',
	})
	public calculatePricesRequestConfig: AxiosRequestConfig = {
		url: '/checkout/calculate_prices/',
		method: "POST",
	}
	@state()
	private isGlobalLoading: boolean = false;

	private httpService: HttpRequest = new HttpRequest();
	public readonly status: BehaviorSubject<EComponentStatuses> = new BehaviorSubject<EComponentStatuses>(EComponentStatuses.LOADING);

	public init(): void {
		this.costLineEl = document.querySelector(`.${this.CLASS_COST_LINE}`);
		if (!this.costLineEl) {
			return;
		}
		this.cartCostLine = new CartCostLine(this.costLineEl);
		this.loanTotalCalculatorEl = document.querySelector(`.${this.CLASS_LOAN_TOTAL}`);
		const requestConfigAttribute = this.loanTotalCalculatorEl?.getAttribute(this.DATA_ATTR_REQUEST_CONFIG);
		if (requestConfigAttribute) {
			const requestConfig: AxiosRequestConfig = JSON.parse(requestConfigAttribute);
			this.calculatePricesRequestConfig = requestConfig;
		}
		this.attachEvent();
	}

	private attachEvent(): void {
		if (this.loanTotalCalculatorEl) {
			this.calculatePrices();
			this.status
				.asObservable()
				.subscribe(status => {
					this.cartCostLine.handleLoader(status);
				});
		}
	}

	public calculatePrices(price: string = null, type: EPriceRequestTypes = null): void {
		if (this.isGlobalLoading) {
			return
		}
		this.isGlobalLoading = true;
		const {url, method} = this.calculatePricesRequestConfig;
		const postData: TTariffPriceRequest = {
			price: price,
			type: type
		}
		this.status.next(EComponentStatuses.LOADING);
		this.httpService
			.request<TPricesResponse>({
				url: url,
				method:method,
				data: postData,
			})
			.then((response) => {
				this.status.next(EComponentStatuses.READY);
				this.emitEventOnResponseCalculatePrices(parseFloat(response.data?.data?.pricePerMonth) || 0)
				if (this.loanTotalCalculatorEl) {
					this.updatePrice(parseFloat(response.data?.data?.pricePerMonth) || 0);
				}
				this.isGlobalLoading = false;
			})
			.catch(() => {
				this.isGlobalLoading = false;
				this.status.next(EComponentStatuses.ERROR);
			})
			.finally(() => {
				this.isGlobalLoading = false;
			});
	}

	private emitEventOnResponseCalculatePrices(price: number): void {
		const event = new CustomEvent('on-response-calculate-prices', {
			detail: {
				'price': price
			},
		});
		this.dispatchEvent(event);
	}

	private updatePrice(price: number): void {
		const pricePerMonth = MoneyFormatter.format(
			price.toString(),
			EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT,
		);
		this.cartCostLine.updateItemValue(ECartCostLineNames.PRICE_PER_MONTH, pricePerMonth);
	}
}
