import {ELoaderSize, ELoaderType} from "@components/loader/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";

@customElement('c-loader')
export default class LoaderComponent extends WebComponentLite {
	@property({
		reflect: true,
		type: Boolean,
	})
	public active: boolean = false;

	@property({
		reflect: true,
		type: String,
	})
	public type: ELoaderType = ELoaderType.PRIMARY;

	@property({
		reflect: true,
		type: String,
	})
	public size: ELoaderSize = ELoaderSize.MD;

	protected getMods(): string {
		const mods: Array<string> = [this.size, this.type];
		return mods
			.filter(Boolean)
			.map(mod => `c-loader_${mod}`)
			.join(' ');
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-loader ${this.getMods()}">
				<div class="c-loader__content">
					<div class="c-loader__icon"></div>
				</div>
			</div>
		`;
	}
}
