import {fromEvent} from "rxjs";
import CreateEvent from "@modules/event/Event";
import {ECollapseState, ICollapse, TCollapseOption} from "@modules/collapse/model";

export default class Collapse implements ICollapse {
	protected blockEl: HTMLElement;
	protected handlerEl: HTMLElement;
	protected containerEl: HTMLElement;

	private option: TCollapseOption;

	constructor(
		blockEl: HTMLElement,
		options?: TCollapseOption
	) {
		this.blockEl = blockEl;
		this.option = {
			...this.getDefaultOption(),
			...options,
		};
		this.initElements();
		this.attachEvent();
	}

	public open(): void {
		this.hooksBeforeOpen();
		this.openWithoutHooks();
		this.hooksAfterOpen();
	}

	public close(): void {
		this.hooksBeforeClose();
		this.closeWithoutHooks();
		this.hooksAfterClose();
	}

	public toggle(): void {
		if (this.getState() == ECollapseState.OPEN) {
			this.close();
		} else {
			this.open();
		}
	}

	public getState(): ECollapseState {
		const hasOpenClass: boolean = this.blockEl.classList.contains(this.option.stateClasses.open);
		return hasOpenClass ? ECollapseState.OPEN : ECollapseState.CLOSED;
	}

	public getHTMLElement(): HTMLElement {
		return this.blockEl;
	}

	protected hooksBeforeOpen(): void {
		if (this.option?.hooks?.beforeOpen) {
			this.option.hooks.beforeOpen(this);
		}
	}

	protected hooksAfterOpen(): void {
		if (this.option?.hooks?.afterOpen) {
			this.option.hooks.afterOpen(this);
		}
	}

	protected openWithoutHooks(): void {
		this.clearClassState();
		this.setClassOpen();
		this.dispatchEvent(ECollapseState.OPEN);
	}

	protected hooksBeforeClose(): void {
		if (this.option?.hooks?.beforeClose) {
			this.option.hooks.beforeClose(this);
		}
	}

	protected hooksAfterClose(): void {
		if (this.option?.hooks?.afterClose) {
			this.option.hooks.afterClose(this);
		}
	}

	protected closeWithoutHooks(): void {
		this.clearClassState();
		this.setClassClose();
		this.dispatchEvent(ECollapseState.CLOSED);
	}

	private getDefaultOption(): TCollapseOption {
		return {
			classes: {
				block: 'js-collapse',
				handler: 'js-collapseHandler',
				container: 'js-collapseContainer',
			},
			stateClasses: {
				open: 'is-open',
				close: 'is-close',
			},
		}
	}

	private attachEvent(): void {
		if (this.handlerEl) {
			fromEvent(this.handlerEl, 'click')
				.subscribe(() => {
					this.toggle();
				})
		}
	}

	private initElements(): void {
		this.findElements();
	}

	private findElements(): void {
		this.containerEl = this.blockEl.querySelector(`.${this.option.classes.container}`);
		if (!this.containerEl) {
			throw new Error('Collapse: container is not defined !');
		}

		this.handlerEl = this.blockEl.querySelector(`.${this.option.classes.handler}`);
	}

	private clearClassState(): void {
		this.blockEl.classList.remove(this.option.stateClasses.open);
		this.blockEl.classList.remove(this.option.stateClasses.close);
	}

	private setClassOpen(): void {
		this.blockEl.classList.add(this.option.stateClasses.open);
	}

	private setClassClose(): void {
		this.blockEl.classList.add(this.option.stateClasses.close);
	}

	private dispatchEvent(state: ECollapseState): boolean {
		let event: Event;

		if (state == ECollapseState.OPEN) {
			event = new CreateEvent('open').createCustom();
		} else {
			event = new CreateEvent('closed').createCustom();
		}

		const canceled: boolean = !this.blockEl.dispatchEvent(event);
		return !canceled;
	}
}
