import {
	ECollapseState,
	ECollapseType,
	ICollapse,
	ICollapseFactory,
	TCollapseGroupItem,
	TCollapseGroupOption,
	TCollapseOption
} from "@modules/collapse/model";
import CollapseFactory from "@modules/collapse/CollapseFactory";

export default class CollapseGroup {
	protected groupEl: HTMLElement;

	private option: TCollapseGroupOption;
	private type: ECollapseType;

	private itemCollection: Array<TCollapseGroupItem> = [];

	private collapseFactory: ICollapseFactory = new CollapseFactory();

	private accordionMode: boolean;

	constructor(
		groupEl: HTMLElement,
		type: ECollapseType = ECollapseType.DEFAULT,
		option?: TCollapseGroupOption,
	) {
		this.type = type;
		this.groupEl = groupEl;
		this.option = {
			...this.getDefaultOption(),
			...option,
		};
		this.accordionMode = this.getAccordionMode();
		this.init();
	}

	private getDefaultOption(): TCollapseGroupOption {
		return {
			classes: {
				group: 'js-accordionGroup',
				item: 'js-accordionGroupItem',
			},
			dataAttributes: {
				accordion: 'data-accordion',
			}
		}
	}

	private init(): void {
		const els: Array<HTMLElement> = Array.from(
			this.groupEl.querySelectorAll(`.${this.option.classes.item}`)
		);
		els.forEach(el => {
			this.itemCollection.push({
				element: el,
				instance: this.getCollapse(el),
			})
		});
	}

	private getCollapse(el: HTMLElement): ICollapse {
		const option: TCollapseOption = {
			hooks: {
				beforeOpen: (instance) => {
					if (this.accordionMode) {
						this.handleAccordion(instance);
					}
				}
			}
		}
		if (this.type === ECollapseType.ANIMATED) {
			return this.collapseFactory.createCollapseAnimated(el, option);
		} else {
			return this.collapseFactory.createCollapse(el, option);
		}
	}

	private getAccordionMode(): boolean {
		return this.groupEl.hasAttribute(this.option.dataAttributes.accordion);
	}

	private handleAccordion(activeCollapse: ICollapse): void {
		this.itemCollection
			.filter(item => item.instance.getState() === ECollapseState.OPEN)
			.filter(item => item.instance !== activeCollapse)
			.forEach(item => {
				item.instance.close();
			});
	}
}
