import {ICookie} from "@modules/cookie/model";
import Cookies, {CookieAttributes} from "js-cookie";

export default class Cookie implements ICookie {
	private static instance: ICookie;

	constructor() {
		if (Cookie.instance) {
			throw new Error("Error: Instantiation failed: Use Cookie.getInstance() instead of new.");
		}
		Cookie.instance = this;
	}

	public static getInstance(): ICookie {
		if (!Cookie.instance) {
			Cookie.instance = new Cookie();
		}
		return this.instance;
	}

	public has(name: string): boolean {
		return Boolean(this.get(name));
	}

	public get(name: string): string {
		return Cookies.get(name)
	}

	public set(name: string, value: string, options?: CookieAttributes): void {
		Cookies.set(name, value, options)
	}

	public delete(name: string): void {
		Cookies.remove(name);
	}
}

