import SlideMenu from "@components/slide-menu/SlideMenu";
import {isHTMLElement, strToBoolean} from "@helpers/index";
import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";

export default class SlideMenuAutoActivation extends DynamicInsertionElements {
	private name: string = 'slide-menu';
	private CLASS_HANDLER: string = 'js-slideMenuAuto';

	constructor() {
		super();
	}

	public init(): void {
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		const handlers: Array<HTMLElement> = Array.from(scope.getElementsByClassName(this.CLASS_HANDLER)) as Array<HTMLElement>;
		handlers.forEach(handler => {
			if (strToBoolean(handler.getAttribute(this.getDataActivatedName()))) {
				return
			}
			handler.setAttribute(this.getDataActivatedName(), 'true');
			new SlideMenu(handler);
		});
	}

	private getDataActivatedName(): string {
		return `data-${this.name}-activated`
	}
}
