import StepComponent from "@components/steps/StepComponent";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, PropertyValues, queryAll, TemplateResult} from "lit-element";

@customElement('c-steps')
class StepsComponent extends WebComponentLite {

	@queryAll('c-step')
	steps: NodeListOf<StepComponent>;

	@property({
		attribute: false
	})
	selectedIndex: number = 0;

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.setDefaultActiveStep();
	}

	public nextStep() {
		if (this.selectedIndex >= this.steps.length - 1) {
			return;
		}
		this.selectedIndex++
	}

	public prevStep() {
		if (this.selectedIndex <= 0) {
			return;
		}
		this.selectedIndex--;
	}

	private setDefaultActiveStep() {
		const activeIndex = Array.from(this.steps).findIndex(step => step.selected);
		if (activeIndex != -1 || !this.steps.length) {
			this.selectedIndex = activeIndex
			return;
		}
		this.steps.item(0).selected = true;
	}

	protected nextBtnHandler(event: Event) {
		event.preventDefault();
		this.nextStep();
	}

	protected prevBtnHandler(event: Event) {
		event.preventDefault();
		this.prevStep();
	}

	protected isDisabledNextBtn(): boolean {
		return this.selectedIndex >= this.steps.length - 1;
	}

	protected isDisabledPrevBtn(): boolean {
		return this.selectedIndex <= 0;
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-steps">
				<header class="c-steps__header">
					${this.selectedIndex + 1} шаг из ${this.steps.length}
				</header>

				<div class="c-steps__content">
					${Array.from(this.steps).map((step, index) => {
						step.selected = this.selectedIndex === index;
						return step;
					})}
				</div>

				<footer class="c-steps__footer">
					<button
						@click="${this.prevBtnHandler}"
						?disabled="${this.isDisabledPrevBtn()}">
						Prev
					</button>
					<button
						@click="${this.nextBtnHandler}"
						?disabled="${this.isDisabledNextBtn()}">
						Next
					</button>
				</footer>
			</div>
		`;
	}
}
