export default class MenuBtn {
	private CLASS_MOD_ACTIVE: string = 'c-menu-btn_active';

	private btnEl: HTMLButtonElement;

	constructor(menu: HTMLButtonElement) {
		this.btnEl = menu;
	}

	public open(): void {
		this.btnEl.classList.add(this.CLASS_MOD_ACTIVE);
	}

	public close(): void {
		this.btnEl.classList.remove(this.CLASS_MOD_ACTIVE);
	}

	public isOpen(): boolean {
		return this.btnEl.classList.contains(this.CLASS_MOD_ACTIVE);
	}
}
