import {isDefinitionElementEvent} from "@helpers/index";
import {fromEvent} from "rxjs";
import {TProductItem} from "@modules/analytics/model";
import {filter} from "rxjs/operators";

declare global {
	interface Window {
		dataLayer: any;
	}
}

export default class AnalyticsClickOnProductCard {
	private productCardEl: HTMLElement;

	private DATA_ATTR_ANALYTICS_DATA: string = 'data-analytics-data';

	constructor(productCardEl: HTMLElement) {
		this.productCardEl = productCardEl;
	}

	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.productCardEl, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, '.js-addToCartHandler')),
			)
			.subscribe(event => {
				this.onClick(event);
			});
	}

	private onClick(event: Event): void {
		const analyticsData: TProductItem = this.getAnalyticsData();
		window.dataLayer = window.dataLayer || [];
		window.dataLayer.push({
			'event': 'addToCart', // Название ивента для передачи информации в GTM
			'ecommerce': {
				'currencyCode': 'KZT',
				'add': {
					'products': [{
						'name': `${analyticsData.name}`,
						'id': `${analyticsData.id}`,
						'price': `${analyticsData.price}`,
						'brand': `${analyticsData.brand}`,
						'category': `${analyticsData.category}`,
						'quantity': `${analyticsData.quantity}`
					}]
				}
			}
		});
	}

	private getAnalyticsData(): TProductItem {
		let data;
		try {
			data = JSON.parse(this.productCardEl.getAttribute(this.DATA_ATTR_ANALYTICS_DATA))
		} catch (e) {
			data = {}
		}
		return data;
	}
}
