import {EBtnSize, EBtnType} from "@components/btn/model";
import CodeInputComponent from "@components/code-input/CodeInputComponent";
import {TCodeInputChangeEventDetail} from "@components/code-input/model";
import {TEnterSMSCodeFullFilledCodeEventDetail} from "@components/enter-sms-code/model";
import {isNotNullOrUndefined} from "@helpers/index";
import {ITimer} from "@modules/timer/model";
import Timer from "@modules/timer/Timer";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, state, property, query, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-enter-sms-code')
export default class EnterSMSCodeComponent extends WebComponentLite {
	@property({
		type: Boolean,
		attribute: 'btn-retry',
		reflect: true,
	})
	public btnRetry: boolean = true;

	@property({
		type: Boolean,
		attribute: 'loading-btn-retry',
		reflect: true,
	})
	public loadingBtnRetry: boolean = false;

	@property({
		type: Boolean,
		attribute: 'disable-btn-retry',
		reflect: true,
	})
	public disableBtnRetry: boolean = false;

	@property({
		type: String,
		attribute: 'phone-number',
	})
	public phoneNumber: string;

	@property({
		type: Number,
	})
	public estimate: number = 0;

	@property({
		type: Number,
		attribute: 'attempts-count-send',
	})
	public attemptsCountSend: number;

	@property({
		type: Number,
		attribute: 'attempts-count-validate',
	})
	public attemptsCountValidate: number;

	@property({
		type: String,
		reflect: true,
	})
	public value: string;

	@state()
	private timerOutput: string = '00:00';

	@state()
	private isTimerActive: boolean = false;

	@query('[enter-sms-code-code-input]')
	private codeInput: CodeInputComponent;

	private timer: ITimer = new Timer();

	constructor() {
		super();
		this.handleTimerStream();
	}

	public focus(): void {
		this.codeInput
			.updateComplete
			.then(() => {
				this.codeInput.focus();
			});
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);
		if (name === 'estimate') {
			this.startTimer(parseFloat(value));
		}
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-enter-sms-code">
				<c-code-input enter-sms-code-code-input
							  name="code"
							  .value="${this.value}"
							  @on-change="${this.handleInputFieldChange}">
				</c-code-input>

				<div class="u-text-center c-enter-sms-code__message">
					<p class="c-enter-sms-code__timer">
						На номер ${this.phoneNumber} был отправлен код.
						<br>
						Повторно можно отправить через: ${this.timerOutput}
					</p>

					<p class="c-enter-sms-code__count">
						${unsafeHTML(this.getCountMessage())}
					</p>
				</div>

				${this.isVisibleBtnRetry() ?
					html`
						<c-btn class="c-enter-sms-code__btn-retry"
							   type="${EBtnType.PRIMARY}"
							   size="${EBtnSize.MD}"
							   ?loading="${this.isLoadingBtnRetry()}"
							   ?disabled="${this.isDisableBtnRetry()}"
							   @click="${this.handleClickBtnRetry}">
							Выслать смс повторно
						</c-btn>
					` :
					null
				}
			</div>
		`;
	}

	protected handleInputFieldChange(event: CustomEvent<TCodeInputChangeEventDetail>): void {
		this.value = this.codeInput.value;
		if (event.detail.isComplete) {
			this.emitEventFullyFilledCode();
		}
	}

	protected handleClickBtnRetry(event: Event): void {
		event.preventDefault();
		this.emitEventClickBtnRetry();
	}

	protected getCountMessage(): string {
		const message: Array<string> = [];
		if (isNotNullOrUndefined(this.attemptsCountSend)) {
			message.push(`Осталось попыток отправки смс кода: ${this.attemptsCountSend}`)
		}
		if (isNotNullOrUndefined(this.attemptsCountValidate)) {
			message.push(`Осталось попыток проверки кода: ${this.attemptsCountValidate}`)
		}
		return message.reverse().join('<br>');
	}

	protected isVisibleBtnRetry(): boolean {
		if (this.attemptsCountSend <= 0 || this.isTimerActive || !this.btnRetry) {
			return false;
		}
		return true;
	}

	protected isDisableBtnRetry(): boolean {
		return this.disableBtnRetry;
	}

	protected isLoadingBtnRetry(): boolean {
		return this.loadingBtnRetry;
	}

	public startTimer(value: number): void {
		if (value > 0) {
			this.isTimerActive = true;
		}
		this.timer.start(value);
	}

	private handleTimerStream(): void {
		this.timer
			.getStream()
			.subscribe(stream => {
				this.isTimerActive = true;
				this.timerOutput = stream.fillTime;

				if (stream.second === 0) {
					this.isTimerActive = false;
					this.timerEnded();
				}
			});
	}

	protected timerEnded(): void {
		this.emitEventTimerEnded();
	}

	private emitEventClickBtnRetry(): void {
		const event = new CustomEvent('on-click-btn-retry', {
			detail: {},
		});
		this.dispatchEvent(event);
	}

	private emitEventFullyFilledCode(): void {
		const event = new CustomEvent<TEnterSMSCodeFullFilledCodeEventDetail>('on-full-filled-code', {
			detail: {
				value: this.value,
			},
		});
		this.dispatchEvent(event);
	}

	private emitEventTimerEnded(): void {
		const event = new CustomEvent('on-timer-ended', {
			detail: {},
		});
		this.dispatchEvent(event);
	}
}
