import {fromEvent, merge} from "rxjs";

export default class TextareaAutoHeight {
	private textareaEl: HTMLTextAreaElement;

	constructor(textareaEl: HTMLTextAreaElement) {
		this.textareaEl = textareaEl;
		this.attachEvent();
		this.textareaEl.style.overflow = 'hidden';
		this.resize();
	}

	private attachEvent(): void {
		merge(
			fromEvent(this.textareaEl, 'change'),
			fromEvent(this.textareaEl, 'cut'),
			fromEvent(this.textareaEl, 'paste'),
			fromEvent(this.textareaEl, 'drop'),
			fromEvent(this.textareaEl, 'keydown'),
			fromEvent(this.textareaEl, 'keyup'),
		)
			.subscribe(() => {
				this.resize();
			});
	}

	private resize(): void {
		this.textareaEl.style.height = 'auto';
		this.textareaEl.style.height = this.textareaEl.scrollHeight + 'px';
	}
}
