import {isHTMLElement, strToBoolean} from "@helpers/index";
import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import Select from "@modules/select/Select";

export default class SelectAutoActivation extends DynamicInsertionElements {
	private CLASS_HANDLER: string = 'js-selectAuto';
	private name: string = 'select';

	constructor() {
		super();
	}

	public init(): void {
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		const blocks: Array<HTMLElement> = Array.from(scope.getElementsByClassName(this.CLASS_HANDLER)) as Array<HTMLElement>;
		blocks.forEach(block => {
			if (strToBoolean(block.getAttribute(this.getDataActivatedName()))) {
				return
			}
			block.setAttribute(this.getDataActivatedName(), 'true');

			new Select(block);
		});
	}

	private getDataActivatedName(): string {
		return `data-${this.name}-activated`
	}
}
