import {TContractTariff} from "@models/Contract";

export enum EProductBundlerOptionPaymentMethodTypes {
	CONTRACT_TARIFF = 'CONTRACT_TARIFF',
	FULL_PRICE = 'FULL_PRICE',
}

export type TProductBundlerOptionPaymentMethodRequest = {
	skuId: string
}

export type TProductBundlerOptionPaymentMethodResponse = Array<TProductBundlerOptionPaymentMethod>;

export type TProductBundlerOptionPaymentMethod =
	TProductBundlerOptionPaymentMethodContract
	| TProductBundlerOptionPaymentMethodFullPrice;

export type TProductBundlerOptionPaymentMethodCommon = {
	id: number
}

export type TProductBundlerOptionPaymentMethodContract = TProductBundlerOptionPaymentMethodCommon & {
	type: EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF
	contractTariff: TContractTariff
}

export type TProductBundlerOptionPaymentMethodFullPrice = TProductBundlerOptionPaymentMethodCommon & {
	type: EProductBundlerOptionPaymentMethodTypes.FULL_PRICE
	price: string
}
