import {EBtnSize, EBtnType} from "@components/btn/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html} from "lit-element";

@customElement('c-network-error')
export default class NetworkErrorComponent extends WebComponentLite {
	protected render(): unknown {
		return html`
			<div class="u-text-center">
				<h4>При загрузке данных произошла ошибка !</h4>
				<p class="u-mb-20">Проверьте подключение к интернету</p>
				<c-btn type="${EBtnType.PRIMARY}"
					   size="${EBtnSize.MD}"
					   @click="${this.handleBtnClickReload}">
					Повторить
				</c-btn>
			</div>
		`;
	}

	private handleBtnClickReload(event: Event): void {
		event.preventDefault();
		const customEvent = new CustomEvent('on-click-reload', {
			detail: {},
		});
		this.dispatchEvent(customEvent);
	}
}
