import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";

@customElement('c-step')
export default class StepComponent extends WebComponentLite {

	@property({
		reflect: true,
		type: Boolean
	})
	selected: boolean = false;

	protected render(): TemplateResult {
		const children = this.getChildren();

		return html`
			${children}
		`;
	}
}


