import {EKeyboardState, IKeyboardState} from "@modules/keyboard/model";
import {IScreenOrientation} from "@modules/screen/model";
import ScreenOrientationService from "@modules/screen/ScreenOrientationService";
import {BehaviorSubject, fromEvent, Observable} from "rxjs";
import {filter} from "rxjs/operators";

export default class KeyboardState implements IKeyboardState {
	private static instance: IKeyboardState;

	private screenOrientationService: IScreenOrientation;
	private initialFullViewHeight: number;

	private stream: BehaviorSubject<EKeyboardState> = new BehaviorSubject<EKeyboardState>(null);

	constructor() {
		if (KeyboardState.instance) {
			throw new Error("Error: Instantiation failed: Use KeyboardState.getInstance() instead of new.");
		}
		KeyboardState.instance = this;
		this.screenOrientationService = ScreenOrientationService.getInstance();
		this.initialFullViewHeight = this.getFullHeight();
		this.attachEvent();
	}

	public static getInstance(): IKeyboardState {
		if (!KeyboardState.instance) {
			KeyboardState.instance = new KeyboardState();
		}
		return this.instance;
	}

	public getState(): EKeyboardState {
		return EKeyboardState.ACTIVE;
	}

	public getStream(): Observable<EKeyboardState> {
		return this.stream
			.asObservable()
			.pipe(
				filter(stream => Boolean(stream))
			)
	}

	private attachEvent(): void {
		fromEvent(window, 'resize')
			.subscribe(() => {
				this.checkChange();
			});

		this.screenOrientationService
			.getStream()
			.subscribe(() => {
				this.initialFullViewHeight = this.getFullHeight();
				this.checkChange();
			});
	}

	private checkChange(): void {
		const activeInputEl: HTMLInputElement | HTMLTextAreaElement = this.getActiveInputElement();
		if (activeInputEl && this.isLessMainViewHeight()) {
			this.stream.next(EKeyboardState.ACTIVE);
		} else {
			this.stream.next(EKeyboardState.INACTIVE);
		}
	}

	private isLessMainViewHeight(): boolean {
		const currentViewHeight: number = document.documentElement.clientHeight;
		return currentViewHeight < this.initialFullViewHeight;
	}

	private getActiveInputElement(): HTMLInputElement | HTMLTextAreaElement {
		const activeElTagName: string = document.activeElement.tagName.toLowerCase();
		const inputTagNames: Array<string> = ['input', 'textarea'];
		if (inputTagNames.includes(activeElTagName)) {
			return document.activeElement as HTMLInputElement | HTMLTextAreaElement;
		}
		return null;
	}

	private getFullHeight(): number {
		return document.documentElement.clientHeight;
	}
}
