import {TProduct} from "@models/Product";

export type TInsurance = TInsuranceLife | TInsuranceCovid | TInsuranceDevice

export enum EInsuranceTypes {
	LIFE = 'LIFE',
	COVID = 'COVID',
	DEVICE = 'PHONE',
}

export type TInsuranceBase = {
	id: number
	name: string
	shortDescription: string
	description?: string
	price: string
	pricePerMonth: string
	payoutPrice: string
}

export type TInsuranceLife = TInsuranceBase & {
	type: EInsuranceTypes.LIFE
}
export type TInsuranceCovid = TInsuranceBase & {
	type: EInsuranceTypes.COVID
}
export type TInsuranceDevice = TInsuranceBase & {
	type: EInsuranceTypes.DEVICE
	periods?: Array<TInsurancePeriod>
	products?: Array<TProduct>
}

export type TInsurancePeriod = {
	value: number
}
