import {IPlatform} from "@modules/platform/model";

export default class Platform implements IPlatform {
	private static instance: IPlatform;

	constructor() {
		if (Platform.instance) {
			throw new Error("Error: Instantiation failed: Use Platform.getInstance() instead of new.");
		}
		Platform.instance = this;
	}

	public static getInstance(): IPlatform {
		if (!Platform.instance) {
			Platform.instance = new Platform();
		}
		return this.instance;
	}

	public isAndroid(): boolean {
		return navigator.userAgent.toLowerCase().indexOf("android") !== -1
	}

	public isMobile(): boolean {
		const fromUserAgent: boolean = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
		const fromCSSPointer: boolean = window.matchMedia("(pointer:coarse)").matches;
		return fromUserAgent || fromCSSPointer;
	}
}
