import {customElement, html, PropertyValues, queryAsync, state, TemplateResult} from "lit-element";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {fromEvent} from "rxjs";
import {EPaymentMethodTypes} from "@models/PaymentMethod";
import PaymentMethodComponent from "@components/payment-method/PaymentMethodComponent";
import {EComponentStatuses} from "@models/Status";
import {TPaymentMethodStatusChangeEventDetail} from "@components/payment-method/model";

@customElement('c-payment-method-select')
export default class PaymentMethodSelectComponent extends PaymentMethodComponent {
	private ATTR_ITEM_DATA: string = 'item-data';

	@state()
	private items: Array<ToggleCardComponent> = [];

	@state()
	private selected: EPaymentMethodTypes;

	@queryAsync('[payment-method]')
	private paymentMethod: Promise<PaymentMethodComponent>;

	public updateData(): void {
		this.paymentMethod
			.then(item => {
				if (!item) {
					return;
				}
				item.updateData();
			});
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.status.next(EComponentStatuses.LOADING);
		this.items = this.getSlotList('item') as Array<ToggleCardComponent>;
		this.setFirstSelected();
		this.attachEvent();
	}

	protected update(changedProperties: PropertyValues) {
		super.update(changedProperties);

		if (changedProperties.has('selected')) {
			this.status.next(EComponentStatuses.LOADING);
			this.updateStatusOnPaymentMethod();
		}
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-payment-method-select">
				<ul class="c-payment-method-select__list">
					${this.items.map(item => {
						return html`
							<li class="c-payment-method-select__item">
								${item}
							</li>
						`
					})}
				</ul>
				${this.getItemContent()}
			</div>
		`;
	}

	private getItemContent(): TemplateResult {
		let template: TemplateResult;
		const itemData = this.getItemData(this.items.find(item => item.value === this.selected));
		switch (this.selected) {
			case EPaymentMethodTypes.CREDIT:
				template = html`
					<div class="u-d-none"></div>
					<c-payment-method-loan payment-method
										   .initSelectedPeriod="${itemData['init-selected-period']}"
										   .getRequestConfig="${itemData['get-request-config']}"
										   type="${this.selected}">
				`;
				break;
			case EPaymentMethodTypes.INSTALLMENT_PLAN:
				template = html`
					<c-payment-method-loan payment-method
										   .initSelectedPeriod="${itemData['init-selected-period']}"
										   .getRequestConfig="${itemData['get-request-config']}"
										   type="${this.selected}">
				`;
				break;
			case EPaymentMethodTypes.FULL_PRICE:
			default:
				this.status.next(EComponentStatuses.READY);
				return null;
		}
		return html`
			<div class="c-payment-method-select__content">
				${template}
			</div>
		`;
	}

	protected attachEvent(): void {
		super.attachEvent();
		this.items
			.forEach(item => {
				fromEvent(item, 'on-change')
					.subscribe(event => {
						if (!item.checked) {
							return;
						}
						this.selected = item.value as EPaymentMethodTypes;
					});
			});
	}

	private updateStatusOnPaymentMethod(): void {
		if (this.selected === EPaymentMethodTypes.FULL_PRICE) {
			this.status.next(EComponentStatuses.READY);
			return;
		}
		this.paymentMethod
			.then(item => {
				if (!item) {
					return;
				}
				fromEvent<CustomEvent<TPaymentMethodStatusChangeEventDetail>>(item, 'on-status-change')
					.subscribe(event => {
						this.status.next(event.detail.status);
					})
			});
	}

	private setFirstSelected(): void {
		const checkedItem = this.items.find(item => item.checked);
		if (!checkedItem) {
			return;
		}
		this.selected = checkedItem.value as EPaymentMethodTypes;
	}

	private getItemData(item: HTMLElement): object {
		const strData = item?.getAttribute(this.ATTR_ITEM_DATA);
		let data = {};
		try {
			data = JSON.parse(strData) ?? {};
		} catch (e) {
			console.log(e);
		}
		return data;
	}
}
