import EnterPhoneNumberComponent from "@components/enter-phone-number/EnterPhoneNumberComponent";
import {TEnterPhoneNumberActionBtnClickEventDetail} from "@components/enter-phone-number/model";
import EnterSMSCodeComponent from "@components/enter-sms-code/EnterSMSCodeComponent";
import {TEnterSMSCodeFullFilledCodeEventDetail} from "@components/enter-sms-code/model";
import {
	EValidatePhoneNumberAndSMSStages,
	TValidatePhoneNumberAndSMSEnterSMSData,
	TValidatePhoneNumberAndSMSEventReject,
	TValidatePhoneNumberAndSMSEventResponse
} from "@components/validate-phone-number-and-sms/model";
import {objectToFormData} from "@helpers/index";
import {TSMSValidationGetResponse, TSMSValidationValidateReject} from "@models/SMSValidation";
import HttpRequest from "@modules/http-request/HttpRequest";
import {TRequestResponse} from "@modules/http-request/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosError, AxiosRequestConfig, AxiosResponse} from "axios";
import {customElement, html, state, property, PropertyValues, queryAsync, TemplateResult} from "lit-element";
import {from, of} from "rxjs";
import {filter, map, tap} from "rxjs/internal/operators";
import {switchMap} from "rxjs/operators";

@customElement('c-validate-phone-number-and-sms')
export default class ValidatePhoneNumberAndSmsComponent extends WebComponentLite {
	@property({
		type: Object,
		attribute: 'add-data-validate-sms'
	})
	public addDataValidateSMS: object = {};

	@property({
		type: Object,
		attribute: 'add-data-get-sms'
	})
	public addDataGetSMS: object = {};

	@property({
		type: Object,
		attribute: 'get-sms-request-config',
	})
	public getSMSRequestConfig: AxiosRequestConfig = {
		url: '/test/send_otp/',
		method: "POST",
	}

	@property({
		type: Object,
		attribute: 'validate-sms-request-config',
	})
	public validateSMSRequestConfig: AxiosRequestConfig = {
		url: '/test/verify_otp/',
		method: "POST",
	}

	@state()
	private isNetworkError: boolean = false;

	@state()
	private stage: EValidatePhoneNumberAndSMSStages = EValidatePhoneNumberAndSMSStages.ENTER_PHONE_NUMBER;

	@state()
	private isSMSRequesting: boolean = false;

	@state()
	private isSMSValidation: boolean = false;

	@state()
	private enterSMSData: TValidatePhoneNumberAndSMSEnterSMSData;

	@queryAsync('[validate-phone-number-and-sms-enter-phone-number]')
	protected enterPhoneNumberComponentAsync: Promise<EnterPhoneNumberComponent>;

	@queryAsync('[validate-phone-number-and-sms-enter-sms-code]')
	private enterSMSCodeComponentAsync: Promise<EnterSMSCodeComponent>;

	private httpService: HttpRequest = new HttpRequest();

	private phoneNumber: string;
	private smsCode: string;

	public focusInputEnterPhoneNumber(): void {
		from(this.enterPhoneNumberComponentAsync)
			.pipe(
				switchMap(component => {
					if (!component) {
						console?.error('ValidatePhoneNumberAndSmsComponent: enterPhoneNumberComponent is not defined !');
						return of(null);
					}
					return from(component.updateComplete)
						.pipe(
							map(() => component)
						)
				})
			)
			.pipe(
				filter(value => Boolean(value))
			)
			.subscribe(component => {
				component.focus();
			});
	}

	protected render(): unknown {
		if (this.isNetworkError) {
			return this.getNetworkErrorTemplate();
		}

		return html`
			${this.getTemplateByStage(this.stage)}
		`;
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.initSlots();
	}

	private getNetworkErrorTemplate(): TemplateResult {
		return html`
			<c-network-error @on-click-reload="${this.handleBtnClickReload}">
		`;
	}

	protected getEnterPhoneNumberTemplate(): TemplateResult {
		return html`
			<c-enter-phone-number validate-phone-number-and-sms-enter-phone-number
								  ?loading-btn="${this.isLoadingBtnGetSMS()}"
								  ?disable-btn="${this.isDisabledBtnGetSMS()}"
								  @on-action-btn-click="${this.handleClickBtnGetSMS}">
			</c-enter-phone-number>
		`;
	}

	protected getEnterSMSCodeTemplate(): TemplateResult {
		return html`
			<c-enter-sms-code validate-phone-number-and-sms-enter-sms-code
							  .attemptsCountSend="${this.enterSMSData.attemptsCountSend}"
							  .attemptsCountValidate="${this.enterSMSData.attemptsCountValidate}"
							  ?loading-btn-retry="${this.isLoadingBtnGetNewSMS()}"
							  ?disable-btn-retry="${this.isDisabledBtnGetNewSMS()}"
							  @on-click-btn-retry="${this.handleClickBtnGetNewSMSCode}"
							  @on-full-filled-code="${this.handleFullFilledCode}">
			</c-enter-sms-code>
		`;
	}

	protected initSlots(): void {

	}

	protected getTemplateByStage(stage: EValidatePhoneNumberAndSMSStages): TemplateResult {
		switch (stage) {
			case EValidatePhoneNumberAndSMSStages.ENTER_PHONE_NUMBER:
				return this.getEnterPhoneNumberTemplate();
			case EValidatePhoneNumberAndSMSStages.VALIDATE_SMS_CODE:
				return this.getEnterSMSCodeTemplate();
		}
	}

	protected changeStage(stage: EValidatePhoneNumberAndSMSStages) {
		this.stage = stage;
		switch (stage) {
			case EValidatePhoneNumberAndSMSStages.ENTER_PHONE_NUMBER:
				this.stageEnterPhoneNumber();
				break;
			case EValidatePhoneNumberAndSMSStages.VALIDATE_SMS_CODE:
				this.stageValidateSMSCode();
				break;
		}
	}

	protected stageEnterPhoneNumber(): void {
		this.phoneNumber = null;
		this.smsCode = null;
	}

	protected stageValidateSMSCode(): Promise<void> {
		return from(this.enterSMSCodeComponentAsync)
			.pipe(
				switchMap(el => {
					return from(el.updateComplete)
						.pipe(
							map(() => el)
						)
				}),
				tap(component => {
					component.focus();
					if (this.enterSMSData.estimate) {
						component.startTimer(this.enterSMSData.estimate)
					}
				}),
				map(() => {
					return null;
				})
			).toPromise();
	}

	private getSMSCode(): void {
		if (this.isSMSRequesting) {
			return;
		}
		this.isSMSRequesting = true;
		this.isNetworkError = false;
		const data: FormData = this.getDataForGetSMSCode();
		this.httpService
			.request<TSMSValidationGetResponse>({
				...this.getSMSRequestConfig,
				data: data,
			})
			.then((response) => {
				this.enterSMSData = {
					estimate: response.data.data.duration,
					attemptsCountSend: response.data.data.attemptsCountSend,
					attemptsCountValidate: response.data.data.attemptsCountValidate,
				}
				this.changeStage(EValidatePhoneNumberAndSMSStages.VALIDATE_SMS_CODE);
				this.onResponseGetSMSCode(response);
			})
			.catch((reject: AxiosError) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
				this.onRejectGetSMSCode(reject);
			})
			.finally(() => {
				this.isSMSRequesting = false;
			})
	}

	protected getDataForGetSMSCode(): FormData {
		return objectToFormData({
			...this.addDataGetSMS || {},
			phoneNumber: this.phoneNumber,
		});
	}

	protected onResponseGetSMSCode<T = TSMSValidationGetResponse>(response: AxiosResponse<TRequestResponse<T>>): void {
		this.emitEventOnResponseGetSMSCode(response);
	}

	protected onRejectGetSMSCode<T = unknown>(reject: AxiosError<TRequestResponse<T>>): void {
		this.emitEventOnRejectGetSMSCode(reject);
	}

	private emitEventOnResponseGetSMSCode<T = unknown>(response: AxiosResponse<TRequestResponse<T>>): void {
		const event: CustomEvent<TValidatePhoneNumberAndSMSEventResponse<T>> = new CustomEvent('on-response-get-sms-code', {
			detail: {
				response: response
			},
		});
		this.dispatchEvent(event);
	}

	private emitEventOnRejectGetSMSCode<T = unknown>(response: AxiosError<TRequestResponse<T>>): void {
		const event: CustomEvent<TValidatePhoneNumberAndSMSEventReject<T>> = new CustomEvent('on-reject-get-sms-code', {
			detail: {
				response: response
			},
		});
		this.dispatchEvent(event);
	}

	private validateSMSCode(): void {
		if (this.isSMSValidation) {
			return;
		}
		this.isSMSValidation = true;
		this.isNetworkError = false;
		const data: FormData = this.getDataForValidateSMSCode();
		this.httpService
			.request<unknown>({
				...this.validateSMSRequestConfig,
				data: data,
			})
			.then((response) => {
				this.onResponseValidateSMSCode(response);
			})
			.catch((reject: AxiosError<TRequestResponse<TSMSValidationValidateReject>>) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
				if (reject.response.status === 400) {
					this.enterSMSData = {
						attemptsCountValidate: reject.response.data.data.attemptsCountValidate,
						attemptsCountSend: reject.response.data.data.attemptsCountSend,
					}
				}
				this.onRejectValidateSMSCode(reject);
			})
			.finally(() => {
				this.isSMSValidation = false;
			})
	}

	protected getDataForValidateSMSCode(): FormData {
		return objectToFormData({
			...this.addDataValidateSMS || {},
			phoneNumber: this.phoneNumber,
			code: this.smsCode,
		});
	}

	protected onResponseValidateSMSCode<T = unknown>(response: AxiosResponse<TRequestResponse<T>>): void {
		this.emitEventOnResponseValidateSMSCode(response);
	}

	protected onRejectValidateSMSCode<T = TSMSValidationValidateReject>(reject: AxiosError<TRequestResponse<T>>): void {
		this.emitEventOnRejectValidateSMSCode(reject);
	}

	private emitEventOnResponseValidateSMSCode<T = unknown>(response: AxiosResponse<TRequestResponse<T>>): void {
		const event: CustomEvent<TValidatePhoneNumberAndSMSEventResponse<T>> = new CustomEvent('on-response-validate-sms-code', {
			detail: {
				response: response
			},
		});
		this.dispatchEvent(event);
	}

	private emitEventOnRejectValidateSMSCode<T = unknown>(response: AxiosError<TRequestResponse<T>>): void {
		const event: CustomEvent<TValidatePhoneNumberAndSMSEventReject<T>> = new CustomEvent('on-reject-validate-sms-code', {
			detail: {
				response: response
			},
		});
		this.dispatchEvent(event);
	}

	protected isDisabledBtnGetSMS(): boolean {
		return this.isSMSRequesting;
	}

	protected isLoadingBtnGetSMS(): boolean {
		return this.isSMSRequesting;
	}

	protected isDisabledBtnGetNewSMS(): boolean {
		return this.isSMSRequesting;
	}

	protected isLoadingBtnGetNewSMS(): boolean {
		return this.isSMSRequesting;
	}

	protected handleBtnClickReload(event: Event): void {
		event.preventDefault();
		this.getSMSCode();
	}

	protected handleClickBtnGetSMS(event: CustomEvent<TEnterPhoneNumberActionBtnClickEventDetail>): void {
		this.phoneNumber = event.detail.phoneNumber;
		this.getSMSCode();
	}

	protected handleClickBtnGetNewSMSCode(event: CustomEvent): void {
		this.getSMSCode();
	}

	protected handleFullFilledCode(event: CustomEvent<TEnterSMSCodeFullFilledCodeEventDetail>): void {
		this.smsCode = event.detail.value;
		this.validateSMSCode();
	}
}
