import {ESlideMenuDirection, TItemShiftStream} from "@components/slide-menu/model";
import {getDefinitionElement, isDefinitionElementEvent} from "@helpers/index";
import {combineLatest, fromEvent, Subject} from "rxjs";
import {filter, pairwise, startWith} from "rxjs/internal/operators";

export default class SlideMenu {
	private CLASS_MAIN: string = 'js-slideMenu';
	private CLASS_CONTAINER: string = 'js-slideMenuContainer';
	private CLASS_LIST: string = 'js-slideMenuList';
	private CLASS_ITEM: string = 'js-slideMenuItem';
	private CLASS_HEADER: string = 'js-slideMenuHeader';
	private CLASS_BODY: string = 'js-slideMenuBody';
	private CLASS_HANDLER: string = 'js-slideMenuHandler';

	private DATA_ATTR_DIRECTION: string = 'data-slide-menu-direction';

	private slideMenuEl: HTMLElement;
	private containerEl: HTMLElement;

	private immersionLevel: number = 0;

	private isAnimating: boolean = false;

	private itemShiftStream: Subject<TItemShiftStream> = new Subject<TItemShiftStream>();

	constructor(slideMenu: HTMLElement) {
		this.slideMenuEl = slideMenu;
		this.initElements();
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.slideMenuEl, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, `.${this.CLASS_HANDLER}`)),
			)
			.subscribe(event => {
				event.preventDefault();
				const handler: HTMLElement = getDefinitionElement(event.target as HTMLElement, `.${this.CLASS_HANDLER}`);
				const direction: ESlideMenuDirection = this.getDirection(handler);
				if (direction === ESlideMenuDirection.NEXT) {
					this.slideNext(handler);
				} else if (direction === ESlideMenuDirection.BACK) {
					this.slideBack(handler);
				}
			});

		combineLatest([
			fromEvent<TransitionEvent>(this.containerEl, 'transitionend'),
			this.itemShiftStream.asObservable(),
		])
			.pipe(
				startWith([null, null]),
				pairwise(),
				filter(([prev, current]) => {
					const prevTranTimeStamp = prev?.[0]?.timeStamp;
					const currentTranTimeStamp = current[0].timeStamp;
					return prevTranTimeStamp !== currentTranTimeStamp;
				})
			)
			.subscribe(([prev, [transitionEndEvent, itemShift]]) => {
				this.isAnimating = false;
				if (itemShift.direction === ESlideMenuDirection.BACK) {
					itemShift.body.style.visibility = 'hidden';
				}
			});
	}

	private getPrevBody(handler: HTMLElement): HTMLElement {
		const item: HTMLElement = handler.closest(`.${this.CLASS_ITEM}`);
		const body: HTMLElement = item.closest(`.${this.CLASS_BODY}`);
		return body ? body : this.containerEl.querySelector(`.${this.CLASS_LIST}`);
	}

	private slideNext(handler: HTMLElement): void {
		if (this.isAnimating) {
			return;
		}
		const item: HTMLElement = this.getItem(handler);
		const body: HTMLElement = this.getBody(item);
		this.containerEl.style.maxHeight = this.containerEl.offsetHeight + 'px';
		this.containerEl.style.minHeight = this.containerEl.offsetHeight + 'px';
		this.isAnimating = true;
		this.immersionLevel += 1;
		body.style.visibility = 'visible';
		this.itemShiftStream.next({
			direction: ESlideMenuDirection.NEXT,
			body,
			item,
			handler,
		});
		this.containerEl.style.maxHeight = body.offsetHeight + 'px';
		this.containerEl.style.minHeight = body.offsetHeight + 'px';
		this.shiftContainer(this.immersionLevel);
	}

	private slideBack(handler: HTMLElement): void {
		if (this.isAnimating) {
			return;
		}
		const item: HTMLElement = this.getItem(handler);
		const body: HTMLElement = this.getBody(item);
		const prevBody: HTMLElement = this.getPrevBody(handler);
		this.containerEl.style.maxHeight = body.offsetHeight + 'px';
		this.containerEl.style.minHeight = body.offsetHeight + 'px';
		this.isAnimating = true;
		this.immersionLevel -= 1;
		this.itemShiftStream.next({
			direction: ESlideMenuDirection.BACK,
			body,
			item,
			handler,
		});
		this.containerEl.style.maxHeight = prevBody.offsetHeight + 'px';
		this.containerEl.style.minHeight = prevBody.offsetHeight + 'px';
		this.shiftContainer(this.immersionLevel);
	}

	private shiftContainer(level: number): void {
		this.containerEl.style.transform = `translateX(-${100 * level}%)`;
	}

	private getBody(item: HTMLElement): HTMLElement {
		let body: HTMLElement = item.querySelector(`.${this.CLASS_BODY}`);
		if (!body) {
			throw new Error('SlideMenu: body is not defined !');
		}
		return body;
	}

	private getItem(handler: HTMLElement): HTMLElement {
		let item: HTMLElement;
		if (handler.classList.contains(this.CLASS_ITEM)) {
			item = handler;
		} else {
			item = handler.closest(`.${this.CLASS_ITEM}`);
		}
		if (!item) {
			throw new Error('SlideMenu: item is not defined !');
		}
		return item;
	}

	private getDirection(handler: HTMLElement): ESlideMenuDirection {
		const dataValue: string = handler.getAttribute(this.DATA_ATTR_DIRECTION);
		switch (dataValue) {
			case ESlideMenuDirection.BACK:
				return ESlideMenuDirection.BACK;
			case ESlideMenuDirection.NEXT:
				return ESlideMenuDirection.NEXT;
			default:
				throw new Error('SlideMenu: direction is not defined !');
		}
	}

	private initElements(): void {
		this.containerEl = this.slideMenuEl.querySelector(`.${this.CLASS_CONTAINER}`);
		if (!this.containerEl) {
			throw new Error('SlideMenu: container is not defined !');
		}
	}
}

