import {EOperatorCardDirection, TOperatorCard} from "@components/operator-card/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";

@customElement('c-operator-card')
export default class OperatorCardComponent extends WebComponentLite {
	@property({
		type: Object
	})
	public data: TOperatorCard;

	@property({
		type: String,
	})
	public direction: EOperatorCardDirection = EOperatorCardDirection.VERTICAL;

	protected render(): TemplateResult {
		return html`
			<div class="c-operator-card ${this.getMods()}">
				<div class="c-operator-card__img-box">
					<img class="c-operator-card__img"
						 src="${this.data?.img?.url}"
						 alt="${this.data?.img?.altText}">
				</div>
				<h4 class="c-operator-card__title">${this.data?.title}</h4>
			</div>
		`;
	}

	protected getMods(): string {
		const mods: Array<string> = [this.direction];
		return mods
			.filter(Boolean)
			.map(mod => `c-operator-card_${mod}`)
			.join(' ');
	}
}
