import {EBtnSize, EBtnType} from "@components/btn/model";
import {TCartPreviewResponse} from "@components/cart-preview/model";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {TCartItem, TCartRemoveRequest, TCartRemoveResponse} from "@models/Cart";
import CartQuantity from "@modules/cart/CartQuantity";
import ProductCartHandler from "@modules/cart/ProductCartHandler";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import HttpRequest from "@modules/http-request/HttpRequest";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosRequestConfig} from "axios";
import {customElement, html, state, property, PropertyValues, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-cart-preview')
export default class CartPreviewComponent extends WebComponentLite {
	@property({
		type: String,
		attribute: 'cart-url',
	})
	public cartUrl: string = '';

	@property({
		type: String,
		attribute: 'catalog-url',
	})
	public catalogUrl: string = '';

	@property({
		type: Object,
		attribute: 'get-request-config',
	})
	public getRequestConfig: AxiosRequestConfig = {
		url: '/checkout/preview/',
		method: "GET",
	}

	@property({
		type: Object,
		attribute: 'remove-request-config',
	})
	public removeRequestConfig: AxiosRequestConfig = {
		url: '/cart/remove/',
		method: "POST",
	}

	@state()
	private isLoading: boolean = false;

	@state()
	private isGlobalLoading: boolean = false;

	@state()
	private error: unknown;

	@state()
	private items: Array<TCartItem> = [];

	private httpService: HttpRequest = new HttpRequest();
	private cartQuantity: CartQuantity = CartQuantity.getInstance();
	private productCartHandler: ProductCartHandler = ProductCartHandler.getInstance();

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
	}

	protected render(): TemplateResult {
		let template: TemplateResult;

		if (this.isLoading) {
			template = this.getLoaderTemplate();
		} else if (this.error) {
			template = this.getErrorTemplate();
		} else if (!this.items.length) {
			template = this.getEmptyTemplate();
		} else {
			template = this.getContentTemplate();
		}

		return html`
			<article class="c-cart-preview">
				${template}

				<c-loader class="c-cart-preview__loader ${this.isGlobalLoading ? 'c-cart-preview__loader_active' : ''}"
						  .active="${this.isGlobalLoading}"
						  .type="${ELoaderType.PRIMARY}"
						  .size="${ELoaderSize.MD}">
			</article>
		`;
	}

	private getContentTemplate(): TemplateResult {
		return html`
			<div class="c-cart-preview__body">
				<ul class="c-cart-preview__list">
					${this.items.map(item => this.getItemTemplate(item))}
				</ul>
				${this.getSummaryTemplate()}
			</div>
			<footer class="c-cart-preview__footer">
				<c-btn .link="${this.cartUrl}"
					   .size="${EBtnSize.LG}"
					   .type="${EBtnType.PRIMARY}">
					Перейти в корзину
				</c-btn>
			</footer>
		`;
	}

	private getItemTemplate(item: TCartItem): TemplateResult {
		const {availability, quantity, product: {price, img, name}} = item;

		const notAvailableClass = 'c-cart-preview-item_not_available';

		const formatterPrice = MoneyFormatter.format(
			price,
			EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);
		return html`
			<li class="c-cart-preview__item c-cart-preview-item ${availability ? '' : notAvailableClass}">
				<div class="c-cart-preview-item__img-box">
					<img data-src="${img}" alt="pic" class="c-cart-preview-item__img u-shimmer js-lazyLoadImage">
				</div>
				<div class="c-cart-preview-item__body">
					<h6 class="c-cart-preview-item__name">${name}</h6>
					<p class="c-cart-preview-item__price">
						${unsafeHTML(formatterPrice)}
						${quantity > 1
							? html`
								<span>x${quantity}</span>
							`
							: null
						}
					</p>
					${
						availability
							? null
							: html`
								<p class="c-cart-preview-item__not-available">Нет в наличии</p>
							`
					}
				</div>
				<div class="c-cart-preview-item__controls">
					<button
						class="c-cart-preview-item__remove"
						@click="${(event) => this.handleClickRemoveItem(event, item)}">
						<i class="o-icon o-icon_garbage-can-filled"></i>
					</button>
				</div>
			</li>
		`
	}

	private handleClickRemoveItem(event: Event, item: TCartItem): void {
		event.preventDefault();
		this.removeItem(item);
	}

	private getSummaryTemplate(): TemplateResult {
		const totalPrice = this.items.reduce((prevValue, currValue) => {
			const {quantity, product: {price}} = currValue;
			return prevValue + (parseFloat(price) * quantity);
		}, 0);
		const formatterTotalPrice = MoneyFormatter.format(
			totalPrice.toString(),
			EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);
		return html`
			<section class="c-cart-preview__summary">
				<h4 class="c-cart-preview__summary-title">Итого</h4>
				<p class="c-cart-preview__summary-total">${unsafeHTML(formatterTotalPrice)}</p>
			</section>
		`;
	}

	private getLoaderTemplate(): TemplateResult {
		return html`
			<c-loader active
					  type="${ELoaderType.PRIMARY}"
					  size="${ELoaderSize.MD}">
			</c-loader>
		`;
	}

	private getErrorTemplate(): TemplateResult {
		return html`
			<div class="u-text-center">
				<h4>При загрузке данных произошла ошибка !</h4>
				<p class="u-mb-20">Проверьте подключение к интернету</p>
				<c-btn type="${EBtnType.PRIMARY}"
					   size="${EBtnSize.MD}"
					   @click="${this.handleClickBtnReload}">
					Повторить
				</c-btn>
			</div>
		`;
	}

	private handleClickBtnReload(event: Event): void {
		event.stopPropagation();
		event.stopImmediatePropagation()
		event.preventDefault();
		this.getData();
	}

	private getEmptyTemplate(): TemplateResult {
		return html`
			<div class="c-result c-result_404">
				<div class="c-result__icon">
					<i class="o-icon o-icon_cart"></i>
				</div>
				<h5 class="c-result__title">
					Ваша корзина пуста
				</h5>
				<p class="c-result__sub-title">
					В нашем магазине есть множество товаров, которые вас могут заинтересовать!
				</p>
			</div>
			<div class="u-text-center">
				<c-btn .link="${this.catalogUrl}"
					   .size="${EBtnSize.MD}"
					   .type="${EBtnType.PRIMARY}">
					К покупкам
				</c-btn>
			</div>
		`;
	}

	public getData(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.error = null;
		const {url, method} = this.getRequestConfig;
		this.httpService
			.request<TCartPreviewResponse>({
				url: url,
				method: method,
			})
			.then(response => {
				this.items = response.data?.data || [];
			})
			.catch(reject => {
				this.error = reject;
			})
			.finally(() => {
				this.isLoading = false;
			});
	}

	private removeItem(item: TCartItem): void {
		const {url, method} = this.removeRequestConfig;
		const data: TCartRemoveRequest = {
			sku: item.product.sku,
		}
		this.isGlobalLoading = true;
		this.httpService
			.request<TCartRemoveResponse>({
				url: url,
				method: method,
				data: data,
			})
			.then((response) => {
				this.getData();
				this.isGlobalLoading = false;
				this.cartQuantity.update(response.data.data.cartItemCount);
				this.productCartHandler.setUnactive(data.sku);
			})
			.catch(() => {
				this.isGlobalLoading = false;
			})
			.finally(() => {
				this.isGlobalLoading = false;
			});
	}
}
