import Mask, {EMaskTypes, MASK_FORMATS} from "@modules/mask/Mask";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import IMask from "imask";
import {customElement, html, property, PropertyValues, query, TemplateResult} from "lit-element";
import {fromEvent, merge} from "rxjs";
import AnyMaskedOptions = IMask.AnyMaskedOptions;
import InputMask = IMask.InputMask;

@customElement('c-input')
export default class InputComponent extends WebComponentLite {
	@property({
		type: String,
		reflect: true,
	})
	public label: string = '';

	@property({
		type: String,
		reflect: true,
	})
	public name: string = '';

	@property({
		type: String,
		reflect: true,
	})
	public value: string = '';

	@property({
		type: String,
		reflect: true,
	})
	public type: string = '';

	@property({
		type: Boolean,
		reflect: true,
	})
	public textarea: boolean = false;

	@property({
		type: String,
		reflect: true,
	})
	public fieldClass: string = '';

	@property({
		type: String,
		attribute: 'mask-type'
	})
	public maskType: EMaskTypes;

	@query('[input-field]')
	private field: HTMLInputElement;

	public mask: InputMask<AnyMaskedOptions>;

	public getMask(): InputMask<AnyMaskedOptions> {
		return this.mask;
	}

	public focus(): void {
		this.field.focus();
	}

	public getValue(): string {
		return this.field.value;
	}

	public setValue(value: string): void {
		this.field.value = value;
		this.dispatchInputChange();
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
		this.initMask();
	}

	disconnectedCallback() {
		super.disconnectedCallback();
		this.mask?.destroy();
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-input js-field">
				${this.getField()}
				<label class="c-input__label">${this.label}</label>
				<div class="c-input__bar"></div>
			</div>
		`;
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);
		if (this.field && name === 'value') {
			if (this.mask) {
				setTimeout(() => {
					this.mask.updateValue();
					this.mask.updateControl();
				}, 0);
			}
		}
	}

	private initMask(): void {
		if (this.maskType) {
			this.mask = IMask(
				this.field,
				MASK_FORMATS[this.maskType].option,
			);
		}
	}

	private dispatchInputChange(): void {
		this.field.dispatchEvent(new Event('input'));
		this.field.dispatchEvent(new Event('change'));
	}

	private getField(): TemplateResult {
		if (this.textarea) {
			return html`
				<textarea input-field
						  class="c-input__field ${this.fieldClass}"
						  .type="${this.type}"
						  .value="${this.value}"
						  .name="${this.name}"
						  placeholder="textarea">
				</textarea>
			`;
		}
		return html`
			<input input-field
				   class="c-input__field ${this.fieldClass}"
				   .type="${this.type}"
				   .value="${this.value}"
				   .name="${this.name}"
				   placeholder="&amp;nbsp;">
		`;
	}

	private attachEvent(): void {
		if (this.mask) {
			this.mask.on('accept', () => {
				this.emitEventOnChange();
			});
			this.mask.on('complete', () => {
				this.emitEventOnChange();
			});
		} else {
			merge(
				fromEvent(this.field, 'input'),
				// fromEvent(this.field, 'change').pipe(tap(() => console.log('event change'))),
				fromEvent(this.field, 'cut'),
				fromEvent(this.field, 'paste'),
				fromEvent(this.field, 'drop'),
				// fromEvent(this.field, 'keydown').pipe(tap(() => console.log('event keydown'))),
			)
				.subscribe(() => {
					this.emitEventOnChange();
				});
		}
	}

	private emitEventOnChange(): void {
		if (this.mask) {
			this.value = Mask.getFormattedValue(this.field.value, this.maskType);
		} else {
			this.value = this.field.value;
		}
		const event = new CustomEvent('on-change', {
			detail: {}
		});
		this.dispatchEvent(event);
	}
}
