import SmoothScroll from "@modules/scroll/SmoothScroll";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, state, PropertyValues} from "lit-element";
import {fromEvent} from "rxjs";
import {debounceTime} from "rxjs/operators";

@customElement('c-up-button')
export default class UpButtonComponent extends WebComponentLite {
	@state()
	private isVisible: boolean = false;

	private smoothScroll: SmoothScroll = new SmoothScroll({
		block: 'start',
	});

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.updateVisibility();
		this.attachEvent();
	}

	protected render(): unknown {
		return html`
			<button class="c-up-button ${this.isVisible ? 'c-up-button_show' : ''}"
					@click="${this.handleClick}">
				<i class="o-icon o-icon_angle-top-filled"></i>
			</button>
		`
	}

	private attachEvent(): void {
		fromEvent(window, 'scroll')
			.pipe(
				debounceTime(60),
			)
			.subscribe(() => {
				this.updateVisibility();
			});
	}

	private handleClick(event: Event): void {
		event.preventDefault();
		this.smoothScroll.scrollToTop();
	}

	private updateVisibility(): void {
		if (window.scrollY > window.innerHeight * 2) {
			this.isVisible = true;
			return;
		}
		this.isVisible = false;
	}
}
