import {TCartItem} from "@models/Cart";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-edit-products-cart-accessory-card')
export default class EditProductsCartAccessoryCardComponent extends WebComponentLite {
	@property({
		type: Object,
	})
	public data: TCartItem;

	protected render(): TemplateResult {
		const {product: {img, name, price}} = this.data;

		const formattedPrice: string = MoneyFormatter.format(
			price,
			EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);

		return html`
			<div class="c-edit-products-cart-accessory-card">
				<div class="c-edit-products-cart-accessory-card__img-box">
					<img data-src="${img}"
						 alt="pic"
						 class="c-edit-products-cart-accessory-card__img u-shimmer js-lazyLoadImage">
				</div>
				<h4 class="c-edit-products-cart-accessory-card__name">${name}</h4>
				<p class="c-edit-products-cart-accessory-card__price">${unsafeHTML(formattedPrice)}</p>
			</div>
		`;
	}
}
