import {interval} from "rxjs";
import {takeWhile} from "rxjs/operators";

export default class CircleTimer {
	private CLASS_BLOCK: string = 'js-circleTimer';
	private CLASS_CIRCLE: string = 'js-circleTimerSvgCircle';
	private CLASS_LABEL: string = 'js-circleTimerLabel';

	private CLASS_INFINITY: string = 'is-infinity';

	private circleTimerEl: HTMLElement;
	private svgCircleEl: SVGCircleElement;
	private labelEl: HTMLElement;

	constructor(circleTimerEl: HTMLElement) {
		this.circleTimerEl = circleTimerEl;
		this.init();
	}

	public run(seconds: number): void {
		const radius = this.svgCircleEl.r.baseVal.value;
		const circumference = radius * 2 * Math.PI;

		if (seconds === Infinity) {
			const infinityVal: number = (circumference * 40) / 100;
			this.svgCircleEl.style.strokeDasharray = circumference.toString();
			this.svgCircleEl.style.strokeDashoffset = circumference.toString();
			this.setProgress(infinityVal, circumference, this.svgCircleEl);
			this.circleTimerEl.classList.add(this.CLASS_INFINITY);
			return;
		}

		this.svgCircleEl.style.strokeDasharray = circumference.toString();
		this.svgCircleEl.style.strokeDashoffset = circumference.toString();

		interval(1000)
			.pipe(
				takeWhile(value => {
					return value < seconds + 1
				}),
			)
			.subscribe(second => {
				const done: number = seconds - second;
				const percent: number = (done / seconds) * 100;
				this.labelEl.textContent = done.toString();
				this.setProgress(percent, circumference, this.svgCircleEl);
			})
	}

	private init(): void {
		this.findElements();
	}

	private findElements(): void {
		this.svgCircleEl = this.circleTimerEl.querySelector(`.${this.CLASS_CIRCLE}`);
		this.labelEl = this.circleTimerEl.querySelector(`.${this.CLASS_LABEL}`);
	}

	private setProgress(value: number, circumference: number, circle: SVGCircleElement): void {
		const offset = circumference - value / 100 * circumference;
		circle.style.strokeDashoffset = offset.toString();
	}
}
