import {EAnimationType, IAnimation} from "@modules/animation/model";
import Animation from "@modules/animation/Animation";
import {ICollapse, TCollapseOption} from "@modules/collapse/model";
import Collapse from "@modules/collapse/Collapse";

export default class CollapseAnimated extends Collapse implements ICollapse {
	private animation: IAnimation;

	constructor(blockEl: HTMLElement, options?: TCollapseOption) {
		super(blockEl, options);

		this.animation = this.getAnimation();
	}

	public open() {
		this.hooksBeforeOpen();
		this.animation.show();
	}

	public close() {
		this.hooksBeforeClose();
		this.animation.hide();
	}

	private getAnimation(): IAnimation {
		return new Animation(
			this.containerEl,
			{
				name: 'o-animation-height',
				type: EAnimationType.TRANSITION,
				beforeEnter: () => {
					const height = this.getContainerHeight();
					this.containerEl.style.maxHeight = '0px';

					setTimeout(() => {
						this.containerEl.style.maxHeight = height + 'px';
					}, 60);
				},
				afterEnter: () => {
					super.openWithoutHooks();
					this.hooksAfterOpen();
					this.containerEl.style.maxHeight = '';
				},
				beforeLeave: () => {
					const height = this.getContainerHeight();
					this.containerEl.style.maxHeight = height + 'px';

					setTimeout(() => {
						this.containerEl.style.maxHeight = '0px';
					}, 60);
				},
				afterLeave: () => {
					super.closeWithoutHooks();
					this.hooksAfterClose();
					this.containerEl.style.maxHeight = '';
				},
			}
		)
	}

	private getContainerHeight(): number {
		// this.containerEl.style.display = 'block';
		return this.containerEl.offsetHeight;
	}
}
