import {TCartItem} from "@models/Cart";
import Carousel from "@modules/carousel/Carousel";
import {ICarousel} from "@modules/carousel/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, PropertyValues, query, TemplateResult} from "lit-element";

@customElement('c-edit-products-cart-carousel-accessories')
export default class EditProductsCartCarouselAccessoriesComponent extends WebComponentLite {
	@property({
		type: Array,
	})
	data: Array<TCartItem> = [];

	@property({
		type: Array
	})
	selectedSkus: Array<string> = [];

	@property({
		type: Function
	})
	onChange: (item: TCartItem, checked: boolean) => boolean | Promise<boolean>;

	@query('.c-edit-products-cart-carousel-accessories')
	private containerEl: HTMLElement;

	private carousel: ICarousel;

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.carousel = new Carousel(this.containerEl);
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-edit-products-cart-carousel-accessories" data-carousel-config-name="cartAccessories">
				<header class="c-edit-products-cart-carousel-accessories__header">
					<h4 class="c-edit-products-cart-carousel-accessories__title">Подберите аксессуар к девайсу</h4>
					<div class="c-edit-products-cart-carousel-accessories__controls js-carouselControls">
						<button class="c-edit-products-cart-carousel-accessories__control js-carouselBtnPrev">
							<i class="o-icon o-icon_angle-left-filled"></i>
						</button>
						<button class="c-edit-products-cart-carousel-accessories__control js-carouselBtnNext">
							<i class="o-icon o-icon_angle-right-filled"></i>
						</button>
					</div>
				</header>

				<div class="c-edit-products-cart-carousel-accessories__carousel-viewport js-carouselViewport">
					<div class="c-edit-products-cart-carousel-accessories__carousel-container js-carouselContainer">
						${this.data.map(item => {
							const checked = this.selectedSkus.includes(item.product.sku);
							return html`
								<div class="c-edit-products-cart-carousel-accessories__carousel-item js-carouselItem">
									<c-toggle-card marker
												   type="checkbox"
												   name="selected-accessories"
												   class="u-h-100"
												   .value="${item.product.sku}"
												   ?checked="${checked}"
												   .onChange="${() => this.handleChange(item, checked)}">
										<c-edit-products-cart-accessory-card .data="${item}">
									</c-toggle-card>
								</div>
							`;
						})}
					</div>
				</div>
			</div>
		`;
	}

	private async handleChange(item: TCartItem, checked: boolean): Promise<boolean> {
		return this.onChange(item, !checked);
	}
}
