import Platform from "@modules/platform/Platform";
import {
	ISelect,
	ISelectView,
	TSelectChange,
	TSelectItem,
	TSelectOptions,
	TSelectSelectionProperties
} from "@modules/select/model";
import SelectViewModalAdapter from "@modules/select/SelectViewModalAdapter";
import {fromEvent, Observable, Subject} from "rxjs";
import {filter} from "rxjs/operators";

export default class Select implements ISelect {
	protected selectEl: HTMLElement;
	protected handlerEl: HTMLElement;
	protected originalEl: HTMLSelectElement;
	protected outputValueEl: HTMLElement;
	protected optionsEl: HTMLElement;
	protected view: ISelectView;

	private options: TSelectOptions;

	private eventChange: Subject<Array<TSelectChange>> = new Subject<Array<TSelectChange>>();

	constructor(selectEl: HTMLElement, options?: TSelectOptions) {
		this.selectEl = selectEl;
		this.options = {
			...this.getDefaultOption(),
			...options,
		};
		this.initElements();
		const selectionProperties: TSelectSelectionProperties = this.getSelectionProperties();
		this.view = new SelectViewModalAdapter(selectionProperties);
		this.attachEvent();
		this.syncOriginal();
	}

	private getDefaultOption(): TSelectOptions {
		return {
			classes: {
				select: 'js-select',
				handler: 'js-selectHandler',
				original: 'js-selectOriginal',
				outputValue: 'js-selectOutputValue',
			},
			stateClasses: {
				disabled: 'is-disabled',
				focused: 'is-focused',
				multiple: 'is-multiple',
				open: 'is-open',
				selected: 'is-selected',
				native: 'is-native',
			},
			dataAttributes: {
				label: 'data-select-label',
				close: 'data-select-close',
				accept: 'data-select-accept',
			}
		}
	}

	public open(): void {
		if (this.isDisabled()) {
			return;
		}
		this.setClassOpen();
		this.view.open();
	}

	public close(): void {
		this.removeClassOpen();
		this.view.close();
	}

	public disabled(): void {
		if (this.isOpen()) {
			this.close();
		}
		this.originalEl.disabled = true;
		this.selectEl.classList.add(this.options.stateClasses.disabled);
		this.syncClassDisabled();
	}

	public enabled(): void {
		this.originalEl.disabled = false;
		this.selectEl.classList.remove(this.options.stateClasses.disabled);
		this.syncClassDisabled();
	}

	public selectByIndex(index: number): void {
		const option: HTMLOptionElement = this.originalEl.options.item(index);
		if (!option) {
			throw new Error(`Select: option with index ${index} not found !`);
		}
		if (option.selected) {
			return;
		}
		option.selected = true;
		this.setSelectedLabels();
		this.syncClassSelected();
		this.eventChange.next([
			{
				value: option.value,
				index,
			}
		]);
		this.originalEl.dispatchEvent(new Event('change'));
	}

	public selectByValue(value: string): void {
		const option: HTMLOptionElement = Array.from(this.originalEl.options).find(option => option.value == value);
		if (!option) {
			throw new Error(`Select: option with value ${value} not found !`);
		}
		this.selectByIndex(option.index);
	}

	public isOpen(): boolean {
		return this.selectEl.classList.contains(this.options.stateClasses.open);
	}

	public isMultiple(): boolean {
		return this.originalEl.hasAttribute('multiple');
	}

	public getStreamOnChange(): Observable<Array<TSelectChange>> {
		return this.eventChange.asObservable();
	}

	public getValue(): Array<string> {
		return Array.from(this.originalEl.selectedOptions).map(option => option.value);
	}

	public getOriginalSelect(): HTMLSelectElement {
		return this.originalEl;
	}

	private initElements(): void {
		if (this.selectEl.classList.contains(this.options.classes.handler)) {
			this.handlerEl = this.selectEl;
		} else {
			this.handlerEl = this.selectEl.querySelector(`.${this.options.classes.handler}`);
			if (!this.handlerEl) {
				throw new Error('Select: header is not defined !');
			}
		}

		this.originalEl = this.selectEl.querySelector(`.${this.options.classes.original}`);
		if (!this.originalEl) {
			throw new Error('Select: select is not defined !');
		}

		this.outputValueEl = this.selectEl.querySelector(`.${this.options.classes.outputValue}`);
		if (!this.outputValueEl) {
			throw new Error('Select: output value is not defined !');
		}
	}

	private attachEvent(): void {
		fromEvent(this.handlerEl, 'click')
			.pipe(
				filter(() => !this.isShowNative()),
			)
			.subscribe((event) => {
				event.preventDefault();
				this.toggle();
			});

		fromEvent(this.originalEl, 'keydown')
			.pipe(
				filter(() => !this.isShowNative()),
			)
			.subscribe(event => {
				if ((event as any).key === "Enter" || (event as any).code === 'Space') {
					event.preventDefault();
					this.toggle();
				}
			});

		fromEvent(this.originalEl, 'focus')
			.subscribe(() => {
				this.setClassFocus();
			});

		fromEvent(this.originalEl, 'blur')
			.subscribe(() => {
				this.removeClassFocus();
			});

		fromEvent(this.originalEl, 'change')
			.subscribe(() => {
				this.originalEl.blur();
				this.updateSelected();
			});

		this.view
			.getStreamOnSelect()
			.subscribe(streamOnSelect => {
				this.view.close();
				this.updateOriginalSelect(streamOnSelect);
			});

		this.view
			.getStreamOnClose()
			.subscribe(() => {
				this.close();
			});
	}

	private isShowNative(): boolean {
		return Platform.getInstance().isMobile();
	}

	private isDisabled(): boolean {
		return this.originalEl.disabled;
	}

	private syncOriginal(): void {
		this.setSelectedLabels();
		this.syncClassSelected();
		this.syncClassDisabled();
		this.syncClassNative();
	}

	private syncClassSelected(): void {
		const selectedOptions: Array<HTMLOptionElement> = Array.from(this.originalEl.selectedOptions);
		if (selectedOptions.length) {
			this.setClassSelected();
		} else {
			this.removeClassSelected();
		}
	}

	private syncClassDisabled(): void {
		if (this.originalEl.disabled) {
			this.setClassDisabled();
		} else {
			this.removeClassDisabled();
		}
	}

	private syncClassNative(): void {
		if (this.isShowNative()) {
			this.setClassNative();
		} else {
			this.removeClassNative();
		}
	}

	private setSelectedLabels(): void {
		const selectedLabels: Array<string> = Array.from(this.originalEl.selectedOptions).map(option => option.innerText);
		this.outputValueEl.innerText = selectedLabels.join(', ');
	}

	private updateSelected(): void {
		const selectedOptions: Array<HTMLOptionElement> = Array.from(this.originalEl.selectedOptions);
		const items: Array<TSelectChange> = selectedOptions.map(option => {
			return {
				index: option.index,
				value: option.value,
			}
		});
		this.syncOriginal();
		this.eventChange.next(items);
	}

	protected getSelectionProperties(): TSelectSelectionProperties {
		return {
			label: this.selectEl.getAttribute(this.options.dataAttributes.label),
			close: this.selectEl.getAttribute(this.options.dataAttributes.close),
			accept: this.selectEl.getAttribute(this.options.dataAttributes.accept),
			items: this.getSelectItems(),
			select: this.originalEl,
		}
	}

	private getSelectItems(): Array<TSelectItem> {
		return Array
			.from(this.originalEl.options)
			.map(option => {
				return {
					title: option.innerText,
					value: option.value,
					disabled: option.disabled,
					selected: option.selected,
					option: option,
				}
			});
	}

	private updateOriginalSelect(selectionItems: Array<TSelectItem>): void {
		Array
			.from(this.originalEl.options)
			.forEach(option => {
				option.selected = selectionItems.find(item => option.value === item.value).selected;
			});
		this.originalEl.dispatchEvent(new Event('change'));
	}

	private setClassOpen(): void {
		this.selectEl.classList.add(this.options.stateClasses.open);
	}

	private removeClassOpen(): void {
		this.selectEl.classList.remove(this.options.stateClasses.open);
	}

	private setClassFocus(): void {
		this.selectEl.classList.add(this.options.stateClasses.focused);
	}

	private removeClassFocus(): void {
		this.selectEl.classList.remove(this.options.stateClasses.focused);
	}

	private setClassSelected(): void {
		this.selectEl.classList.add(this.options.stateClasses.selected);
	}

	private removeClassSelected(): void {
		this.selectEl.classList.remove(this.options.stateClasses.selected);
	}

	private setClassDisabled(): void {
		this.selectEl.classList.add(this.options.stateClasses.disabled);
	}

	private removeClassDisabled(): void {
		this.selectEl.classList.remove(this.options.stateClasses.disabled);
	}

	private setClassNative(): void {
		this.selectEl.classList.add(this.options.stateClasses.native);
	}

	private removeClassNative(): void {
		this.selectEl.classList.remove(this.options.stateClasses.native);
	}

	private toggle(): void {
		if (this.isOpen()) {
			this.close()
		} else {
			this.open();
		}
	}
}
