import Carousel from "@modules/carousel/Carousel";
import {ICarousel} from "@modules/carousel/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, PropertyValues, query, state, TemplateResult} from "lit-element";
import {TProduct} from "@models/Product";
import {TInsuranceDevice} from "@models/Insurance";

@customElement('c-insurance-device-product-carousel')
export default class InsuranceDeviceProductCarouselComponent extends WebComponentLite {
	@property({
		type: Array,
	})
	data: Array<TProduct> = [];

	@property({
		type: Object,
	})
	insurance: TInsuranceDevice;

	@property({
		type: Array
	})
	get selectedSkus() {
		return this._selectedSkus;
	}

	set selectedSkus(value: Array<string>) {
		if (!this.isFirstSetedSelectedSku) {
			this.setFirstSelectedIndexes(value)
		}
		this._selectedSkus = value;
		this.isFirstSetedSelectedSku = true;
	}

	@state()
	_selectedSkus: Array<string> = [];

	private isFirstSetedSelectedSku: boolean = false;

	@property({
		type: Function
	})
	onChange: (item: TProduct, checked: boolean) => boolean | Promise<boolean>;

	@query('.c-insurance-device-product-carousel')
	private containerEl: HTMLElement;

	@state()
	private checkedIndexes: Array<number> = [];

	private carousel: ICarousel;

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.carousel = new Carousel(this.containerEl);
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-insurance-device-product-carousel" data-carousel-config-name="insuranceProduct">
				<div class="c-insurance-device-product-carousel__carousel-viewport js-carouselViewport">
					<div class="c-insurance-device-product-carousel__carousel-container js-carouselContainer">
						${this.data.map((product, index) => {
							const name = `insurance-${this.insurance.id}-device-product-sku`;
							const checked = this.checkedIndexes.includes(index);

							return html`
								<div class="c-insurance-device-product-carousel__carousel-item js-carouselItem">
									<c-toggle-card marker
												   type="checkbox"
												   name="${name}"
												   class="u-h-100"
												   .value="${product.sku}"
												   ?checked="${checked}"
												   .onChange="${() => this.handleChange(product, checked, index)}">
										<c-insurance-device-product-card .data="${product}">
									</c-toggle-card>
								</div>
							`;
						})}
					</div>
				</div>
			</div>
		`;
	}

	private async handleChange(item: TProduct, checked: boolean, index: number): Promise<boolean> {
		if (checked) {
			this.checkedIndexes = this.checkedIndexes.filter(i => i !== index);
		} else {
			this.checkedIndexes = [
				...this.checkedIndexes,
				index
			];
		}
		return this.onChange(item, !checked);
	}

	private setFirstSelectedIndexes(skus: Array<string>): void {
		const itemSkus = this.data.map(item => item.sku);
		this.checkedIndexes = skus.map(checkedSku => {
			const index = itemSkus.findIndex(sku => sku == checkedSku);
			itemSkus[index] = `${itemSkus[index]}#`;
			return index;
		});
	}
}
