//Collapse
export interface ICollapse {
	open(): void

	close(): void

	toggle(): void

	getState(): ECollapseState

	getHTMLElement(): HTMLElement
}

export type TCollapseOption = {
	classes?: TCollapseClasses
	stateClasses?: TCollapseStateClasses
	hooks?: TCollapseHooks
}

export type TCollapseClasses = {
	block?: string
	handler?: string
	container?: string
}

export type TCollapseStateClasses = {
	open?: string
	close?: string
}

export type TCollapseHooks = {
	beforeClose?: (instance: ICollapse) => void
	beforeOpen?: (instance: ICollapse) => void
	afterClose?: (instance: ICollapse) => void
	afterOpen?: (instance: ICollapse) => void
}

export enum ECollapseState {
	OPEN = 'OPEN',
	CLOSED = 'CLOSED',
}

export enum ECollapseType {
	DEFAULT,
	ANIMATED,
}

//Collapse end


//CollapseGroup
export type TCollapseGroupOption = {
	classes?: TCollapseGroupClasses
	dataAttributes?: TCollapseGroupDataAttributes
}

export type TCollapseGroupDataAttributes = {
	accordion?: string
}

export type TCollapseGroupClasses = {
	group?: string
	item?: string
}

export type TCollapseGroupItem = {
	element: HTMLElement,
	instance: ICollapse
}
//CollapseGroup end


//Factory
export interface ICollapseFactory {
	createCollapse(blockEl: HTMLElement, option?: TCollapseOption): ICollapse

	createCollapseAnimated(blockEl: HTMLElement, option?: TCollapseOption): ICollapse
}
