export function getExtensionFiles(str: string): string {
	return str.match(/\.([^.]*?)(?=\?|#|$)/)[1];
}

export function getSpecificFirstLevelChildren(parent: HTMLElement, className: string): Array<Element> {
	const children: Array<Element> = Array.from(parent.children);
	return children.filter((element: Element) => {
		return element.classList.contains(className);
	});
}

export function isSupportTouch() {
	try {
		document.createEvent('TouchEvent');
		return true;
	} catch (e) {
		return false;
	}
}

export function secondsToFullTime(sec: number) {
	const hours = Math.floor(sec / 3600);
	const minutes = Math.floor(sec / 60) % 60;
	const seconds = sec % 60;

	return [hours, minutes, seconds]
		.map((v) => v < 10 ? '0' + v : v)
		.filter((v, i) => v !== '00' || i > 0)
		.join(':');
}

export function strToBoolean(val: string | boolean) {
	return !!JSON.parse(String(val).toLowerCase());
}

export const isVisibleElement = (element: HTMLElement): boolean => {
	const display: string = getComputedStyle(element).display;
	return element.style.display !== 'none' && display !== 'none';
}

export function isHTMLElement(element: any) {
	try {
		//Using W3 DOM2 (works for FF, Opera and Chrome)
		return element instanceof HTMLElement;
	} catch (e) {
		//Browsers not supporting W3 DOM2 don't have HTMLElement and
		//an exception is thrown and we end up here. Testing some
		//properties that all elements have (works on IE7)
		return (typeof element === "object") &&
			(element.nodeType === 1) && (typeof element.style === "object") &&
			(typeof element.ownerDocument === "object");
	}
}

export function getPercentage(value: number, from: number): number {
	return (value / from) * 100;
}

export function stringToHTML(template: string): HTMLElement {
	const wrapper: HTMLElement = document.createElement("div");
	wrapper.innerHTML = template;
	return wrapper.children[0] as HTMLElement;
}

export function openFullscreen(el: HTMLElement) {
	if (el.requestFullscreen) {
		el.requestFullscreen();
	} else if ((el as any).mozRequestFullScreen) { /* Firefox */
		(el as any).mozRequestFullScreen();
	} else if ((el as any).webkitRequestFullscreen) { /* Chrome, Safari and Opera */
		(el as any).webkitRequestFullscreen();
	} else if ((el as any).msRequestFullscreen) { /* IE/Edge */
		(el as any).msRequestFullscreen();
	}
}

export function closeFullscreen() {
	if (document.exitFullscreen) {
		document.exitFullscreen();
	} else if ((document as any).mozCancelFullScreen) { /* Firefox */
		(document as any).mozCancelFullScreen();
	} else if ((document as any).webkitExitFullscreen) { /* Chrome, Safari and Opera */
		(document as any).webkitExitFullscreen();
	} else if ((document as any).msExitFullscreen) { /* IE/Edge */
		(document as any).msExitFullscreen();
	}
}

export function formDataToObject(formData: FormData): Object {
	let obj: any = {};
	formData.forEach((value, key) => {
		if (obj[key]) {
			if (Array.isArray(obj[key])) {
				obj[key].push(value);
			} else {
				obj[key] = [obj[key], value]
			}
		} else {
			obj[key] = value;
		}
	});
	return obj;
}

export function objectToFormData(object: Object): FormData {
	const data: FormData = new FormData();
	Object
		.entries(object)
		.forEach(([key, value]) => {
			let finalValue: string = value;
			if (typeof value === 'object') {
				if (Array.isArray(value)) {
					value.forEach(v => {
						data.append(key, v);
					});
				} else {
					finalValue = JSON.stringify(value);
					data.append(key, finalValue);
				}
			} else {
				data.append(key, finalValue);
			}
		});
	return data;
}

export function concatFormData(...formData: Array<FormData>): FormData {
	const data: FormData = new FormData();
	formData.forEach(item => {
		item.forEach((value, key) => {
			data.append(key, value);
		});
	});
	return data;
}

export function isNotNullOrUndefined(value: unknown): boolean {
	return value !== null && value !== undefined;
}

export function isFunction(fn: unknown): fn is Function {
	return typeof fn === 'function';
}

export function isAsyncFunction<T = unknown>(fn: unknown): fn is Promise<T> {
	const isAsync = fn.constructor.name === "AsyncFunction";
	return isFunction(fn) && isAsync;
}

export async function asyncDelay(ms) {
	return await new Promise(resolve => setTimeout(resolve, ms));
}

export async function asyncRequestAnimationFrame() {
	return await  new Promise(resolve => requestAnimationFrame(resolve));
}

export function pluralize(count: number, ...words: Array<string>) {
	const cases = [2, 0, 1, 1, 1, 2];
	return count + ' ' + words[(count % 100 > 4 && count % 100 < 20) ? 2 : cases[Math.min(count % 10, 5)]];
}
