import {isHTMLElement, stringToHTML} from "@helpers/index";
import Animation from "@modules/animation/Animation";
import {EAnimationType} from "@modules/animation/model";
import DialogOpening from "@modules/dialog/DialogOpening";
import {EDialogState, IDialog, TDialogOptions} from "@modules/dialog/model";
import CreateEvent from "@modules/event/Event";
import {IScrollable} from "@modules/scroll/model";
import Scrollable from "@modules/scroll/Scrollable";
import {BehaviorSubject, fromEvent, Observable} from "rxjs";
import {filter} from "rxjs/operators";

export default class Dialog<TOptions extends TDialogOptions> implements IDialog {
	private dialogEl: HTMLElement;
	private viewEl: HTMLElement;

	private parentEl: HTMLElement;

	private animation: Animation;
	private animationView: Animation;

	private onClickStream: BehaviorSubject<Event> = new BehaviorSubject<Event>(null);

	private _options: TOptions;

	private dialogOpening: DialogOpening = DialogOpening.getInstance();

	private scrollable: IScrollable = new Scrollable(document.body, {
		saveScrollIndent: true,
	});

	constructor(options?: TOptions) {
		const defaultOptions: TDialogOptions = this.getDefaultOptions();
		this.options = {
			...defaultOptions,
			...options,
			animation: {
				...defaultOptions.animation,
				...options.animation,
			}
		};
		this.init();
	}

	public open(): void {
		this.scrollable.disable();
		this.dialogOpening.show();
		this.animation.insert(document.body);

		if (this.animationView) {
			this.animationView.show();
		}
	}

	public close(): void {
		if (!this.options.closable) {
			return;
		}
		this.animation.hide();

		if (this.animationView) {
			this.animationView.hide();
		}
		this.dialogOpening.hide();
		if (!this.dialogOpening.hasOpen()) {
			this.scrollable.enable();
		}
	}

	public getState(): EDialogState {
		return this.dialogEl.classList.contains(this._options.stateClasses.open) ? EDialogState.OPEN : EDialogState.CLOSED;
	}

	public destroy(): void {
		this.dialogEl.remove();
	}

	public getViewEl(): HTMLElement {
		return this.viewEl;
	}

	public getDialogEl(): HTMLElement {
		return this.dialogEl;
	}

	public getOnClickStream(): Observable<Event> {
		return this.onClickStream
			.asObservable()
			.pipe(
				filter(event => Boolean(event))
			)
	}

	private init(): void {
		this.initElements();
		this.initState();
		this.attachEventOnClick();
		this.initAnimation();
	}

	private initAnimation(): void {
		this.animation = new Animation(this.dialogEl, {
			...this._options.animation.dialogOption,
			beforeEnter: () => {
				if (this._options.hooks?.beforeOpen) {
					this._options.hooks.beforeOpen();
				}
			},
			afterEnter: (el) => {
				this.clearClassState();
				this.dispatchEvent(EDialogState.OPEN);
				this.setClassState(EDialogState.OPEN);

				if (this._options.hooks?.afterOpen) {
					this._options.hooks.afterOpen();
				}

				if (this._options.animation.dialogOption?.afterEnter) {
					this._options.animation.dialogOption.afterEnter(el);
				}
			},
			beforeLeave: () => {
				if (this._options.hooks?.beforeClose) {
					this._options.hooks.beforeClose();
				}
			},
			afterLeave: (el) => {
				if (this.parentEl) {
					this.parentEl.appendChild(this.dialogEl);
				}

				this.clearClassState();
				this.dispatchEvent(EDialogState.CLOSED);
				this.setClassState(EDialogState.CLOSED);

				if (this._options.hooks?.afterClose) {
					this._options.hooks.afterClose();
				}

				if (this._options.animation.dialogOption?.afterLeave) {
					this._options.animation.dialogOption.afterLeave(el);
				}
			}
		});

		if (this._options.animation.viewOption) {
			this.animationView = new Animation(
				this.viewEl,
				this._options.animation.viewOption
			);
		}
	}

	private getDefaultOptions(): Partial<TDialogOptions> {
		return {
			element: "create",
			closable: true,
			maskClosable: true,
			hasBackdrop: true,
			classes: {
				dialog: 'js-dialog',
				view: 'js-dialogView',
			},
			stateClasses: {
				open: 'is-open',
				closed: 'is-closed',
			},
			actionClasses: {
				close: 'js-dialogCloseHandler',
			},
			animation: {
				dialogOption: {
					name: 'o-animation-fade-in',
					type: EAnimationType.ANIMATION,
				},
				viewOption: {
					name: 'o-animation-zoom-in',
					type: EAnimationType.ANIMATION,
				},
			},
		};
	}

	private initElements(): void {
		if (this._options.element === "create") {
			this.dialogEl = this.create();
		} else {
			if (!isHTMLElement(this._options.element)) {
				throw new Error('Dialog: Element is for HTMLElement !');
			}
			this.dialogEl = this._options.element;
		}
		this.findElements();
		this.parentEl = this.dialogEl.parentElement;
	}

	private create(): HTMLDialogElement {
		const template: string = this.getTemplate();
		const templateEl: HTMLElement = stringToHTML(template);
		return document.body.appendChild(templateEl) as HTMLDialogElement;
	}

	protected getTemplate(): string {
		return `
			  <dialod class="c-dialog ${this._options.classes.dialog} ${this._options.hasBackdrop ? 'c-dialog_backdrop' : ''}" role="dialog">
				<div class="c-dialog__view ${this._options.classes.view}"></div>
			  </dialod>
		`;
	}

	private findElements(): void {
		this.viewEl = this.dialogEl.querySelector(`.${this._options.classes.view}`);
	}

	private initState(): void {
		this.dialogEl.classList.add(this._options.stateClasses.closed);
	}

	private attachEventOnClick(): void {
		fromEvent(this.dialogEl, 'click')
			.subscribe(event => {
				const target: HTMLElement = event.target as HTMLElement;
				const view: HTMLElement = target.closest(`.${this._options.classes.view}`);
				const close: HTMLElement = target.closest(`.${this._options.actionClasses.close}`);

				//handler click close btn
				if (close) {
					event.preventDefault();
					this.close();
					return;
				}

				//handler click mask
				if (!view) {
					if (this._options.maskClosable && this.getState() === EDialogState.OPEN) {
						this.close();
						return;
					}
				}
				this.onClickStream.next(event);
			})
	}

	private setClassState(state: EDialogState): void {
		switch (state) {
			case EDialogState.OPEN:
				this.dialogEl.classList.add(this._options.stateClasses.open);
				return;
			case EDialogState.CLOSED:
				this.dialogEl.classList.add(this._options.stateClasses.closed);
				return;
		}
	}

	private clearClassState(): void {
		this.dialogEl.classList.remove(this._options.stateClasses.open);
		this.dialogEl.classList.remove(this._options.stateClasses.closed);
	}

	private dispatchEvent(state: EDialogState): boolean {
		let event: Event;

		if (state == EDialogState.OPEN) {
			event = new CreateEvent('open').createCustom();
		} else {
			event = new CreateEvent('closed').createCustom();
		}

		const canceled: boolean = !this.dialogEl.dispatchEvent(event);
		return !canceled;
	}

	get options(): TOptions {
		return this._options;
	}

	set options(options: TOptions) {
		this._options = {
			...this.options,
			...options,
		};
	}

	set closable(state: boolean) {
		this.options.closable = state;
	}

	set maskClosable(state: boolean) {
		this.options.maskClosable = state;
	}
}
