import GlobalLoader from "@modules/loader/GlobalLoader";
import HttpRequest from "@modules/http-request/HttpRequest";
import FormControlsChange from "@modules/form/FormControlsChange";
import {Method} from "axios";
import {stringToHTML} from "@helpers/index";
import StickyFilters from "@scenario/StickyFilters";
import ModalFilters from "@scenario/ModalFilters";
import SmoothScroll from "@modules/scroll/SmoothScroll";

export type FilterResponse = {
	body: string
	url: string
}

export default class ApplyFiltersRequestReplacement {
	private CLASS_BLOCK: string = 'js-applyFiltersRequestReplacement';
	private CLASS_FORM: string = 'js-applyFiltersRequestReplacementForm';
	private CLASS_BLOCK_REPLACEMENT: string = 'js-applyFiltersRequestReplacementBlockReplacement';

	private blockEl: HTMLElement;
	private blockReplacementEl: HTMLElement;
	private formEl: HTMLFormElement;

	private globalLoader: GlobalLoader;
	private httpService: HttpRequest;
	private formControlsChange: FormControlsChange;
	private isRequesting: boolean = false;
	private modalFilter: ModalFilters;
	private smoothScroll: SmoothScroll;

	public init(modalFilter: ModalFilters): void {
		this.blockEl = document.querySelector(`.${this.CLASS_BLOCK}`);
		this.blockReplacementEl = this.blockEl?.querySelector(`.${this.CLASS_BLOCK_REPLACEMENT}`);
		this.formEl = this.blockEl?.querySelector(`.${this.CLASS_FORM}`);
		this.modalFilter = modalFilter;

		if (!this.blockEl || !this.blockReplacementEl || !this.formEl) {
			return;
		}

		this.smoothScroll = new SmoothScroll();
		this.httpService = new HttpRequest();
		this.globalLoader = GlobalLoader.getInstance();
		this.attachEventOnFormChange();
	}

	private attachEventOnFormChange(): void {
		this.formControlsChange = new FormControlsChange(
			this.formEl, {
				onChange: (field) => {
					this.clearTogglesWithAppointmentForAll(field);
					this.request();
				}
			}
		);
	}

	private request(): void {
		if (this.isRequesting) {
			return;
		}
		this.isRequesting = true;
		this.globalLoader.show();
		const url = this.formEl.action;
		const method = this.formEl.method;
		const formData = new FormData(this.formEl);

		this.httpService
			.request<FilterResponse>({
				url: url,
				method: method as Method,
				data: formData,
			})
			.then(response => {
				this.handleResponse(response.data.data);
			})
			.catch(reject => {
				//handle error
			})
			.finally(() => {
				this.globalLoader.hide();
				this.isRequesting = false;
			});
	}

	private handleResponse(response: FilterResponse): void {
		this.replaceContent(
			stringToHTML(response.body)
		);
		this.initNewReplacementEl();
		this.attachEventOnFormChange();
		this.smoothScroll.scrollTo(this.formEl);
		this.modalFilter.updateFilterContent(this.formEl);
		if (this.modalFilter.isOpen()) {
			this.modalFilter.close();
		}
		if (response.url) {
			window.history.pushState({}, document.title, response.url);
		}
		new StickyFilters().init();
	}

	private replaceContent(contentEl: HTMLElement): void {
		this.blockReplacementEl.replaceWith(contentEl);
	}

	private initNewReplacementEl(): void {
		this.blockReplacementEl = this.blockEl?.querySelector(`.${this.CLASS_BLOCK_REPLACEMENT}`);
		this.formEl = this.blockEl?.querySelector(`.${this.CLASS_FORM}`);
	}

	private clearTogglesWithAppointmentForAll(clickedToggle: HTMLInputElement): void {
		const toggles: Array<HTMLInputElement> = [
			...Array.from(document.querySelectorAll(`input[type="checkbox"][value=""]`)),
			...Array.from(document.querySelectorAll(`input[type="radio"][value=""]`))
		] as Array<HTMLInputElement>;
		toggles.forEach(toggle => {
			const allToggles = Array.from(
				document.querySelectorAll(
					`input[type='radio'][name='${toggle.name}'], input[type='checkbox'][name='${toggle.name}']`
				)
			)
				.filter(i => i != toggle) as Array<HTMLInputElement>;

			if (toggle === clickedToggle && clickedToggle.checked) {
				allToggles.forEach(i => i.checked = false);
			}

			toggle.checked = false;
		});
	}
}
