export default class SmoothScroll {

	constructor(private options?: ScrollIntoViewOptions) {
	}

	public scrollTo(element: HTMLElement): void {
		// window.scrollTo({
		// 	top: this.getTopPosition(element),
		// 	behavior: this.options?.behavior || "smooth"
		// });

		element.scrollIntoView({
			...{
				behavior: 'smooth',
			}, ...this.options
		});
	}

	public scrollToTop(): void {
		window.scrollTo({
			top: 0,
			behavior: this.options?.behavior || "smooth"
		});
	}

	private getTopPosition(element: HTMLElement): number {
		let top: number = 0;
		// const fixedHeader: HTMLElement = document.getElementById(EIdsLayout.header);
		// if (fixedHeader) {
		// 	top -= fixedHeader.offsetHeight;
		// }
		top += element.offsetTop;
		return top;
	}
}
