import {isHTMLElement, strToBoolean} from "@helpers/index";
import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import TextareaAutoHeight from "@modules/textarea/TextareaAutoHeight";

export default class TextareaAutoHeightAutoActivation extends DynamicInsertionElements {
	private CLASS_HANDLER: string = 'js-textareaAutoHeight';

	private DATA_ATTR_ACTIVATED: string = 'data-textarea-auto-height-activated';

	public init(): void {
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		const textareas: Array<HTMLTextAreaElement> = Array.from(
			scope.getElementsByClassName(this.CLASS_HANDLER)
		) as Array<HTMLTextAreaElement>;
		textareas
			.filter(textarea => {
				return !strToBoolean(textarea.getAttribute(this.DATA_ATTR_ACTIVATED))
			})
			.forEach(textarea => {
				textarea.setAttribute(this.DATA_ATTR_ACTIVATED, 'true');
				new TextareaAutoHeight(textarea);
			})
	}
}
