import BtnSwitch from "@modules/btn/BtnSwitch";
import {IBtnSwitch} from "@modules/btn/model";
import CollapseAnimated from "@modules/collapse/CollapseAnimated";
import {ECollapseState, ICollapse} from "@modules/collapse/model";
import SmoothScroll from "@modules/scroll/SmoothScroll";
import {fromEvent} from "rxjs";

export default class AccentCollapseRemoteWithBtnSwitch {
	private DATA_ATTR_BTN_HANDLER_CLASS: string = 'data-accent-collapse-remote-with-btn-switch-handler';

	private collapseEl: HTMLElement;
	private collapse: ICollapse;
	private smoothScroll: SmoothScroll;
	private btnSwitchs: Array<IBtnSwitch>;

	private btnHandlerEls: Array<HTMLButtonElement> = [];

	constructor(collapseEl: HTMLElement) {
		this.collapseEl = collapseEl;
		this.collapse = new CollapseAnimated(collapseEl);
		this.smoothScroll = new SmoothScroll();
		const classBtnHandler: string = collapseEl.getAttribute(this.DATA_ATTR_BTN_HANDLER_CLASS);
		this.btnHandlerEls = Array.from(
			document.querySelectorAll(`.${classBtnHandler}`)
		);
		this.btnSwitchs = this.btnHandlerEls.map(handler => new BtnSwitch(handler));
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.btnHandlerEls, 'click')
			.subscribe(event => {
				event.preventDefault();
				this.toggle();
			});
	}

	private toggle(): void {
		if (this.collapse.getState() === ECollapseState.OPEN) {
			this.close();
		} else {
			this.open();
		}
	}

	private open(): void {
		this.collapse.open();
		this.btnSwitchs.forEach(btnSwitch => {
			btnSwitch.switch(false);
		});
		this.smoothScroll.scrollTo(this.collapseEl);
	}

	private close(): void {
		this.collapse.close();
		this.btnSwitchs.forEach(btnSwitch => {
			btnSwitch.switch(true);
		});
	}
}
