import CartPreviewComponent from "@components/cart-preview/CartPreviewComponent";
import {isDefinitionElementEvent} from "@helpers/index";
import Animation from "@modules/animation/Animation";
import {EAnimationType, IAnimation} from "@modules/animation/model";
import GlobalClick from "@modules/global-click/GlobalClick";
import {EWindowBreakpoints} from "@variables";
import {animationFrameScheduler, fromEvent} from "rxjs";
import {filter} from "rxjs/internal/operators";
import {debounceTime, throttleTime} from "rxjs/operators";

export default class CartPreviewDisplay {
	private CLASS_HANDLER: string = 'js-cartPreviewDisplayHandler';
	private ID_CART_PREVIEW: string = 'js-cartPreviewDisplay';

	private handlerEl: HTMLElement;
	private cartPreviewEl: CartPreviewComponent;
	private cartPreviewAnimation: IAnimation;
	private isOpen: boolean = false;

	public init(): void {
		this.handlerEl = document.querySelector(`.${this.CLASS_HANDLER}`);
		this.cartPreviewEl = document.getElementById(this.ID_CART_PREVIEW) as CartPreviewComponent;

		if (!this.handlerEl) {
			return;
		}
		this.cartPreviewAnimation = new Animation(this.cartPreviewEl, {
			type: EAnimationType.ANIMATION,
			name: 'o-animation-fade-down',
			beforeEnter: () => {
				this.updateCartPreviewPosition();
			},
		})
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.handlerEl, 'click')
			.subscribe(async event => {
				await this.handleHandlerClick(event);
			});

		fromEvent(window, 'resize')
			.pipe(
				debounceTime(25),
				throttleTime(0, animationFrameScheduler),
			)
			.subscribe(() => {
				this.updateCartPreviewPosition();
				if (!this.canShowPreview()) {
					this.hide();
				}
			});

		GlobalClick
			.getInstance()
			.getStreamOnClick()
			.pipe(
				filter(event => !isDefinitionElementEvent(event, `#${this.ID_CART_PREVIEW}`)),
				filter(event => !isDefinitionElementEvent(event, `.${this.CLASS_HANDLER}`)),
			)
			.subscribe(() => {
				if (!this.isOpen) {
					return;
				}
				this.hide();
			});
	}

	private async handleHandlerClick(event: Event): Promise<void> {
		if (this.canShowPreview()) {
			event.preventDefault();
			if (this.isOpen) {
				this.hide();
				return;
			}
			await this.show();
		}
	}

	private async show(): Promise<void> {
		this.isOpen = true;
		await this.cartPreviewEl.updateComplete;
		this.cartPreviewEl.getData();
		this.cartPreviewAnimation.show();
	}

	private hide(): void {
		this.isOpen = false;
		this.cartPreviewAnimation.hide();
	}

	private updateCartPreviewPosition(): void {
		const top: number = this.handlerEl.offsetTop + this.handlerEl.scrollHeight + 10;
		let right: number = 0;
		const handlerWidth: number = this.handlerEl.offsetWidth;
		const handlerLeft: number = this.handlerEl.offsetLeft;
		const distanceRightOfHandler: number = window.innerWidth - (handlerLeft + handlerWidth);
		const cartPreviewWidth: number = this.cartPreviewEl.offsetWidth;
		if (distanceRightOfHandler < (cartPreviewWidth / 2)) {
			right = 20;
		} else {
			right = distanceRightOfHandler - (cartPreviewWidth / 2);
		}
		this.cartPreviewEl.style.top = `${top}px`;
		this.cartPreviewEl.style.right = `${right}px`;
	}

	private canShowPreview(): boolean {
		return window.innerWidth >= EWindowBreakpoints.lg;
	}
}
