import {IDynamicInsertionElements} from "@modules/dynamic-element/model";

export abstract class DynamicInsertionElements implements IDynamicInsertionElements {
	private mutationObserver: MutationObserver;

	public attachEventDynamicInsertion() {
		if (!this.mutationObserver) {
			this.mutationObserver = new MutationObserver(this.mutationObserverListener);
			this.mutationObserver.observe(document.body, {
				childList: true,
				characterData: false,
				subtree: true,
			});
		}
	}

	protected dynamicInsertHandler(nodeModules: Node): void {

	}

	private mutationObserverListener: MutationCallback = (events: MutationRecord[]) => {
		events.forEach(event => {
			event.addedNodes.forEach(nodeModules => {
				this.dynamicInsertHandler(nodeModules)
			});
		})
	};
}
