import 'lazysizes/plugins/bgset/ls.bgset.min';
import lazySizes from 'lazysizes';

export default class LazyLoadImage {
	private CLASS_HANDLER: string = 'js-lazyLoadImage';
	private CLASS_LOADED: string = 'is-lazyload-loaded';
	private CLASS_LOADING: string = 'is-lazyload-loading';
	private CLASS_PRELOAD: string = 'is-lazyload-preload';
	private CLASS_ERROR: string = 'is-lazyload-error';

	public init(): void {
		lazySizes.cfg.lazyClass = this.CLASS_HANDLER;
		lazySizes.cfg.loadedClass = this.CLASS_LOADED;
		lazySizes.cfg.loadingClass = this.CLASS_LOADING;
		lazySizes.cfg.preloadClass = this.CLASS_PRELOAD;
		lazySizes.cfg.errorClass = this.CLASS_ERROR;
		lazySizes.init();
	}
}
