import {
	TPaymentMethodStatusChange,
	TPaymentMethodStatusChangeEventDetail
} from "@components/payment-method/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {property, PropertyValues} from "lit-element";
import {BehaviorSubject} from "rxjs";
import {EComponentStatuses} from "@models/Status";

export default class PaymentMethodComponent extends WebComponentLite {
	@property({
		type: Function,
		attribute: 'on-status-change',
	})
	public onStatusChange: TPaymentMethodStatusChange;

	public readonly status: BehaviorSubject<EComponentStatuses>;

	constructor(initStatus: EComponentStatuses) {
		super();
		this.status = new BehaviorSubject<EComponentStatuses>(initStatus);
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
	}

	protected attachEvent(): void {
		this.attachEventOnStatusChange();
	}

	public updateData(): void {

	}

	private attachEventOnStatusChange(): void {
		this.status
			.asObservable()
			.subscribe(status => {
				this.emitEventOnStatusChange(status);
			});
	}

	private emitEventOnStatusChange(status: EComponentStatuses = EComponentStatuses.READY): void {
		const event: CustomEvent<TPaymentMethodStatusChangeEventDetail> = new CustomEvent('on-status-change', {
			detail: {
				status: status,
			}
		});
		this.dispatchEvent(event);
	}
}
