import {IBtn} from "@modules/btn/model";
import {fromEvent, merge} from "rxjs";
import Btn from "@modules/btn/Btn";
import EditProductsCartComponent from "@components/edit-products-cart/EditProductsCartComponent";
import PaymentMethodSelectComponent from "@components/payment-method/select/PaymentMethodSelectComponent";
import {EComponentStatuses} from "@models/Status";

export default class UpdateBtnCartOnDeviceStep {
	private CLASS_EDIT_PRODUCTS: string = 'js-cartEditProducts';
	private CLASS_PAYMENT_METHOD_SELECT: string = 'js-cartPaymentMethodSelect';

	private CLASS_BTN: string = 'js-cartSubmitter';

	private editProductsEl: EditProductsCartComponent;
	private paymentMethodSelectEl: PaymentMethodSelectComponent;
	private btnEl: HTMLButtonElement;

	private btn: IBtn;

	public init(): void {
		this.editProductsEl = document.querySelector(`.${this.CLASS_EDIT_PRODUCTS}`);
		this.paymentMethodSelectEl = document.querySelector(`.${this.CLASS_PAYMENT_METHOD_SELECT}`);
		this.btnEl = document.querySelector(`.${this.CLASS_BTN}`);

		if (!this.btnEl || !this.editProductsEl || !this.paymentMethodSelectEl) {
			return;
		}
		this.btn = new Btn(this.btnEl);
		this.attachEvent();
	}

	private attachEvent(): void {
		merge(
			fromEvent(this.editProductsEl, 'on-status-change'),
			fromEvent(this.paymentMethodSelectEl, 'on-status-change')
		)
			.subscribe(() => {
				this.updateBtnAvailable();
			});
	}

	private updateBtnAvailable(): void {
		const editProductsStatus = this.editProductsEl.status.getValue();
		const paymentMethodStatus = this.paymentMethodSelectEl.status.getValue();

		if (editProductsStatus === EComponentStatuses.READY && paymentMethodStatus === EComponentStatuses.READY) {
			this.btn.enable();
		} else {
			this.btn.disable();
		}
	}
}
