export default class DialogOpening {
	private static instance: DialogOpening;

	private numberOpenWindows: number = 0;

	constructor() {
		if (DialogOpening.instance) {
			throw new Error("Error: Instantiation failed: Use DialogOpening.getInstance() instead of new.");
		}
		DialogOpening.instance = this;
	}

	public static getInstance(): DialogOpening {
		if (!DialogOpening.instance) {
			DialogOpening.instance = new DialogOpening();
		}
		return this.instance;
	}

	public show(): void {
		this.numberOpenWindows += 1;
	}

	public hide(): void {
		this.numberOpenWindows -= 1;
		if (this.numberOpenWindows < 0) {
			this.numberOpenWindows = 0;
		}
	}

	public hasOpen(): boolean {
		return this.numberOpenWindows > 0;
	}
}
