import {EToggleType} from "@components/toggle-card/model";
import {isFunction} from "@helpers/index";
import {TDynamicAction} from "@models/Action";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";
import {fromEvent} from "rxjs";

@customElement('c-toggle-card')
export default class ToggleCardComponent extends WebComponentLite {
	@property({
		type: Boolean,
		reflect: true,
	})
	public checked: boolean = false;

	@property({
		type: Boolean,
		reflect: true,
	})
	public disabled: boolean = false;

	@property({
		type: String,
		reflect: true,
	})
	public name: string;

	@property({
		type: String,
		reflect: true
	})
	public value: string;

	@property({
		type: String,
		attribute: 'price-per-month-value',
	})
	public pricePerMonth: string;

	@property({
		type: String,
		attribute: 'insurance-type-value',
	})
	public insuranceType: string;

	@property({
		reflect: true,
		type: EToggleType,
	})
	public type: EToggleType = EToggleType.CHECKBOX;

	@property({
		type: Boolean,
		reflect: true,
	})
	public marker: boolean = false;

	@property({
		type: Boolean,
		reflect: true,
	})
	public dark: boolean = false;

	@property({
		type: Function,
		attribute: 'on-change',
	})
	public onChange: TDynamicAction<boolean>;

	@property({
		type: Object,
		attribute: 'detail-value',
	})
	public detailValue: Object = {};

	constructor() {
		super();
		this.attachEvent();
	}

	public connectedCallback() {
		super.connectedCallback();
		if (this.checked && this.type === EToggleType.RADIO) {
			this.active();
		}
	}

	protected render(): TemplateResult {
		const marker = this.getMarker();
		const children = this.getChildren();

		let detailValue: string = this.getDetailValue();

		return html`
			<div class="c-toggle-card">
				<input class="c-toggle-card__hidden-input"
					   .type="${this.type}"
					   .name="${this.name}"
					   .value="${this.value}"
					   ?checked="${this.checked}"
					   ?disabled="${this.disabled}">
				<input class="c-toggle-card__hidden-input"
						?disabled="${!this.checked}"
						type="text"
						name="${this.name}_monthly_payment"
						.value="${this.pricePerMonth}">
				<input class="c-toggle-card__hidden-input"
						?disabled="${!this.checked}"
						type="text"
						name="${this.name}_type"
						.value="${this.insuranceType}">
				<input class="c-toggle-card__hidden-input"
					   ?disabled="${!this.checked}"
					   type="text"
					   .name="${this.name}-${this.value}-detail"
					   .value="${detailValue}">
				${marker}
				<div class="c-toggle-card__content">
					${children}
				</div>
			</div>
		`;
	}

	private getDetailValue(): string {
		let detailValue: string = '{}';

		if (this.detailValue) {
			try {
				detailValue = JSON.stringify(this.detailValue);
			} catch (e) {
				console.log('ToggleCardComponent: errors when trying to convert an object to a string !');
			}
		}
		return detailValue;
	}

	protected getMarker(): TemplateResult {
		if (!this.marker) {
			return null;
		}
		return html`
			<div class="c-toggle-card__marker">
				<div class="c-toggle-card__marker-icon"></div>
			</div>
		`;
	}

	private attachEvent(): void {
		fromEvent(this, 'click')
			.subscribe(async () => {
				await this.handleClick();
			})
	}

	private async handleClick(): Promise<void> {
		if (this.disabled) {
			return;
		}
		let checked: boolean;
		if (this.onChange && isFunction(this.onChange)) {
			checked = await this.onChange();
		} else {
			checked = this.checked && this.type !== EToggleType.RADIO;
		}

		if (checked) {
			this.disable();
		} else {
			this.active();
		}
	}

	private active(): void {
		if (this.type === EToggleType.RADIO) {
			this.disableActiveRelatedElements();
		}
		this.checked = true;
	}

	private disable(): void {
		this.checked = false;
	}

	private disableActiveRelatedElements(): void {
		this.getRelatedElements()
			.forEach(el => el.checked = false);
	}

	private getRelatedElements(): Array<ToggleCardComponent> {
		return Array.from(
			document.querySelectorAll(`${this.localName}[type="${this.type}"][name="${this.name}"]:not([value="${this.value}"])`)
		)
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);

		if (name === 'checked' && old !== value) {
			const event = new CustomEvent('on-change', {
				detail: {}
			});
			this.dispatchEvent(event);
		}
	}

}
