import {fromEvent} from "rxjs";

export enum EPostMessageEvents {
	RELOAD_PAGE = 'reload-page',
}

export default class PostMessageHandling {
	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent<MessageEvent>(window, 'message')
			.subscribe(event => {
				this.handle(event.data);
			});
	}

	private handle(event: EPostMessageEvents): void {
		switch (event) {
			case EPostMessageEvents.RELOAD_PAGE:
				window.location.reload();
				break;
		}
	}
}
