import {IModal} from "@modules/modal/model";
import {fromEvent} from "rxjs";
import Modal from "@modules/modal/Modal";

export default class ModalAutoActivation {
	private CLASS_HANDLER: string = 'js-modalHandler';
	private DATA_ATTR_MODAL_ID: string = 'data-modal-id';

	public init(): void {
		this.activate(document);
	}

	private activate(scope: HTMLElement | Document): void {
		const handlers: Array<HTMLElement> = Array.from(
			scope.getElementsByClassName(this.CLASS_HANDLER)
		) as Array<HTMLElement>;
		handlers.forEach(handler => {
			const modalId: string = handler.getAttribute(this.DATA_ATTR_MODAL_ID);
			const modalEl: HTMLDialogElement = document.getElementById(modalId) as HTMLDialogElement;
			if (modalEl) {
				const modal: IModal = new Modal({
					element: modalEl
				});
				this.attachEvent(handler, modal);
			}
		});
	}

	private attachEvent(handler: HTMLElement, modal: IModal): void {
		fromEvent(handler, 'click')
			.subscribe(() => {
				modal.open();
			})
	}
}
