import {EIdsLayout} from "@variables";
import MenuBtn from "@components/menu-btn/MenuBtn";
import {fromEvent} from "rxjs";
import {EAnimationType, IAnimation} from "@modules/animation/model";
import Animation from "@modules/animation/Animation";
import GlobalClick from "@modules/global-click/GlobalClick";
import {debounceTime, filter} from "rxjs/operators";
import {isDefinitionElementEvent} from "@helpers/index";
import {IScrollable} from "@modules/scroll/model";
import Scrollable from "@modules/scroll/Scrollable";

export default class SideNav {
	private CLASS_HANDLER: string = 'js-sideNavHandler';

	private sideNavEl: HTMLElement;
	private handlerEl: HTMLButtonElement;

	private menuBtn: MenuBtn;
	private animation: IAnimation;
	private scrollable: IScrollable;

	private isOpen: boolean = false;

	public init(): void {
		this.sideNavEl = document.getElementById(EIdsLayout.sideNav);
		this.handlerEl = document.querySelector(`.${this.CLASS_HANDLER}`);
		if (!this.sideNavEl || !this.handlerEl) {
			return;
		}

		this.scrollable = new Scrollable(document.body);
		this.menuBtn = new MenuBtn(this.handlerEl);
		this.animation = new Animation(
			this.sideNavEl,
			{
				type: EAnimationType.TRANSITION,
				name: 'c-side-nav-animation',
				displayProperty: 'flex',
				beforeEnter: () => {
					this.isOpen = true;
				},
				beforeLeave: () => {
					this.isOpen = false;
				}
			}
		)
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.handlerEl, 'click')
			.subscribe(event => {
				event.preventDefault();
				event.stopPropagation();
				this.toggle();
			});

		GlobalClick
			.getInstance()
			.getStreamOnClick()
			.pipe(
				filter(event => !isDefinitionElementEvent(event, `#${EIdsLayout.sideNav}`)),
			)
			.subscribe(() => {
				if (this.isOpen) {
					this.close();
				}
			});

		fromEvent(window, 'resize')
			.pipe(
				debounceTime(60),
			)
			.subscribe(() => {
				this.setHeight();
			})
	}

	private toggle(): void {
		if (this.isOpen) {
			this.close();
		} else {
			this.open();
		}
	}

	private open(): void {
		window.scrollTo(0, 0);
		this.setHeight();
		this.scrollable.disable();
		this.animation.show();
		this.menuBtn.open();
	}

	private close(): void {
		this.scrollable.enable();
		this.animation.hide();
		this.menuBtn.close();
	}

	private setHeight(): void {
		const header: HTMLElement = document.getElementById(EIdsLayout.header);
		let height: number = header?.offsetHeight || 0;
		this.sideNavEl.style.cssText += `
			top: ${height}px;
			height: calc(100% - ${height}px);
		`
	}
}
