import {IResize} from "@modules/resize/model";
import Resize from "@modules/resize/Resize";
import {EWindowBreakpoints} from "@variables";
import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import ScrollTriggerInstance = gsap.plugins.ScrollTriggerInstance;

gsap.registerPlugin(ScrollTrigger);

export default class StickyFilters {
	private CLASS_MAIN: string = 'js-stickyFilters';
	private CLASS_HANDLER: string = 'js-stickyFiltersHandler';

	private mainEl: HTMLElement;
	private handlerEl: HTMLElement;

	private resize: IResize;

	private trigger: ScrollTriggerInstance;

	public init(): void {
		this.mainEl = document.querySelector(`.${this.CLASS_MAIN}`)
		this.handlerEl = this.mainEl?.querySelector(`.${this.CLASS_HANDLER}`);
		if (!this.handlerEl) {
			return;
		}
		this.resize = new Resize(document.body);
		this.initTrigger();
		this.checkDisableState();
		this.attachEvent();
	}

	private initTrigger(): void {
		const topIndent: number = 20;
		this.trigger = ScrollTrigger.create({
			trigger: this.handlerEl,
			start: `top ${topIndent}px`,
			endTrigger: this.mainEl,
			end: () => `bottom ${this.handlerEl.offsetHeight + topIndent}px`,
			pin: true,
			pinSpacing: false,
			markers: false, //for debug
		});
	}

	private attachEvent(): void {
		this.resize
			.getStream()
			.subscribe(() => {
				this.checkDisableState();
				if (!this.isDisabledWidth()) {
					ScrollTrigger.refresh();
				}
			});
	}

	private isDisabledWidth(): boolean {
		return window.innerWidth < EWindowBreakpoints.md
	}

	private checkDisableState(): void {
		if (this.isDisabledWidth()) {
			this.trigger.disable();
		} else {
			this.trigger.enable();
		}
	}
}
