import Badge from "@components/badge/Badge";

export default class CartQuantity {
	private CLASS_BADGE: string = 'js-cartBudge';

	private static instance: CartQuantity;

	private badges: Array<Badge> = [];

	constructor() {
		if (CartQuantity.instance) {
			throw new Error("Error: Instantiation failed: Use CartQuantity.getInstance() instead of new.");
		}
		CartQuantity.instance = this;
		const badgeEls: Array<HTMLElement> = Array.from(document.getElementsByClassName(this.CLASS_BADGE)) as Array<HTMLElement>;
		this.badges = badgeEls.map(badgeEl => new Badge(badgeEl));
	}

	public static getInstance(): CartQuantity {
		if (!CartQuantity.instance) {
			CartQuantity.instance = new CartQuantity();
		}
		return this.instance;
	}

	public update(value: number): void {
		const badgeValue = value == 0 ? '' : value.toString();
		this.badges.forEach(badge => badge.setValue(badgeValue));
	}
}
