//from - https://unmanner.github.io/imaskjs/guide.html#support-older
import {isHTMLElement, strToBoolean} from "@helpers/index";
import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import IMask from 'imask';

export type TMaskFormats = {
	[key in EMaskTypes]: TMaskFormat;
}

export type TMaskFormat = {
	placeholder: string;
	class: string;
	option: IMask.AnyMaskedOptions;
}

export enum EMaskTypes {
	PHONE = 'PHONE',
	DATE = 'DATE',
	IIN = 'IIN',
	VISA_CARD = 'VISA_CARD',
	PASSPORT = 'PASSPORT',
	ID_CARD = 'ID_CARD',
	NUMBER = 'NUMBER',
	SMS_CODE = 'SMS_CODE',
	NUMBER_LATIN_UPPER = 'NUMBER_LATIN_UPPER',
	LATIN_UPPER = 'LATIN_UPPER',
	MONEY = 'MONEY',
}

export const MASK_FORMATS: TMaskFormats = {
	[EMaskTypes.PHONE]: {
		placeholder: '+7 (xxx) xxx-xx-xx',
		class: 'js-maskPhone',
		option: {
			mask: '{+7} (000) 000-00-00',
			lazy: true,
			placeholderChar: 'x',
		},
	},
	[EMaskTypes.DATE]: {
		placeholder: '',
		class: 'js-maskDate',
		option: {
			mask: Date,
			overwrite: true,
			autofix: true,
			min: new Date(1500, 0, 1),
			max: new Date(9999, 0, 1),
		},
	},
	[EMaskTypes.IIN]: {
		placeholder: '',
		class: 'js-maskIIN',
		option: {
			mask: '00000000000000',
		},
	},
	[EMaskTypes.VISA_CARD]: {
		placeholder: '0000 0000 0000 0000',
		class: 'js-maskVisaCard',
		option: {
			mask: '0000 0000 0000 0000',
		},
	},
	[EMaskTypes.PASSPORT]: {
		placeholder: '',
		class: 'js-maskPassport',
		option: {
			mask: '000000000000',
		},
	},
	[EMaskTypes.ID_CARD]: {
		placeholder: '',
		class: 'js-maskIdCard',
		option: {
			mask: '0000000',
		},
	},
	[EMaskTypes.NUMBER]: {
		placeholder: '',
		class: 'js-maskNumber',
		option: {
			mask: /^\d+$/,
		},
	},
	[EMaskTypes.SMS_CODE]: {
		placeholder: '0000',
		class: 'js-maskSMSCode',
		option: {
			mask: '0000',
			lazy: false,
			placeholderChar: '-',
		},
	},
	[EMaskTypes.NUMBER_LATIN_UPPER]: {
		placeholder: '',
		class: 'js-maskNumberLatinUpper',
		option: {
			mask: /^[A-z0-9]+$/,
			prepare: function (value: string) {
				return value.toUpperCase();
			},
		},
	},
	[EMaskTypes.LATIN_UPPER]: {
		placeholder: '',
		class: 'js-maskLatinUpper',
		option: {
			mask: /^[A-z]+$/,
			prepare: function (value: string) {
				return value.toUpperCase();
			},
		},
	},
	[EMaskTypes.MONEY]: {
		placeholder: '',
		class: 'js-maskMoney',
		option: {
			mask: Number,
			thousandsSeparator: ' ',
			min: 0,
		}
	}
};

export default class Mask extends DynamicInsertionElements {
	private DATA_ACTIVATED: string = 'data-mask-activated';

	private formats: TMaskFormats = MASK_FORMATS;

	public init(): void {
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	static getFormattedValue(value: string, maskType: EMaskTypes): string {
		const maskFormat: TMaskFormat = MASK_FORMATS[maskType];
		if (!maskFormat) {
			throw new Error(`mask with key ${maskType} not found`)
		}
		const mask = IMask.createMask(maskFormat.option);
		mask.resolve(value);

		return mask.value;
	}

	static isCompleteValue(value: string, maskType: EMaskTypes): boolean {
		const maskFormat: TMaskFormat = MASK_FORMATS[maskType];
		if (!maskFormat) {
			throw new Error(`mask with key ${maskType} not found`)
		}
		const mask = IMask.createMask(maskFormat.option);
		mask.resolve(value);
		return mask.isComplete;
	}

	static getUnmaskedValue(value: string, maskType: EMaskTypes): string {
		const maskFormat: TMaskFormat = MASK_FORMATS[maskType];
		if (!maskFormat) {
			throw new Error(`mask with key ${maskType} not found`)
		}
		const mask = IMask.createMask(maskFormat.option);
		mask.resolve(value);

		return mask.unmaskedValue;
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		for (let key in this.formats) {
			const fields: Array<HTMLInputElement> = Array.from(
				document.getElementsByClassName(this.formats[key].class)
			) as Array<HTMLInputElement>;
			fields.forEach(field => {
				if (strToBoolean(field.getAttribute(this.DATA_ACTIVATED))) {
					return
				}
				field.setAttribute(this.DATA_ACTIVATED, 'true');
				IMask(
					field,
					this.formats[key].option,
				);
				// (fields.item(i) as HTMLInputElement).placeholder = this.formats[
				//     key
				// ].placeholder;
			})
		}
	}
}
