export default class HandleEmptyContent {
	private _handlerEl: HTMLElement;

	private mutationObserver: MutationObserver;

	constructor(handlerEl: HTMLElement) {
		this._handlerEl = handlerEl;
		this.init();
	}

	get handlerEl(): HTMLElement {
		return this._handlerEl;
	}

	private init(): void {
		this.attachEvent();
		this.update();
	}

	private attachEvent(): void {
		this.mutationObserver = new MutationObserver((events: MutationRecord[]) => {
			events.forEach(event => {
				if (event.addedNodes.length || event.removedNodes.length) {
					this.update();
				}
			});
		});
		this.mutationObserver.observe(this._handlerEl, {
			childList: true,
			subtree: true,
		});
	}

	protected update(): void {

	}

}
