import {EMoneyFormatterTypes, IMoneyFormatter} from "@modules/formatter/model";
import Mask, {EMaskTypes} from "@modules/mask/Mask";

export class MoneyFormatter implements IMoneyFormatter {
	static readonly symbol = '₸';

	static format(value: string, type?: EMoneyFormatterTypes): string {
		switch (type) {
			case EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT:
				return MoneyFormatter.toSymbolSup(value);
			case EMoneyFormatterTypes.THOUSAND_SUPERSCRIPT:
				return MoneyFormatter.toThousandSup(value);
			case EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT:
				return MoneyFormatter.toThousandMonthSup(value);
			default:
				return MoneyFormatter.toDefault(value);
		}
	}

	static toDefault(value: string): string {
		return MoneyFormatter.getMaskedValue(value) + MoneyFormatter.symbol;
	}

	static toSymbolSup(value: string): string {
		return `${MoneyFormatter.getMaskedValue(value)}<sup>${MoneyFormatter.symbol}</sup>`;
	}

	static toThousandSup(value: string): string {
		const price: Array<string> = MoneyFormatter.getMaskedValue(value).split(' ');
		const first: string = price[0];
		price.splice(0, 1);
		const sup: string = price.join(' ');

		return `${first}<sup>${sup} ${MoneyFormatter.symbol}</sup>`;
	}

	static toThousandMonthSup(value: string): string {
		const price: Array<string> = MoneyFormatter.getMaskedValue(value).split(' ');
		const first: string = price[0];
		price.splice(0, 1);
		const sup: string = price.join(' ');

		return `${first}<sup>${sup} ${MoneyFormatter.symbol}/мес</sup>`;
	}

	static getMaskedValue(value: string): string {
		return Mask.getFormattedValue(value, EMaskTypes.MONEY);
	}
}
