import HandleEmptyContent from "@modules/handle-empty-content/HandleEmptyContent";

export default class HandleEmptyContentRefresh extends HandleEmptyContent {
	constructor(handlerEl: HTMLElement) {
		super(handlerEl);
	}

	protected update(): void {
		const length: number = this.handlerEl.children.length;

		if (length) {
			return;
		}

		window.location.reload();
	}
}
