import {ELoaderModification, ILoader} from "@components/loader/model";
import Loader from "@components/loader/Loader";

export default class GlobalLoader {
	private static instance: GlobalLoader;

	private loader: ILoader;
	private loaderPlacementEl: HTMLElement;

	constructor() {
		if (GlobalLoader.instance) {
			throw new Error("Error: Instantiation failed: Use GlobalLoader.getInstance() instead of new.");
		}
		GlobalLoader.instance = this;
		this.init();
	}

	public static getInstance(): GlobalLoader {
		if (!GlobalLoader.instance) {
			GlobalLoader.instance = new GlobalLoader();
		}
		return this.instance;
	}

	public show(): void {
		this.loader.show();
	}

	public hide(): void {
		this.loader.hide();
	}

	private init(): void {
		this.loader = new Loader({
			element: "create",
			modifications: [
				ELoaderModification.fixed
			]
		});
		this.loaderPlacementEl = document.body.appendChild(this.loader.getHTMLElement());
	}
}
