import {EProductBasketBundlerOptions} from "@components/product-basket-bundler/model";
import {EProductBasketBundlerOptionStates} from "@components/product-basket-bundler/product-basket-bundler-option-states/model";
import {
	EProductBundlerOptionPaymentMethodTypes,
	TProductBundlerOptionPaymentMethod,
	TProductBundlerOptionPaymentMethodContract,
	TProductBundlerOptionPaymentMethodFullPrice,
	TProductBundlerOptionPaymentMethodRequest,
	TProductBundlerOptionPaymentMethodResponse
} from "@components/product-basket-bundler/product-bundler-option-payment-method/model";
import ProductBundlerOptionComponent
	from "@components/product-basket-bundler/product-bundler-option/ProductBundlerOptionComponent";
import {TTariffCard} from "@components/tariff-card/model";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import HttpRequest from "@modules/http-request/HttpRequest";
import {customElement, html, state, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-product-bundler-option-payment-method')
export default class ProductBundlerOptionPaymentMethodComponent extends ProductBundlerOptionComponent {
	type = EProductBasketBundlerOptions.PAYMENT_METHOD;

	@state()
	private selected: TProductBundlerOptionPaymentMethod;

	@state()
	private data: TProductBundlerOptionPaymentMethodResponse;

	public requestData(): void {
		this.getData();
	}

	public resetData(): void {
		this.data = []
		this.selected = null;
		this.store.selectedPaymentMethod.next(null);
	}

	protected reloadData() {
		this.getData();
	}

	private getData(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.isNetworkError = false;
		const data: TProductBundlerOptionPaymentMethodRequest = {
			skuId: this.store.skuId,
		};
		this.states.updateState({
			key: this.type,
			state: EProductBasketBundlerOptionStates.LOADING,
		});
		this.httpService
			.request<TProductBundlerOptionPaymentMethodResponse>({
				url: '/products/payment-type/',
				method: "GET",
				data: data,
			})
			.then((response) => {
				this.data = response.data.data;
				this.selected = this.data[0];
				this.store.selectedPaymentMethod.next(this.selected);
				this.states.updateState({
					key: this.type,
					state: EProductBasketBundlerOptionStates.READY,
				});
			})
			.catch((reject) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
				this.states.updateState({
					key: this.type,
					state: EProductBasketBundlerOptionStates.ERROR,
					message: reject,
				});
			})
			.finally(() => {
				this.isLoading = false;
			});
	}

	protected getContentTemplate(): TemplateResult {
		if (!this.data) {
			return this.getNotDataTemplate();
		}

		return html`
			<h4 class="c-product-bundler-option__section-title">Выберите способ оплаты</h4>
			<div class="u-space-items u-space-items-16 u-d-inline-block u-w-100">
				${this.data.map(item =>
					this.getToggleCardTemplate(item.type)
				)}
			</div>
		`;
	}

	protected getNotDataTemplate(): TemplateResult {
		return html`
			<div class="u-text-center">
				<h4>Данных нет !</h4>
			</div>
		`;
	}

	private getToggleCardTemplate(type: EProductBundlerOptionPaymentMethodTypes): TemplateResult {
		switch (type) {
			case EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF:
				return this.getToggleCardContractTemplate();
			case EProductBundlerOptionPaymentMethodTypes.FULL_PRICE:
				return this.getToggleCardFullPriceTemplate();
			default:
				return html`<p>type not found</p>`;
		}
	}

	private getToggleCardContractTemplate(): TemplateResult {
		const data = this.data?.find(item => item.type === EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF) as TProductBundlerOptionPaymentMethodContract;
		if (!data) {
			return null;
		}

		let tariffCard: TTariffCard;
		const pricePerMonth: string = MoneyFormatter.format(
			data.contractTariff?.pricePerMonth,
			EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT
		);
		tariffCard = {
			id: data.id,
			title: 'Оформить в Рассрочку',
			subTitle: `На ${data.contractTariff?.period} месяцев`,
			costs: [
				{
					title: `${pricePerMonth}`,
					subTitle: `На ${data.contractTariff?.period} месяцев`
				},
				{
					title: `${data.contractTariff?.overpayment}<sup>%</sup>`,
					subTitle: 'Переплата'
				}
			],
		};

		return html`
			<c-toggle-card marker
						   type="radio"
						   name="${this.type}"
						   .value="${tariffCard.id}"
						   ?checked="${tariffCard.id === this.selected?.id}"
						   @on-change="${this.handleChange}">
				<c-tariff-card .data="${tariffCard}"
							   class="u-w-100">
			</c-toggle-card>
		`;
	}

	private getToggleCardFullPriceTemplate(): TemplateResult {
		const data = this.data?.find(item => item.type === EProductBundlerOptionPaymentMethodTypes.FULL_PRICE) as TProductBundlerOptionPaymentMethodFullPrice;

		if (!data) {
			return null;
		}

		const price: string = MoneyFormatter.format(
			data.price,
			EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);

		return html`
			<c-toggle-card marker
						   type="radio"
						   name="${this.type}"
						   .value="${data.id}"
						   ?checked="${data.id === this.selected?.id}"
						   @on-change="${this.handleChange}">
				<div class="u-d-flex u-flex-row u-justify-content-between u-align-items-center">
					<h2 class="c-product-bundler-option-payment-method__full-price-value u-flex-shrink-0">
						${unsafeHTML(price)}
					</h2>
					<p class="c-product-bundler-option-payment-method__full-price-title">Купить за полную стоимость</p>
				</div>
			</c-toggle-card>
		`;
	}

	private handleChange(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		const id: number = parseFloat(toggle.value);
		if (!toggle.checked) {
			return;
		}
		this.selected = this.data.find(item => item.id == id);
		this.store.selectedPaymentMethod.next(this.selected);
	}
}
