import {TAccessoryCard} from "@components/accessory-card/model";
import {EProductBasketBundlerOptions} from "@components/product-basket-bundler/model";
import {EProductBasketBundlerOptionStates} from "@components/product-basket-bundler/product-basket-bundler-option-states/model";
import {
	TProductBundlerOptionAccessoryRequest,
	TProductBundlerOptionAccessoryResponse
} from "@components/product-basket-bundler/product-bundler-option-accessory/model";
import ProductBundlerOptionComponent
	from "@components/product-basket-bundler/product-bundler-option/ProductBundlerOptionComponent";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {TAccessory} from "@models/Accessory";
import HttpRequest from "@modules/http-request/HttpRequest";
import {customElement, html, state, query, TemplateResult} from "lit-element";

@customElement('c-product-bundler-option-accessory')
export default class ProductBundlerOptionAccessoryComponent extends ProductBundlerOptionComponent {
	type = EProductBasketBundlerOptions.ACCESSORY;

	private data: TProductBundlerOptionAccessoryResponse;

	@state()
	private selected: Array<TAccessory> = [];

	@query('[toggle-skip]')
	private toggleSkipEl: ToggleCardComponent;

	private formattedData: Array<TAccessoryCard> = [];

	public requestData(): void {
		this.getData();
	}

	public resetData(): void {
		this.data = [];
		this.selected = [];
		this.formattedData = [];
		this.store.selectedAccessory.next([]);
	}

	protected reloadData() {
		this.getData();
	}

	protected getContentTemplate(): TemplateResult {
		return html`
			<h4 class="c-product-bundler-option__section-title">Подберите аксесуар к девайсу</h4>
			<div class="u-space-items u-space-items-16 u-d-inline-block u-w-100">
				${this.formattedData?.map(accessory => {
						const isSelected = Boolean(this.selected.find(item => item.id === accessory.id));
						return html`
							<c-toggle-card marker
										   type="checkbox"
										   name="accessory"
										   .value="${accessory.id}"
										   ?checked="${isSelected}"
										   @on-change="${this.handleChange}">
								<c-accessory-card .data="${accessory}">
							</c-toggle-card>
						`
					}
				)}
				${this.getSkipTemplate()}
			</div>
		`;
	}

	private getSkipTemplate(): TemplateResult {
		return html`
			<c-toggle-card marker
						   toggle-skip
						   type="checkbox"
						   name="accessory"
						   .value="${null}"
						   @on-change="${this.handleClickSkip}">
				<div class="u-h-100 u-d-flex u-flex-row u-align-items-center">
					<p>Мне не нужны аксессуары</p>
				</div>
			</c-toggle-card>
		`;
	}

	private handleClickSkip(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		this.selected = [];
	}

	private handleChange(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (toggle.checked) {
			this.toggleSkipEl.checked = false;
			this.selected = [
				...this.selected,
				this.data.find(item => item.id == toggle.value),
			];
		} else {
			this.selected = this.selected.filter(item => item.id !== toggle.value);
		}
		this.store.selectedAccessory.next(this.selected);
	}

	private getData(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.isNetworkError = false;
		this.states.updateState({
			key: this.type,
			state: EProductBasketBundlerOptionStates.LOADING,
		});
		const data: TProductBundlerOptionAccessoryRequest = {
			skuId: this.store.skuId,
			period: this.store.selectedContractTariff.getValue()?.period?.value,
		};
		this.httpService
			.request<TProductBundlerOptionAccessoryResponse>({
				url: '/products/accessories/',
				method: "GET",
				data: data,
			})
			.then((response) => {
				this.data = response.data.data;
				this.formattedData = this.formatData(this.data);
				this.states.updateState({
					key: this.type,
					state: EProductBasketBundlerOptionStates.READY,
				});
			})
			.catch((reject) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
				this.states.updateState({
					key: this.type,
					state: EProductBasketBundlerOptionStates.ERROR,
				});
			})
			.finally(() => {
				this.isLoading = false;
			})
	}

	private formatData(data: Array<TAccessory>): Array<TAccessoryCard> {
		return data?.map(item => {
			return {
				id: item.id,
				title: item.title,
				img: item.img,
				price: item.price,
				pricePerMonth: item.pricePerMonth,
			}
		}) || [];
	}
}
