import {getDefinitionElement, getIndexElement, isDefinitionElementEvent} from "@helpers/index";
import {ITab} from "@modules/tab/model";
import {fromEvent} from "rxjs";
import {filter} from "rxjs/operators";

export default class Tab implements ITab {
	private CLASS_TAB: string = 'js-tab';
	private CLASS_HEADER: string = 'js-tabHeader';
	private CLASS_HEADER_ITEM: string = 'js-tabHeaderItem';
	private CLASS_HEADER_ITEM_ACTIVE: string = 'is-active';
	private CLASS_CONTENT: string = 'js-tabContent';
	private CLASS_CONTENT_ITEM: string = 'js-tabContentItem';
	private CLASS_CONTENT_ITEM_ACTIVE: string = 'is-active';

	protected tabEl: HTMLElement;
	protected headerEl: HTMLElement;
	protected contentEl: HTMLElement;

	constructor(tabEl: HTMLElement) {
		this.tabEl = tabEl;
		this.findElements();
		this.attachEvent();
		this.initFirstActive();
	}

	public goTo(index: number): void {
		this.setActiveHeaderItem(index);
		this.setActiveContentItem(index);
	}

	protected findElements(): void {
		this.headerEl = this.tabEl.querySelector(`.${this.CLASS_HEADER}`);
		if (!this.headerEl) {
			throw new Error(`Tab: header is not defined !`);
		}
		this.contentEl = this.tabEl.querySelector(`.${this.CLASS_CONTENT}`);
		if (!this.contentEl) {
			throw new Error(`Tab: content is not defined !`);
		}
	}

	private initFirstActive(): void {
		const activeIndex = this.getCurrentActiveIndex();
		this.goTo(activeIndex >= 0 ? activeIndex : 0);
	}

	private setActiveHeaderItem(index: number): void {
		const currentActiveItem: HTMLElement = this.getActiveHeaderItem();
		const nextItem: HTMLElement = this.headerEl.children.item(index) as HTMLElement;
		if (currentActiveItem) {
			currentActiveItem.classList.remove(this.CLASS_HEADER_ITEM_ACTIVE);
		}
		nextItem.classList.add(this.CLASS_HEADER_ITEM_ACTIVE);
	}

	private setActiveContentItem(index: number): void {
		const currentActiveItem: HTMLElement = this.getActiveContentItem();
		const nextItem: HTMLElement = this.contentEl.children.item(index) as HTMLElement;
		if (currentActiveItem) {
			currentActiveItem.classList.remove(this.CLASS_CONTENT_ITEM_ACTIVE);
		}
		nextItem.classList.add(this.CLASS_CONTENT_ITEM_ACTIVE);
	}

	public getCurrentActiveIndex(): number {
		const currentActiveItem: HTMLElement = this.getActiveHeaderItem();
		return getIndexElement(this.headerEl, currentActiveItem);
	}

	protected getActiveHeaderItem(): HTMLElement {
		return this.headerEl.querySelector(`.${this.CLASS_HEADER_ITEM}.${this.CLASS_HEADER_ITEM_ACTIVE}`);
	}

	protected getActiveContentItem(): HTMLElement {
		return this.contentEl.querySelector(`.${this.CLASS_CONTENT_ITEM}.${this.CLASS_CONTENT_ITEM_ACTIVE}`);
	}

	private attachEvent(): void {
		fromEvent(this.headerEl, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, `.${this.CLASS_HEADER_ITEM}`)),
			)
			.subscribe(event => {
				const item = getDefinitionElement(event.target as HTMLElement, `.${this.CLASS_HEADER_ITEM}`);
				this.onHeaderItemClick(event, item);
			});
	}

	protected onHeaderItemClick(event: Event, item: HTMLElement): void {
		event.preventDefault();
		if (item.classList.contains(this.CLASS_HEADER_ITEM_ACTIVE)) {
			return;
		}
		const index = this.getIndexOnHeaderItem(item);
		this.scrollToHeaderItem(item);
		this.goTo(index);
	}

	protected getIndexOnHeaderItem(item: HTMLElement): number {
		return getIndexElement(this.headerEl, item);
	}

	private scrollToHeaderItem(item: HTMLElement): void {
		const scrollWidth: number = this.headerEl.scrollWidth;
		const itemLeft: number = item.offsetLeft;
		const itemWidth: number = item.offsetWidth;
		this.headerEl.scrollLeft = (itemLeft + itemWidth) - (scrollWidth / 2);
	}
}

