import {EAlertMods, EAlertTypes} from "@components/alert/model";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {
	TSelectContractTariffSelected,
	TSelectTariffFormattedData,
	TSelectTariffInitSelected,
	TSelectTariffPeriod,
	TSelectTariffSelectedMap,
	TSelectTariffStatusChangeEventDetail
} from "@components/select-tariff/model";
import {TTariffCard} from "@components/tariff-card/model";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {TTariff, TTariffPeriod, TTariffTree} from "@models/Tariff";
import {TariffFormatter} from "@modules/formatter/TariffFormatter";
import HttpRequest from "@modules/http-request/HttpRequest";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosError, AxiosRequestConfig} from "axios";
import {customElement, html, property, PropertyValues, state, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";
import {objectToFormData} from "@helpers/index";
import {BehaviorSubject} from "rxjs";
import {EComponentStatuses} from "@models/Status";

@customElement('c-select-tariff')
export default class SelectTariffComponent extends WebComponentLite {
	@property({
		type: Boolean,
		attribute: 'request-on-creation',
	})
	public requestOnCreation: boolean = false;

	@property({
		type: Object,
		attribute: 'add-data-get'
	})
	public addDataGet: object = {};

	@property({
		type: Object,
		attribute: 'get-request-config',
	})
	public getRequestConfig: AxiosRequestConfig = {
		url: '/get-tariffs',
		method: "GET",
	}

	@property({
		type: Object,
		attribute: 'init-selected',
	})
	public initSelected: TSelectTariffInitSelected;

	@property({
		type: Object,
		attribute: 'calculate-request-config',
	})
	public calculatePricesRequestConfig: AxiosRequestConfig = {
		url: '/checkout/calculate_prices/',
		method: "POST",
	}

	@state()
	private isLoading: boolean = false;

	@state()
	private isNetworkError: boolean = false;

	@state()
	private formattedData: TSelectTariffFormattedData;

	@state()
	private data: TTariffTree;

	@state()
	private selected: TSelectTariffSelectedMap = {
		period: null,
		tariff: null,
	}

	@state()
	private periodTitle: string = '';

	private isFirstLoaded: boolean;

	private httpService: HttpRequest = new HttpRequest();
	public readonly status: BehaviorSubject<EComponentStatuses> = new BehaviorSubject<EComponentStatuses>(EComponentStatuses.LOADING);

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
		if (this.requestOnCreation) {
			this.getData();
		}
	}

	protected render(): unknown {
		if (this.isLoading) {
			return html`
				<c-loader active
						  type="${ELoaderType.PRIMARY}"
						  size="${ELoaderSize.MD}"
						  class="c-product-basket-bundler__loader">
				</c-loader>
			`;
		}

		if (this.isNetworkError) {
			return this.getNetworkErrorTemplate();
		}

		if (!this.formattedData) {
			return this.getNotDataTemplate();
		}

		if (!this.formattedData?.periods?.length) {
			return this.getConnectionResultMessageTemplate();
		}

		return this.getContentTemplate();
	}

	private getData(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.isNetworkError = false;
		const {url, method} = this.getRequestConfig;
		const formData: FormData = objectToFormData({
			...this.addDataGet || {},
		});
		this.status.next(EComponentStatuses.LOADING);
		this.httpService
			.request<TTariffTree>({
				url: url,
				method: method,
				data: formData,
			})
			.then((response) => {
				const data = response.data.data;
				this.data = data;
				this.formattedData = this.getFormattedData(data);
				this.updatePeriodTitle();
				if (this.isFirstLoaded) {
					this.selectFirst();
				} else {
					this.setInitSelected();
				}
				this.isFirstLoaded = true;
				this.status.next(EComponentStatuses.READY);
			})
			.catch((reject: AxiosError) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
				this.status.next(EComponentStatuses.ERROR);
			})
			.finally(() => {
				this.isLoading = false;
			})
	}

	private getFormattedData(data: TTariffTree): TSelectTariffFormattedData {
		return {
			...data,
			periods: this.getFormattedPeriods(data.periods)
		}
	}

	private getFormattedPeriods(periods: Array<TTariffPeriod>): Array<TSelectTariffPeriod> {
		return periods?.map(item => {
			return {
				title: 'месяцев',
				value: item.value,
				tariffs: this.getFormattedTariffs(item.tariffs)
			}
		}) || [];
	}

	private getFormattedTariffs(tariffs: Array<TTariff>): Array<TTariffCard> {
		return tariffs?.map(item => TariffFormatter.toTariffCard(item, this.data.operator)) || [];
	}

	private selectFirst(): void {
		const period = this.data?.periods?.[0] || null;
		const tariff = period?.tariffs?.[0] || null;
		this.selected = {
			period: period ? period.value : null,
			tariff: tariff ? tariff.id : null,
		};
		this.emitEventOnChange({
			isSkip: Boolean(tariff),
			period: period,
			tariff: tariff,
		});
	}

	private setInitSelected(): void {
		const initSelected = this.initSelected;
		if (!initSelected || !Object.entries(initSelected).length) {
			this.selectFirst();
			return;
		}
		const period = this.data?.periods?.find(period => period.value === initSelected.period) || null;
		const tariff = period?.tariffs?.find(tariff => tariff.id === initSelected.tariff) || null;
		this.selected = {
			period: period ? period.value : null,
			tariff: tariff ? tariff.id : null,
		};
		this.emitEventOnChange({
			isSkip: Boolean(tariff),
			period: period,
			tariff: tariff,
		});
	}

	private getNetworkErrorTemplate(): TemplateResult {
		return html`
			<c-network-error @on-click-reload="${this.handleBtnClickReload}">
		`;
	}

	private getNotDataTemplate(): TemplateResult {
		return html`
			<div class="u-text-center">
				<h4>Данных нет !</h4>
			</div>
		`;
	}

	private getContentTemplate(): TemplateResult {
		return html`
			<div class="u-space-items u-space-items-44 u-d-inline-block u-w-100">
				${this.getPeriodTemplate()}
				${this.getTariffTemplate()}
			</div>
		`;
	}

	private getConnectionResultMessageTemplate(): TemplateResult {
		return html`
			<p class="c-select-tariff__connection-result-message">
				${unsafeHTML(
					this.data.tariffConnection?.message
				)}
			</p>
		`;
	}

	private getPeriodTemplate(): TemplateResult {
		const periods = this.formattedData?.periods || [];

		return html`
			<section class="c-section">
				<header class="c-section__header">
					<h4 class="c-section__title">${this.periodTitle}</h4>
				</header>
				<div class="c-section__body">
					<ul class="c-operator-card-list">
						${periods.map(period =>
							html`
								<li class="c-operator-card-list__item">
									<c-toggle-card type="radio"
												   name="period"
												   class="u-w-100 u-h-100"
												   .value="${period.value}"
												   ?checked="${this.selected.period == period.value}"
												   @on-change="${this.handleClickPeriod}">
										<div
											class="u-w-100 u-h-100 u-d-flex u-align-items-center u-justify-content-center">
											<c-term-card .data="${period}">
										</div>
									</c-toggle-card>
								</li>
							`
						)}
					</ul>
				</div>
			</section>
		`;
	}

	private getTariffTemplate(): TemplateResult {
		const selectedPeriod = this.selected.period;
		const tariffs = this.formattedData.periods.find(period => period.value === selectedPeriod)?.tariffs || [];
		let show: boolean = true;

		if (!tariffs.length && !this.data.reasonAbsenceTariffs) {
			show = false;
		}

		if (!selectedPeriod || !show) {
			return null;
		}

		console.log(this.formattedData)

		return html`
			<section class="c-section">
				<header class="c-section__header">
					<h4 class="c-section__title">Тарифные планы</h4>
				</header>
				<div class="c-section__body">
					<div class="u-space-items u-space-items-16 u-d-inline-block u-w-100">
						${tariffs.length ?
							html`
								${tariffs.map(tariff =>
									html`
										<c-toggle-card marker
													   type="radio"
													   name="tariff"
													   .value="${tariff.id}"
													   .detailValue="${this.getTariffByPeriodAndId(selectedPeriod, tariff.id)}"
													   ?checked="${this.selected.tariff == tariff.id}"
													   @on-change="${this.handleClickTariff}">
											<c-tariff-card class="u-min-w-100"
														   .data="${tariff}">
										</c-toggle-card>
									`
								)}
								${this.getSkipTariffTemplate()}
							` :
							this.reasonAbsenceTariffsTemplate()
						}
					</div>
				</div>
			</section>
		`;
	}

	private getTariffByPeriodAndId(period: number, tariffId: number): TTariff | null {
		const tariffs = this.data.periods.find(per => per.value === period)?.tariffs || [];
		if (!tariffs.length) {
			return null;
		}
		return tariffs.find(tariff => tariff.id === tariffId);
	}

	private updatePeriodTitle(): void {
		const tariffs = this.formattedData.periods.find(period => period.value === this.selected.period)?.tariffs || [];
		if (this.selected && this.selected.period && !tariffs?.length) {
			this.periodTitle = 'Срок Рассрочки';
			return;
		}
		this.periodTitle = 'Срок Контракта';
	}

	private reasonAbsenceTariffsTemplate(): TemplateResult {
		return html`
			<c-alert type="${EAlertTypes.WARNING}"
					 .mods="${[EAlertMods.SM]}">
				${this.data.reasonAbsenceTariffs}
			</c-alert>
		`
	}

	private getSkipTariffTemplate(): TemplateResult {
		return html``;  // tariffs are required now
		// return html`
		// 	<c-toggle-card marker
		// 				   type="radio"
		// 				   name="tariff"
		// 				   .value="${null}"
		// 				   ?checked="${this.selected.tariff == null}"
		// 				   @on-change="${this.handleClickSkipTariff}">
		// 		<div class="u-h-100 u-d-flex u-flex-row u-align-items-center">
		// 			<p>Без контракта</p>
		// 		</div>
		// 	</c-toggle-card>
		// `;
	}

	private handleBtnClickReload(event: Event): void {
		event.preventDefault();
		this.getData();
	}

	private handleClickPeriod(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		const periodValue: number = parseFloat(toggle.value);
		const period: TTariffPeriod = this.data.periods.find(item => item.value == periodValue);
		const firstTariff: TTariff = period.tariffs?.[0];
		const isSkip: boolean = !period.tariffs.length ? false : !Boolean(this.selected.tariff);
		this.emitEventOnChange({
			isSkip: isSkip,
			period: period,
			tariff: isSkip ? null : firstTariff,
		});
		this.selected = {
			period: periodValue,
			tariff: isSkip ? null : (firstTariff ? firstTariff.id : null),
		}
		this.updatePeriodTitle();
	}

	private handleClickTariff(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		const tariffId: number = parseFloat(toggle.value);
		const period: TTariffPeriod = this.data.periods.find(item => item.value == this.selected.period);
		this.selected = {
			period: this.selected.period,
			tariff: tariffId,
		}
		this.emitEventOnChange({
			isSkip: false,
			period: period,
			tariff: period.tariffs.find(item => item.id == tariffId),
		});
	}

	private handleClickSkipTariff(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		const tariffId: number = parseFloat(toggle.value)
		this.selected = {
			period: this.selected.period,
			tariff: tariffId,
		}
		const period: TTariffPeriod = this.data.periods.find(item => item.value == this.selected.period)
		this.emitEventOnChange({
			isSkip: true,
			period: period,
			tariff: null,
		});
	}

	private emitEventOnChange(data: TSelectContractTariffSelected): void {
		const event = new CustomEvent('on-change', {
			detail: data,
		});
		this.dispatchEvent(event);
	}

	private attachEvent(): void {
		this.attachEventOnStatusChange();
	}

	private attachEventOnStatusChange(): void {
		this.status
			.asObservable()
			.subscribe(status => {
				this.emitEventOnStatusChange(status);
			});
	}

	private emitEventOnStatusChange(status: EComponentStatuses): void {
		const event: CustomEvent<TSelectTariffStatusChangeEventDetail> = new CustomEvent('on-status-change', {
			detail: {
				status: status,
			}
		});
		this.dispatchEvent(event);
	}
}
