import {strToBoolean} from "@helpers/index";
import Tab from "@modules/tab/Tab";

export default class TabAutoActivation {
	private CLASS_SELECTOR: string = 'js-tabAuto';

	private readonly name: string = 'tab-auto';

	public init(): void {
		this.activate(document);
	}

	private activate(scope: HTMLElement | Document): void {
		Array
			.from(scope.getElementsByClassName(this.CLASS_SELECTOR))
			.forEach(tab => {
				if (strToBoolean(tab.getAttribute(this.getDataAttrActivated()))) {
					return
				}
				tab.setAttribute(this.getDataAttrActivated(), 'true');

				new Tab(tab as HTMLElement);
			});
	}

	private getDataAttrActivated(): string {
		return `data-${this.name}-activated`;
	}
}
