import {fromEvent, Observable} from "rxjs";
import {IGlobalClick} from "@modules/global-click/model";

export default class GlobalClick implements IGlobalClick {
	private static instance: GlobalClick;

	private streamOnClick: Observable<Event>;

	constructor() {
		if (GlobalClick.instance) {
			throw new Error("Error: Instantiation failed: Use GlobalClick.getInstance() instead of new.");
		}
		GlobalClick.instance = this;
		this.init();
	}

	public static getInstance() {
		if (!GlobalClick.instance) {
			GlobalClick.instance = new GlobalClick();
		}
		return GlobalClick.instance;
	}

	public getStreamOnClick(): Observable<Event> {
		return this.streamOnClick;
	}

	private init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		this.streamOnClick = fromEvent(document, 'click');
	}
}
