import {EProductBasketBundlerOptionControlSignals} from "@components/product-basket-bundler/product-basket-bundler-option-controls/model";
import {Subject} from "rxjs";

export default class ProductBasketBundlerOptionControls {
	public signal: Subject<EProductBasketBundlerOptionControlSignals> = new Subject<EProductBasketBundlerOptionControlSignals>();

	public emitSignal(signal: EProductBasketBundlerOptionControlSignals): void {
		this.signal.next(signal);
	}
}
