import {
	EScheduleDaysOfWeek,
	EScheduleStatus,
	scheduleDaysOfWeekShotTranslationRu,
	TSchedule,
	TScheduleBeforeTime,
	TScheduleItemData,
	TScheduleTime
} from "@components/schedule/model";
import {ITimer} from "@modules/timer/model";
import Timer from "@modules/timer/Timer";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, state, property} from "lit-element";

@customElement('c-schedule')
export default class ScheduleComponent extends WebComponentLite {
	@property({
		type: String,
	})
	public title: string = 'График работы';

	@property({
		type: Array,
	})
	set data(value: TSchedule) {
		this.reset();
		this._data = value;
	};

	get data(): TSchedule {
		return this._data;
	}

	@state()
	private status: EScheduleStatus;

	@state()
	private timerOutput: string = '00:00';

	@state()
	private _data: TSchedule = [];

	private startTrackingWithMinute: number = 30;
	private timerBefore: ITimer = new Timer();
	private isTimerActivated: boolean = false;

	protected render(): unknown {
		return html`
			<section class="c-schedule">
				<h4 class="c-schedule__title">${this.title}:</h4>
				<table class="c-schedule__table">
					<tbody>
					${Object.values(EScheduleDaysOfWeek).map(dayWeek => {
						const translate = scheduleDaysOfWeekShotTranslationRu;
						const item = this._data.find((item) => {
							return item.hasOwnProperty(dayWeek);
						})
						if (!item) {
							return null;
						}
						const itemData = item[dayWeek];
						const isCurrentDayWeek = this.isCurrentDayWeek(dayWeek);
						if (isCurrentDayWeek) {
							this.updateStatus(itemData);
							this.initTimer(itemData);
						}
						return html`
							<tr class="c-schedule__row ${isCurrentDayWeek ? this.getStatusClass(this.status) : ''}">
								<th class="c-schedule__col c-schedule__label">${translate[dayWeek]}</th>
								<td class="c-schedule__col c-schedule__time">
									<time>${itemData.start} - ${itemData.end}</time>
								</td>
								<td class="c-schedule__col c-schedule__status">
									${isCurrentDayWeek ? this.getStatusText(this.status) : ''}
								</td>
							</tr>
						`;
					})}
					</tbody>
				</table>
			</section>
		`;
	}

	disconnectedCallback() {
		super.disconnectedCallback();
		this.timerBefore.stop();
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		if (name == 'data') {
			this.reset();
		}
		super.attributeChangedCallback(name, old, value);
	}

	private reset(): void {
		this.timerBefore.stop();
		this.isTimerActivated = false;
		this.timerOutput = '00:00';
	}

	private isCurrentDayWeek(dayWeek: EScheduleDaysOfWeek): boolean {
		const currentDate: Date = new Date();
		const currentDayWeek: EScheduleDaysOfWeek = Object.values(EScheduleDaysOfWeek)[currentDate.getDay() - 1];
		return dayWeek === currentDayWeek;
	}

	private initTimer(data: TScheduleItemData): void {
		if (this.isTimerActivated) {
			return;
		}
		this.updateStatus(data);
		const startBeforeTime: TScheduleBeforeTime = this.getBeforeTime(data.start);
		const endBeforeTime: TScheduleBeforeTime = this.getBeforeTime(data.end);
		let isStarted: boolean = false;
		if (startBeforeTime.minutes > 0 && startBeforeTime.minutes <= this.startTrackingWithMinute) {
			isStarted = true;
			this.timerBefore.start(startBeforeTime.milliseconds);
		} else if (endBeforeTime.minutes > 0 && endBeforeTime.minutes <= this.startTrackingWithMinute) {
			isStarted = true;
			this.timerBefore.start(endBeforeTime.milliseconds);
		}
		if (isStarted) {
			this.isTimerActivated = true;
			this.timerBefore
				.getStream()
				.subscribe(stream => {
					this.timerOutput = stream.fillTime;

					if (stream.second === 0) {
						this.isTimerActivated = true;
						this.timerEnded(data);
					}
				});
		}
	}

	private timerEnded(data: TScheduleItemData): void {
		this.updateStatus(data);
	}

	private getBeforeTime(time: string): TScheduleBeforeTime {
		const currentDate: Date = new Date();
		const endTime: TScheduleTime = this.parseTime(time);
		const hoursDiff: number = endTime.hours - currentDate.getHours();
		const minutesDiff: number = endTime.minutes - currentDate.getMinutes();
		const diffInMinutes: number = hoursDiff == 0 ? minutesDiff : (hoursDiff * 60) - minutesDiff;
		const diffInMilliseconds: number = diffInMinutes * 60000;

		return {
			minutes: diffInMinutes,
			milliseconds: diffInMilliseconds,
		}
	}

	private parseTime(value: string): TScheduleTime {
		const parseTime: Array<string> = value.split(':')
		return {
			hours: parseFloat(parseTime[0]),
			minutes: parseFloat(parseTime[1]),
		}
	}

	private updateStatus(data: TScheduleItemData): void {
		const startBeforeTime: TScheduleBeforeTime = this.getBeforeTime(data.start);
		const endBeforeTime: TScheduleBeforeTime = this.getBeforeTime(data.end);
		if (startBeforeTime.minutes < 0 && endBeforeTime.minutes > 0) {
			this.status = EScheduleStatus.OPEN;
			return;
		}
		this.status = EScheduleStatus.CLOSE;
	}

	private getStatusText(status: EScheduleStatus): string {
		if (status == EScheduleStatus.OPEN) {
			return 'Сейчас открыто';
		}
		return 'Закрыто';
	}

	private getStatusClass(status: EScheduleStatus): string {
		if (status == EScheduleStatus.OPEN) {
			return 'c-schedule__row_open';
		}
		return 'c-schedule__row_close';
	}
}


