import ToggleChange from "@modules/toggle/ToggleChange";
import {TToggleChangeStreamOnChange} from "@modules/toggle/model";

export default class ToggleOnceActive extends ToggleChange {
	private CLASS_HIDE: string = 'u-d-none';
	private DATA_ATTR_CONNECTED_CLASS: string = 'data-toggle-visibility-connected-class';

	constructor() {
		super({
			classHandler: 'js-toggleVisibilityHasOnceActiveHandler',
		});
		this.subscribeChanges();
	}

	private subscribeChanges(): void {
		this.getStreamOnChange()
			.subscribe(streamOnChange => {
				this.onChange(streamOnChange);
			})
	}

	private onChange(streamOnChange: TToggleChangeStreamOnChange) {
		const toggle = streamOnChange.input;
		const name: string = toggle.getAttribute('name');
		const connectedClass: string = toggle.getAttribute(this.DATA_ATTR_CONNECTED_CLASS);
		const activeToggles: Array<HTMLInputElement> = this.getToggleElementsByName(name).filter(toggle => toggle.checked);

		if (!connectedClass) {
			return
		}
		const connectedElements: Array<HTMLElement> = Array.from(document.getElementsByClassName(connectedClass)) as Array<HTMLElement>;
		connectedElements.forEach(connectedElement => {
			if (activeToggles.length) {
				connectedElement.classList.remove(this.CLASS_HIDE);
			} else {
				connectedElement.classList.add(this.CLASS_HIDE);
			}
		})
	}
}
