import Checklist from "@components/checklist/Checklist";
import {IChecklist} from "@components/checklist/model";
import {getDefinitionElement, isDefinitionElementEvent, stringToHTML} from "@helpers/index";
import FilterList from "@modules/filter-list/FilterList";
import {IFilterList} from "@modules/filter-list/model";
import Modal from "@modules/modal/Modal";
import {EModalModification, IModal} from "@modules/modal/model";
import {ISelectView, TSelectItem, TSelectSelectionProperties} from "@modules/select/model";
import {fromEvent, Observable, Subject} from "rxjs";
import {filter, map} from "rxjs/operators";

export default class SelectViewModalAdapter implements ISelectView {
	protected CLASS_LIST: string = 'js-selectModalList';
	protected CLASS_ITEM: string = 'js-selectModalItem';
	protected CLASS_BTN: string = 'js-selectModalBtn';
	protected CLASS_FILTER_INPUT: string = 'js-selectModalFilterInput';

	protected DATA_ATTR_ITEM_VALUE: string = 'data-select-modal-item-value';

	protected filterInputEl: HTMLInputElement;
	protected filterListEl: HTMLElement;
	protected acceptBtnEl: HTMLElement;

	protected modalInstance: IModal;
	protected filterList: IFilterList;
	protected checklist: IChecklist = new Checklist();
	protected itemsState: Array<TSelectItem> = [];

	private streamOnSelect: Subject<Array<TSelectItem>> = new Subject<Array<TSelectItem>>();
	private streamOnClose: Subject<void> = new Subject<void>();

	private isMultiple: boolean = false;

	constructor(protected selectionProperties: TSelectSelectionProperties) {
		this.itemsState = selectionProperties.items;
		this.init();
	}

	set items(items: Array<TSelectItem>) {
		this.selectionProperties.items = items;
	}

	public open(): void {
		this.modalInstance.open();
	}

	public close(): void {
		this.modalInstance.close();
	}

	public getStreamOnSelect(): Observable<Array<TSelectItem>> {
		return this.streamOnSelect.asObservable();
	}

	public getStreamOnClose(): Observable<void> {
		return this.streamOnClose.asObservable();
	}

	private init(): void {
		this.modalInstance = new Modal({
			element: "create",
			modifications: [
				EModalModification.FIXED_HEIGHT_MD
			],
			maskClosable: false,
			title: this.selectionProperties.label,
			hooks: {
				afterClose: () => {
					this.filterInputEl.value = '';
					this.filterList.showAllItems();
					this.streamOnClose.next();
				},
			},
		});
		this.modalInstance.getExtraTopEl().appendChild(
			stringToHTML(this.getSearchBarTemplate())
		);
		this.modalInstance.getBodyEl().appendChild(
			stringToHTML(this.getContentTemplate())
		);
		this.modalInstance.getFooterEl().appendChild(
			stringToHTML(this.getFooterTemplate())
		);
		this.findElements();
		this.filterList = new FilterList({
			input: this.filterInputEl,
			list: this.filterListEl,
		});
		this.updateFilterListState(this.itemsState);
		this.isMultiple = this.selectionProperties.select.multiple;
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.filterListEl, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, `.${this.CLASS_ITEM}`)),
				map(event => getDefinitionElement(event.target as HTMLElement, `.${this.CLASS_ITEM}`)),
			)
			.subscribe(itemEl => {
				const value: string = this.getItemValue(itemEl);
				const isChecked: boolean = this.checklist.isChecked(itemEl);
				if (!this.isMultiple && isChecked) {
					return;
				}
				this.itemsState = this.itemsState.map(item => {
					if (item.value === value) {
						return {
							...item,
							selected: this.isMultiple ? !item.selected : true,
						}
					} else {
						return {
							...item,
							selected: this.isMultiple ? item.selected : false,
						}
					}
				})
				if (isChecked) {
					if (this.isMultiple) {
						this.checklist.unchecked(itemEl);
					}
				} else {
					this.uncheckedAllIsNotMultiple();
					this.checklist.checked(itemEl);
				}
			});

		fromEvent(this.acceptBtnEl, 'click')
			.subscribe(() => {
				this.streamOnSelect.next(this.itemsState);
			});
	}

	private uncheckedAllIsNotMultiple(): void {
		if (this.isMultiple) {
			return;
		}
		const items: Array<HTMLElement> = Array.from(
			this.filterListEl.getElementsByClassName(this.CLASS_ITEM)
		) as Array<HTMLElement>;
		this.checklist.unchecked(...items);
	}

	private findElements(): void {
		this.filterInputEl = this.modalInstance.getExtraTopEl().querySelector(`.${this.CLASS_FILTER_INPUT}`);
		this.filterListEl = this.modalInstance.getBodyEl().querySelector(`.${this.CLASS_LIST}`);
		this.acceptBtnEl = this.modalInstance.getFooterEl().querySelector(`.${this.CLASS_BTN}`);
	}

	protected getContentTemplate(): string {
		return `
      <div class="c-checklist c-checklist_pointer c-checklist_without-separators c-checklist_sm">
        <ul class="c-checklist__list js-filterList ${this.CLASS_LIST}">
          ${this.getItemsTemplate()}
        </ul>
      </div>
    `;
	}

	protected getItemsTemplate(): string {
		return this.itemsState
			.map(item => {
				return this.getItemTemplate(item)
			})
			.join(' ')
	}

	protected getItemTemplate(item: TSelectItem): string {
		return `
		  <li class="c-checklist__item js-filterListItem ${this.CLASS_ITEM}"
			  data-filter-list-key="${item.title}"
			  ${this.DATA_ATTR_ITEM_VALUE}="${item.value}">
			<div class="c-checklist__icon">
			  <i class="o-icon o-icon_checkmark-filled"></i>
			</div>
			<span class="c-checklist__value">${item.title}</span>
		  </li>
    	`;
	}

	protected getSearchBarTemplate(): string {
		return `
		  <label class="c-search-field js-field c-search-field_gray u-mb-12 u-min-w-100">
			<input class="c-search-field__input js-filterListInput ${this.CLASS_FILTER_INPUT}"
				   type="search" name="search" placeholder="Поиск">
			  <button class="c-search-field__btn-clear js-clearInput" type="button">
				<i class="o-icon o-icon_close-filled"></i>
			  </button>
			<div class="c-search-field__append">
			  <button class="c-search-field__btn-magnifier" type="submit">
				<i class="o-icon o-icon_magnifier"></i>
			  </button>
			</div>
		  </label>
    	`;
	}

	protected getFooterTemplate(): string {
		return `
		<button class="c-btn c-btn_primary c-btn_md c-btn_block ${this.CLASS_BTN}">
			<div class="c-btn__loader">
				<i class="o-icon o-icon_loader-line-filled"></i>
			</div>
			<span class="c-btn__text">${this.selectionProperties.accept}</span>
		</button>
    `;
	}

	private updateFilterListState(items: Array<TSelectItem>): void {
		this.getFilterListItems()
			.forEach(checklistItem => {
				if (items.find(option => option.value === this.getItemValue(checklistItem)).selected) {
					this.checklist.checked(checklistItem);
				} else {
					this.checklist.unchecked(checklistItem);
				}
			})
	}

	private getItemValue(item: HTMLElement): string {
		return item.getAttribute(this.DATA_ATTR_ITEM_VALUE);
	}

	private getFilterListItems(): Array<HTMLElement> {
		return Array.from(this.filterListEl.getElementsByClassName(this.CLASS_ITEM)) as Array<HTMLElement>;
	}
}
