import {ETariffCardCostItemSize, TTariffCard} from "@components/tariff-card/model";
import {TOperator} from "@models/Operator";
import {TTariff} from "@models/Tariff";

export class TariffFormatter {
	static toTariffCard(tariff: TTariff, operator?: TOperator): TTariffCard {
		return {
			id: tariff.id,
			title: tariff.name,
			subTitle: `С тарифным планом на ${tariff.period} месяцев`,
			infoTitle: `безлимит внутри сети ${TariffFormatter.getOperatorName(operator)}`.toUpperCase(),
			costs: [
				{
					title: 'UNLIM',
					subTitle: tariff.unlimitedDesc,
					size: ETariffCardCostItemSize.SM,
				},
				{
					title: `${tariff.megabyte / 1000} GB`,
					subTitle: tariff.megabyteDesc,
				},
				{
					title: `${tariff.minutes} минут`,
					subTitle: tariff.minutesDesc,
					size: ETariffCardCostItemSize.SM,
				},
				{
					title: `${(+tariff.price).toFixed(0)} ₸`,
					subTitle: tariff.priceDesc,
					size: ETariffCardCostItemSize.SM,
				}
			]
		}
	}

	static getOperatorName(operator: TOperator): string {
		if (!operator) {
			return '';
		}
		const operatorName: string = operator.name.toLowerCase();
		if (operatorName == 'kcell' || operatorName == 'activ') {
			return 'kcell и activ'
		}
		return operatorName;
	}
}
