import EnterPhoneNumberWithConfirmationComponent
	from "@components/enter-phone-number/EnterPhoneNumberWithConfirmationComponent";
import {EValidatePhoneNumberWithConfirmationAndSmsSlots} from "@components/validate-phone-number-and-sms/model";
import ValidatePhoneNumberAndSmsComponent
	from "@components/validate-phone-number-and-sms/ValidatePhoneNumberAndSmsComponent";
import {concatFormData, objectToFormData} from "@helpers/index";
import {customElement, html, queryAsync, TemplateResult} from "lit-element";

@customElement('c-validate-phone-number-with-confirmation-and-sms')
export default class ValidatePhoneNumberWithConfirmationAndSmsComponent extends ValidatePhoneNumberAndSmsComponent {
	@queryAsync('[validate-phone-number-and-sms-enter-phone-number-with-confirmation]')
	protected enterPhoneNumberComponentAsync: Promise<EnterPhoneNumberWithConfirmationComponent>;

	private confirmationChecked: boolean = false;

	protected getEnterPhoneNumberTemplate(): TemplateResult {
		const confirmContent = this.getSlot(EValidatePhoneNumberWithConfirmationAndSmsSlots.CONFIRMATION_CONTENT);

		return html`
			<c-enter-phone-number-with-confirmation validate-phone-number-and-sms-enter-phone-number-with-confirmation
													?loading-btn="${this.isLoadingBtnGetSMS()}"
													?disable-btn="${this.isDisabledBtnGetSMS()}"
													.confirmationChecked="${this.confirmationChecked}"
													@on-action-btn-click="${this.handleClickBtnGetSMS}">
				${confirmContent}
			</c-enter-phone-number-with-confirmation>
		`;
	}

	protected getDataForGetSMSCode(): FormData {
		return concatFormData(
			super.getDataForGetSMSCode(),
			objectToFormData({
				confirmed: this.confirmationChecked,
			}),
		);
	}

}
