import {TTariffCard, TTariffCardCostItem} from "@components/tariff-card/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-tariff-card')
export default class TariffCardComponent extends WebComponentLite {
	@property({
		type: Object
	})
	data: TTariffCard;

	protected render(): TemplateResult {

		return html`
			<article class="c-tariff-card">
				<header class="c-tariff-card__header">
					<h4 class="c-tariff-card__title">
						${unsafeHTML(this.data?.title)}
					</h4>
					${this.data?.subTitle ?
						html`
							<h6 class="c-tariff-card__sub-title">${unsafeHTML(this.data?.subTitle)}</h6>
						` :
						null}
					${this.getPrepend()}
				</header>
				<div class="c-tariff-card__body">
					${this.getCostTemplate()}
				</div>
				<footer class="c-tariff-card__footer">
					${this.data?.infoTitle ?
						html`
							<h4 class="c-tariff-card__info-title">${unsafeHTML(this.data?.infoTitle)}</h4>
						` :
						null}
				</footer>
			</article>
		`;
	}

	private getPrepend(): TemplateResult {
		if (!this.data?.prepend) {
			return null;
		}
		let result;
		if (this.data.prepend instanceof TemplateResult) {
			result = this.data.prepend;
		} else {
			result = unsafeHTML(this.data.prepend)
		}
		return html`
			<div class="c-tariff-card__prepend">
				${result}
			</div>
		`;
	}

	private getCostTemplate(): TemplateResult {
		const cost: Array<TTariffCardCostItem> = this.data?.costs || [];

		if (!cost.length) {
			return null;
		}

		return html`
			<div class="c-tariff-card-cost">
				${cost.map(item =>
					html`
						<section
							class="c-tariff-card-cost__item ${this.getMods('c-tariff-card-cost__item', [item.size])}">
							<h4 class="c-tariff-card-cost__title">
								${item.prepend ? html`<span>${item.prepend}</span>` : null}
								${unsafeHTML(item.title)}
							</h4>
							${item.subTitle ?
								html`
									<p class="c-tariff-card-cost__sub-title">${unsafeHTML(item.subTitle)}</p>
								` :
								null}
						</section>
					`
				)}
			</div>
		`;
	}

	protected getMods(key: string, list: Array<string>): string {
		return list
			.filter(Boolean)
			.map(mod => `${key}_${mod}`)
			.join(' ');
	}
}
