import {getDefinitionElement, isDefinitionElementEvent} from "@helpers/index";
import {fromEvent} from "rxjs";
import {filter} from "rxjs/operators";

export default class ApplyFilters {
	private CLASS_HANDLER: string = 'js-applyFilters';

	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		const selectorHandler: string = `.${this.CLASS_HANDLER}`;
		fromEvent(document, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, selectorHandler)),
			)
			.subscribe(event => {
				event.preventDefault();
				const handler: HTMLElement = getDefinitionElement(event.target as HTMLElement, selectorHandler);
				const form: HTMLFormElement = handler.closest(`FORM`);
				this.clearTogglesWithAppointmentForAll();
				form.submit();
			})
	}

	private clearTogglesWithAppointmentForAll(): void {
		const toggles: Array<HTMLInputElement> = [
			...Array.from(document.querySelectorAll(`input[type="checkbox"][value=""]`)),
			...Array.from(document.querySelectorAll(`input[type="radio"][value=""]`))
		] as Array<HTMLInputElement>;
		toggles.forEach(toggle => {
			toggle.checked = false;
		})
	}

}
