export interface IBtn {
	addLoader(): this

	removeLoader(): this

	disable(): this

	fakeDisable(): this

	enable(): this

	fakeEnable(): this

	isDisabled(): boolean

	isFakeDisabled(): boolean

	isLoading(): boolean

	setText(text: string): this

	setPrependIcon(iconClass: string): this

	removePrependIcon(): this

	setAppendIcon(iconClass: string): this

	removeAppendIcon(): this

	getBtn(): HTMLButtonElement
}

export type TBtnOption = {
	classes?: {
		loading?: string
		disabled?: string
	}
}

export interface IBtnSwitch extends IBtn {
	switch(state: boolean): this
}

export enum EBtnSwitchElements {
	TEXT = 'TEXT',
	PREPEND_ICON = 'PREPEND_ICON',
	APPEND_ICON = 'APPEND_ICON',
}

export type IBtnSwitchDataElementAttrs = {
	[EBtnSwitchElements.TEXT]: IBtnSwitchElement
	[EBtnSwitchElements.PREPEND_ICON]: IBtnSwitchElement
	[EBtnSwitchElements.APPEND_ICON]: IBtnSwitchElement
}

export type IBtnSwitchElement = {
	true: string
	false: string
	action: (value: string) => unknown
}
