import {TAnimationOption} from "@modules/animation/model";
import {Observable} from "rxjs";

export interface IDialog {
	options: TDialogOptions

	open(): void

	close(): void

	getState(): EDialogState

	destroy(): void

	getDialogEl(): HTMLElement

	getViewEl(): HTMLElement

	getOnClickStream(): Observable<Event>

	maskClosable: boolean

	closable: boolean
}

export type TDialogOptions = {
	element?: HTMLElement | 'create'
	classes?: TDialogClasses
	stateClasses?: TDialogStateClasses
	actionClasses?: TDialogActionClasses
	animation?: TDialogAnimation
	hooks?: TDialogHooks
	maskClosable?: boolean
	closable?: boolean
	hasBackdrop?: boolean
}

export type TDialogAnimation = {
	dialogOption?: TAnimationOption
	viewOption?: TAnimationOption
}

export type TDialogHooks = {
	beforeOpen?: () => void
	beforeClose?: () => void
	afterOpen?: () => void
	afterClose?: () => void
}

export type TDialogClasses = {
	dialog?: string
	view?: string
}

export type TDialogStateClasses = {
	open?: string
	closed?: string
}

export type TDialogActionClasses = {
	close?: string
}

export enum EDialogState {
	OPEN = 'OPEN',
	CLOSED = 'CLOSED',
}

