import EditProductsCartComponent from "@components/edit-products-cart/EditProductsCartComponent";
import PaymentMethodSelectComponent from "@components/payment-method/select/PaymentMethodSelectComponent";
import {fromEvent} from "rxjs";

export default class UpdateCartPaymentMethodOnItemChange {
	private CLASS_ITEM_CONTROLLER: string = 'js-cartEditProducts';
	private CLASS_PAYMENT_METHOD_SELECT: string = 'js-cartPaymentMethodSelect';

	private editProductsEl: EditProductsCartComponent;
	private paymentMethodSelectEl: PaymentMethodSelectComponent;

	public init(): void {
		this.editProductsEl = document.querySelector(`.${this.CLASS_ITEM_CONTROLLER}`);
		this.paymentMethodSelectEl = document.querySelector(`.${this.CLASS_PAYMENT_METHOD_SELECT}`);

		if (!this.editProductsEl || !this.paymentMethodSelectEl) {
			return;
		}
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.editProductsEl, 'on-change')
			.subscribe(() => {
				this.paymentMethodSelectEl.updateData();
			});
	}
}
