import {IDialog, TDialogActionClasses, TDialogClasses, TDialogOptions} from "@modules/dialog/model";

export interface IModal extends IDialog {
	updateContent(): void

	getViewEl(): HTMLElement

	getContentEl(): HTMLElement

	getHeaderEl(): HTMLElement

	getExtraTopEl(): HTMLElement

	getBodyEl(): HTMLElement

	getFooterEl(): HTMLElement

	getTitleEl(): HTMLElement

	maskClosable: boolean

	closable: boolean
}

export type TModalOptions = TDialogOptions & {
	element?: HTMLDialogElement | 'create'
	modifications?: Array<EModalModification>
	title?: string
	classes?: TModalClasses
	actionClasses?: TModalActionClasses
}

export type TModalClasses = TDialogClasses & {
	content?: string
	header?: string
	extraTop?: string
	body?: string
	footer?: string
	title?: string
}

export type TModalActionClasses = TDialogActionClasses & {
	closeIcon?: string
}

export enum EModalModification {
	FIXED_HEIGHT_MD = 'c-modal_fixed-height-md'
}
