import BackendData from "@modules/backend-data/BackendData";
import {IBackendData} from "@modules/backend-data/model";
import Cookie from "@modules/cookie/Cookie";
import {ECookie, ICookie} from "@modules/cookie/model";
import {ICSRFToken} from "@modules/csrf-token/model";

export default class CSRFToken implements ICSRFToken {
	private CLASS_INPUT: string = 'js-CSRFTokenInput';

	private static instance: ICSRFToken;

	private backendData: IBackendData = BackendData.getInstance();

	constructor() {
		if (CSRFToken.instance) {
			throw new Error("Error: Instantiation failed: Use CSRFToken.getInstance() instead of new.");
		}
		CSRFToken.instance = this;
	}

	public static getInstance(): ICSRFToken {
		if (!CSRFToken.instance) {
			CSRFToken.instance = new CSRFToken();
		}
		return this.instance;
	}

	public getToken(): string {
		return this.getTokenFromDOM() || this.getTokenFromCookie();
	}

	private getTokenFromDOM(): string {
		const input: HTMLInputElement = this.backendData
			.getContainer()
			.querySelector(`.${this.CLASS_INPUT}`);
		return input?.value;
	}

	private getTokenFromCookie(): string {
		const cookie: ICookie = Cookie.getInstance();
		return cookie.get(ECookie.CSRF_TOKEN);
	}
}
