import {isHTMLElement, strToBoolean} from "@helpers/index";
import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";

export default class AutoActivation extends DynamicInsertionElements {
	public name: string = '';
	public handlerClass: string = '';
	public handleDynamicInsertionElements: boolean;

	constructor(name: string, handlerClass: string, handleDynamicInsertionElements: boolean) {
		super();
		this.name = name;
		this.handlerClass = handlerClass;
		this.handleDynamicInsertionElements = handleDynamicInsertionElements;
	}

	public init(): void {
		this.activate(document);
		if (this.handleDynamicInsertionElements) {
			this.attachEventDynamicInsertion();
		}
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		const handlers: Array<HTMLElement> = this.getHandlers(scope);
		handlers.forEach(handler => {
			if (strToBoolean(handler.getAttribute(this.getDataActivatedName()))) {
				return
			}
			handler.setAttribute(this.getDataActivatedName(), 'true');
			this.initInstance(handler);
		});
	}

	protected initInstance(handler: HTMLElement): void {

	}

	protected getHandlers(scope: HTMLElement | Document): Array<HTMLElement> {
		return Array.from(
			scope.getElementsByClassName(this.handlerClass)
		) as Array<HTMLElement>;
	}

	public getDataActivatedName(): string {
		return `data-${this.name}-activated`
	}
}
