export type TSchedule = Array<TScheduleItem>

export type TScheduleItem = {
	[key in string]: TScheduleItemData
}

export type TScheduleItemData = {
	start: string
	end: string
}

export enum EScheduleDaysOfWeek {
	MONDAY = 'monday',
	TUESDAY = 'tuesday',
	WEDNESDAY = 'wednesday',
	THURSDAY = 'thursday',
	FRIDAY = 'friday',
	SATURDAY = 'saturday',
	SUNDAY = 'sunday',
}

export type TScheduleDaysOfWeekTranslation = {
	[key in EScheduleDaysOfWeek]: string
}

export type TScheduleTime = {
	hours: number
	minutes: number
}

export enum EScheduleStatus {
	OPEN,
	CLOSE,
}

export type TScheduleBeforeTime = {
	minutes: number
	milliseconds: number
}

export const scheduleDaysOfWeekShotTranslationRu: TScheduleDaysOfWeekTranslation = {
	[EScheduleDaysOfWeek.MONDAY]: 'Пн',
	[EScheduleDaysOfWeek.TUESDAY]: 'Вт',
	[EScheduleDaysOfWeek.WEDNESDAY]: 'Ср',
	[EScheduleDaysOfWeek.THURSDAY]: 'Чт',
	[EScheduleDaysOfWeek.FRIDAY]: 'Пт',
	[EScheduleDaysOfWeek.SATURDAY]: 'Сб',
	[EScheduleDaysOfWeek.SUNDAY]: 'Вс',
}
