import KeyboardState from "@modules/keyboard/KeyboardState";
import {EKeyboardState, IKeyboardState} from "@modules/keyboard/model";
import {IPlatform} from "@modules/platform/model";
import Platform from "@modules/platform/Platform";

export default class AndroidActiveKeyboardHandler {
	private CLASS_ACTIVE_KEYBOARD: string = 's-active-keyboard';

	private keyboardState: IKeyboardState;
	private platform: IPlatform;

	public init(): void {
		this.platform = Platform.getInstance();
		if (!this.platform.isAndroid()) {
			return;
		}
		this.keyboardState = KeyboardState.getInstance();
		this.attachEvent();
	}

	private attachEvent(): void {
		this.keyboardState
			.getStream()
			.subscribe(state => {
				this.changeClassByState(state);
			});
	}

	private changeClassByState(keyboardState: EKeyboardState): void {
		if (keyboardState === EKeyboardState.ACTIVE) {
			document.body.classList.add(this.CLASS_ACTIVE_KEYBOARD);
		} else {
			document.body.classList.remove(this.CLASS_ACTIVE_KEYBOARD);
		}
	}
}
