import {EBtnSize, EBtnType} from "@components/btn/model";
import {
	EContractTariffsValidatePhoneToConnectTariffSlots,
	TContractTariffsValidatePhoneToConnectTariffValidateSMSCodeReject,
	TContractTariffsValidatePhoneToConnectTariffValidateSMSCodeResponse
} from "@components/contract-tariffs/contract-tariffs-validate-phone-to-connect-tariff/model";
import {
	TValidatePhoneNumberAndSMSEventReject,
	TValidatePhoneNumberAndSMSEventResponse
} from "@components/validate-phone-number-and-sms/model";
import ValidatePhoneNumberAndSmsComponent
	from "@components/validate-phone-number-and-sms/ValidatePhoneNumberAndSmsComponent";
import {ETariffConnectionStatuses, TTariffConnection} from "@models/Tariff";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosRequestConfig} from "axios";
import {customElement, html, state, queryAsync, TemplateResult, property} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";
import {from} from "rxjs";
import {map, switchMap} from "rxjs/operators";

@customElement('c-contract-tariffs-validate-phone-to-connect-tariff')
export default class ContractTariffsValidatePhoneToConnectTariffComponent extends WebComponentLite {
	@queryAsync('[contract-tariffs-validate-phone-to-connect-tariff-validator]')
	private validatorAsync: Promise<ValidatePhoneNumberAndSmsComponent>;

	@state()
	private tariffConnection: TTariffConnection;

	@property({
		type: Object,
		attribute: 'send-otp-request-config',
	})
	private getSMSRequestConfig: AxiosRequestConfig = {
		url: '/contract-tariff/send_otp/',
		method: 'POST'
	}

	@property({
		type: Object,
		attribute: 'verify-otp-request-config',
	})
	private validateSMSRequestConfig: AxiosRequestConfig = {
		url: '/contract-tariff/verify_otp/',
		method: 'POST'
	}

	protected render(): unknown {
		if (this.tariffConnection) {
			return this.getTariffConnectionTemplate();
		}

		const confirmContent = this.getSlot(EContractTariffsValidatePhoneToConnectTariffSlots.CONFIRMATION_CONTENT);

		return html`
			<c-validate-phone-number-with-confirmation-and-sms
				contract-tariffs-validate-phone-to-connect-tariff-validator
				.getSMSRequestConfig="${this.getSMSRequestConfig}"
				.validateSMSRequestConfig="${this.validateSMSRequestConfig}"
				@on-response-get-sms-code="${this.handleResponseGetSMSCode}"
				@on-reject-get-sms-code="${this.handleRejectGetSMSCode}"
				@on-response-validate-sms-code="${this.handleResponseValidateSMSCode}"
				@on-reject-validate-sms-code="${this.handleRejectValidateSMSCode}">
				${confirmContent}
			</c-validate-phone-number-with-confirmation-and-sms>
		`;
	}

	private getTariffConnectionTemplate(): TemplateResult {
		switch (this.tariffConnection.status) {
			case ETariffConnectionStatuses.DENIED:
				return this.getDeniedConnectTariffTemplate();
			default:
				return html`
					<p>Шаблон отображения не найден</p>
				`
		}
	}

	private getDeniedConnectTariffTemplate(): TemplateResult {
		return html`
			<div class="c-phone-validation-for-contract-tariff__invalid-phone">
				<p class="c-phone-validation-for-contract-tariff__invalid-phone-title">
					${unsafeHTML(this.tariffConnection.message)}
				</p>
				<c-btn class="c-phone-validation-for-contract-tariff__invalid-phone-action"
					   type="${EBtnType.SECONDARY}"
					   size="${EBtnSize.MD}"
					   @click="${this.handleBtnClickChangePhone}">
					Изменить номер
				</c-btn>
			</div>
		`;
	}

	private handleResponseGetSMSCode(event: CustomEvent): void {

	}

	private handleRejectGetSMSCode(event: CustomEvent): void {

	}

	private handleResponseValidateSMSCode(event: CustomEvent<TValidatePhoneNumberAndSMSEventResponse<TContractTariffsValidatePhoneToConnectTariffValidateSMSCodeResponse>>): void {
		this.tariffConnection = event.detail.response.data.data.tariffConnection;
	}

	private handleRejectValidateSMSCode(event: CustomEvent<TValidatePhoneNumberAndSMSEventReject<TContractTariffsValidatePhoneToConnectTariffValidateSMSCodeReject>>): void {

	}

	private handleBtnClickChangePhone(): void {
		this.tariffConnection = null;
		from(this.validatorAsync)
			.pipe(
				switchMap(component => {
					return from(component.updateComplete)
						.pipe(
							map(() => component)
						)
				})
			)
			.subscribe(component => {
				component.focusInputEnterPhoneNumber();
			});
	}
}
