import {ITimer, TTimerValue} from "@modules/timer/model";
import Timer from "@modules/timer/Timer";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, state, property, TemplateResult} from "lit-element";
import {Observable} from "rxjs";

@customElement('c-timer')
export default class TimerTextComponent extends WebComponentLite {
	@property({
		reflect: true,
		type: Number,
	})
	public time: number = 0;

	private timer: ITimer;

	@state()
	private output: string = '00:00';

	constructor() {
		super();
		this.timer = new Timer();
		this.timer
			.getStream()
			.subscribe(stream => {
				this.output = stream.fillTime;
			});
	}

	public getStream(): Observable<TTimerValue> {
		return this.timer.getStream();
	}

	connectedCallback() {
		super.connectedCallback();
		if (this.time > 0) {
			this.timer.start(this.time);
		}
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);
		if (name === 'time') {
			this.timer.start(parseFloat(value));
		}
	}

	protected render(): TemplateResult {
		return html`
			<span>${this.output}</span>
		`;
	}

	disconnectedCallback() {
		super.disconnectedCallback();
		this.timer.stop();
	}
}
