import {Debounce} from "@decorators/method/debounce";
import {carouselConfigs, TCarouselItemOptions} from "@modules/carousel/configs";
import {ICarousel, TCarouselOption} from "@modules/carousel/model";
import {fromEvent} from "rxjs";
import Swiper, {Autoplay, Lazy, Navigation, Pagination, SwiperOptions, Thumbs, Zoom} from "swiper";
import {LazyOptions} from "swiper/types/components/lazy";
import {NavigationOptions} from "swiper/types/components/navigation";
import {PaginationOptions} from "swiper/types/components/pagination";

Swiper.use([Navigation, Pagination, Thumbs, Lazy, Zoom, Autoplay]);

export default class Carousel implements ICarousel {
	private DATA_ATTR_CONFIG_NAME: string = 'data-carousel-config-name';

	private carouselEl: HTMLElement;
	private viewportEl: HTMLElement;
	private containerEl: HTMLElement;
	private controlsEl: HTMLElement;
	private prevBtnEl: HTMLButtonElement;
	private nextBtnEl: HTMLButtonElement;
	private dotsEl: HTMLElement;
	private thumbEl: HTMLElement;
	private thumbViewportEl: HTMLElement;
	private thumbContainerEl: HTMLElement;
	private zoomInEl: HTMLElement;
	private zoomOutEl: HTMLElement;

	private dotPattern: string;

	private carousel: Swiper;
	private carouselThumb: Swiper;

	private swiperOptions: TCarouselItemOptions;

	constructor(carouselEl: HTMLElement, private options?: TCarouselOption) {
		this.carouselEl = carouselEl;
		const defaultOptions: TCarouselOption = {
			classes: {
				handler: 'js-carousel',
				viewport: 'js-carouselViewport',
				container: 'js-carouselContainer',
				item: 'js-carouselItem',
				item3D: 'js-carouselItem3D',
				controls: 'js-carouselControls',
				btnPrev: 'js-carouselBtnPrev',
				btnNext: 'js-carouselBtnNext',
				dots: 'js-carouselDots',
				dot: 'js-carouselDot',
				lazyLoadItem: 'js-carouselLazyLoadItem',
				lazyLoadPlaceholder: 'js-carouselLazyLoadPlaceholder',
				thumb: 'js-carouselThumb',
				thumbViewport: 'js-carouselThumbViewport',
				thumbContainer: 'js-carouselThumbContainer',
				thumbItem: 'js-carouselThumbItem',
				zoomIn: 'js-carouselZoomIn',
				zoomOut: 'js-carouselZoomOut'
			},
			stateClasses: {
				is3DView: 'is-3dView'
			},
			configName: 'default',
		};
		this.options = {
			...defaultOptions,
			...this.options,
		};
		this.init();
	}

	public next(): void {
		this.carousel.slideNext();
	}

	public prev(): void {
		this.carousel.slidePrev();
	}

	public to(index: number): void {
		this.carousel.slideTo(index, 0);
	}

	private init(): void {
		this.findElements();
		if (this.thumbEl) {
			this.carouselThumb = new Swiper(
				this.thumbViewportEl,
				this.getSwiperThumbOptions(),
			)
		}
		this.swiperOptions = this.getSwiperOptions();
		this.carousel = new Swiper(
			this.viewportEl,
			this.swiperOptions,
		);
		this.initCSSMode();
		this.attachEvent();
	}

	private findElements(): void {
		this.viewportEl = this.carouselEl.querySelector(`.${this.options.classes.viewport}`);
		this.containerEl = this.carouselEl.querySelector(`.${this.options.classes.container}`);
		Array.from(this.containerEl.children).forEach(item => {
			item.classList.add(this.options.classes.item);
		});

		//Controls
		this.controlsEl = this.carouselEl.querySelector(`.${this.options.classes.controls}`);
		if (this.controlsEl) {
			this.prevBtnEl = this.controlsEl.querySelector(`.${this.options.classes.btnPrev}`);
			this.nextBtnEl = this.controlsEl.querySelector(`.${this.options.classes.btnNext}`);
		}

		//Dots
		this.dotsEl = this.carouselEl.querySelector(`.${this.options.classes.dots}`);
		if (this.dotsEl) {
			this.dotPattern = this.dotsEl.firstElementChild.outerHTML;
		}

		//Thumb
		this.thumbEl = this.carouselEl.querySelector(`.${this.options.classes.thumb}`);
		if (this.thumbEl) {
			this.thumbViewportEl = this.thumbEl.querySelector(`.${this.options.classes.thumbViewport}`);
			this.thumbContainerEl = this.thumbEl.querySelector(`.${this.options.classes.thumbContainer}`);
			Array.from(this.thumbContainerEl.children).forEach(item => {
				item.classList.add(this.options.classes.thumbItem);
			});
		}

		//Zoom
		this.zoomInEl = this.carouselEl.querySelector(`.${this.options.classes.zoomIn}`);
		this.zoomOutEl = this.carouselEl.querySelector(`.${this.options.classes.zoomOut}`);
	}

	private getBaseSwiperOptions(): TCarouselItemOptions {
		return {
			observer: true,
			observeParents: true,
			slidesPerView: 'auto',
			containerModifierClass: 'is-',
			slideActiveClass: 'is-active',
			slideDuplicateActiveClass: 'is-active',
			slideNextClass: 'is-next',
			slidePrevClass: 'is-prev',
			preloadImages: true,
			lazy: {
				loadPrevNext: true,
				elementClass: this.options.classes.lazyLoadItem,
				preloaderClass: this.options.classes.lazyLoadPlaceholder,
				loadedClass: 'is-loaded',
				loadingClass: 'is-loading',
			},
		}
	}

	private getSwiperOptions(): TCarouselItemOptions {
		let options: TCarouselItemOptions = {
			...this.getBaseSwiperOptions(),
			wrapperClass: this.options.classes.container,
			slideClass: this.options.classes.item,
			zoom: {
				maxRatio: 3,
				minRatio: 1,
			}
		};

		if (this.prevBtnEl && this.nextBtnEl) {
			options.navigation = this.getNavigationOption();
		}

		if (this.dotsEl) {
			options.pagination = this.getDotsOption();
		}

		if (this.thumbEl) {
			options.thumbs = {
				swiper: this.carouselThumb,
				slideThumbActiveClass: 'is-active',
			}
		}

		return {
			...options,
			...this.getSwiperConfig(),
		};
	}

	private getSwiperThumbOptions(): TCarouselItemOptions {
		const baseOptions: TCarouselItemOptions = this.getBaseSwiperOptions();
		let options: TCarouselItemOptions = {
			...baseOptions,
			wrapperClass: this.options.classes.thumbContainer,
			slideClass: this.options.classes.thumbItem,
			spaceBetween: 8,
			breakpoints: {
				320: {
					spaceBetween: 4,
				},
				992: {
					spaceBetween: 8,
				},
			},
			lazy: {
				...baseOptions.lazy as LazyOptions || {} as LazyOptions,
				loadPrevNextAmount: this.thumbContainerEl.children.length,
			}
		};

		return options;
	}

	private getNavigationOption(): NavigationOptions {
		return {
			prevEl: this.prevBtnEl,
			nextEl: this.nextBtnEl,
			disabledClass: 'is-disabled',
		}
	}

	private getDotsOption(): PaginationOptions {
		return {
			el: this.dotsEl,
			clickable: true,
			bulletClass: this.options.classes.dot,
			modifierClass: 'is-',
			bulletActiveClass: 'is-active',
			renderBullet: (index, className) => {
				return this.dotPattern;
			}
		}
	}

	private getSwiperConfig(): TCarouselItemOptions {
		const dataAttrValue: string = this.carouselEl.getAttribute(this.DATA_ATTR_CONFIG_NAME);
		const configName: string = dataAttrValue ? dataAttrValue : this.options.configName;
		return carouselConfigs[configName];
	}

	private attachEvent(): void {
		this.carousel.on('slideChange', () => {
			this.handlePosition3DViewer();
		})

		if (this.zoomInEl && this.zoomOutEl) {
			this.updateStateBtnZoom();

			fromEvent(this.zoomInEl, 'click')
				.subscribe(() => {
					this.carousel.zoom.in();
					this.updateStateBtnZoom();
				})
			fromEvent(this.zoomOutEl, 'click')
				.subscribe(() => {
					this.carousel.zoom.out();
					this.updateStateBtnZoom();
				})
		}
	}

	private updateStateBtnZoom(): void {
		if (this.carousel.zoom.scale === 1) {
			this.zoomInEl.classList.remove('is-disabled');
			this.zoomOutEl.classList.add('is-disabled');
		} else {
			this.zoomInEl.classList.add('is-disabled');
			this.zoomOutEl.classList.remove('is-disabled');
		}
	}

	@Debounce(100)
	private handlePosition3DViewer(): void {
		const slide = this.carousel.slides[this.carousel.realIndex];
		if (slide && slide.classList.contains(this.options.classes.item3D)) {
			this.carousel.params.allowTouchMove = false;
			this.carousel.detachEvents();
			this.carouselEl.classList.add(this.options.stateClasses.is3DView);
			this.update3DViewer(slide);
		} else {
			if (this.carousel.params.allowTouchMove) {
				return;
			}
			this.carouselEl.classList.remove(this.options.stateClasses.is3DView);
			this.carousel.params.allowTouchMove = true;
			this.carousel.attachEvents();
		}
	}

	private initCSSMode(): void {
		const width: number = window.innerWidth;
		const items: [string, SwiperOptions][] = Object
			.entries(this.swiperOptions?.breakpoints || {})
			.filter(([, value]) => value.hasOwnProperty('cssMode'))
			.filter(([key]) => parseFloat(key) <= width)
		if (items.length) {
			if (items[items.length - 1][1].cssMode) {
				this.carousel.detachEvents();
			}
		} else {
			if (this.swiperOptions.cssMode) {
				this.carousel.detachEvents();
			}
		}
	}

	private update3DViewer(item: HTMLElement | Element): void {
		const viewer3D: HTMLElement = item.querySelector('model-viewer');
		const viewer3DClone: Node = viewer3D.cloneNode(true);
		const parent: HTMLElement = viewer3D.parentElement;
		viewer3D.remove();
		parent.appendChild(viewer3DClone);
	}
}
