import AutoActivation from "@modules/auto-activation/AutoActivation";
import RequestReplacementDataAttr from "@modules/request-replacement/RequestReplacementDataAttr";

export default class RequestReplacementDataAttrAutoActivation extends AutoActivation {
	constructor() {
		super('request-replacement-data-attr', 'js-requestReplacementDataAttrAuto', true);
	}

	protected initInstance(handler: HTMLElement) {
		new RequestReplacementDataAttr(handler).init();
	}
}
