import Btn from "@modules/btn/Btn";
import {EBtnSwitchElements, IBtnSwitch, IBtnSwitchDataElementAttrs, IBtnSwitchElement} from "@modules/btn/model";

export default class BtnSwitch extends Btn implements IBtnSwitch {

	private elements: IBtnSwitchDataElementAttrs = {
		[EBtnSwitchElements.TEXT]: {
			true: 'data-btn-true-text',
			false: 'data-btn-false-text',
			action: this.setText,
		},
		[EBtnSwitchElements.PREPEND_ICON]: {
			true: 'data-btn-true-prepend-icon',
			false: 'data-btn-false-prepend-icon',
			action: this.setPrependIcon,
		},
		[EBtnSwitchElements.APPEND_ICON]: {
			true: 'data-btn-true-append-icon',
			false: 'data-btn-false-append-icon',
			action: this.setAppendIcon,
		}
	}

	constructor(btnEl: HTMLButtonElement) {
		super(btnEl);
	}

	public switch(state: boolean): this {
		this.change(state);
		return this;
	}

	private getValue(state: boolean, key: EBtnSwitchElements | string): string {
		const attr: string = this.getElement(key)?.[`${state}`];
		return this.getBtn().getAttribute(attr);
	}

	private getElement(key: EBtnSwitchElements | string): IBtnSwitchElement {
		return this.elements[key];
	}

	private change(state: boolean): void {
		Object
			.entries(this.elements)
			.forEach(([key, element]) => {
				const value = this.getValue(state, key);
				if (!value) {
					return;
				}
				element.action.call(this, value);
			});
	}
}
