import {getDefinitionElement, isDefinitionElementEvent} from "@helpers/index";
import {ICitySelection} from "@modules/city/model";
import FilterList from "@modules/filter-list/FilterList";
import {IFilterList} from "@modules/filter-list/model";
import Modal from "@modules/modal/Modal";
import {IModal} from "@modules/modal/model";
import {fromEvent} from "rxjs";
import {filter, map} from "rxjs/operators";

export default class CitySelection implements ICitySelection {
	private CLASS_SEARCH_FIELD: string = 'js-citySelectionSearchField';
	private CLASS_LIST: string = 'js-citySelectionList';
	private CLASS_ITEM: string = 'js-citySelectionItem';
	private CLASS_FORM: string = 'js-citySelectionForm';
	private CLASS_INPUT: string = 'js-citySelectionInput';

	private ID_MODAL: string = 'js-citySelectionModal';

	private DATA_ATTR_VALUE: string = 'data-selection-city-value';

	private static instance: ICitySelection;

	private modalEL: HTMLDialogElement;
	private searchInputEl: HTMLInputElement;
	private listEl: HTMLElement;
	private formEl: HTMLFormElement;
	private inputEl: HTMLInputElement;

	private modal: IModal;
	private filterList: IFilterList;

	constructor() {
		if (CitySelection.instance) {
			throw new Error("Error: Instantiation failed: Use CitySelection.getInstance() instead of new.");
		}
		CitySelection.instance = this;
		this.init();
	}

	public static getInstance(): ICitySelection {
		if (!CitySelection.instance) {
			CitySelection.instance = new CitySelection();
		}
		return this.instance;
	}

	public open(): void {
		this.modal.open();
	}

	public close(): void {
		this.modal.close();
	}

	private init(): void {
		this.modalEL = document.getElementById(this.ID_MODAL) as HTMLDialogElement;
		if (!this.modalEL) {
			// throw new Error('CitySelection: modal is not defined !');
			return;
		}
		this.modal = new Modal({
			element: this.modalEL,
			maskClosable: false,
		});
		this.findElements();
		this.filterList = new FilterList({
			input: this.searchInputEl,
			list: this.listEl,
			classes: {
				item: this.CLASS_ITEM
			}
		});
		this.attachEvent();
	}

	private attachEvent(): void {
		const selectorHandler: string = `.${this.CLASS_ITEM}`;
		fromEvent(this.listEl, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, selectorHandler)),
				map(event => getDefinitionElement(event.target as HTMLElement, selectorHandler)),
			)
			.subscribe(item => {
				this.inputEl.value = item.getAttribute(this.DATA_ATTR_VALUE);
				window.history.pushState({}, document.title, this.getNewURL())
				this.formEl.submit();
			})
	}

	private getNewURL(): string {
		const url: URL = new URL(window.location.href);
		url.searchParams.delete('page');
		return url.href;
	}

	private findElements(): void {
		this.searchInputEl = this.modal.getContentEl().querySelector(`.${this.CLASS_SEARCH_FIELD}`);
		if (!this.searchInputEl) {
			throw new Error('CitySelection: search input is not defined !');
		}
		this.listEl = this.modal.getContentEl().querySelector(`.${this.CLASS_LIST}`);
		if (!this.listEl) {
			throw new Error('CitySelection: list is not defined !');
		}
		this.formEl = this.modal.getContentEl().querySelector(`.${this.CLASS_FORM}`);
		if (!this.listEl) {
			throw new Error('CitySelection: form is not defined !');
		}
		this.inputEl = this.modal.getContentEl().querySelector(`.${this.CLASS_INPUT}`);
		if (!this.listEl) {
			throw new Error('CitySelection: input city is not defined !');
		}
	}
}
