import {EFormValidatorType, IFormValidator} from "@modules/form/model";
import {TErrorField} from "@modules/http-request/model";

export default class FormValidator implements IFormValidator {
	private CLASS_ITEM: string = 'js-formItem';
	private CLASS_MESSAGE: string = 'js-formItemMessage';

	private CLASS_INVALID: string = 'has-error';
	private CLASS_VALID: string = 'is-valid';

	private formEl: HTMLFormElement | HTMLElement;

	constructor(form: HTMLFormElement | HTMLElement) {
		this.formEl = form;
	}

	public showErrors(errors: Array<TErrorField>): void {
		errors.forEach(error => {
			const items: Array<HTMLElement> = this.getFormItem(...error.fieldNames);
			items.forEach(item => {
				this.showMessage(item, error.messages, EFormValidatorType.INVALID);
			})
		})
	}

	public clearMessages(): void {
		const items: Array<HTMLElement> = this.getAllItems();
		items.forEach(item => {
			this.clearClasses(item);
			this.setMessage(item, ['']);
		});
	}

	public clearValue(): void {
		const items: Array<HTMLElement> = this.getAllItems();
		items
			.map(item => {
				return [
					...Array.from(item.getElementsByTagName('INPUT')),
					...Array.from(item.getElementsByTagName('TEXTAREA')),
					...Array.from(item.getElementsByTagName('SELECT')),
				] as Array<HTMLInputElement>;
			})
			.forEach(inputs => {
				inputs.forEach(input => {
					if (input.type === 'checkbox' || input.type === 'radio') {
						input.checked = false;
					} else {
						input.value = '';
					}
				})
			})
	}

	private showMessage(formItem: HTMLElement, messages: Array<string>, type: EFormValidatorType): void {
		this.clearClasses(formItem);
		formItem.classList.add(this.getClassNameType(type));
		this.setMessage(formItem, messages);
	}

	private getFormItem(...names): Array<HTMLElement> {
		const fields: Array<HTMLInputElement> = names.map(name => {
			return this.formEl.querySelector(`[name="${name}"]`)
		});
		const items: Array<HTMLElement> = fields.map(field => {
			return field.closest(`.${this.CLASS_ITEM}`)
		})
		return items.filter(Boolean);
	}

	private getAllItems(): Array<HTMLElement> {
		return Array.from(this.formEl.getElementsByClassName(this.CLASS_ITEM)) as Array<HTMLElement>;
	}

	private getClassNameType(type: EFormValidatorType): string {
		switch (type) {
			case EFormValidatorType.VALID:
				return this.CLASS_VALID;
			case EFormValidatorType.INVALID:
				return this.CLASS_INVALID;
			default:
				return '';
		}
	}

	private setMessage(formItem: HTMLElement, messages: Array<string>): void {
		const messageEl: HTMLElement = formItem.querySelector(`.${this.CLASS_MESSAGE}`);
		if (!messageEl) {
			return;
		}
		messageEl.innerHTML = messages.reduce((acc, value) => `${acc}<br/>${value}`);
	}

	private clearClasses(formItem: HTMLElement): void {
		formItem.classList.remove(this.CLASS_INVALID);
		formItem.classList.remove(this.CLASS_VALID);
	}
}
