import {IBtn} from "@modules/btn/model";
import {fromEvent, merge} from "rxjs";
import Btn from "@modules/btn/Btn";
import {EComponentStatuses} from "@models/Status";
import SelectInsuranceComponent from "@components/select-insurance/SelectInsuranceComponent";
import {EInsuranceTypes} from "@models/Insurance";
import {TSelectInsuranceSelectedDevice} from "@components/select-insurance/model";
import Toast from "@modules/toast/Toast";
import {EToastType} from "@modules/toast/model";

export default class UpdateBtnCartOnInsuranceStep {
	private CLASS_SELECT_INSURANCE: string = 'js-cartSelectInsurance';

	private CLASS_BTN: string = 'js-cartSubmitter';

	private selectInsuranceEl: SelectInsuranceComponent;
	private btnEl: HTMLButtonElement;

	private btn: IBtn;

	public init(): void {
		this.selectInsuranceEl = document.querySelector(`.${this.CLASS_SELECT_INSURANCE}`);
		this.btnEl = document.querySelector(`.${this.CLASS_BTN}`);

		if (!this.btnEl || !this.selectInsuranceEl) {
			return;
		}
		this.btn = new Btn(this.btnEl);
		this.attachEvent();
	}

	private attachEvent(): void {
		merge(
			fromEvent(this.selectInsuranceEl, 'on-status-change'),
			fromEvent(this.selectInsuranceEl, 'on-change'),
		)
			.subscribe(() => {
				this.updateBtnAvailable();
			});

		fromEvent(this.btnEl, 'click')
			.subscribe(event => {
				if (this.btn.isFakeDisabled()) {
					event.preventDefault();

					if (this.selectInsuranceEl.status.getValue() === EComponentStatuses.READY && !this.isSelectedProductInDeviceInsurance()) {
						Toast
							.getInstance()
							.add({
								type: EToastType.INFO,
								iconClass: 'o-icon_exclamation-filled',
								text: 'Необходимо выбрать устройства, которые хотите застраховать !',
							});
					}
				}
			});
	}

	private updateBtnAvailable(): void {
		const editProductsStatus = this.selectInsuranceEl.status.getValue();

		if (editProductsStatus === EComponentStatuses.READY && this.isSelectedProductInDeviceInsurance()) {
			this.btn.fakeEnable();
		} else {
			this.btn.fakeDisable();
		}
	}

	private isSelectedProductInDeviceInsurance(): boolean {
		const deviceInsurance = <TSelectInsuranceSelectedDevice>this.selectInsuranceEl.selected.find(item => item.insurance.type === EInsuranceTypes.DEVICE);
		if (!deviceInsurance) {
			return true;
		}
		return Boolean(deviceInsurance?.products?.length);
	}
}
