import {EBtnSize, EBtnType} from "@components/btn/model";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {EProductBasketBundlerOptions} from "@components/product-basket-bundler/model";
import ProductBasketBundlerOptionControls
	from "@components/product-basket-bundler/product-basket-bundler-option-controls/ProductBasketBundlerOptionControls";
import {EProductBasketBundlerOptionStates} from "@components/product-basket-bundler/product-basket-bundler-option-states/model";
import ProductBasketBundlerOptionStates
	from "@components/product-basket-bundler/product-basket-bundler-option-states/ProductBasketBundlerOptionStates";
import ProductBasketBundlerStore
	from "@components/product-basket-bundler/product-basket-bundler-store/ProductBasketBundlerStore";
import HttpRequest from "@modules/http-request/HttpRequest";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {html, state, property, PropertyValues, TemplateResult} from "lit-element";

export default class ProductBundlerOptionComponent extends WebComponentLite {
	@property({
		type: Object,
	})
	public store: ProductBasketBundlerStore;

	@property({
		type: Object,
	})
	public states: ProductBasketBundlerOptionStates;

	@property({
		type: Object,
	})
	public controls: ProductBasketBundlerOptionControls;

	@property({
		type: Boolean,
	})
	public isActive: boolean = false;

	@state()
	protected isLoading: boolean = false;

	@state()
	protected isNetworkError: boolean = false;

	protected httpService: HttpRequest = new HttpRequest();

	public readonly type: EProductBasketBundlerOptions;

	public requestData(): void {
		if (this.isLoading) {
			return;
		}
		this.states.updateState({
			key: this.type,
			state: EProductBasketBundlerOptionStates.LOADING,
		});
		console.log('request data');
		this.isLoading = true;
		setTimeout(() => {
			this.isLoading = false;
			this.states.updateState({
				key: this.type,
				state: EProductBasketBundlerOptionStates.READY,
			});
		}, 1500)
	}

	public resetData(): void {
		console.log('reset data');
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		if (this.isActive) {
			this.requestData();
		}
	}

	protected render(): TemplateResult {
		if (this.isLoading) {
			return html`
				<c-loader active
						  type="${ELoaderType.PRIMARY}"
						  size="${ELoaderSize.MD}"
						  class="c-product-basket-bundler__loader">
				</c-loader>
			`;
		}

		if (this.isNetworkError) {
			return this.getNetworkErrorTemplate();
		}

		return this.getContentTemplate();
	}

	protected getContentTemplate(): TemplateResult {
		return html`
			<div class="u-text-center">
				<h4>Тут будут отображаться данные !</h4>
			</div>
		`;
	}

	protected getNetworkErrorTemplate(): TemplateResult {
		return html`
			<div class="u-text-center">
				<h4>При загрузке данных произошла ошибка !</h4>
				<p class="u-mb-20">Проверьте подключение к интернету</p>
				<c-btn type="${EBtnType.PRIMARY}"
					   size="${EBtnSize.LG}"
					   @click="${this.btnReloadHandler}">
					Повторить
				</c-btn>
			</div>
		`;
	}

	protected btnReloadHandler(event: Event): void {
		event.preventDefault();
		this.reloadData();
	}

	protected reloadData(): void {

	}
}
