export interface ILoader {
	reInit(options: TLoaderOption): void

	show(): void

	hide(): void

	getHTMLElement(): HTMLElement
}

export type TLoaderOption = {
	element: HTMLElement | 'create'
	modifications?: Array<ELoaderModification>
}

export enum ELoaderModification {
	md = 'c-loader_md',
	lg = 'c-loader_lg',
	fixed = 'c-loader_fixed'
}

export enum ELoaderSize {
	SM = 'sm',
	MD = 'md',
	LG = 'lg',
}

export enum ELoaderType {
	PRIMARY = 'primary',
	GHOST = 'ghost',
}

export enum ELoaderMods {

}
