import {interval} from "rxjs";

export default class IntervalReloadPage {
	private CLASS_HANDLER: string = 'js-intervalReloadPage';
	private DATA_INTERVAL: string = 'data-interval';

	private handlerEl: HTMLElement;

	public init(): void {
		this.handlerEl = document.querySelector(`.${this.CLASS_HANDLER}`);
		if (this.handlerEl) {
			this.attachInterval();
		}
	}

	private attachInterval(): void {
		const dataVal: string = this.handlerEl.getAttribute(this.DATA_INTERVAL);
		const ms: number = dataVal ? parseFloat(dataVal) : 1000;

		interval(ms)
			.subscribe(() => {
				location.reload();
			})
	}
}
