import {strToBoolean} from "@helpers/index";
import Toast from "@modules/toast/Toast";
import {TToast} from "@modules/toast/model";

export default class ToastCallingStatic {
	private ID_CONTAINER: string = 'js-toastContainer';
	private DATA_DURATION: string = 'data-duration';
	private DATA_CLOSE_ON_CLICK: string = 'data-close-on-click';

	private containerEl: HTMLElement;

	public init(): void {
		this.attachEvent();
	}

	private showExisting(): void {
		const toastEls: Array<HTMLElement> = Array.from(
			this.containerEl.children
		) as Array<HTMLElement>;
		toastEls.forEach(toastEl => {
			const dataDuration: string = toastEl.getAttribute(this.DATA_DURATION);
			const duration: number = dataDuration ? parseFloat(dataDuration) : null;
			const closeOnClick: boolean = strToBoolean(toastEl.getAttribute(this.DATA_CLOSE_ON_CLICK));
			const option: TToast = {
				text: null,
				element: toastEl,
				btn: {
					text: null,
					closeOnClick: closeOnClick
				}
			}
			if (duration) {
				option.duration = duration;
			}
			Toast
				.getInstance()
				.add(option)
		})
	}

	private attachEvent(): void {
		this.containerEl = document.getElementById(this.ID_CONTAINER);
		if (this.containerEl) {
			this.showExisting();
		}
	}
}
