import ModalComponent from "@components/modal/ModalComponent";
import {EProductBasketBundlerOptions} from "@components/product-basket-bundler/model";
import {EProductBasketBundlerOptionStates} from "@components/product-basket-bundler/product-basket-bundler-option-states/model";
import {
	TProductBundlerOptionInsuranceRequest,
	TProductBundlerOptionInsuranceResponse
} from "@components/product-basket-bundler/product-bundler-option-insurance/model";
import ProductBundlerOptionComponent
	from "@components/product-basket-bundler/product-bundler-option/ProductBundlerOptionComponent";
import {ETariffCardCostItemSize, TTariffCard} from "@components/tariff-card/model";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {TInsurance} from "@models/Insurance";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import HttpRequest from "@modules/http-request/HttpRequest";
import {customElement, html, state, query, TemplateResult} from "lit-element";

@customElement('c-product-bundler-option-insurance')
export default class ProductBundlerOptionInsuranceComponent extends ProductBundlerOptionComponent {
	type = EProductBasketBundlerOptions.INSURANCE;

	private data: TProductBundlerOptionInsuranceResponse;

	@state()
	formattedData: Array<TTariffCard> = [];

	@query('[product-bundler-option-insurance-detail-modal]')
	private modal: ModalComponent;

	@state()
	private selected: Array<TInsurance> = [];

	@query('[toggle-skip]')
	private toggleSkipEl: ToggleCardComponent;

	public requestData(): void {
		this.getData();
	}

	public resetData(): void {
		this.selected = [];
		this.data = [];
		this.formattedData = [];
		this.store.selectedInsurance.next([]);
	}

	protected reloadData(): void {
		this.getData();
	}

	protected getContentTemplate(): TemplateResult {
		return html`
			<h4 class="c-product-bundler-option__section-title">Снизим ваши риски</h4>
			<div class="u-space-items u-space-items-16 u-d-inline-block u-w-100">
				${this.formattedData.map(insurance => {
						const isSelected = Boolean(this.selected.find(item => item.id == insurance.id));
						return html`
							<c-toggle-card marker
										   type="checkbox"
										   name="insurance"
										   .value="${insurance.id}"
										   ?checked="${isSelected}"
										   @on-change="${this.handleChange}">
								<c-tariff-card .data="${insurance}">
							</c-toggle-card>
						`
					}
				)}
				${this.getSkipTemplate()}
			</div>
			<c-modal product-bundler-option-insurance-detail-modal>
		`;
	}

	private getSkipTemplate(): TemplateResult {
		return html`
			<c-toggle-card marker
						   toggle-skip
						   type="checkbox"
						   name="insurance"
						   .value="${null}"
						   @on-change="${this.handleClickSkip}">
				<div class="u-h-100 u-d-flex u-flex-row u-align-items-center">
					<p>Мне не нужно выгодное страхование</p>
				</div>
			</c-toggle-card>
		`;
	}

	private handleClickSkip(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		this.selected = [];
	}

	private handleChange(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		const id: number = parseFloat(toggle.value)
		if (toggle.checked) {
			this.toggleSkipEl.checked = false;
			this.selected = [
				...this.selected,
				this.data.find(item => item.id == id),
			];
		} else {
			this.selected = this.selected.filter(item => item.id !== id);
		}
		this.store.selectedInsurance.next(this.selected);
	}

	private getData(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.isNetworkError = false;
		this.states.updateState({
			key: this.type,
			state: EProductBasketBundlerOptionStates.LOADING,
		});
		const data: TProductBundlerOptionInsuranceRequest = {
			skuId: this.store.skuId,
			period: this.store.selectedContractTariff.getValue()?.period?.value,
			paymentTypeId: this.store.selectedPaymentMethod.getValue()?.id,
			tariffPrice: this.store.selectedContractTariff.getValue()?.tariff?.price,
			accessories: this.store.selectedAccessory.getValue()?.map(item => item.sku)
		};
		this.httpService
			.request<TProductBundlerOptionInsuranceResponse>({
				url: '/products/insurance/',
				method: "GET",
				data: data,
			})
			.then((response) => {
				this.data = response.data.data;
				this.formattedData = this.formatData(this.data);
				this.states.updateState({
					key: this.type,
					state: EProductBasketBundlerOptionStates.READY,
				});
			})
			.catch((reject) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
				this.states.updateState({
					key: this.type,
					state: EProductBasketBundlerOptionStates.ERROR,
				});
			})
			.finally(() => {
				this.isLoading = false;
			})
	}

	private formatData(data: TProductBundlerOptionInsuranceResponse): Array<TTariffCard> {
		return data?.map(item => {
			const pricePerMonth = MoneyFormatter.format(
				item.pricePerMonth,
				EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT
			);
			const payoutPrice = MoneyFormatter.format(
				item.payoutPrice,
				EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
			);
			return {
				id: item.id,
				title: item.name,
				subTitle: item.shortDescription,
				prepend: this.getPrependTemplate(item),
				infoTitle: 'ГАРАНТИЯ ПОЛУЧЕНИЯ КРЕДИТА',
				costs: [
					{
						title: pricePerMonth,
					},
					{
						title: payoutPrice,
						subTitle: 'Страховая выплата',
						size: ETariffCardCostItemSize.SM,
					}
				]
			}
		}) || [];
	}

	private getPrependTemplate(item: TInsurance): TemplateResult {
		if (!item.description) {
			return null;
		}
		return html`
			<button class="c-btn-clear u-color-secondary"
					@click="${this.handleBtnOpenDetail(item)}">
				Подробнее
			</button>
		`;
	}

	private handleBtnOpenDetail(item: TInsurance) {
		return (event) => {
			event.preventDefault();
			this.modal.title = item.name;
			this.modal.setBody(
				`
					<div class="s-wysiwyg">
						${item.description}
					</div>
				`
			)
			this.modal.open();
		}
	}
}
