import {EProductBasketBundlerOptions} from "@components/product-basket-bundler/model";
import {EProductBasketBundlerStoreTypes} from "@components/product-basket-bundler/product-basket-bundler-store/model";
import {TProductBundlerOptionPaymentMethod} from "@components/product-basket-bundler/product-bundler-option-payment-method/model";
import {TProductOptionTariffPhoneInfo} from "@components/product-basket-bundler/tariff-model";
import {TSelectContractTariffSelected} from "@components/select-tariff/model";
import {Metadata} from "@decorators/property/metadata";
import {TAccessory} from "@models/Accessory";
import {TInsurance} from "@models/Insurance";
import 'reflect-metadata';
import {BehaviorSubject, merge, Observable} from "rxjs";

export default class ProductBasketBundlerStore {
	public skuId: string;

	public readonly phoneInfo: BehaviorSubject<TProductOptionTariffPhoneInfo> = new BehaviorSubject<TProductOptionTariffPhoneInfo>(null);

	@Metadata('type', EProductBasketBundlerStoreTypes.SELECTED_OPTION)
	@Metadata('optionType', EProductBasketBundlerOptions.PAYMENT_METHOD)
	@Metadata('required', true)
	public readonly selectedPaymentMethod: BehaviorSubject<TProductBundlerOptionPaymentMethod> = new BehaviorSubject<TProductBundlerOptionPaymentMethod>(null);

	@Metadata('type', EProductBasketBundlerStoreTypes.SELECTED_OPTION)
	@Metadata('optionType', EProductBasketBundlerOptions.CONTRACT_TARIFF)
	@Metadata('required', true)
	public readonly selectedContractTariff: BehaviorSubject<TSelectContractTariffSelected> = new BehaviorSubject<TSelectContractTariffSelected>(null);

	@Metadata('type', EProductBasketBundlerStoreTypes.SELECTED_OPTION)
	@Metadata('optionType', EProductBasketBundlerOptions.INSURANCE)
	@Metadata('required', false)
	public readonly selectedInsurance: BehaviorSubject<Array<TInsurance>> = new BehaviorSubject<Array<TInsurance>>([]);

	@Metadata('type', EProductBasketBundlerStoreTypes.SELECTED_OPTION)
	@Metadata('optionType', EProductBasketBundlerOptions.ACCESSORY)
	@Metadata('required', false)
	public readonly selectedAccessory: BehaviorSubject<Array<TAccessory>> = new BehaviorSubject<Array<TAccessory>>([]);

	public getStreamChangeByType(type: EProductBasketBundlerStoreTypes): Observable<unknown> {
		const propertyKeys: Array<string | symbol> = this.getProperties('type', type);
		return merge(
			...propertyKeys.map(key => this[key])
		);
	}

	public getSelectedValueByType(option: EProductBasketBundlerOptions): unknown {
		const propertyKey = this.getPropertyKeyByTypeAndOption(EProductBasketBundlerStoreTypes.SELECTED_OPTION, option);
		if (!propertyKey) {
			throw new Error(`selected by type ${option} not found`);
		}
		const selected: BehaviorSubject<unknown> = this[propertyKey];
		if (!(selected instanceof BehaviorSubject)) {
			throw new Error(`selected by type ${option} not BehaviorSubject`);
		}
		return selected.getValue();
	}

	public clearSelectedValueByType(option: EProductBasketBundlerOptions): void {
		const propertyKey = this.getPropertyKeyByTypeAndOption(EProductBasketBundlerStoreTypes.SELECTED_OPTION, option);
		if (!propertyKey) {
			throw new Error(`selected by type ${option} not found`);
		}
		const selected: BehaviorSubject<unknown> = this[propertyKey];
		if (!(selected instanceof BehaviorSubject)) {
			throw new Error(`selected by type ${option} not BehaviorSubject`);
		}
		selected.next(null);
	}

	public selectedIsRequired(option: EProductBasketBundlerOptions): boolean {
		const propertyKey = this.getPropertyKeyByTypeAndOption(EProductBasketBundlerStoreTypes.SELECTED_OPTION, option);
		if (!propertyKey) {
			throw new Error(`selected by type ${option} not found`);
		}
		return Boolean(Reflect.getMetadata('required', this, propertyKey));
	}

	private getPropertyKeyByTypeAndOption(type: EProductBasketBundlerStoreTypes, option: EProductBasketBundlerOptions): string | symbol {
		return this.getProperties('type', type)
			.find(propertyKey => {
				return Reflect.getMetadata('optionType', this, propertyKey) === option
			});
	}

	private getProperties(key: string, value: string): Array<string | symbol> {
		return (Reflect.getMetadata(key, this) || [])
			.filter(propertyKey => {
				return Reflect.getMetadata(key, this, propertyKey) === value
			});
	}
}
