import {getDefinitionElement, isDefinitionElementEvent} from "@helpers/index";
import SmoothScroll from "@modules/scroll/SmoothScroll";
import {fromEvent} from "rxjs";
import {filter, map} from "rxjs/operators";

export default class SmoothScrollAutoActivation extends SmoothScroll {
	private CLASS_HANDLER: string = 'js-smoothScrollAuto';
	private DATA_ATTR_ELEMENT_ID: string = 'data-smooth-scroll-id';

	constructor() {
		super({
			behavior: "smooth",
		});
	}

	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		const selectorHandler: string = `.${this.CLASS_HANDLER}`;
		fromEvent(document, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, selectorHandler)),
				map(event => getDefinitionElement(event.target as HTMLElement, selectorHandler)),
			)
			.subscribe(handler => {
				this.scrollBy(handler);
			});
	}

	private scrollBy(mover: HTMLElement): void {
		const itemId: string = mover.getAttribute(this.DATA_ATTR_ELEMENT_ID);

		if (itemId) {
			const anchor: HTMLElement = document.getElementById(itemId);

			if (anchor) {
				this.scrollTo(anchor)
			}
		}
	}
}
