import {TOperator} from "@models/Operator";

export type TTariffTree = {
	operator?: TOperator
	periods: Array<TTariffPeriod>
	reasonAbsenceTariffs?: string
	tariffConnection?: TTariffConnection
}

export type TTariffPeriod = {
	value: number
	pricePerMonth: string
	tariffs: Array<TTariff>
}

export type TTariff = {
	id: number
	sku: string
	uuid: string
	name: string
	period: number
	megabyte: number
	megabyteDesc?: string
	minutes: number
	minutesDesc?: string
	pricePerMonth: string
	price: string
	priceDesc?: string
	unlimitedDesc?: string
	
}

export enum ETariffConnectionStatuses {
	APPROVED = 'APPROVED',
	DENIED = 'DENIED',
}

export type TTariffConnection = {
	status: ETariffConnectionStatuses
	message: string
}

export enum EPriceRequestTypes {
	TARIFF = 'TARIFF',
	INSURANCE = 'INSURANCE',
}

export type TTariffPriceRequest = {
	price: string,
	type: EPriceRequestTypes
}

export type TPricesResponse = {
	pricePerMonth: string
	price: string
}
