export interface IAnimation {
	show(): void

	hide(): void

	insert(target: HTMLElement, before?: Node): void

	remove(): void
}

export type TAnimationOption = {
	name?: string
	enterName?: string
	leaveName?: string
	type?: EAnimationType
	duration?: number
	enterClassNames?: TAnimationClasses
	leaveClassNames?: TAnimationClasses
	beforeEnter?: (el: HTMLElement) => void
	afterEnter?: (el: HTMLElement) => void
	beforeLeave?: (el: HTMLElement) => void
	afterLeave?: (el: HTMLElement) => void
	systemDoneCallback?: (el: HTMLElement) => void
	displayProperty?: string
}

export type TAnimationClasses = {
	enter: string
	enterActive: string
	enterTo: string
	leave: string
	leaveActive: string
	leaveTo: string
}

export enum EAnimationType {
	TRANSITION = 'transition',
	ANIMATION = 'animation',
	NONE = 'none'
}
