import {ETermCardSizes, TTermCard} from "@components/term-card/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";

@customElement('c-term-card')
export default class TermCardComponent extends WebComponentLite {
	@property({
		type: ETermCardSizes,
	})
	public size: ETermCardSizes;

	@property({
		type: Object
	})
	data: TTermCard;

	protected getMods(): string {
		const mods: Array<string> = [this.size || ''];
		return mods
			.filter(Boolean)
			.map(mod => `c-term-card_${mod}`)
			.join(' ');
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-term-card ${this.getMods()}">
				<h4 class="c-term-card__title">${this.data?.value}</h4>
				<p class="c-term-card__sub-title">${this.data?.title}</p>
			</div>
		`;
	}
}
