import {EBtnSize, EBtnType} from "@components/btn/model";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {EPhoneValidationForContractTariffStatuses} from "@components/phone-validation-for-contract-tariff/model";
import PhoneValidationForContractTariffComponent
	from "@components/phone-validation-for-contract-tariff/PhoneValidationForContractTariffComponent";
import {
	EProductOptionTariffStages,
	TProductOptionTariffAddToCardData,
	TProductOptionTariffMinimalContractTariffResponse,
	TProductOptionTariffPhoneInfo
} from "@components/product-basket-bundler/tariff-model";
import ProductCostLineComponent from "@components/product-cost-line/ProductCostLineComponent";
import {TSelectContractTariffSelected} from "@components/select-tariff/model";
import {TTariffCard} from "@components/tariff-card/model";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import {TariffFormatter} from "@modules/formatter/TariffFormatter";
import HttpRequest from "@modules/http-request/HttpRequest";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosError} from "axios";
import {customElement, html, state, property, PropertyValues, query, TemplateResult} from "lit-element";
import {fromEvent} from "rxjs";

@customElement('c-product-option-tariff')
export default class ProductOptionTariffComponent extends WebComponentLite {
	@property({
		type: String,
		attribute: 'sku-id',
	})
	public skuId: string;

	@property({
		type: String,
		attribute: 'confirmed-link',
	})
	public confirmedLink: string = '';

	@state()
	private advantageousContractCard: TTariffCard;

	@state()
	protected error: unknown;

	@state()
	private isLoading: boolean = false;

	@state()
	private isNetworkError: boolean = false;

	@state()
	private isAddToCartRequesting: boolean = false;

	@state()
	private stage: EProductOptionTariffStages;

	@state()
	private phoneInfo: TProductOptionTariffPhoneInfo;

	@query('[phone-validation-for-contract-tariff]')
	private phoneValidationForContractTariff: PhoneValidationForContractTariffComponent;

	private productCostLine: ProductCostLineComponent;

	private advantageousContractTariff: TProductOptionTariffMinimalContractTariffResponse;

	private selectedTariff: TSelectContractTariffSelected;

	private httpService: HttpRequest = new HttpRequest();

	constructor() {
		super();
		this.productCostLine = document.querySelector(`c-product-cost-line`);
		this.attachEvent();
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.getAdvantageousContract();
	}

	private getAdvantageousContract(): void {
		if (this.isLoading) {
			return;
		}
		this.isNetworkError = false;
		this.isLoading = true;
		const formData: FormData = new FormData();
		formData.append('skuId', this.skuId);
		this.httpService
			.request<TProductOptionTariffMinimalContractTariffResponse>({
				url: '/products/minimal_contract_tariff/',
				method: "GET",
				data: formData
			})
			.then((response) => {
				const data = response.data.data;
				this.advantageousContractTariff = data;
				this.advantageousContractCard = this.advantageousContractToTariffCard(data);
				this.updatePricePerMonthInProductCostLine(this.advantageousContractTariff.pricePerMonth);
			})
			.catch((reject: AxiosError) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
			})
			.finally(() => {
				this.isLoading = false;
			})
	}

	protected render(): unknown {
		if (this.isLoading) {
			return html`
				<c-loader active
						  type="${ELoaderType.PRIMARY}"
						  size="${ELoaderSize.MD}"
						  class="c-product-basket-bundler__loader">
				</c-loader>
			`;
		}
		if (this.isNetworkError) {
			return this.getNetworkErrorTemplate();
		}
		if (!this.advantageousContractTariff) {
			return this.getNotDataTemplate();
		}
		return this.getContentTemplate();
	}

	private getContentTemplate(): TemplateResult {
		return html`
			${this.getAdvantageousContractTemplate()}
			${!this.stage
				? this.getAdvantageousContractDetailTemplate()
				: null
			}
			${this.stage === EProductOptionTariffStages.SELECT_TARIFF
				? html`<h4 class="c-product-option-tariff__section-title"></h4>`
				: null
			}
			${this.getValidatePhoneTemplate()}
			${this.getResultByStageTemplate(this.stage)}
		`;
	}

	private getNotDataTemplate(): TemplateResult {
		return html`
			<div class="u-text-center">
				<h4>Данных нет !</h4>
			</div>
		`;
	}

	private getNetworkErrorTemplate(): TemplateResult {
		return html`
			<c-network-error @on-click-reload="${this.handleBtnClickReload}">
		`;
	}

	private getAdvantageousContractTemplate(): TemplateResult {
		return html`
			<c-tariff-card class="c-product-option-tariff__advantageous-contract u-min-w-100"
						   .data="${this.advantageousContractCard}">
		`;
	}

	private getAdvantageousContractDetailTemplate(): TemplateResult {
		let data: TTariffCard;
		if (!this.advantageousContractTariff.id) {
			return html`
				<div class="u-mb-10 u-mb-md-30"></div>
			`;
		}
		data = TariffFormatter.toTariffCard(this.advantageousContractTariff);
		return html`
			<div class="c-product-option-tariff__advantageous-contract-detail">
				<c-tariff-card class="u-min-w-100"
							   .data="${data}">
			</div>
		`;
	}

	private getResultByStageTemplate(stage: EProductOptionTariffStages): TemplateResult {
		switch (stage) {
			case EProductOptionTariffStages.SELECT_TARIFF:
				return this.getSelectTariffTemplate();
			case EProductOptionTariffStages.WITHOUT_TARIFF:
				return this.getWithoutTariffTemplate();
			default:
				return null;
		}
	}

	private getValidatePhoneTemplate(): TemplateResult {
		return html`
			<c-phone-validation-for-contract-tariff phone-validation-for-contract-tariff
													.confirmedLink="${this.confirmedLink}"
													@on-click-skip="${this.handleBtnClickSkipTariff}"
													@on-status-change="${this.handlePhoneValidationForContractTariffStatusChange}">
				<form slot="form-validate-sms" action="/products/verify_otp/" method="POST">
					<input type="text" name="skuId" .value="${this.skuId}">
				</form>
				<form slot="form-get-sms" action="/products/send_otp/" method="POST">
					<input type="text" name="skuId" .value="${this.skuId}">
				</form>
			</c-phone-validation-for-contract-tariff>
		`;
	}

	private getSelectTariffTemplate(): TemplateResult {
		return html`
			<div class="c-product-option-tariff__tariff">
				<c-select-tariff request-on-creation="true"
								 get-request-config="${{
									 url: '/products/contract_tariffs/',
									 method: "GET",
								 }}"
								 add-data-get="${{
									 skuId: this.skuId,
									 operatorName: this.phoneInfo.operator.name,
									 phoneNumber: this.phoneInfo.number,
									 hasTariffs: true,
								 }}"
								 @on-change="${this.handleChangeTariff}">
				</c-select-tariff>
			</div>
		`;
	}

	private getWithoutTariffTemplate(): TemplateResult {
		return html`
			<div class="c-product-option-tariff__period">
				<c-select-tariff request-on-creation="true"
								 get-request-config="${{
									 url: '/products/contract_tariffs/',
									 method: "GET",
								 }}"
								 add-data-get="${{
									 skuId: this.skuId,
									 operatorName: this.phoneInfo.operator.name,
									 phoneNumber: this.phoneInfo.number,
									 hasTariffs: false,
								 }}"
								 @on-change="${this.handleChangeTariff}">
				</c-select-tariff>
			</div>

			<c-btn type="${EBtnType.SECONDARY}"
				   size="${EBtnSize.MD}"
				   @click="${this.handleBtnAddTariff}">
				Добавить тариф
			</c-btn>
		`;
	}

	private advantageousContractToTariffCard(data: TProductOptionTariffMinimalContractTariffResponse): TTariffCard {
		const pricePerMonth = MoneyFormatter.format(
			data.pricePerMonth,
			EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT
		);
		return {
			id: null,
			title: 'Условия Контракта',
			subTitle: null,
			infoTitle: null,
			costs: [
				{
					prepend: 'от',
					title: `${pricePerMonth}`,
					subTitle: `На ${data.period} месяцев`,
				},
				{
					title: `0<sup>%</sup>`,
					subTitle: `Переплата`,
				}
			]
		}
	}

	private updatePricePerMonthInProductCostLine(price: string): void {
		if (!this.productCostLine) {
			return;
		}
		this.productCostLine.pricePerMonth = price;
	}

	private handleBtnClickReload(event: Event): void {
		event.preventDefault();
		this.getAdvantageousContract();
	}

	private changeStage(stage: EProductOptionTariffStages) {
		this.stage = stage;
	}

	private handleBtnClickSkipTariff(event: Event): void {
		this.changeStage(EProductOptionTariffStages.WITHOUT_TARIFF);
	}

	private handleBtnAddTariff(event: Event): void {
		event.preventDefault();
		this.phoneValidationForContractTariff.resetStage();
		this.changeStage(null);
	}

	private handlePhoneValidationForContractTariffStatusChange(event: CustomEvent): void {
		const status: EPhoneValidationForContractTariffStatuses = event.detail.status;
		const component: PhoneValidationForContractTariffComponent = (event.target as PhoneValidationForContractTariffComponent);
		this.phoneInfo = {
			operator: component.getOperator(),
			number: component.getPhoneNumber(),
		};
		switch (status) {
			case EPhoneValidationForContractTariffStatuses.APPROVED_ABILITY_TO_CONNECT_TARIFF:
				this.changeStage(EProductOptionTariffStages.SELECT_TARIFF);
				break;
		}
	}

	private handleChangeTariff(event: CustomEvent): void {
		const selected: TSelectContractTariffSelected = event.detail;
		this.selectedTariff = selected;
		this.updateLoanInfo(selected);
	}

	private updateLoanInfo(selected: TSelectContractTariffSelected): void {
		let pricePerMonth: string;
		if (selected.isSkip) {
			pricePerMonth = selected.period.pricePerMonth;
		} else {
			pricePerMonth = selected.tariff.pricePerMonth;
		}
		this.advantageousContractCard = this.advantageousContractToTariffCard({
			...this.advantageousContractTariff,
			pricePerMonth: pricePerMonth,
			period: selected.period.value,
		});
		this.productCostLine.isDisableBtn = false;
		this.updatePricePerMonthInProductCostLine(pricePerMonth);
	}

	private attachEvent(): void {
		fromEvent(this.productCostLine, 'on-btn-click')
			.subscribe(() => {
				this.addToCart();
			});
	}

	private addToCart(): void {
		if (this.isAddToCartRequesting) {
			return;
		}
		this.productCostLine.isLoadingBtn = true;
		this.productCostLine.isDisableBtn = true;
		this.isAddToCartRequesting = true;
		const data: TProductOptionTariffAddToCardData = {
			skuId: this.skuId,
			period: this.selectedTariff.period.value,
			tariff: this.selectedTariff.tariff || null,
			phoneNumber: this.phoneInfo.number || null,
			operatorName: this.phoneInfo.operator.name || null,
		};

		this.httpService
			.request({
				url: '/products/add_to_basket/',
				method: 'POST',
				data: data,
			})
			.then((response) => {
				//TODO: handle response
			})
			.catch((reject) => {
				//TODO: handle error
			})
			.finally(() => {
				this.isAddToCartRequesting = false;
				this.productCostLine.isLoadingBtn = false;
				this.productCostLine.isDisableBtn = false;
			})
	}
}

