import {EBtnSize, EBtnType} from "@components/btn/model";
import {EPhoneValidationForContractTariffStatuses} from "@components/phone-validation-for-contract-tariff/model";
import PhoneValidationForContractTariffComponent
	from "@components/phone-validation-for-contract-tariff/PhoneValidationForContractTariffComponent";
import {EProductBasketBundlerOptions} from "@components/product-basket-bundler/model";
import {EProductBasketBundlerOptionControlSignals} from "@components/product-basket-bundler/product-basket-bundler-option-controls/model";
import {EProductBasketBundlerOptionStates} from "@components/product-basket-bundler/product-basket-bundler-option-states/model";
import {
	EProductBundlerOptionContractTariffPhoneInfo,
	EProductBundlerOptionContractTariffStages
} from "@components/product-basket-bundler/product-bundler-option-contract-tariff/model";
import {EProductBundlerOptionPaymentMethodTypes} from "@components/product-basket-bundler/product-bundler-option-payment-method/model";
import ProductBundlerOptionComponent
	from "@components/product-basket-bundler/product-bundler-option/ProductBundlerOptionComponent";
import {TSelectContractTariffSelected} from "@components/select-tariff/model";
import {customElement, html, property, PropertyValues, query, state, TemplateResult} from "lit-element";

@customElement('c-product-bundler-option-contract-tariff')
export default class ProductBundlerOptionContractTariffComponent extends ProductBundlerOptionComponent {
	@property({
		type: String,
	})
	public confirmedLink: string;

	@query('[phone-validation-for-contract-tariff]')
	private phoneValidationForContractTariff: PhoneValidationForContractTariffComponent;

	@state()
	private stage: EProductBundlerOptionContractTariffStages;

	@state()
	private phoneInfo: EProductBundlerOptionContractTariffPhoneInfo;

	@state()
	private sectionTitle: string = '';

	type = EProductBasketBundlerOptions.CONTRACT_TARIFF;

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
	}

	public requestData(): void {
		this.stage = null;
		this.states.updateState({
			key: this.type,
			state: EProductBasketBundlerOptionStates.WAITING,
		});
	}

	public resetData(): void {
		this.store.selectedContractTariff.next(null);
		this.changeStage(null);
		this.phoneValidationForContractTariff.resetStage();
		this.store.phoneInfo.next(null);
	}

	protected getContentTemplate(): TemplateResult {
		return html`
			<h4 class="c-product-bundler-option__section-title">${this.sectionTitle}</h4>

			${this.getValidatePhoneTemplate()}
			${this.getResultByStageTemplate(this.stage)}
		`;
	}

	private getValidatePhoneTemplate(): TemplateResult {
		return html`
			<c-phone-validation-for-contract-tariff phone-validation-for-contract-tariff
													.confirmedLink="${this.confirmedLink}"
													@on-click-skip="${this.handleBtnClickSkipTariff}"
													@on-status-change="${this.handlePhoneValidationForContractTariffStatusChange}">
				<form slot="form-validate-sms" action="/products/verify_otp/" method="POST">
					<input type="text" name="skuId" .value="${this.store.skuId}">
				</form>
				<form slot="form-get-sms" action="/products/send_otp/" method="POST">
					<input type="text" name="skuId" .value="${this.store.skuId}">
				</form>
			</c-phone-validation-for-contract-tariff>
		`;
	}

	private handlePhoneValidationForContractTariffStatusChange(event: CustomEvent): void {
		const status: EPhoneValidationForContractTariffStatuses = event.detail.status;
		const component: PhoneValidationForContractTariffComponent = (event.target as PhoneValidationForContractTariffComponent);
		this.phoneInfo = {
			operator: component.getOperator(),
			number: component.getPhoneNumber(),
		};
		this.store.phoneInfo.next(this.phoneInfo);
		switch (status) {
			case EPhoneValidationForContractTariffStatuses.APPROVED_ABILITY_TO_CONNECT_TARIFF:
				this.changeStage(EProductBundlerOptionContractTariffStages.SELECT_TARIFF);
				break;
		}
	}

	private getResultByStageTemplate(stage: EProductBundlerOptionContractTariffStages): TemplateResult {
		switch (stage) {
			case EProductBundlerOptionContractTariffStages.SELECT_TARIFF:
				return this.getSelectTariffTemplate(true);
			case EProductBundlerOptionContractTariffStages.WITHOUT_TARIFF:
				return this.getSelectTariffTemplate(false);
			default:
				return null;
		}
	}

	private getSelectTariffTemplate(hasTariffs: boolean): TemplateResult {
		return html`
			<div class="${'c-product-bundler-option-contract-tariff_' + (hasTariffs ? 'tariff' : 'period')}">
				<c-select-tariff request-on-creation="true"
								 get-request-config="${{
									 url: '/products/contract_tariffs/',
									 method: "GET",
								 }}"
								 add-data-get="${{
									 skuId: this.store.skuId,
									 operatorName: this.phoneInfo.operator.name,
									 phoneNumber: this.phoneInfo.number,
									 hasTariffs: hasTariffs,
									 paymentMethodId: this.store.selectedPaymentMethod.getValue()?.id,
								 }}"
								 @on-change="${this.handleChangeTariff}">
				</c-select-tariff>
			</div>
			${!hasTariffs ? this.getAddTariffTemplate() : null}
		`;
	}

	private getAddTariffTemplate(): TemplateResult {
		return html`
			<c-btn type="${EBtnType.SECONDARY}"
				   size="${EBtnSize.MD}"
				   @click="${this.handleBtnAddTariff}">
				Добавить тариф
			</c-btn>
		`;
	}

	private updateTitle(contractTariff?: TSelectContractTariffSelected): void {
		if (contractTariff && !contractTariff.tariff && !contractTariff.isSkip && contractTariff.period) {
			this.sectionTitle = 'Рассрочка';
			return;
		}
		this.sectionTitle = '';
	}

	private handleChangeTariff(event: CustomEvent): void {
		const selected: TSelectContractTariffSelected = event.detail;
		this.store.selectedContractTariff.next(selected);
	}

	private handleBtnClickSkipTariff(event: Event): void {
		if (this.store.selectedPaymentMethod.getValue().type === EProductBundlerOptionPaymentMethodTypes.FULL_PRICE) {
			this.resetData();
			this.store.phoneInfo.next(this.phoneInfo);
			this.store.selectedContractTariff.next({
				tariff: null,
				isSkip: true,
				period: null,
			});
			this.controls.emitSignal(EProductBasketBundlerOptionControlSignals.NEXT);
		} else {
			this.changeStage(EProductBundlerOptionContractTariffStages.WITHOUT_TARIFF);
		}
	}

	private handleBtnAddTariff(event: Event): void {
		event.preventDefault();
		this.resetData();
	}

	private changeStage(stage: EProductBundlerOptionContractTariffStages) {
		this.stage = stage;
		if (this.stage) {
			this.states.updateState({
				key: this.type,
				state: EProductBasketBundlerOptionStates.READY,
			});
		} else {
			this.states.updateState({
				key: this.type,
				state: EProductBasketBundlerOptionStates.WAITING,
			});
		}
	}

	private attachEvent(): void {
		this.store.selectedContractTariff
			.subscribe(contractTariff => {
				this.updateTitle(contractTariff);
			});
	}
}
