import {IDynamicActivationSubjectFactory, TDynamicActivationOption} from "@modules/dynamic-element/model";
import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import {isHTMLElement, strToBoolean} from "@helpers/index";

export default class DynamicActivation extends DynamicInsertionElements {
	private readonly option: TDynamicActivationOption;
	private readonly dataAttrActivated: string = '';
	private readonly subjectFactory: IDynamicActivationSubjectFactory;

	constructor(subjectFactory: IDynamicActivationSubjectFactory, option: TDynamicActivationOption) {
		super();
		this.option = option;
		this.dataAttrActivated = this.getDataAttrActivated();
		this.subjectFactory = subjectFactory;
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		Array
			.from(scope.querySelectorAll(this.option.selector))
			.forEach(handler => {
				if (strToBoolean(handler.getAttribute(this.dataAttrActivated))) {
					return
				}
				handler.setAttribute(this.dataAttrActivated, 'true');
				this.subjectFactory.create(handler as HTMLElement);
			});
	}

	private getDataAttrActivated(): string {
		return `data-${this.option.key}-activated`;
	}
}
