import {TOperator} from "@models/Operator";
import {TTariffConnection} from "@models/Tariff";

export enum EValidateSMSCodeStatuses {
	VALIDATION_SUCCESSFUL = 'VALIDATION_SUCCESSFUL',
	VALIDATION_ERROR = 'VALIDATION_ERROR',
	OUT_SMS_REQUESTS = 'OUT_SMS_REQUESTS',
	OUT_VALIDATION_REQUESTS = 'OUT_VALIDATION_REQUESTS',
	ERROR = 'ERROR',
}

export type TValidateSMSCodeBootstrapData = {
	timer?: number
	attemptsCountSend?: number
	attemptsCountValidate?: number
}

export type TValidateSMSCodeResponse = {
	operator: TOperator
	tariffConnection: TTariffConnection
}

export type TValidateSMSCodeEventDetail = {
	status: EValidateSMSCodeStatuses,
	operator: TOperator
	tariffConnection: TTariffConnection
}

export enum EValidateSMSCodeSlots {
	FORM_VALIDATE = 'form-validate',
	FORM_GET = 'form-get',
}
