import {TAnimationOption} from "@modules/animation/model";

export interface IDropdown {
	open(): void

	close(): void

	toggle(): void

	getState(): EDropdownState
}

export type TDropdownOption = {
	classes?: TDropdownClasses
	stateClasses?: TDropdownStateClasses
	animation?: TAnimationOption
	hooks?: TDropdownHooks
	closeAnyClick?: boolean
}

export type TDropdownClasses = {
	block?: string
	handler?: string
	container?: string
	item?: string
}

export type TDropdownStateClasses = {
	open?: string
	opens?: string
	close?: string
	closes?: string
}

export type TDropdownHooks = {
	onItemClick?: (item: HTMLElement) => void
	afterClose?: () => void
	afterOpen?: () => void
}

export enum EDropdownState {
	OPEN = 'OPEN',
	CLOSED = 'CLOSED',
}
