import {IFilterList, TFilterListOptions} from "@modules/filter-list/model";
import {IVisibility} from "@modules/visibility/model";
import Visibility from "@modules/visibility/Visibility";
import {fromEvent, merge} from "rxjs";
import {debounceTime, distinctUntilChanged, pluck} from "rxjs/operators";

export default class FilterList implements IFilterList {
	private visibility: IVisibility = new Visibility();

	private listEl: HTMLElement;
	private inputEl: HTMLInputElement;
	private itemEls: Array<HTMLElement>;

	constructor(private options: TFilterListOptions) {
		const defaultOptions: Partial<TFilterListOptions> = this.getDefaultOptions();
		this.options = {
			...defaultOptions,
			...this.options,
		};
		this.init();
	}

	public showAllItems(): void {
		this.visibility.show(this.itemEls);
	}

	private init(): void {
		this.initElements();
		this.attachEvent();
	}

	private initElements(): void {
		this.listEl = this.options.list;
		if (!this.listEl) {
			throw new Error('FilterList: list is not defined !');
		}
		this.inputEl = this.options.input;
		if (!this.inputEl) {
			throw new Error('FilterList: input is not defined !');
		}
		this.itemEls = Array.from(
			this.listEl.querySelectorAll(`[${this.options.dataAttributes.key}].${this.options.classes.item}`)
		)
	}

	private attachEvent(): void {
		merge(
			fromEvent(this.inputEl, 'input'),
			fromEvent(this.inputEl, 'keyup'),
		)
			.pipe(
				pluck('target', 'value'),
				debounceTime(250),
				distinctUntilChanged()
			)
			.subscribe(value => {
				const searchValue: string = value.toString().toUpperCase().trim();
				if (!searchValue.trim()) {
					return this.showAllItems();
				}
				const items = {
					coinciding: [],
					mismatched: [],
				};
				this.itemEls
					.forEach(item => {
						const key: string = item.getAttribute(this.options.dataAttributes.key);
						if (key.trim().toUpperCase().indexOf(searchValue) === -1) {
							items.mismatched.push(item);
						} else {
							items.coinciding.push(item);
						}
					});
				this.visibility.hide(items.mismatched);
				this.visibility.show(items.coinciding);
			})
	}

	private getDefaultOptions(): Partial<TFilterListOptions> {
		return {
			classes: {
				list: 'js-filterList',
				item: 'js-filterListItem',
			},
			dataAttributes: {
				key: 'data-filter-list-key',
			}
		}
	}

}
