import {TSchedule} from "@components/schedule/model";
import ScheduleComponent from "@components/schedule/ScheduleComponent";
import {fromEvent} from "rxjs";

export default class UpdateScheduleFromSelect {
	private CLASS_HANDLER: string = 'js-updateScheduleFromSelectHandler';
	private DATA_ATTR_ID_SCHEDULE: string = 'data-id-schedule';
	private DATA_ATTR_OPTION_SCHEDULE: string = 'data-schedule';

	private selectEl: HTMLSelectElement;
	private scheduleEl: ScheduleComponent;

	public init(): void {
		this.selectEl = document.querySelector(`.${this.CLASS_HANDLER}`);
		this.scheduleEl = document.getElementById(this.selectEl?.getAttribute(this.DATA_ATTR_ID_SCHEDULE)) as ScheduleComponent;

		if (!this.selectEl || !this.scheduleEl) {
			return;
		}
		this.attachEvent();
		this.setSchedule();
	}

	private setSchedule(): void {
		const selectedOption = Array.from(this.selectEl.selectedOptions)?.[0];
		if (!selectedOption) {
			return;
		}
		const schedule: TSchedule = JSON.parse(selectedOption.getAttribute(this.DATA_ATTR_OPTION_SCHEDULE));
		this.scheduleEl
			.updateComplete
			.then(() => {
				this.scheduleEl.data = schedule;
			});
	}

	private attachEvent(): void {
		fromEvent(this.selectEl, 'change')
			.subscribe(() => {
				this.setSchedule();
			})
	}
}
