export function Metadata(key: string | symbol, value: unknown): PropertyDecorator {
	return function (target: Object, propertyKey: string | symbol) {
		Reflect.defineMetadata(key, value, target, propertyKey);

		let propertyKeys = Reflect.getOwnMetadata(key, target) || (Reflect.getMetadata(key, target) || []).slice(0);
		Reflect.defineMetadata(key, propertyKeys, target);
		propertyKeys.push(propertyKey);
	};
}

export function getMetadataPropertyKeys(key: string, target: Object): Array<string | symbol> {
	return Reflect.getMetadata(key, target) || [];
}

export function getPropertyMetadataKeys(target: Object, propertyKey: string): Array<string | symbol> {
	return Reflect.getMetadataKeys(target, propertyKey) || [];
}
