import {isHTMLElement} from "@helpers/index";
import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import {IToggleChange, TToggleChangeOptions, TToggleChangeStreamOnChange} from "@modules/toggle/model";
import {BehaviorSubject, fromEvent, Observable} from "rxjs";
import {filter} from "rxjs/operators";

export default class ToggleChange extends DynamicInsertionElements implements IToggleChange {
	private DATA_ATTR_ACTIVATED: string = 'data-toggle-change-activated';
	private DATA_ATTR_ACTIVATED_HANDLERS: string = 'data-toggle-change-activated-handlers';

	private eventChange: BehaviorSubject<TToggleChangeStreamOnChange> = new BehaviorSubject<TToggleChangeStreamOnChange>(null);

	constructor(protected options?: TToggleChangeOptions) {
		super();
		if (!options) {
			this.options = {
				classHandler: 'js-toggleChangeHandler',
				hasDynamicActivated: true
			}
		}
	}

	public init(): void {
		const elements: Array<HTMLInputElement> = this.getToggleElementByClassName(this.options.classHandler);
		this.attachEventOnChange(elements);
		this.initFirstChange(elements);
		if (this.options.hasDynamicActivated) {
			this.attachEventDynamicInsertion();
		}
	}

	public getToggleElementByClassName(className: string): Array<HTMLInputElement> {
		return Array.from(document.getElementsByClassName(className)) as Array<HTMLInputElement>;
	}

	public getToggleElementsByName(name: string): Array<HTMLInputElement> {
		return Array.from(document.querySelectorAll(
			`input[type='radio'][name='${name}'], input[type='checkbox'][name='${name}']`
		));
	}

	public getStreamOnChange(): Observable<TToggleChangeStreamOnChange> {
		return this.eventChange
			.asObservable()
			.pipe(
				filter(stream => {
					return Boolean(stream);
				})
			)
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			const elements: Array<HTMLInputElement> = this.getToggleElementByClassName(this.options.classHandler);
			this.attachEventOnChange(elements);
		}
	}

	private attachEventOnChange(elements: Array<HTMLInputElement>): ToggleChange {
		elements
			.filter(toggle => {
				return !this.isActivated(toggle)
			})
			.forEach(toggle => {
				this.addActivatedHandlers(toggle, this.options.classHandler);

				this.attachEventOnUnconnectedToggles(toggle.name);
				fromEvent(toggle, 'change')
					.subscribe(event => {
						if (toggle.type === 'radio') {
							this.radioHandler(toggle);
						}

						this.eventChange.next({
							event: event,
							input: event.target as HTMLInputElement,
							state: (event.target as HTMLInputElement).checked
						});
					})
			});
		return this;
	}

	private attachEventOnUnconnectedToggles(name: string): void {
		const toggle: HTMLInputElement = document.querySelector(`input.${this.options.classHandler}`);
		if (toggle) {
			const unconnectedToggles: Array<HTMLInputElement> = Array.from(document.querySelectorAll(
				`input[name='${name}']:not(.${this.options.classHandler})`
			));
			this.attachEventOnChange(unconnectedToggles);
		}
	}

	private initFirstChange(elements: Array<HTMLInputElement>): void {
		elements
			.sort((a, b) => Number(a.checked) - Number(b.checked))
			.forEach(toggle => {
				this.eventChange.next({
					event: null,
					input: toggle,
					state: toggle.checked
				});
			})
	}

	private radioHandler(radio: HTMLInputElement): void {
		const name: string = radio.getAttribute('name');
		const unactiveConnectedElements: Array<HTMLInputElement> = this.getToggleElementsByName(name).filter(toggle => !toggle.checked);
		unactiveConnectedElements.forEach(toggle => {
			this.eventChange.next({
				event: null,
				input: toggle,
				state: toggle.checked
			});
		})
	}

	private isActivated(handler: HTMLInputElement): boolean {
		const dataAttrActive: string = handler.getAttribute(this.DATA_ATTR_ACTIVATED);
		const activatedHandlers: Array<string> = this.getActivatedHandlers(handler);
		if (!dataAttrActive) {
			handler.setAttribute(this.DATA_ATTR_ACTIVATED, 'true');
		}
		return activatedHandlers.includes(this.options.classHandler);
	}

	private getActivatedHandlers(handler: HTMLInputElement): Array<string> {
		const value: string = handler.getAttribute(this.DATA_ATTR_ACTIVATED_HANDLERS);
		return value?.split(',') || [];
	}

	private addActivatedHandlers(handler: HTMLInputElement, value: string): void {
		const currentValue: Array<string> = this.getActivatedHandlers(handler);
		currentValue.push(value);
		handler.setAttribute(this.DATA_ATTR_ACTIVATED_HANDLERS, currentValue.toString());
	}
}
