import {IBackendData} from "@modules/backend-data/model";

export default class BackendData implements IBackendData {
	private CLASS_CONTAINER: string = 'js-backendData';

	private static instance: IBackendData;

	private container: HTMLElement;

	constructor() {
		if (BackendData.instance) {
			throw new Error("Error: Instantiation failed: Use BackendData.getInstance() instead of new.");
		}
		BackendData.instance = this;
		this.init();
	}

	public static getInstance(): IBackendData {
		if (!BackendData.instance) {
			BackendData.instance = new BackendData();
		}
		return this.instance;
	}

	public getContainer(): HTMLElement {
		return this.container;
	}

	private init(): void {
		this.container = document.querySelector(`.${this.CLASS_CONTAINER}`);
		if (!this.container) {
			throw new Error(`BackendData: container is not defined !`);
		}
	}
}
