import {EBtnSize, EBtnType} from "@components/btn/model";
import CheckboxComponent from "@components/checkbox/CheckboxComponent";
import InputComponent from "@components/input/InputComponent";
import {
	EPhoneValidationForContractTariffSlots,
	EPhoneValidationForContractTariffStages,
	EPhoneValidationForContractTariffStatuses,
} from "@components/phone-validation-for-contract-tariff/model";
import {
	EValidateSMSCodeSlots,
	EValidateSMSCodeStatuses,
	TValidateSMSCodeEventDetail
} from "@components/validate-sms-code/model";
import {TOperator} from "@models/Operator";
import {TSMSValidationGetResponse} from "@models/SMSValidation";
import {ETariffConnectionStatuses} from "@models/Tariff";
import HttpRequest from "@modules/http-request/HttpRequest";
import Mask, {EMaskTypes} from "@modules/mask/Mask";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosError, Method} from "axios";
import {customElement, html, state, property, PropertyValues, queryAsync, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";
import {from, of} from "rxjs";
import {filter} from "rxjs/internal/operators";
import {map, switchMap} from "rxjs/operators";

@customElement('c-phone-validation-for-contract-tariff')
export default class PhoneValidationForContractTariffComponent extends WebComponentLite {
	@property({
		type: String,
		attribute: 'confirmed-link',
	})
	public confirmedLink: string = '';

	@state()
	private getSMSResponse: TSMSValidationGetResponse;

	@state()
	private validateSMSCodeDetail: TValidateSMSCodeEventDetail

	@state()
	private stage: EPhoneValidationForContractTariffStages = EPhoneValidationForContractTariffStages.ENTER_PHONE_NUMBER;

	@state()
	private isSMSRequesting: boolean = false;

	@state()
	private isCompletePhoneNumber: boolean = false;

	@queryAsync('[fieldPhoneNumber]')
	private fieldPhoneNumberAsync: Promise<InputComponent>;

	@state()
	private fieldPhoneNumber: InputComponent;

	@queryAsync('[fieldConfirmed]')
	private fieldConfirmedAsync: Promise<CheckboxComponent>;

	@state()
	private confirmed: boolean = false;

	@state()
	private phoneNumber: string;

	@state()
	private isNetworkError: boolean = false;

	private formValidateSMSEl: HTMLFormElement;
	private formGetSMSEl: HTMLFormElement;

	private httpService: HttpRequest = new HttpRequest();

	public getOperator(): TOperator {
		return this.validateSMSCodeDetail.operator;
	}

	public getPhoneNumber(): string {
		return this.phoneNumber;
	}

	public resetStage(): void {
		this.changeStage(EPhoneValidationForContractTariffStages.ENTER_PHONE_NUMBER);
	}

	public focusInputEnterPhoneNumber(): void {
		from(this.fieldPhoneNumberAsync)
			.pipe(
				switchMap(component => {
					if (!component) {
						console?.error('PhoneValidationForContractTariffComponent: fieldPhoneNumber is not defined !');
						return of(null);
					}
					return from(component.updateComplete)
						.pipe(
							map(() => component)
						)
				})
			)
			.pipe(
				filter(value => Boolean(value))
			)
			.subscribe(component => {
				component.focus();
			});
	}

	protected render(): unknown {
		if (this.isNetworkError) {
			return this.getNetworkErrorTemplate();
		}
		return html`
			${this.getTemplateByStage(this.stage)}
		`;
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.initSlots();
	}

	protected updated(_changedProperties: PropertyValues) {
		super.updated(_changedProperties);
		this.updateAsyncEls();
	}

	private initSlots(): void {
		this.formValidateSMSEl = this.getSlot(EPhoneValidationForContractTariffSlots.FORM_VALIDATE_SMS) as HTMLFormElement;
		if (!this.formValidateSMSEl) {
			throw new Error(`ValidateSMSCodeComponent: slot with name '${EPhoneValidationForContractTariffSlots.FORM_VALIDATE_SMS}' is not defined !`);
		}
		this.formValidateSMSEl.setAttribute('slot', EValidateSMSCodeSlots.FORM_VALIDATE);
		this.formGetSMSEl = this.getSlot(EPhoneValidationForContractTariffSlots.FORM_GET_SMS) as HTMLFormElement;
		if (!this.formGetSMSEl) {
			throw new Error(`ValidateSMSCodeComponent: slot with name '${EPhoneValidationForContractTariffSlots.FORM_GET_SMS}' is not defined !`);
		}
		this.formGetSMSEl.setAttribute('slot', EValidateSMSCodeSlots.FORM_GET);
	}

	private getNetworkErrorTemplate(): TemplateResult {
		return html`
			<c-network-error @on-click-reload="${this.handleBtnClickReload}">
		`;
	}

	private getEnterPhoneNumberTemplate(): TemplateResult {
		return html`
			<form @submit="${this.handleFormSubmit}">
				<c-form-item class="u-mb-24">
					<c-input fieldPhoneNumber
							 label="Номер телефона"
							 name="phone"
							 value=""
							 type="tel"
							 fieldClass=""
							 .maskType="${EMaskTypes.PHONE}"
							 @on-change="${this.handleInputPhoneNumberChange}">
					</c-input>
				</c-form-item>
				<c-checkbox fieldConfirmed
							name="confirmed"
							value="confirmed"
							class="u-mb-40"
							@on-change="${this.handleChangeConfirmed}">
					<div class="u-ml-10">
						Согласен на сбор, хранение и обработку персональных данных в соответствии с Законом РК
						<a class="c-link c-link_secondary"
						   .href="${this.confirmedLink}"
						   target="_blank">
							О персональных данных и их защите
						</a>
					</div>
				</c-checkbox>
				<div class="c-phone-validation-for-contract-tariff__get-sms-code">
					<c-btn class="c-phone-validation-for-contract-tariff__get-sms-code-btn"
						   type="${this.confirmed ? EBtnType.PRIMARY : EBtnType.SECONDARY}"
						   size="${EBtnSize.MD}"
						   ?loading="${this.isLoadingBtnGetSMS()}"
						   ?disabled="${this.isDisabledBtnGetSMS()}"
						   @click="${this.handleClickBtnGetSMSCode}">
						Получить код
					</c-btn>
					${!this.confirmed
						? html`
							<p class="c-phone-validation-for-contract-tariff__get-sms-code-hint">
								Что бы получить код, необходимо принять согласие
							</p>
						`
						: null
					}
				</div>
			</form>
		`;
	}

	private getValidateSMSCodeTemplate(): TemplateResult {
		return html`
			<c-validate-sms-code .phoneNumber="${this.phoneNumber}"
								 .confirmed="${this.confirmed}"
								 .bootstrapData="${{
									 timer: this.getSMSResponse.duration,
									 attemptsCountSend: this.getSMSResponse.attemptsCountSend,
									 attemptsCountValidate: this.getSMSResponse.attemptsCountValidate,
								 }}"
								 @on-status-change="${this.handleValidateSMSCodeStatusChange}">
				${this.formValidateSMSEl}
				${this.formGetSMSEl}
			</c-validate-sms-code>
		`;
	}

	private getDeniedAbilityToConnectTariffTemplate(): TemplateResult {
		const tariffConnection = this.validateSMSCodeDetail.tariffConnection;
		return html`
			<div class="c-phone-validation-for-contract-tariff__invalid-phone">
				<p class="c-phone-validation-for-contract-tariff__invalid-phone-title">
					${unsafeHTML(tariffConnection.message)}
				</p>
				<c-btn class="c-phone-validation-for-contract-tariff__invalid-phone-action"
					   type="${EBtnType.SECONDARY}"
					   size="${EBtnSize.MD}"
					   @click="${this.handleBtnClickChangePhone}">
					Изменить номер
				</c-btn>
				<button class="c-phone-validation-for-contract-tariff__invalid-phone-skip-btn"
						@click="${this.handleBtnClickContinueWithoutTariff}">
					Продолжить без тарифа
				</button>
			</div>
		`;
	}

	private getPhoneInformation(): TemplateResult {
		const phoneNumber: string = Mask.getFormattedValue(this.phoneNumber, EMaskTypes.PHONE);
		const operator: TOperator = this.validateSMSCodeDetail.operator;

		return html`
			<div class="c-phone-validation-for-contract-tariff__result">
				<section class="c-section">
					<header class="c-section__header">
						<h4 class="c-section__title">Ваш номер телефона</h4>
					</header>
					<div class="c-section__body">
						<p class="c-phone-validation-for-contract-tariff__result-phone">
							${phoneNumber}
						</p>
					</div>
				</section>
				<section class="c-section u-d-none">
					<header class="c-section__header">
						<h4 class="c-section__title">Ваш оператор</h4>
					</header>
					<div class="c-section__body">
						<div class="c-phone-validation-for-contract-tariff__result-operator">
							<div class="c-phone-validation-for-contract-tariff__result-operator-img-box">
								<img class="c-phone-validation-for-contract-tariff__result-operator-img"
									 .src="${operator.logo}"
									 .alt="${operator.logo}">
							</div>
							<h4 class="c-phone-validation-for-contract-tariff__result-operator-title">
								${operator.name}
							</h4>
						</div>
					</div>
				</section>
			</div>
		`;
	}

	private getTemplateByStage(stage: EPhoneValidationForContractTariffStages): TemplateResult {
		switch (stage) {
			case EPhoneValidationForContractTariffStages.ENTER_PHONE_NUMBER:
				return this.getEnterPhoneNumberTemplate();
			case EPhoneValidationForContractTariffStages.VALIDATE_SMS_CODE:
				return this.getValidateSMSCodeTemplate();
			case EPhoneValidationForContractTariffStages.DENIED_ABILITY_TO_CONNECT_TARIFF:
				return this.getDeniedAbilityToConnectTariffTemplate();
			case EPhoneValidationForContractTariffStages.PHONE_INFORMATION:
				return this.getPhoneInformation();
			case EPhoneValidationForContractTariffStages.EMPTY:
				return null;
		}
	}

	private handleInputPhoneNumberChange(event: Event): void {
		this.phoneNumber = Mask.getUnmaskedValue(this.fieldPhoneNumber.getValue(), EMaskTypes.PHONE);
		this.checkPhoneNumberFiled();
	}

	private checkPhoneNumberFiled(): void {
		this.isCompletePhoneNumber = Mask.isCompleteValue(this.fieldPhoneNumber.getValue(), EMaskTypes.PHONE);
	}

	private isLoadingBtnGetSMS(): boolean {
		if (this.isSMSRequesting) {
			return true;
		}
		return false;
	}

	private isDisabledBtnGetSMS(): boolean {
		if (this.isSMSRequesting || !this.isCompletePhoneNumber || !this.confirmed) {
			return true;
		}
		return false;
	}

	private handleClickBtnGetSMSCode(event: Event): void {
		event.preventDefault();
		if (this.isDisabledBtnGetSMS()) {
			return;
		}
		this.getSMSCode();
	}

	private handleFormSubmit(event: Event): void {
		event.preventDefault();
	}

	private changeStage(stage: EPhoneValidationForContractTariffStages) {
		this.stage = stage;
		switch (stage) {
			case EPhoneValidationForContractTariffStages.ENTER_PHONE_NUMBER:
				this.stageEnterPhoneNumber();
				break;
			case EPhoneValidationForContractTariffStages.PHONE_INFORMATION:
				this.stageApprovedAbilityToConnectTariff();
				break;
			case EPhoneValidationForContractTariffStages.DENIED_ABILITY_TO_CONNECT_TARIFF:
				this.stageDeniedAbilityToConnectTariff();
				break;
		}
	}

	private stageEnterPhoneNumber() {
		this.fieldPhoneNumberAsync
			.then(fieldPhoneNumber => {
				this.fieldPhoneNumber = fieldPhoneNumber;
				return fieldPhoneNumber.updateComplete;
			})
			.then(() => {
				this.fieldPhoneNumber.focus();
				this.checkPhoneNumberFiled();
			});
	}

	private stageApprovedAbilityToConnectTariff(): void {
		this.emitEventStatusChange(EPhoneValidationForContractTariffStatuses.APPROVED_ABILITY_TO_CONNECT_TARIFF);
	}

	private stageDeniedAbilityToConnectTariff(): void {
		this.emitEventStatusChange(EPhoneValidationForContractTariffStatuses.DENIED_ABILITY_TO_CONNECT_TARIFF);
	}

	private getSMSCode(): void {
		if (this.isSMSRequesting) {
			return;
		}
		this.isSMSRequesting = true;
		this.isNetworkError = false;
		const formData: FormData = new FormData(this.formGetSMSEl);
		formData.append('phoneNumber', this.phoneNumber);
		formData.append('confirmed', String(this.confirmed));
		this.httpService
			.request<TSMSValidationGetResponse>({
				url: this.formGetSMSEl.action,
				method: this.formGetSMSEl.method as Method,
				data: formData,
			})
			.then((response) => {
				const data = response.data.data;
				this.getSMSResponse = data;
				this.changeStage(EPhoneValidationForContractTariffStages.VALIDATE_SMS_CODE);
			})
			.catch((reject: AxiosError) => {
				if (HttpRequest.isNetworkError(reject)) {
					this.isNetworkError = true;
				}
			})
			.finally(() => {
				this.isSMSRequesting = false;
			})
	}

	private updateAsyncEls(): void {
		this.fieldPhoneNumberAsync
			.then(field => {
				this.fieldPhoneNumber = field;
			});
	}

	private handleChangeConfirmed(): void {
		this.fieldConfirmedAsync
			.then(field => {
				this.confirmed = field.checked;
			});
	}

	private handleBtnClickChangePhone(event: Event): void {
		event.preventDefault();
		this.changeStage(EPhoneValidationForContractTariffStages.ENTER_PHONE_NUMBER);
	}

	private handleBtnClickContinueWithoutTariff(event: Event): void {
		event.preventDefault();
		this.changeStage(EPhoneValidationForContractTariffStages.EMPTY);
		const customEvent = new CustomEvent('on-click-skip', {
			detail: {},
		});
		this.dispatchEvent(customEvent);
	}

	private emitEventStatusChange(status: EPhoneValidationForContractTariffStatuses): void {
		const customEvent = new CustomEvent('on-status-change', {
			detail: {
				status: status,
			},
		});
		this.dispatchEvent(customEvent);
	}

	private handleBtnClickReload(event: Event): void {
		event.preventDefault();
		this.getSMSCode();
	}

	private handleValidateSMSCodeStatusChange(event: CustomEvent): void {
		const detail: TValidateSMSCodeEventDetail = event.detail;
		this.validateSMSCodeDetail = detail;
		switch (detail.status) {
			case EValidateSMSCodeStatuses.VALIDATION_SUCCESSFUL:
				if (detail.tariffConnection.status === ETariffConnectionStatuses.APPROVED) {
					this.changeStage(EPhoneValidationForContractTariffStages.PHONE_INFORMATION);
				} else {
					this.changeStage(EPhoneValidationForContractTariffStages.DENIED_ABILITY_TO_CONNECT_TARIFF);
				}
				break;
		}
	}
}
