import {EBtnMods, EBtnSize, EBtnState, EBtnType} from "@components/btn/model";
import {TProductCostLineOnBtnClickEventDetail} from "@components/product-cost-line/model";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import StickyCostLine from "@scenario/StickyCostLine";
import {customElement, html, property, PropertyValues, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-product-cost-line')
export default class ProductCostLineComponent extends WebComponentLite {
	@property({
		reflect: true,
		type: String,
		attribute: 'price',
	})
	price: string;

	@property({
		reflect: true,
		type: String,
		attribute: 'price-per-month',
	})
	pricePerMonth: string;

	@property({
		reflect: true,
		type: Boolean,
		attribute: 'is-disable-btn',
	})
	isDisableBtn: boolean;

	@property({
		reflect: true,
		type: Boolean,
		attribute: 'is-loading-btn',
	})
	isLoadingBtn: boolean;

	@property({
		reflect: true,
		type: String,
		attribute: 'btn-text',
	})
	btnText: string = 'В корзину';


	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		new StickyCostLine().init();
	}

	protected render(): TemplateResult {
		return html`
			${this.getFixedWrapper(this.getContent())}
			${this.getStaticWrapper(this.getContent())}
		`;
	}

	private getFixedWrapper(content: TemplateResult): TemplateResult {
		return html`
			<section class="c-product-cost-line c-product-cost-line_fixed js-stickyCostLineFixedDuplicate">
				${content}
			</section>
		`;
	}

	private getStaticWrapper(content: TemplateResult): TemplateResult {
		return html`
			<section class="c-product-cost-line js-stickyCostLineHandler">
				${content}
			</section>
		`;
	}

	private getContent(): TemplateResult {
		const price: string = MoneyFormatter.format(
			(this.price || '0'), EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);
		const pricePerMonth: string = MoneyFormatter.format(
			(this.pricePerMonth || '0'), EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT
		);
		return html`
			<div class="c-product-cost-line__content">
				<div class="c-product-cost-line__info">
					<div class="c-product-cost-line__title-box">
						<h3 class="c-product-cost-line__title">ИТОГО</h3>
					</div>
					<div class="c-product-cost-line__price-box">
						<h4 class="c-product-cost-line__sub-title">Ежемесячный платеж</h4>
						<div class="c-product-cost-line__cost">
							${unsafeHTML(pricePerMonth)}
						</div>
					</div>
					<div class="c-product-cost-line__price-box">
						<h4 class="c-product-cost-line__sub-title">К оплате</h4>
						<div class="c-product-cost-line__cost">
							${unsafeHTML(price)}
						</div>
					</div>
				</div>
				<div class="c-product-cost-line__btn-box">
					<c-btn type="${EBtnType.PRIMARY}"
						   size="${EBtnSize.LG}"
						   .mods="${[EBtnMods.ADAPTIVE_TO_MD]}"
						   ?loading="${this.isLoadingBtn}"
						   ?disabled="${this.isDisableBtn}"
						   @click="${this.handleBtnClick}">
						${this.btnText}
					</c-btn>
				</div>
			</div>
		`;
	}

	private handleBtnClick(event: CustomEvent): void {
		let states: Array<EBtnState> = [];
		if (this.isDisableBtn) {
			states.push(EBtnState.DISABLED);
		}
		if (this.isLoadingBtn) {
			states.push(EBtnState.LOADING);
		}
		if (!this.isDisableBtn && !this.isLoadingBtn) {
			states.push(EBtnState.ACTIVE);
		}
		let detail: TProductCostLineOnBtnClickEventDetail = {
			states: states,
		}
		const customEvent = new CustomEvent<TProductCostLineOnBtnClickEventDetail>('on-btn-click', {
			detail: detail,
		});
		this.dispatchEvent(customEvent);
	}
}
