import {getDefinitionElement, getIndexElement, isDefinitionElementEvent} from "@helpers/index";
import Animation from "@modules/animation/Animation";
import {EAnimationType, IAnimation} from "@modules/animation/model";
import {ICarousel} from "@modules/carousel/model";
import {IImageViewer, TImageViewerOption} from "@modules/image-viewer/model";
import {IScrollable} from "@modules/scroll/model";
import Scrollable from "@modules/scroll/Scrollable";
import {fromEvent} from "rxjs";
import {filter, map} from "rxjs/operators";

export default class ImageViewer implements IImageViewer {
	private CLASS_OPEN: string = 'is-open';

	private closeBtnEl: HTMLButtonElement;
	private animation: IAnimation;

	private scrollable: IScrollable = new Scrollable(document.body, {
		saveScrollIndent: true,
	});

	constructor(
		private viewerEl: HTMLElement,
		private carousel: ICarousel,
		private options: TImageViewerOption
	) {
		const defaultOptions: Partial<TImageViewerOption> = {
			classes: {
				classHandler: 'js-imageViewerHandler',
				classBlock: 'js-imageViewer',
				classClose: 'js-imageViewerClose',
			}
		};
		this.options = {
			...defaultOptions,
			...this.options,
		};
		this.init();
	}

	private init(): void {
		this.findElements();
		this.animation = new Animation(this.viewerEl, {
			type: EAnimationType.ANIMATION,
			name: 'o-animation-fade-in',
			displayProperty: 'flex',
			afterEnter: () => {
				this.viewerEl.classList.add(this.CLASS_OPEN);
				// openFullscreen(this.viewerEl);
			},
			beforeLeave: () => {
				// closeFullscreen();
			},
			afterLeave: () => {
				this.viewerEl.classList.remove(this.CLASS_OPEN);
			}
		})
		this.attachEvent();
	}

	public open(): void {
		this.scrollable.disable();
		this.animation.show();
	}

	public close(): void {
		this.scrollable.enable();
		this.animation.hide();
	}

	public isOpen(): boolean {
		return this.viewerEl.classList.contains(this.CLASS_OPEN);
	}

	private findElements(): void {
		this.closeBtnEl = this.viewerEl.querySelector(`.${this.options.classes.classClose}`);
	}

	private attachEvent(): void {
		const selectorHandler: string = `.${this.options.classes.classHandler}`;
		fromEvent(document, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, selectorHandler)),
				map(event => getDefinitionElement(event.target as HTMLElement, selectorHandler)),
			)
			.subscribe(handler => {
				const index: number = getIndexElement(handler.parentElement, handler as HTMLElement);
				this.carousel.to(index);
				if (!this.isOpen()) {
					this.open();
				}
			})

		fromEvent(this.closeBtnEl, 'click')
			.subscribe(() => {
				this.close();
			})
	}
}
