export default class EmitPostMessageFromIframe {
	private CLASS_HANDLER: string = 'js-emitPostMessageFromIframe';
	private DATA_ATTR_EVENT_NAME: string = 'data-emit-post-message-from-iframe-event-name';

	public init(): void {
		this.getHandlers()
			.forEach(handler => {
				this.emit(handler);
			});
	}

	private getHandlers(): Array<HTMLElement> {
		return Array.from(
			document.getElementsByClassName(this.CLASS_HANDLER)
		) as Array<HTMLElement>;
	}

	private emit(handler: HTMLElement): void {
		const eventName: string = handler.getAttribute(this.DATA_ATTR_EVENT_NAME);
		parent.postMessage(eventName, '*');
	}
}
