import {IDynamicActivationSubjectFactory} from "@modules/dynamic-element/model";
import AccentCollapseRemoteWithBtnSwitch from "@scenario/AccentCollapseRemoteWithBtnSwitch";

export default class AccentCollapseRemoteWithBtnSwitchFactory implements IDynamicActivationSubjectFactory {
	create(handler: HTMLElement) {
		if (!handler) {
			return;
		}
		new AccentCollapseRemoteWithBtnSwitch(handler);
	}
}
