import { TemplateResult } from 'lit-element';

export type TTariffCard = {
	id: number;
	title: string;
	type?: string;
	subTitle?: string;
	prepend?: string | TemplateResult;
	costs: Array<TTariffCardCostItem>;
	infoTitle?: string;
};

export type TTariffCardCostItem = {
	prepend?: string;
	title: string;
	subTitle?: string;
	price?: string;
	size?: ETariffCardCostItemSize;
};

export enum ETariffCardCostItemSize {
	SM = 'sm'
}
