import {IChecklist} from "@components/checklist/model";

export default class Checklist implements IChecklist {
	private CLASS_CHECKED: string = 'c-checklist__item_checked';

	public checked(...items): void {
		items.filter(item => {
			item.classList.add(this.CLASS_CHECKED);
		})
	}

	public unchecked(...items): void {
		items.filter(item => {
			item.classList.remove(this.CLASS_CHECKED);
		})
	}

	public isChecked(item: HTMLElement): boolean {
		return item.classList.contains(this.CLASS_CHECKED);
	}
}
