import ToggleChange from "@modules/toggle/ToggleChange";
import {TToggleChangeStreamOnChange} from "@modules/toggle/model";
import {filter} from "rxjs/operators";

export default class ToggleAnyKind extends ToggleChange {
	constructor() {
		super({
			classHandler: 'js-handleAnyKindToggle',
		});
		this.subscribeChanges();
	}

	private subscribeChanges(): void {
		this.getStreamOnChange()
			.pipe(
				filter(streamOnChange => Boolean(streamOnChange.event))
			)
			.subscribe(streamOnChange => {
				this.onChange(streamOnChange);
			})
	}

	private onChange(streamOnChange: TToggleChangeStreamOnChange) {
		const toggle = streamOnChange.input;
		const name: string = toggle.name;
		const value: string = toggle.value;
		if (value === '') {
			const toggles: Array<HTMLInputElement> = this.getToggleElementsByName(name);
			toggles.forEach(inputToggle => {
				if (inputToggle.value !== '') {
					inputToggle.checked = false;
				}
			})
		} else {
			const anyKindToggles: Array<HTMLInputElement> = Array.from(
				document.querySelectorAll(`input[type='${toggle.type}'][name='${name}'][value=""]`)
			);
			anyKindToggles.forEach(emptyToggle => {
				emptyToggle.checked = false;
			});
		}
	}
}
