export interface IToast {
	add(toast: TToast): void
}

export type TToast = {
	element?: HTMLElement | 'create',
	text: string
	type?: EToastType
	duration?: number
	stopOnFocus?: boolean
	circleTimer?: boolean
	iconClass?: string
	btn?: {
		text: string
		closeOnClick?: boolean
	},
	onClick?: (event: Event, toast: HTMLElement) => void
	onBtnClick?: (event: Event, toast: HTMLElement) => void
}

export enum EToastType {
	SUCCESS = 'SUCCESS',
	INFO = 'INFO',
	ERROR = 'ERROR',
}
