import Toastify from "toastify-js";
import "toastify-js/src/toastify.css";
import {EToastType, IToast, TToast} from "@modules/toast/model";
import {isDefinitionElementEvent, stringToHTML} from "@helpers/index";
import CircleTimer from "@components/circle-timer/CircleTimer";
import {fromEvent} from "rxjs";

export default class Toast implements IToast {
	private CLASS_TIMER: string = 'js-toastTimer';
	private CLASS_BTN: string = 'js-toastBtn';

	private static instance: IToast;

	constructor() {
		if (Toast.instance) {
			throw new Error("Error: Instantiation failed: Use Toast.getInstance() instead of new.");
		}
		Toast.instance = this;
	}

	public static getInstance(): IToast {
		if (!Toast.instance) {
			Toast.instance = new Toast();
		}
		return this.instance;
	}

	public add(option?: TToast) {
		option = {
			...this.getDefaultOption(),
			...option || {},
		};

		let template: HTMLElement;
		if (option.element === "create") {
			template = this.create(option);
		} else {
			template = option.element;
		}
		let toastifyoptions: any = {
			node: template,
			duration: option.duration,
			gravity: 'bottom',
			position: 'center',
			stopOnFocus: option.stopOnFocus,
		}
		const toastify = Toastify(toastifyoptions)
		toastify.showToast();
		if (this.hasOnClickSubscribers(option)) {
			fromEvent(template, 'click')
				.subscribe(event => {
					if (isDefinitionElementEvent(event, `.${this.CLASS_BTN}`)) {
						if (option.onBtnClick) {
							option.onBtnClick.call(event, template);
						}

						if (option.btn?.closeOnClick) {
							const block: HTMLElement = (event.target as HTMLElement).closest(`.toastify`);
							toastify.removeElement(block);
							window.clearTimeout(toastify.toastElement.timeOutValue);
						}
					}

					if (option.onClick) {
						option.onBtnClick.call(event, template);
					}
				})
		}
	}

	private hasOnClickSubscribers(option: TToast): boolean {
		return Boolean(option.onBtnClick) ||
			Boolean(option.onBtnClick) ||
			Boolean(option.btn?.closeOnClick);
	}

	private create(option: TToast): HTMLElement {
		const template: HTMLElement = stringToHTML(this.getTemplate(option));
		if (option.circleTimer) {
			const circleTimer: HTMLElement = template.querySelector(`.${this.CLASS_TIMER}`);
			const seconds: number = option.duration / 1000;
			new CircleTimer(circleTimer)
				.run(option.duration === -1 ? Infinity : seconds);
		}
		return template;
	}

	private getTemplate(option: TToast): string {
		return `
      <div class="c-toast ${this.getTypeClass(option.type)}">
        ${this.getIconTemplate(option)}
        <p class="c-toast__title">${option.text}</p>
        <div class="c-toast__extra ${this.hasBtn(option) ? '' : 'u-d-none'}">
            ${this.hasBtn(option) ? this.getButtonTemplate(option) : ''}
        </div>
      </div>
    `;
	}

	private getTemplateTimer(seconds: number): string {
		return `
      <div class="c-circle-timer js-toastTimer">
        <svg class="c-circle-timer__circle" viewBox="0 0 24 24" width='24' heigth='24'>
          <circle
            class="c-circle-timer__arch js-circleTimerSvgCircle"
            stroke="black"
            stroke-width="2"
            fill="transparent"
            r="10"
            cx="12"
            cy="12"
            stroke-dasharray="24"
            stroke-dashoffset="40">
          </circle>
        </svg>
        <div class="c-circle-timer__label js-circleTimerLabel">${seconds}</div>
      </div>
    `
	}

	private getIconTemplate(option: TToast): string {
		if (!this.hasIcon(option)) {
			return '';
		}
		if (option.circleTimer) {
			return `
        <div class="c-toast__icon">
            ${this.getTemplateTimer(option.duration / 1000)}
        </div>
      `
		} else if (option.iconClass) {
			return `
        <div class="c-toast__icon">
            <i class="o-icon ${option.iconClass}"></i>
        </div>
      `
		}
	}

	private getButtonTemplate(option: TToast): string {
		return `
      <button class="c-btn c-btn_text ${this.CLASS_BTN}">
        <span class="c-btn__text">${option.btn.text}</span>
      </button>
    `
	}

	private hasBtn(option: TToast): boolean {
		return Boolean(option.btn);
	}

	private hasIcon(option: TToast): boolean {
		return option.circleTimer || Boolean(option.iconClass);
	}

	private getDefaultOption(): TToast {
		return {
			element: "create",
			text: 'Message Text',
			duration: 3000,
			stopOnFocus: true,
			type: EToastType.INFO,
			circleTimer: false
		}
	}

	private getTypeClass(type: EToastType): string {
		switch (type) {
			case EToastType.SUCCESS:
				return 'c-toast_success';
			case EToastType.INFO:
				return 'c-toast_info';
			case EToastType.ERROR:
				return 'c-toast_error';
			default:
				return 'c-toast_info'
		}
	}
}
