import SelectTariffComponent from "@components/select-tariff/SelectTariffComponent";
import Btn from "@modules/btn/Btn";
import {IBtn} from "@modules/btn/model";
import {fromEvent} from "rxjs";

export default class CheckoutAvailableBtnNextWhenSelectTariff {
	private CLASS_HANDLER: string = 'js-checkoutAvailableBtnNextWhenSelectTariff';
	private DATA_ATTR_SELECT_TARIFF_ID: string = 'data-select-tariff';

	private btn: IBtn;
	private btnEl: HTMLButtonElement;
	private selectTariff: SelectTariffComponent;

	public init(): void {
		this.btnEl = document.querySelector(`.${this.CLASS_HANDLER}`);
		this.selectTariff = document.getElementById(this.btnEl?.getAttribute(this.DATA_ATTR_SELECT_TARIFF_ID)) as SelectTariffComponent;
		if (!this.btnEl || !this.selectTariff) {
			return;
		}
		this.btn = new Btn(this.btnEl);
		this.attachEvent();
	}

	public attachEvent(): void {
		fromEvent(this.selectTariff, 'on-change')
			.subscribe(() => {
				this.btn.enable();
			});
	}
}
