import {IScrollable, TScrollableIndents, TScrollableOptions} from "@modules/scroll/model";

export default class Scrollable implements IScrollable {
	private CLASS_UTIL_SCROLL_OVERFLOW: string = 'u-overflow-hidden';

	private handlerEl: HTMLElement;

	private options: TScrollableOptions;

	constructor(handler?: HTMLElement, options?: TScrollableOptions) {
		this.handlerEl = handler || document.body;
		this.options = {
			...this.getDefaultOptions(),
			...options,
		}
	}

	public enable(): void {
		if (this.options.saveScrollIndent) {
			this.removeScrollIndent();
		}
		this.handlerEl.classList.remove(this.CLASS_UTIL_SCROLL_OVERFLOW);
	}

	public disable(): void {
		if (this.options.saveScrollIndent) {
			this.addScrollIndent();
		}
		this.handlerEl.classList.add(this.CLASS_UTIL_SCROLL_OVERFLOW);
	}

	private addScrollIndent(): void {
		const indents: TScrollableIndents = this.getIndents();
		this.handlerEl.style.paddingRight = `${indents.container + indents.scroll}px`;
	}

	private removeScrollIndent(): void {
		this.handlerEl.style.paddingRight = '';
	}

	private getIndents(): TScrollableIndents {
		let containerIndent: number = 0;
		let handlerStyle = window.getComputedStyle(this.handlerEl);
		if (handlerStyle) {
			containerIndent = parseFloat(handlerStyle.paddingRight);
		}
		let scrollIndent = window.innerWidth - document.body.clientWidth;

		return {
			scroll: scrollIndent,
			container: containerIndent,
		}
	}

	private getDefaultOptions(): TScrollableOptions {
		return {
			saveScrollIndent: false,
		}
	}
}
