import {TProductBasketBundlerOptionState} from "@components/product-basket-bundler/product-basket-bundler-option-states/model";
import {BehaviorSubject, Subject} from "rxjs";

export default class ProductBasketBundlerOptionStates {
	public states: BehaviorSubject<Array<TProductBasketBundlerOptionState>> = new BehaviorSubject<Array<TProductBasketBundlerOptionState>>([]);

	public onChangeState: Subject<TProductBasketBundlerOptionState> = new Subject<TProductBasketBundlerOptionState>();

	public updateState(state: TProductBasketBundlerOptionState): void {
		const hasState = this.states.getValue().find(item => item.key === state.key);
		if (hasState) {
			this.states.next(
				this.states.getValue().map(item => {
					if (item.key === state.key) {
						this.onChangeState.next(state);
						return state;
					}
					return item;
				})
			)
		} else {
			this.states.next(
				[
					...this.states.getValue(),
					state,
				]
			)
		}
		this.onChangeState.next(state);
	}
}
