import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, PropertyValues, query, TemplateResult} from "lit-element";
import {IModal} from "@modules/modal/model";
import Modal from "@modules/modal/Modal";
import {strToBoolean} from "@helpers/index";

@customElement('c-modal')
export default class ModalComponent extends WebComponentLite {
	@property({
		type: Boolean,
		reflect: true
	})
	backdrop: boolean = true;

	@property({
		type: Boolean,
		reflect: true
	})
	maskClosable: boolean = true;

	@property({
		type: Boolean,
		reflect: true
	})
	closable: boolean = true;

	@property({
		type: String,
		reflect: true
	})
	title: string;

	@property({
		type: String,
		reflect: true
	})
	subTitle: string;

	@property({
		type: Boolean,
		reflect: true
	})
	opened: boolean = false;

	@query('.js-modal')
	private modalEl: HTMLDialogElement;

	private modal: IModal;

	constructor() {
		super();
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.modal = new Modal({
			element: this.modalEl,
			maskClosable: this.maskClosable,
		});
	}

	attributeChangedCallback(name: string, old: string | null, value: string | null) {
		super.attributeChangedCallback(name, old, value);
		if (name === 'opened') {
			this.toggle(strToBoolean(value === '' ? true : value));
		}

		if (name === 'maskclosable') {
			if (this.modal) {
				this.modal.maskClosable = strToBoolean(value === '' ? true : value)
			}
		}

		if (name === 'closable') {
			if (this.modal) {
				this.modal.closable = strToBoolean(value === '' ? true : value)
			}
		}
	}

	public open(): void {
		this.modal.open();
	}

	public close(): void {
		this.modal.close();
	}

	public setBody(content: string): void {
		this.modal.getBodyEl().innerHTML = content;
	}

	public setFooter(content: string): void {
		this.modal.getFooterEl().innerHTML = content;
	}

	private toggle(state: boolean): void {
		if (state) {
			this.open();
		} else {
			this.close();
		}
	}

	protected render(): TemplateResult {
		return html`
			<dialog class="c-modal js-modal ${this.backdrop ? 'c-modal_backdrop' : ''}">
				<div class="c-modal__view js-modalView">
					<div class="c-modal__content js-modalContent">
						<button class="c-modal__close js-modalClose">
							<i class="o-icon o-icon_close-filled"></i>
						</button>

						${this.getHeader()}

						${this.getExtraTop()}

						${this.getBody()}

						${this.getFooter()}
					</div>
				</div>
			</dialog>
		`;
	}

	private getHeader(): TemplateResult {
		return html`
			<header class="c-modal__header js-modalHeader">
				<div class="c-modal__title">
					<h5 class="js-modalTitle">${this.title}</h5>
					<p>${this.subTitle || ''}</p>
				</div>	
				
			</header>
		`;
	}

	private getExtraTop(): TemplateResult {
		const content = this.getSlot('extra-top');
		return html`
			<div class="c-modal__extra-top js-modalExtraTop">
				${content}
			</div>
		`;
	}

	private getBody(): TemplateResult {
		const content = this.getSlot('body');
		return html`
			<div class="c-modal__body js-modalBody js-customScroll">
				${content}
			</div>
		`;
	}

	private getFooter(): TemplateResult {
		const content = this.getSlot('footer');
		return html`
			<footer class="c-modal__footer js-modalFooter">
				${content}
			</footer>
		`;
	}
}
