import {IDynamicActivationSubjectFactory} from "@modules/dynamic-element/model";
import CollapseRemoteWithBtnSwitch from "@scenario/CollapseRemoteWithBtnSwitch";

export default class CollapseRemoteWithBtnSwitchFactory implements IDynamicActivationSubjectFactory {
	create(handler: HTMLElement) {
		if (!handler) {
			return;
		}
		new CollapseRemoteWithBtnSwitch(handler);
	}
}
