import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property} from "lit-element";

@customElement('c-checkbox')
export default class CheckboxComponent extends WebComponentLite {
	@property({
		type: String,
	})
	public value: string;

	@property({
		type: String,
	})
	public name: string = '';

	@property({
		type: Boolean,
		reflect: true,
	})
	public checked: boolean = false;

	@property({
		type: Boolean,
	})
	public disabled: boolean = false;

	protected render(): unknown {
		const children = this.getChildren();

		return html`
			<label class="c-checkbox">
				<input class="c-checkbox__input"
					   type="checkbox"
					   .name="${this.name}"
					   .value="${this.value}"
					   ?checked="${this.checked}"
					   ?disabled="${this.disabled}"
					   @change="${this.handleChange}">
				<div class="c-checkbox__checkmark">
					<i class="o-icon o-icon_checkmark-filled"></i>
				</div>
				<span class="c-checkbox__label">
					${children}
				</span>
			</label>
		`;
	}

	private handleChange(event: Event): void {
		this.checked = (event.target as HTMLInputElement).checked;
		const customEvent = new CustomEvent('on-change', {
			detail: {},
		});
		this.dispatchEvent(customEvent);
	}
}
