import {TAccessoryCard} from "@components/accessory-card/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, TemplateResult} from "lit-element";

@customElement('c-accessory-card')
export default class AccessoryCardComponent extends WebComponentLite {
	@property({
		type: Object
	})
	data: TAccessoryCard

	protected render(): TemplateResult {
		const price = MoneyFormatter.format(
			this.data.price,
		)
		const pricePerMonth = MoneyFormatter.format(
			this.data.pricePerMonth,
		)
		return html`
			<div class="c-accessory-card">
				<header class="c-accessory-card__header">
					<div class="c-accessory-card__img-box">
						<img class="c-accessory-card__img"
							 src="${this.data?.img?.url}" alt="${this.data?.img?.altText}">
					</div>
				</header>
				<div class="c-accessory-card__body">
					<h4 class="c-accessory-card__title">${this.data?.title}</h4>
					<div class="c-accessory-card__costs">
						<div class="c-accessory-card__price-box">
							<h4 class="c-accessory-card__price-per-month">${pricePerMonth}/мес</h4>
						</div>
						<div class="c-accessory-card__price-box">
							<h5 class="c-accessory-card__price">${price}</h5>
							<p class="c-accessory-card__price-label">Полная стоимость</p>
						</div>
					</div>
				</div>
			</div>
		`;
	}
}
