import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import {isHTMLElement, strToBoolean} from "@helpers/index";
import Carousel from "@modules/carousel/Carousel";

export default class CarouselAutoActivation extends DynamicInsertionElements {
	private CLASS_HANDLER: string = 'js-carousel';

	private DATA_ATTR_ACTIVATED: string = 'data-carousel-activated';

	public init(): void {
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	private activate(scope: HTMLElement | Document): void {
		let carousels: Array<HTMLElement> = Array.from(scope.getElementsByClassName(this.CLASS_HANDLER)) as Array<HTMLElement>;
		carousels.filter(carousel => {
			return !strToBoolean(carousel.getAttribute(this.DATA_ATTR_ACTIVATED))
		});
		carousels.filter(carousel => {
			carousel.setAttribute(this.DATA_ATTR_ACTIVATED, 'true');
			new Carousel(carousel);
		});
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}
}
