import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, TemplateResult} from "lit-element";

@customElement('c-form-item')
export default class FormItemComponent extends WebComponentLite {
	protected render(): TemplateResult {
		const child = this.getChildren();
		return html`
			<div class="c-form-item js-formItem">
				<div class="c-form-item__control">
					${child}
				</div>
				<div class="c-form-item__message-wrapper">
					<p class="c-form-item__message js-formItemMessage">This is message</p>
				</div>
			</div>
		`;
	}
}
