import {isFunction} from "@helpers/index";
import {TDynamicAction} from "@models/Action";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, state, property, PropertyValues, TemplateResult} from "lit-element";

@customElement('c-range-count')
export default class RangeCountComponent extends WebComponentLite {
	@property({
		type: Number,
	})
	public max: number = 1000;

	@property({
		type: Number,
	})
	public min: number = 0;

	@property({
		type: Number,
	})
	public step: number = 1;

	@property({
		type: Number,
		reflect: true,
	})
	public value: number;

	@property({
		type: Function,
		attribute: 'on-decrement',
	})
	public onDecrement: TDynamicAction<boolean>;

	@property({
		type: Function,
		attribute: 'on-increment',
	})
	public onIncrement: TDynamicAction<boolean>;

	@state()
	private isLoadingDecrement: boolean = false;

	@state()
	private isLoadingIncrement: boolean = false;

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		if (this.value == null) {
			this.value = this.min ?? 0;
		}
	}

	protected render(): unknown {
		return html`
			<div class="c-range-count js-rangeCount">
				<input type="number"
					   hidden="hidden"
					   .value="${this.value}"/>
				${this.getBtnDecrementTemplate()}
				<output class="c-range-count__result">${this.value}</output>
				${this.getBtnIncrementTemplate()}
			</div>
		`;
	}

	private getBtnDecrementTemplate(): TemplateResult {
		return html`
			<button
				class="c-range-count__btn c-range-count__btn_left ${this.isLoadingDecrement ? 'c-range-count__btn_loading' : ''}"
				?disabled="${this.isDisableDecrement()}"
				@click="${this.handleClickDecrement}">
				<i class="o-icon o-icon_minus-filled c-range-count__btn-icon"></i>
				<i class="o-icon o-icon_loader-line-filled c-range-count__btn-loader"></i>
			</button>
		`;
	}

	private getBtnIncrementTemplate(): TemplateResult {
		return html`
			<button
				class="c-range-count__btn c-range-count__btn_right ${this.isLoadingIncrement ? 'c-range-count__btn_loading' : ''}"
				?disabled="${this.isDisableIncrement()}"
				@click="${this.handleClickIncrement}">
				<i class="o-icon o-icon_plus-filled c-range-count__btn-icon"></i>
				<i class="o-icon o-icon_loader-line-filled c-range-count__btn-loader"></i>
			</button>
		`;
	}

	private isDisableDecrement(): boolean {
		return this.value <= this.min || this.isLoadingIncrement;
	}

	private isDisableIncrement(): boolean {
		return this.value >= this.max || this.isLoadingDecrement;
	}

	private async handleClickDecrement(event: Event): Promise<void> {
		event.preventDefault();
		if (this.isLoadingDecrement) {
			return;
		}
		if (isFunction(this.onDecrement)) {
			this.isLoadingDecrement = true;
			const res = await this.onDecrement();
			if (res) {
				this.decrement();
			}
			this.isLoadingDecrement = false;
		} else {
			this.decrement();
		}
	}

	public decrement(): void {
		if (this.value - 1 >= this.min) {
			this.value--;
		} else {
			this.value = this.min;
		}
	}

	private async handleClickIncrement(event: Event): Promise<void> {
		event.preventDefault();
		if (this.isLoadingIncrement) {
			return;
		}
		if (this.onIncrement && typeof this.onIncrement === 'function') {
			this.isLoadingIncrement = true;
			const res = await this.onIncrement();
			if (res) {
				this.increment();
			}
			this.isLoadingIncrement = false;
		} else {
			this.increment();
		}
	}

	public increment(): void {
		if (this.value + 1 <= this.max) {
			this.value++;
		} else {
			this.value = this.max;
		}
	}
}
