import {EAlertMods, EAlertTypes} from "@components/alert/model";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property} from "lit-element";

@customElement('c-alert')
export default class AlertComponent extends WebComponentLite {
	@property({
		type: String,
	})
	public type: EAlertTypes;

	@property({
		type: Array,
	})
	public mods: Array<EAlertMods> = [];

	@property({
		type: String,
		attribute: 'icon-class',
	})
	public iconClass: string;

	protected render(): unknown {
		return html`
			<div class="c-alert ${this.getMods()}">
				<div class="c-alert__icon-box">
					<i class="o-icon ${this.getIconClass()}"></i>
				</div>
				<div class="c-alert__message">
					${this.getChildren()}
				</div>
			</div>
		`;
	}

	protected getMods(): string {
		const mods: Array<string> = [this.type, ...this.mods];
		return mods
			.filter(Boolean)
			.map(mod => `c-alert_${mod}`)
			.join(' ');
	}

	protected getIconClass(): string {
		switch (this.type) {
			case EAlertTypes.SUCCESS:
				return 'o-icon_checkmark-filled';
			case EAlertTypes.INFO:
				return 'o-icon_exclamation-filled';
			case EAlertTypes.WARNING:
				return 'o-icon_exclamation-filled';
			case EAlertTypes.ERROR:
				return 'o-icon_close-filled';
			default:
				return this.iconClass;
		}
	}
}
