import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, property, PropertyValues, query, queryAsync, state, TemplateResult} from "lit-element";
import {ETariffCardCostItemSize, TTariffCard} from "@components/tariff-card/model";
import ModalComponent from "@components/modal/ModalComponent";
import {EInsuranceTypes, TInsurance, TInsuranceDevice} from "@models/Insurance";
import HttpRequest from "@modules/http-request/HttpRequest";
import {BehaviorSubject} from "rxjs";
import {EComponentStatuses} from "@models/Status";
import {AxiosRequestConfig} from "axios";
import {ELoaderSize, ELoaderType} from "@components/loader/model";
import {objectToFormData} from "@helpers/index";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import ToggleCardComponent from "@components/toggle-card/ToggleCardComponent";
import {
	ESelectInsuranceMods,
	TSelectInsuranceInitSelected,
	TSelectInsuranceInitSelectedBase,
	TSelectInsuranceInitSelectedDevice,
	TSelectInsuranceOnChangeEventDetail,
	TSelectInsuranceSelected,
	TSelectInsuranceSelectedDevice,
	TSelectInsuranceStatusChangeEventDetail
} from "@components/select-insurance/model";
import InsuranceDeviceTypeComponent from "@components/select-insurance/device/InsuranceDeviceTypeComponent";

@customElement('c-select-insurance')
export default class SelectInsuranceComponent extends WebComponentLite {
	@property({
		type: Object,
		attribute: 'add-data-get'
	})
	public addDataGet: object = {};

	@property({
		type: Object,
		attribute: 'get-request-config',
	})
	public getRequestConfig: AxiosRequestConfig = {
		url: '/get-insurance',
		method: "GET",
	}

	@property({
		type: Object,
		attribute: 'init-selected',
	})
	public initSelected: TSelectInsuranceInitSelected;

	@property({
		reflect: true,
		type: Array,
	})
	public mods: Array<ESelectInsuranceMods> = [];

	@query('[product-bundler-option-insurance-detail-modal]')
	private modal: ModalComponent;

	@state()
	private _selected: TSelectInsuranceSelected = [];

	@state()
	private isLoading: boolean = false;

	@state()
	private error: unknown;

	@state()
	private formattedData: Array<TTariffCard> = [];

	@query('[toggle-skip]')
	private toggleSkipEl: ToggleCardComponent;

	@queryAsync('[insurance-device-type]')
	private insuranceDeviceType: Promise<InsuranceDeviceTypeComponent>;

	private data: Array<TInsurance> = [];

	private isFirstLoaded: boolean;
	private httpService: HttpRequest = new HttpRequest();
	public readonly status: BehaviorSubject<EComponentStatuses> = new BehaviorSubject<EComponentStatuses>(EComponentStatuses.LOADING);

	get selected(): TSelectInsuranceSelected {
		return [
			...this._selected
		];
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.attachEvent();
		this.getData();

		const event = new CustomEvent<TSelectInsuranceOnChangeEventDetail>('first-updated', {
			detail: this._selected,
		});
		this.dispatchEvent(event);
	}

	private getData(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.error = null;
		this.status.next(EComponentStatuses.LOADING);
		const {url, method} = this.getRequestConfig;
		const formData: FormData = objectToFormData(this.addDataGet || {});
		this.httpService
			.request<Array<TInsurance>>({
				url: url,
				method: method,
				data: formData,
			})
			.then((response) => {
				this.data = response.data.data;
				this.formattedData = this.formatData(this.data);
				this.setInitSelected();
				this.isFirstLoaded = false;
				this.status.next(EComponentStatuses.READY);
			})
			.catch((reject) => {
				this.error = reject;
				this.status.next(EComponentStatuses.ERROR);
			})
			.finally(() => {
				this.isLoading = false;
			});
	}

	private setInitSelected(): void {
		if (!this.data.length) {
			return;
		}
		const initSelected = this.initSelected;
		if (!initSelected || !Object.entries(initSelected).length) {
			return;
		}
		this._selected = this.initSelected.map(initSelect => {
			const insurance = this.data.find(item => item.id === initSelect.id);
			if (this.isInitSelectedDevice(initSelect, insurance)) {
				return {
					insurance: insurance,
					period: initSelect.period,
					products: initSelect.productSkus?.filter(sku => {
						return Boolean((insurance as TInsuranceDevice).products.find(item => item.sku == sku));
					}) || [],
				}
			}
			return {
				insurance: insurance,
			}
		});
	}

	private isInitSelectedDevice(initSelected: TSelectInsuranceInitSelectedBase | TSelectInsuranceInitSelectedDevice, insurance: TInsurance): initSelected is TSelectInsuranceInitSelectedDevice {
		return initSelected.id === insurance.id && insurance.type === EInsuranceTypes.DEVICE;
	}

	private formatData(data: Array<TInsurance>): Array<TTariffCard> {
		return data?.map(item => {
			const pricePerMonth = MoneyFormatter.format(
				item.pricePerMonth,
				EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT
			);
			const payoutPrice = MoneyFormatter.format(
				item.payoutPrice,
				EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
			);
			const fullPrice = MoneyFormatter.format(
				item.price,
				EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
			);
			return {
				id: item.id,
				type: item.type,
				title: item.name,
				subTitle: item.shortDescription,
				prepend: this.getPrependTemplate(item),
				infoTitle: 'ГАРАНТИЯ ПОЛУЧЕНИЯ КРЕДИТА',
				costs: [
					{
						title: pricePerMonth,
					},
					{
						title: payoutPrice,
						subTitle: 'Страховая выплата',
						size: ETariffCardCostItemSize.SM,
					},
					{
						title: fullPrice,
						subTitle: 'Полная стоимость',
						size: ETariffCardCostItemSize.SM,
					}
				]
			}
		}) || [];
	}

	private getPrependTemplate(item: TInsurance): TemplateResult {
		if (!item.description) {
			return null;
		}
		return html`
			<button class="c-btn-clear u-color-secondary"
					@click="${this.handleBtnOpenDetail(item)}">
				Подробнее
			</button>
		`;
	}

	private handleBtnOpenDetail(item: TInsurance) {
		return (event) => {
			event.preventDefault();
			this.modal.title = item.name;
			this.modal.setBody(
				`
					<div class="s-wysiwyg">
						${item.description}
					</div>
				`
			)
			this.modal.open();
		}
	}

	protected render(): unknown {
		if (this.isLoading) {
			return this.getLoaderTemplate();
		}

		if (this.error) {
			return this.getErrorTemplate();
		}

		if (!this.formattedData.length) {
			return this.getNotDataTemplate();
		}

		return this.getContentTemplate();
	}

	private getLoaderTemplate(): TemplateResult {
		return html`
			<c-loader active
					  type="${ELoaderType.PRIMARY}"
					  size="${ELoaderSize.MD}"
					  class="c-product-basket-bundler__loader">
			</c-loader>
		`;
	}

	private getErrorTemplate(): TemplateResult {
		return html`
			<c-network-error @on-click-reload="${this.handleBtnClickReload}">
		`;
	}

	private handleBtnClickReload(): void {
		this.getData();
	}

	private getNotDataTemplate(): TemplateResult {
		return html`
			<div class="c-result c-result_404">
				<div class="c-result__icon">
					<i class="o-icon o-icon_close"></i>
				</div>
				<h5 class="c-result__title">
					К сожалению, в данный момент нет предложений по страхованию
				</h5>
				<p class="c-result__sub-title">
					Вы можете продолжить без них !
				</p>
			</div>
		`;
	}

	protected getMods(): string {
		const mods: Array<string> = [...this.mods];
		return mods
			.filter(Boolean)
			.map(mod => `c-select-insurance_${mod}`)
			.join(' ');
	}

	protected getContentTemplate(): TemplateResult {
		return html`
			<div class="c-select-insurance ${this.getMods()}">
				<ul class="c-select-insurance__list">
					${this.formattedData.map(insurance => {
							const item = this._selected.find(item => item.insurance.id == insurance.id);
							const isSelected = Boolean(item);
							const originalInsurance = this.data.find(item => item.id === insurance.id);
							return html`
								<li class="c-select-insurance__item">
									<c-toggle-card marker
												   class="c-select-insurance__item-card"
												   type="checkbox"
												   name="insurance"
												   .value="${insurance.id}"
												   .insuranceType="${insurance.type}"
												   ?checked="${isSelected}"
												   @on-change="${this.handleChange}">
										<c-tariff-card .data="${insurance}">
									</c-toggle-card>

									${
										originalInsurance.type === EInsuranceTypes.DEVICE && isSelected
											? html`
												<div class="c-select-insurance__item-additional">
													${this.getDeviceTypeTemplate(originalInsurance, item as TSelectInsuranceSelectedDevice)}
												</div>
											`
											: null
									}
								</li>
							`
						}
					)}
					${this.getSkipTemplate()}
				</ul>
				<c-modal product-bundler-option-insurance-detail-modal>
			</div>
		`;
	}

	private getDeviceTypeTemplate(insurance: TInsuranceDevice, syncSelectedData: TSelectInsuranceSelectedDevice): TemplateResult {
		const initSelected = this.initSelected?.find(item => item.id == insurance.id) || {};

		return html`
			<c-insurance-device-type insurance-device-type
									 .data="${insurance}"
									 .syncSelectedData="${syncSelectedData}"
									 .emitEventChange="${() => this.emitEventOnChange()}"
									 .initSelected="${initSelected}">
		`
	}

	private getSkipTemplate(): TemplateResult {
		return html`
			<li class="c-select-insurance__item">
				<c-toggle-card marker
							   class="c-select-insurance__item-card"
							   toggle-skip
							   type="checkbox"
							   name="insurance"
							   .value="${null}"
							   @on-change="${this.handleClickSkip}">
					<div class="u-h-100 u-d-flex u-flex-row u-align-items-center">
						<p>Мне не нужно выгодное страхование</p>
					</div>
				</c-toggle-card>
			</li>
		`;
	}

	private handleClickSkip(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		if (!toggle.checked) {
			return;
		}
		this._selected = [];
	}

	private handleChange(event: Event): void {
		const toggle: ToggleCardComponent = event.currentTarget as ToggleCardComponent;
		const id: number = parseFloat(toggle.value)
		if (toggle.checked) {
			const insurance = this.data.find(item => item.id == id);
			const hasSuchType = this._selected.find(item => item.insurance.type === insurance.type);
			this.toggleSkipEl.checked = false;

			if (hasSuchType) {
				this._selected = [
					...this._selected.filter(item => item.insurance.type !== insurance.type),
					{
						insurance: insurance
					}
				];
			} else {
				this._selected = [
					...this._selected,
					{
						insurance: insurance
					}
				];
			}
		} else {
			this._selected = this._selected.filter(item => item.insurance.id !== id);
		}
		this.emitEventOnChange();
	}

	private attachEvent(): void {
		this.attachEventOnStatusChange();
	}

	private attachEventOnStatusChange(): void {
		this.status
			.asObservable()
			.subscribe(status => {
				this.emitEventOnStatusChange(status);
			});
	}

	private emitEventOnChange(): void {
		const event = new CustomEvent<TSelectInsuranceOnChangeEventDetail>('on-change', {
			detail: this._selected,
		});
		this.dispatchEvent(event);
	}

	private emitEventOnStatusChange(status: EComponentStatuses): void {
		const event = new CustomEvent<TSelectInsuranceStatusChangeEventDetail>('on-status-change', {
			detail: {
				status: status,
			}
		});
		this.dispatchEvent(event);
	}
}
