import ProductCostLineComponent from "@components/product-cost-line/ProductCostLineComponent";
import {fromEvent} from "rxjs";
import {TProductCostLineOnBtnClickEventDetail} from "@components/product-cost-line/model";
import {EBtnState} from "@components/btn/model";
import {TProductItem} from "@modules/analytics/model";

declare global {
	interface Window {
		dataLayer: any;
	}
}

export default class AnalyticsAddToCartInProductCostLine {
	private productCostLineEl: ProductCostLineComponent;
	private DATA_ATTR_ANALYTICS_DATA: string = 'data-analytics-data';

	constructor(productCostLineEl: ProductCostLineComponent) {
		this.productCostLineEl = productCostLineEl;
	}

	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.productCostLineEl, 'on-btn-click')
			.subscribe((event: CustomEvent<TProductCostLineOnBtnClickEventDetail>) => {
				this.onCLick(event);
			});
	}

	private onCLick(event: CustomEvent<TProductCostLineOnBtnClickEventDetail>): void {
		//Если кнопка не активна то ничего не делаем
		if (!event.detail.states.includes(EBtnState.ACTIVE)) {
			return;
		}
		const analyticsData: TProductItem = this.getAnalyticsData();
		window.dataLayer = window.dataLayer || [];
		window.dataLayer.push({
			'event': 'addToCart', // Название ивента для передачи информации в GTM
			'ecommerce': {
				'currencyCode': 'KZT',
				'add': {
					'products': [{
						'name': `${analyticsData.name}`,
						'id': `${analyticsData.id}`,
						'price': `${analyticsData.price}`,
						'brand': `${analyticsData.brand}`,
						'category': `${analyticsData.category}`,
						'quantity': `${analyticsData.quantity}`
					}]
				}
			}
		});
	}

	private getAnalyticsData(): TProductItem {
		let data;
		try {
			data = JSON.parse(this.productCostLineEl.getAttribute(this.DATA_ATTR_ANALYTICS_DATA))
		} catch (e) {
			data = {}
		}
		return data;
	}
}
