import RequestReplacement from "@modules/request-replacement/RequestReplacement";
import {Method} from "axios";
import {ERequestReplacementDataType} from "@modules/request-replacement/model";
import {objectToFormData} from "@helpers/index";

export default class RequestReplacementDataAttr extends RequestReplacement {
	private DATA_ATTR_URL: string = 'data-request-replacement-data-attr-url';
	private DATA_ATTR_METHOD: string = 'data-request-replacement-data-attr-method';
	private DATA_ATTR_DATA: string = 'data-request-replacement-data-attr-data';

	protected getUrl(): string {
		return this.rootEl.getAttribute(this.DATA_ATTR_URL);
	}

	protected getMethod(): Method {
		return this.rootEl.getAttribute(this.DATA_ATTR_METHOD) as Method;
	}

	protected getData(): FormData | object | null {
		let data: object;
		try {
			data = JSON.parse(this.rootEl.getAttribute(this.DATA_ATTR_DATA));
		} catch (e) {
			data = {};
		}

		if (this.getDataType() === ERequestReplacementDataType.FORM_DATA) {
			return objectToFormData(data);
		}

		return data;
	}
}
