import {fromEvent} from "rxjs";
import {TProductCartItem} from "@modules/analytics/model";

declare global {
	interface Window {
		dataLayer: any;
	}
}

export default class AnalyticsClickOnProductCard {
	private productCardEl: HTMLElement;

	private DATA_ATTR_ANALYTICS_DATA: string = 'data-analytics-data';

	constructor(productCardEl: HTMLElement) {
		this.productCardEl = productCardEl;
	}

	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.productCardEl, 'click')
			.subscribe(event => {
				this.onClick(event);
			});
	}

	private onClick(event: Event): void {
		const analyticsData: TProductCartItem = this.getAnalyticsData();
		window.dataLayer = window.dataLayer || [];
		window.dataLayer.push({
			'event': 'productClick', // Название ивента для передачи информации в GTM
			'ecommerce': {
				'click': {
					'actionField': {'list': `${analyticsData.list}`},
					'products': [{
						'name': `${analyticsData.name}`,
						'id': `${analyticsData.id}`,
						'price': `${analyticsData.price}`,
						'brand': `${analyticsData.brand}`,
						'category': `${analyticsData.category}`,
						'position': `${analyticsData.position}`
					}]
				}
			}
		});
	}

	private getAnalyticsData(): TProductCartItem {
		let data;
		try {
			data = JSON.parse(this.productCardEl.getAttribute(this.DATA_ATTR_ANALYTICS_DATA))
		} catch (e) {
			data = {}
		}
		return data;
	}
}
