import CitySelection from "@modules/city/CitySelection";
import {IConfirmCity} from "@modules/city/model";
import Cookie from "@modules/cookie/Cookie";
import {ICookie} from "@modules/cookie/model";
import Modal from "@modules/modal/Modal";
import {IModal} from "@modules/modal/model";
import {fromEvent} from "rxjs";
import Environment from "@modules/env/Environment";
import {EEnvironmentType} from "@modules/env/model";

export default class ConfirmCity implements IConfirmCity {
	private CLASS_FORM: string = 'js-confirmCityForm';
	private CLASS_BTN_OK: string = 'js-confirmCityBtnOk';
	private CLASS_BTN_NO: string = 'js-confirmCityBtnNo';

	private ID_MODAL: string = 'js-confirmCityModal';
	private COOKIE_NAME_CITY: string = 'market_city';

	private static instance: IConfirmCity;

	private modalEl: HTMLDialogElement;
	private formEl: HTMLFormElement;
	private btnOk: HTMLButtonElement;
	private btnNo: HTMLButtonElement;

	private modal: IModal;
	private cookie: ICookie = Cookie.getInstance();

	constructor() {
		if (ConfirmCity.instance) {
			throw new Error("Error: Instantiation failed: Use ConfirmCity.getInstance() instead of new.");
		}
		ConfirmCity.instance = this;
		this.init();
	}

	public static getInstance(): IConfirmCity {
		if (!ConfirmCity.instance) {
			ConfirmCity.instance = new ConfirmCity();
		}
		return this.instance;
	}

	public open(): void {
		this.modal.open();
	}

	public close(): void {
		this.modal.close();
	}

	private init(): void {
		this.modalEl = document.getElementById(this.ID_MODAL) as HTMLDialogElement;
		if (!this.modalEl) {
			// throw new Error('ConfirmCity: modal is not defined !');
			return;
		}

		this.findElement();
		this.modal = new Modal({
			maskClosable: false,
			closable: false,
			element: this.modalEl,
		});
		this.attachEvent();
	}

	private findElement(): void {
		this.formEl = this.modalEl.querySelector(`.${this.CLASS_FORM}`);
		if (!this.formEl) {
			throw new Error('ConfirmCity: form is not defined !');
		}
		this.btnOk = this.modalEl.querySelector(`.${this.CLASS_BTN_OK}`);
		if (!this.btnOk) {
			throw new Error('ConfirmCity: btn ok is not defined !');
		}
		this.btnNo = this.modalEl.querySelector(`.${this.CLASS_BTN_NO}`);
		if (!this.btnNo) {
			throw new Error('ConfirmCity: btn no is not defined !');
		}
	}

	private attachEvent(): void {
		addEventListener("load", () => {
			setTimeout(() => {
				const isProdMode = Environment.getEnv() === EEnvironmentType.PRODUCTION;
				const hasCityCookie = this.cookie.has(this.COOKIE_NAME_CITY);
				if (isProdMode && !hasCityCookie) {
					this.open();
				}
			}, 1000);
		});

		fromEvent(this.btnOk, 'click')
			.subscribe(event => {
				event.preventDefault();
				this.formEl.submit();
			});

		fromEvent(this.btnNo, 'click')
			.subscribe(event => {
				event.preventDefault();
				CitySelection.getInstance().open();
				this.close();
			});
	}
}
