import {isFunction} from "@helpers/index";
import {TDynamicAction} from "@models/Action";
import {TCartItem} from "@models/Cart";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {customElement, html, state, property, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-edit-products-cart-item')
export default class EditProductsCartItemComponent extends WebComponentLite {
	@property({
		type: Object,
		reflect: true,
	})
	public data: TCartItem;

	@property({
		type: Function,
		attribute: 'on-remove'
	})
	public onRemove: TDynamicAction<boolean>;

	@property({
		type: Function,
		attribute: 'on-decrement',
	})
	public onDecrement: TDynamicAction<boolean>;

	@property({
		type: Function,
		attribute: 'on-increment',
	})
	public onIncrement: TDynamicAction<boolean>;

	@state()
	private isRemoving: boolean = false;

	@state()
	private isChanging: boolean = false;

	protected render(): TemplateResult {
		if (!this.data) {
			return null;
		}

		const {availability, product: {img, price, name}} = this.data;

		const notAvailableClass = 'c-edit-products-cart-item_not_available';

		const formatterPrice = MoneyFormatter.format(
			price,
			EMoneyFormatterTypes.SYMBOL_SUPERSCRIPT
		);

		return html`
			<div class="c-edit-products-cart-item ${availability ? '' : notAvailableClass}">
				<div class="c-edit-products-cart-item__img-box">
					<img data-src="${img}"
						 alt="pic"
						 class="c-edit-products-cart-item__img u-shimmer js-lazyLoadImage">
				</div>
				<div class="c-edit-products-cart-item__body">
					<h6 class="c-edit-products-cart-item__name">${name}</h6>
					<p class="c-edit-products-cart-item__price">${unsafeHTML(formatterPrice)}</p>
					${this.getBtnRemoveTemplate()}

					<div class="c-edit-products-cart-item__controls">
						${this.getRangeOrNotAvailableTemplate()}
						${this.getBtnRemoveTemplate()}
					</div>
				</div>
				${this.getRangeOrNotAvailableTemplate()}
				<p class="c-edit-products-cart-item__price">${unsafeHTML(formatterPrice)}</p>
			</div>
		`;
	}

	private getBtnRemoveTemplate(): TemplateResult {
		const removingClass = 'c-edit-products-cart-item__remove_active';

		return html`
			<button
				class="c-edit-products-cart-item__remove ${this.isRemoving ? removingClass : ''}"
				?disabled="${this.isRemoving}"
				@click="${this.handleClickRemove}">
				<span>Удалить</span>
				<i class="o-icon o-icon_garbage-can-filled c-edit-products-cart-item__remove-icon"></i>
				<i class="o-icon o-icon_loader-line-filled c-edit-products-cart-item__remove-loader"></i>
			</button>
		`;
	}

	private getRangeTemplate(): TemplateResult {
		const {quantity, min, max} = this.data;

		return html`
			<div class="c-edit-products-cart-item__range">
				<c-range-count .min="${min}"
							   .max="${max}"
							   .value="${quantity}"
							   .onDecrement="${this.onDecrement}"
							   .onIncrement="${this.onIncrement}"/>
			</div>
		`;
	}

	private async handleClickRemove(event: Event): Promise<void> {
		event.preventDefault();
		if (this.isRemoving) {
			return;
		}
		if (isFunction(this.onRemove)) {
			this.isRemoving = true;
			const res = await this.onRemove();
			if (res) {
				this.onRemove();
			}
			this.isRemoving = false;
		}
	}

	private getRangeOrNotAvailableTemplate(): TemplateResult {
		const {availability} = this.data;

		if (availability) {
			return this.getRangeTemplate();
		}

		return html`
			<p class="c-edit-products-cart-item__not-available">Нет в наличии</p>
		`;
	}
}
