import {ILoader, TLoaderOption} from "@components/loader/model";
import {isHTMLElement, stringToHTML} from "@helpers/index";

export default class Loader implements ILoader {
	private CLASS_BLOCK: string = 'js-loader';

	private CLASS_ACTIVE: string = 'is-active';

	private blockEl: HTMLElement;

	constructor(protected options: TLoaderOption) {
		const defaultOptions: TLoaderOption = {
			element: "create",
		};
		this.options = {
			...defaultOptions,
			...this.options,
		};
		this.init();
	}

	public reInit(options: TLoaderOption): void {
		const defaultOptions: TLoaderOption = {
			element: "create",
		};
		this.options = {
			...defaultOptions,
			...this.options,
		};
		this.init();
	}

	public show(): void {
		this.blockEl.classList.add(this.CLASS_ACTIVE);
	}

	public hide(): void {
		this.blockEl.classList.remove(this.CLASS_ACTIVE);
	}

	public getHTMLElement(): HTMLElement {
		return this.blockEl;
	}

	private create(): HTMLElement {
		const template: string = this.getTemplate();
		return stringToHTML(template);
	}

	private getTemplate(): string {
		const modifications: string = this.options.modifications
			?.reduce((acc, val) => acc + ` ${val}`, '') || ''
		return `
		  <div class="c-loader ${this.CLASS_BLOCK} ${modifications}">
			<div class="c-loader__content">
			  <div class="c-loader__icon"></div>
			</div>
		  </div>
    	`;
	}

	private init(): void {
		this.initElements();
	}

	private initElements(): void {
		if (this.options.element === "create") {
			this.blockEl = this.create();
		} else {
			if (!isHTMLElement(this.options.element)) {
				throw new Error('Element is for HTMLElement !');
			}
			this.blockEl = this.options.element;
		}
	}
}
