import ConfirmDialogComponent from "@components/confirm-dialog/ConfirmDialogComponent";
import {fromEvent, merge} from "rxjs";
import HttpRequest from "@modules/http-request/HttpRequest";
import {IBtn} from "@modules/btn/model";
import Btn from "@modules/btn/Btn";
import {Method} from "axios";
import EditProductsCartComponent from "@components/edit-products-cart/EditProductsCartComponent";

export type CartValidateForUnavailableProductsResponse = {
	hasUnavailableProducts: boolean
}

export default class CartValidateForUnavailableProducts {
	private CLASS_EDIT_PRODUCTS: string = 'js-cartEditProducts';
	private CLASS_CART_FORM: string = 'js-cartForm';
	private CLASS_CONFIRM_DIALOG_SOME_PRODUCT_NOT_AVAILABLE = 'js-cartConfirmDialogSomeProductNotAvailable';

	private DATA_ATTR_URL_VALIDATE: string = 'data-url-validate-for-unavailable-products';
	private DATA_ATTR_METHOD_VALIDATE: string = 'data-method-validate-for-unavailable-products';

	private CLASS_BTN: string = 'js-cartValidateForUnavailableProducts';

	private btnEl: HTMLButtonElement;
	private confirmDialogSomeProductNotAvailableEl: ConfirmDialogComponent;
	private formEl: HTMLFormElement;
	private httpService: HttpRequest;
	private isValidating: boolean = false;
	private btn: IBtn;

	public init(): void {
		this.btnEl = document.querySelector(`.${this.CLASS_BTN}`);
		this.formEl = document.querySelector(`.${this.CLASS_CART_FORM}`);
		this.confirmDialogSomeProductNotAvailableEl = document.querySelector(`.${this.CLASS_CONFIRM_DIALOG_SOME_PRODUCT_NOT_AVAILABLE}`);

		if (!this.btnEl || !this.formEl || !this.confirmDialogSomeProductNotAvailableEl) {
			return;
		}

		this.httpService = new HttpRequest();
		this.btn = new Btn(this.btnEl);
		this.attachEvent();
	}

	public attachEvent(): void {
		merge(
			fromEvent(this.btnEl, 'click'),
			fromEvent(this.formEl, 'submit'),
		)
			.subscribe(event => {
				event.preventDefault();
				this.requestValidate();
			});

		this.confirmDialogSomeProductNotAvailableEl.onConfirm = () => {
			this.formEl.submit();
			return true;
		}
		this.confirmDialogSomeProductNotAvailableEl.onCancel = () => {
			this.handleCancelConfirm();
			return true;
		}
	}

	private requestValidate(): void {
		if (this.isValidating) {
			return;
		}
		this.isValidating = true;
		this.btn.addLoader();
		const url = this.formEl.getAttribute(this.DATA_ATTR_URL_VALIDATE);
		const method = this.formEl.getAttribute(this.DATA_ATTR_METHOD_VALIDATE);
		const formData = new FormData(this.formEl);

		this.httpService
			.request<CartValidateForUnavailableProductsResponse>({
				url: url,
				method: method as Method,
				data: formData,
			})
			.then(response => {
				this.handleResponse(response.data.data);
			})
			.catch(reject => {
				//handle error
			})
			.finally(() => {
				this.isValidating = false;
				this.btn.removeLoader();
			});
	}

	private handleResponse(data: CartValidateForUnavailableProductsResponse): void {
		const {hasUnavailableProducts} = data;
		if (hasUnavailableProducts) {
			this.confirmDialogSomeProductNotAvailableEl.open();
		} else {
			this.formEl.submit();
		}
	}

	private handleCancelConfirm(): void {
		const editProductEl: EditProductsCartComponent = document.querySelector(`.${this.CLASS_EDIT_PRODUCTS}`);
		if (editProductEl) {
			editProductEl.updateData();
		}
	}
}
