import SelectTariffComponent from "@components/select-tariff/SelectTariffComponent";
import CartCostLine from "@modules/cart/CartCostLine";
import {fromEvent} from "rxjs";
import {TSelectContractTariffSelected} from "@components/select-tariff/model";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {ECartCostLineNames} from "@modules/cart/model";
import {EPriceRequestTypes} from "@models/Tariff";
import LoanCalculator from "@scenario/LoanCalculator";

export default class UpdateCartCostOnTariffStep {
	private CLASS_COST_LINE: string = 'js-cartCostLine';
	private CLASS_SELECT_TARIFF: string = 'js-cartSelectTariff';

	private selectTariffEl: SelectTariffComponent;
	private costLineEl: HTMLElement;
	private cartCostLine: CartCostLine;
	private loanCalculator: LoanCalculator;

	public init(): void {
		this.selectTariffEl = document.querySelector(`.${this.CLASS_SELECT_TARIFF}`);
		this.costLineEl = document.querySelector(`.${this.CLASS_COST_LINE}`);

		if (!this.selectTariffEl || !this.costLineEl) {
			return;
		}
		this.cartCostLine = new CartCostLine(this.costLineEl);
		this.loanCalculator = new LoanCalculator()
		this.loanCalculator.calculatePricesRequestConfig = this.selectTariffEl.calculatePricesRequestConfig;
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent<CustomEvent>(this.loanCalculator, 'on-response-calculate-prices')
			.subscribe(data => {
				this.updatePricePerMonth(parseFloat(data.detail?.price) || 0);
			});
		fromEvent<CustomEvent<TSelectContractTariffSelected>>(this.selectTariffEl, 'on-change')
			.subscribe(data => {
				//TODO: call this only if loan
				this.loanCalculator.calculatePrices(data.detail.tariff?.price, EPriceRequestTypes.TARIFF);
				//TODO: call this only if full price
				this.updateCostLine(data.detail);
			});
		this.loanCalculator.status
			.asObservable()
			.subscribe(status => {
				this.cartCostLine.handleLoader(status);
			});
	}

	private updateCostLine(data: TSelectContractTariffSelected): void {
		// this.updatePricePerMonth(parseFloat(data?.tariff?.pricePerMonth) || 0);
		this.updateFullPrice(parseFloat(data?.tariff?.price) || 0);
	}

	private updatePricePerMonth(value: number): void {
		const initValueStr: string = this.cartCostLine.getItemInitialValue(ECartCostLineNames.PRICE_PER_MONTH);
		const initValue: number = initValueStr ? parseFloat(initValueStr) : 0;
		const pricePerMonth = MoneyFormatter.format(
			// (initValue + value).toString(),
			value.toString(),
			EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT,
		);
		this.cartCostLine.updateItemValue(ECartCostLineNames.PRICE_PER_MONTH, pricePerMonth);
	}

	private updateFullPrice(value: number): void {
		const initValueStr: string = this.cartCostLine.getItemInitialValue(ECartCostLineNames.FULL_PRICE);
		const initValue: number = initValueStr ? parseFloat(initValueStr) : 0;
		const price = MoneyFormatter.format(
			(initValue + value).toString(),
			EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT,
		);
		this.cartCostLine.updateItemValue(ECartCostLineNames.FULL_PRICE, price);
	}
}
