import {TField} from "@modules/field/model";

export type TFormControlsOptions = {
	onChange?: (field) => void;
}

export type TFormControlsChangeSchema = Record<string, unknown>;

export default class FormControlsChange {
	private dirty: boolean = false;
	private formSchema: TFormControlsChangeSchema;

	constructor(private form: HTMLFormElement, private options?: TFormControlsOptions) {
		if (!this.form) {
			throw new Error('Form is not defined !');
		}
		this.formSchema = this.createSchema(this.form);
		this.attachEvent();
	}

	public hasChanges(): boolean {
		return this.dirty;
	}

	private createSchema(form: HTMLFormElement): TFormControlsChangeSchema {
		const schema: TFormControlsChangeSchema = {};
		const formData: FormData = new FormData(form);
		const toggles: Array<HTMLInputElement> = Array.from(this.form.querySelectorAll(`[type="checkbox"], [type="radio"]`));
		toggles.forEach((toggle) => {
			schema[`${toggle.name}:${toggle.value}`] = toggle.checked;
		});
		formData.forEach((value, key) => {
			const element = this.form.querySelector(`[name="${key}"]`) as HTMLInputElement
			if (this.isToggle(element)) {
				return;
			}
			schema[key] = value;
		});
		return schema;
	}

	private isToggle(element: HTMLInputElement): boolean {
		const type = element.type.toLowerCase();
		return type === 'checkbox' || type === 'radio';
	}

	private checkDirty() {
		const currentSchema: TFormControlsChangeSchema = this.createSchema(this.form);
		this.dirty = false;
		Object.keys(currentSchema).forEach((key) => {
			if (this.formSchema[key] != currentSchema[key]) {
				this.dirty = true;
			}
		});
	}

	private attachEvent(): void {
		const filed: Array<TField> = Array.from(this.form.querySelectorAll(`INPUT, TEXTAREA, SELECT`));
		this.createInstance(filed);
	}

	private createInstance(fields: Array<TField>): void {
		fields.forEach((filed) => {
			this.attachChange(filed);
		});
	}

	private attachChange(field: TField): void {
		field.addEventListener('change', () => {
			this.checkDirty();
			this.options?.onChange?.(field);
		});
	}
}
