import {IBtn} from "@modules/btn/model";
import {fromEvent, merge} from "rxjs";
import Btn from "@modules/btn/Btn";
import {EComponentStatuses} from "@models/Status";
import SelectTariffComponent from "@components/select-tariff/SelectTariffComponent";

export default class UpdateBtnCartOnTariffStep {
	private CLASS_SELECT_TARIFF: string = 'js-cartSelectTariff';

	private CLASS_BTN: string = 'js-cartSubmitter';

	private selectTariffEl: SelectTariffComponent;
	private btnEl: HTMLButtonElement;

	private btn: IBtn;

	public init(): void {
		this.selectTariffEl = document.querySelector(`.${this.CLASS_SELECT_TARIFF}`);
		this.btnEl = document.querySelector(`.${this.CLASS_BTN}`);

		if (!this.btnEl || !this.selectTariffEl) {
			return;
		}
		this.btn = new Btn(this.btnEl);
		this.attachEvent();
	}

	private attachEvent(): void {
		merge(
			fromEvent(this.selectTariffEl, 'on-status-change'),
		)
			.subscribe(() => {
				this.updateBtnAvailable();
			});
	}

	private updateBtnAvailable(): void {
		const selectTariffStatus = this.selectTariffEl.status.getValue();

		if (selectTariffStatus === EComponentStatuses.READY) {
			this.btn.enable();
		} else {
			this.btn.disable();
		}
	}
}
