import {ECartCostLineNames} from "@modules/cart/model";
import {EComponentStatuses} from "@models/Status";

export type TCartCostLineItem = {
	rootEl: HTMLElement
	titleEl: HTMLElement
	valueEl: HTMLElement
}

export default class CartCostLine {
	private CLASS_ITEM: string = 'js-cartCostLineItem';
	private CLASS_ITEM_TITLE: string = 'js-cartCostLineItemTitle';
	private CLASS_ITEM_VALUE: string = 'js-cartCostLineItemValue';

	private DATA_ATTR_ITEM_NAME: string = 'data-name';
	private DATA_ATTR_ITEM_EL_INITIAL_VALUE: string = 'data-initial-value';

	private el: HTMLElement;

	constructor(el: HTMLElement) {
		this.el = el;
	}

	public updateItemValue(name: ECartCostLineNames | string, value: string): void {
		const item = this.getItemByName(name);
		if (!item) {
			console.error(`CartCostLine: item with name ${name} is not defined !`);
			return;
		}
		item.valueEl.innerHTML = value;
	}

	public getItemInitialValue(name: ECartCostLineNames): string {
		const item = this.getItemByName(name);
		if (!item) {
			return null;
		}
		return item.valueEl.getAttribute(this.DATA_ATTR_ITEM_EL_INITIAL_VALUE) || null;
	}

	public handleLoader(status: EComponentStatuses): void {
		if (status === EComponentStatuses.LOADING) {
			const item = this.getItemByName(ECartCostLineNames.PRICE_PER_MONTH);
			if (!item) {
				console.error(`CartCostLine: item with name ${name} is not defined !`);
				return;
			}
			item.valueEl.innerHTML = '<div class="c-loader_ghost c-loader_md">\n' +
				'                                        <div class="c-loader__content">\n' +
				'                                            <div class="c-loader__icon"></div>\n' +
				'                                        </div>\n' +
				'                                    </div>';
		}
	}

	private getItemByName(name: string): TCartCostLineItem {
		const rootEl: HTMLElement = this.el.querySelector(`.${this.CLASS_ITEM}[${this.DATA_ATTR_ITEM_NAME}=${name}]`);

		if (!rootEl) {
			return null;
		}

		const titleEl: HTMLElement = rootEl.querySelector(`.${this.CLASS_ITEM_TITLE}`);
		const valueEl: HTMLElement = rootEl.querySelector(`.${this.CLASS_ITEM_VALUE}`);

		return {
			rootEl,
			titleEl,
			valueEl,
		}
	}
}
