import {EBtnSize, EBtnType} from "@components/btn/model";
import CodeInputComponent from "@components/code-input/CodeInputComponent";
import {
	EValidateSMSCodeSlots,
	EValidateSMSCodeStatuses,
	TValidateSMSCodeBootstrapData,
	TValidateSMSCodeEventDetail,
	TValidateSMSCodeResponse
} from "@components/validate-sms-code/model";
import {isNotNullOrUndefined} from "@helpers/index";
import {TSMSValidationGetResponse, TSMSValidationValidateReject} from "@models/SMSValidation";
import HttpRequest from "@modules/http-request/HttpRequest";
import {TRequestResponse} from "@modules/http-request/model";
import Mask, {EMaskTypes} from "@modules/mask/Mask";
import {ITimer} from "@modules/timer/model";
import Timer from "@modules/timer/Timer";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {AxiosError, Method} from "axios";
import {customElement, html, state, property, PropertyValues, query, TemplateResult} from "lit-element";
import {unsafeHTML} from "lit-html/directives/unsafe-html";

@customElement('c-validate-sms-code')
export default class ValidateSMSCodeComponent extends WebComponentLite {
	@property({
		type: String,
		attribute: 'phone-number'
	})
	phoneNumber: string = undefined;

	@property({
		type: Boolean,
	})
	public confirmed: boolean = false;

	@property({
		type: String,
		attribute: 'form-code-name'
	})
	formCodeName: string = 'code';

	@property({
		type: String,
		attribute: 'form-phone-number-name'
	})
	formPhoneNumberName: string = 'phoneNumber';

	@property({
		type: Object,
		attribute: 'bootstrap-data'
	})
	bootstrapData: TValidateSMSCodeBootstrapData;

	@property({
		type: Boolean,
		attribute: 'request-on-creation',
	})
	public requestOnCreation: boolean = false;

	@query('[validate-sms-code-field]')
	private codeInput: CodeInputComponent;

	@state()
	private attemptsCountSend: number = 0;

	@state()
	private attemptsCountValidate: number = 0;

	@state()
	private timerOutput: string = '00:00';

	@state()
	private isValidationRequesting: boolean = false;

	@state()
	private isSMSRequesting: boolean = false;

	@state()
	private isTimerActive: boolean = false;

	protected httpService: HttpRequest = new HttpRequest();

	private formValidateEl: HTMLFormElement;
	private formGetEl: HTMLFormElement;
	private timer: ITimer;

	constructor() {
		super();
		this.timer = new Timer();
		this.timer
			.getStream()
			.subscribe(stream => {
				this.isTimerActive = true;
				this.timerOutput = stream.fillTime;

				if (stream.second === 0) {
					this.isTimerActive = false;
					this.timerEnded();
				}
			});
	}

	protected firstUpdated(_changedProperties: PropertyValues) {
		super.firstUpdated(_changedProperties);
		this.initSlots();
		this.initBootstrapData();
		if (this.requestOnCreation) {
			this.getSMSCode();
		}
		this.focusInputField();
	}

	private initSlots(): void {
		this.formValidateEl = this.getSlot(EValidateSMSCodeSlots.FORM_VALIDATE) as HTMLFormElement;
		if (!this.formValidateEl) {
			throw new Error(`ValidateSMSCodeComponent: slot with name '${EValidateSMSCodeSlots.FORM_VALIDATE}' is not defined !`);
		}
		this.formGetEl = this.getSlot(EValidateSMSCodeSlots.FORM_GET) as HTMLFormElement;
		if (!this.formGetEl) {
			throw new Error(`ValidateSMSCodeComponent: slot with name '${EValidateSMSCodeSlots.FORM_GET}' is not defined !`);
		}
	}

	protected render(): TemplateResult {
		return html`
			<div class="c-validate-sms-code">
				<c-code-input validate-sms-code-field
							  name="code"
							  @on-change="${this.handleFieldChange}">
				</c-code-input>

				<div class="u-text-center c-validate-sms-code__message">
					<p class="c-validate-sms-code__timer">
						На номер ${this.getFormattedPhoneNumber()} был отправлен код.
						<br>
						Повторно можно отправить через: ${this.timerOutput}
					</p>

					<p class="c-validate-sms-code__count">
						${unsafeHTML(this.getCountMessage())}
					</p>
				</div>

				${this.isVisibleBtnRetry() ?
					html`
						<c-btn class="c-validate-sms-code__btn-retry"
							   type="${EBtnType.PRIMARY}"
							   size="${EBtnSize.MD}"
							   ?loading="${this.isLoadingBtnRetry()}"
							   ?disabled="${this.isDisableBtnRetry()}"
							   @click="${this.handleClickBtnRetry}">
							Выслать смс повторно
						</c-btn>
					` :
					null
				}

				<div class="u-d-none">
					${this.formValidateEl}
					${this.formGetEl}
				</div>
			</div>
		`;
	}

	private getCountMessage(): string {
		const message: Array<string> = [];
		if (isNotNullOrUndefined(this.attemptsCountSend)) {
			message.push(`Осталось попыток отправки смс кода: ${this.attemptsCountSend}`)
		}
		if (isNotNullOrUndefined(this.attemptsCountValidate)) {
			message.push(`Осталось попыток проверки кода: ${this.attemptsCountValidate}`)
		}
		return message.reverse().join('<br>');
	}

	private getFormattedPhoneNumber(): string {
		if (!this.phoneNumber) {
			throw new Error(`ValidateSMSCodeComponent: phoneNumber is not defined !`);
		}
		return Mask.getFormattedValue(this.phoneNumber, EMaskTypes.PHONE);
	}

	private handleClickBtnRetry() {
		this.codeInput.value = '';
		this.codeInput.focus();
		this.getSMSCode();
	}

	private getSMSCode(): void {
		if (this.isLoadingBtnRetry() || this.isDisableBtnRetry()) {
			return;
		}
		this.isSMSRequesting = true;
		const formData: FormData = new FormData(this.formGetEl);
		formData.append(this.formPhoneNumberName, this.phoneNumber);
		formData.append('confirmed', String(this.confirmed));
		this.httpService
			.request<TSMSValidationGetResponse>({
				url: this.formGetEl.action,
				method: this.formGetEl.method as Method,
				data: formData,
			})
			.then(response => {
				const data = response.data.data;
				this.updateAttemptsCountSend(data.attemptsCountSend)
				this.updateAttemptsCountValidate(data.attemptsCountValidate);
				this.startTimer(data.duration);
			})
			.catch((reject: AxiosError) => {
				//TODO: handle error
			})
			.finally(() => {
				this.isSMSRequesting = false;
			});
	}

	private validateSMSCode(): void {
		if (this.isValidationRequesting) {
			return;
		}
		this.isValidationRequesting = true;
		const formData: FormData = new FormData(this.formValidateEl);
		formData.append(this.formCodeName, this.codeInput.value);
		formData.append(this.formPhoneNumberName, this.phoneNumber);
		this.httpService
			.request<TValidateSMSCodeResponse>({
				url: this.formValidateEl.action,
				method: this.formValidateEl.method as Method,
				data: formData,
			})
			.then(response => {
				this.emitEvent({
					status: EValidateSMSCodeStatuses.VALIDATION_SUCCESSFUL,
					operator: response.data.data.operator,
					tariffConnection: response.data.data.tariffConnection,
				});
			})
			.catch((reject: AxiosError<TRequestResponse<TSMSValidationValidateReject>>) => {
				const data = reject.response.data.data;
				this.updateAttemptsCountSend(data.attemptsCountSend);
				this.updateAttemptsCountValidate(data.attemptsCountValidate);
				this.emitEvent({
					status: EValidateSMSCodeStatuses.VALIDATION_ERROR,
					operator: null,
					tariffConnection: null,
				});
			})
			.finally(() => {
				this.isValidationRequesting = false;
			});
	}

	private handleFieldChange(event: CustomEvent): void {
		if (event.detail.isComplete) {
			this.validateSMSCode();
		}
	}

	private startTimer(value: number): void {
		if (value > 0) {
			this.isTimerActive = true;
		}
		this.timer.start(value);
	}

	private timerEnded(): void {

	}

	private updateAttemptsCountSend(value: number): void {
		this.attemptsCountSend = value;
		if (isNotNullOrUndefined(value) && value <= 0) {
			this.emitEvent({
				status: EValidateSMSCodeStatuses.OUT_SMS_REQUESTS,
				operator: null,
				tariffConnection: null,
			});
		}
	}

	private updateAttemptsCountValidate(value: number): void {
		this.attemptsCountValidate = value;
		if (isNotNullOrUndefined(value) && value <= 0) {
			this.emitEvent({
				status: EValidateSMSCodeStatuses.OUT_VALIDATION_REQUESTS,
				operator: null,
				tariffConnection: null,
			});
		}
	}

	private initBootstrapData(): void {
		if (!this.bootstrapData) {
			return;
		}
		if (isNotNullOrUndefined(this.bootstrapData.timer)) {
			this.startTimer(this.bootstrapData.timer)
		}
		this.updateAttemptsCountSend(this.bootstrapData.attemptsCountSend);
		this.updateAttemptsCountValidate(this.bootstrapData.attemptsCountValidate);
	}

	private focusInputField(): void {
		this.codeInput
			.updateComplete
			.then(() => {
				this.codeInput.focus();
			});
	}

	private emitEvent(data: TValidateSMSCodeEventDetail): void {
		const event = new CustomEvent('on-status-change', {
			detail: data,
		});
		this.dispatchEvent(event);
	}

	private isVisibleBtnRetry(): boolean {
		if (this.attemptsCountSend <= 0 || this.isTimerActive) {
			return false;
		}
		return true;
	}

	private isDisableBtnRetry(): boolean {
		if (this.isSMSRequesting || this.isValidationRequesting) {
			return true;
		}
		return false;
	}

	private isLoadingBtnRetry(): boolean {
		if (this.isSMSRequesting) {
			return true;
		}
		return false;
	}
}
