import {EBtnSize, EBtnType} from "@components/btn/model";
import CheckboxComponent from "@components/checkbox/CheckboxComponent";
import EnterPhoneNumberComponent from "@components/enter-phone-number/EnterPhoneNumberComponent";
import {EEnterPhoneNumberWithConfirmationSlots} from "@components/enter-phone-number/model";
import {customElement, html, property, query, TemplateResult} from "lit-element";

@customElement('c-enter-phone-number-with-confirmation')
export default class EnterPhoneNumberWithConfirmationComponent extends EnterPhoneNumberComponent {
	@property({
		type: Boolean,
		attribute: 'confirmation-checked',
		reflect: true,
	})
	public confirmationChecked: boolean = false;

	@property({
		type: String,
		attribute: 'confirmation-checkbox-name',
	})
	public confirmationCheckboxName: string = 'confirmed';

	@property({
		type: String,
		attribute: 'confirmation-checkbox-value',
	})
	public confirmationCheckboxValue: string = 'confirmed';

	@query('[enter-phone-number-with-confirmation-confirm-checkbox]')
	private confirmationCheckboxField: CheckboxComponent;

	protected render(): unknown {
		return html`
			<div class="c-enter-phone-number c-enter-phone-number_with-confirmation">
				${this.getPhoneNumberFieldTemplate()}
				${this.getConfirmationCheckboxTemplate()}
				${this.getActionBtn()}
			</div>
		`;
	}

	protected getConfirmationCheckboxTemplate(): TemplateResult {
		const confirmContent = this.getSlot(EEnterPhoneNumberWithConfirmationSlots.CONFIRMATION_CONTENT);

		return html`
			<div class="c-enter-phone-number__confirmation-checkbox">
				<c-checkbox enter-phone-number-with-confirmation-confirm-checkbox
							.name="${this.confirmationCheckboxName}"
							.value="${this.confirmationCheckboxValue}"
							?checked="${this.confirmationChecked}"
							@on-change="${this.handleChangeConfirmationCheckbox}">
					<div class="u-ml-10">
						${confirmContent}
					</div>
				</c-checkbox>
			</div>
		`;
	}

	protected getActionBtn(): TemplateResult {
		return html`
			<footer class="c-enter-phone-number__footer">
				<c-btn class="c-enter-phone-number__btn"
					   .type="${this.isDisabledActionBtn() ? EBtnType.SECONDARY : EBtnType.PRIMARY}"
					   .size="${EBtnSize.MD}"
					   ?loading="${this.isLoadingActionBtn()}"
					   ?disabled="${this.isDisabledActionBtn()}"
					   @click="${this.handleClickActionBtn}">
					${this.actionBtnText}
				</c-btn>
				${!this.confirmationChecked
					? html`
						<p class="c-enter-phone-number__hint">
							Что бы получить код, необходимо принять согласие
						</p>
					`
					: null
				}
			</footer>
		`;
	}

	protected isDisabledActionBtn(): boolean {
		return super.isDisabledActionBtn() || !this.confirmationChecked;
	}

	private handleChangeConfirmationCheckbox(event: Event): void {
		this.confirmationChecked = this.confirmationCheckboxField.checked;
	}
}
