import {stringToHTML, strToBoolean} from "@helpers/index";
import HttpRequest from "@modules/http-request/HttpRequest";
import {Method} from "axios";
import {EVisibility, IVisibility} from "@modules/visibility/model";
import Visibility from "@modules/visibility/Visibility";
import {
	ERequestReplacementDataType,
	IRequestReplacement,
	TRequestReplacementStreamOnResponse
} from "@modules/request-replacement/model";
import {Observable, Subject} from "rxjs";
import {ERequestResponseStatus} from "@modules/http-request/model";
import LoaderComponent from "@components/loader/LoaderComponent";

export default class RequestReplacement implements IRequestReplacement {
	protected CLASS_BLOCK: string = 'js-requestReplacement';
	protected CLASS_BLOCK_REPLACEMENT: string = 'js-requestReplacementBlockReplacement';
	protected CLASS_LOADER: string = 'js-requestReplacementLoader';
	protected CLASS_HIDDEN_ELEMENT_IS_LOADING: string = 'js-requestReplacementHiddenElementIsLoading';

	protected DATA_ATTR_REQUEST_ON_START: string = 'data-request-replacement-request-on-start';
	private DATA_ATTR_DATA_TYPE: string = 'data-request-replacement-data-type';

	protected rootEl: HTMLElement;
	protected replacementEl: HTMLElement;
	protected loaderEl: LoaderComponent;

	private isLoading: boolean = false;

	private httpRequest: HttpRequest;
	private visibility: IVisibility;

	private eventChange: Subject<TRequestReplacementStreamOnResponse> = new Subject();

	constructor(rootEl: HTMLElement) {
		this.rootEl = rootEl;
		this.findElements();
		this.httpRequest = new HttpRequest();
		this.visibility = new Visibility();
	}

	public init(): void {
		if (this.isRequestOnStart()) {
			this.request();
		}
	}

	public getStreamOnResponse(): Observable<TRequestReplacementStreamOnResponse> {
		return this.eventChange.asObservable()
	}

	public getDataType(): ERequestReplacementDataType | null {
		return this.rootEl.getAttribute(this.DATA_ATTR_DATA_TYPE) as ERequestReplacementDataType;
	}

	public request(): void {
		if (this.isLoading) {
			return;
		}
		this.isLoading = true;
		this.loaderEl.active = true;
		this.updateVisibilityElementsOnLoading(EVisibility.HIDDEN);

		this.httpRequest.requestHTML({
			url: this.getUrl(),
			method: this.getMethod(),
			data: this.getData(),
		})
			.then((response) => {
				this.replaceContent(stringToHTML(response.data));
				this.findElements();
				this.eventChange.next({
					status: ERequestResponseStatus.SUCCESS,
				});
			})
			.catch(() => {
				this.eventChange.next({
					status: ERequestResponseStatus.ERROR,
				});
			})
			.finally(() => {
				this.isLoading = false;
				this.loaderEl.active = false;
				this.updateVisibilityElementsOnLoading(EVisibility.VISIBLE);
			});
	}

	protected getUrl(): string {
		return '';
	}

	protected getData(): FormData | object | null {
		return null;
	}

	protected getMethod(): Method {
		return 'GET';
	}

	protected findElements(): void {
		this.replacementEl = this.getBlockReplacementEl();
		this.loaderEl = this.rootEl.querySelector(`.${this.CLASS_LOADER}`);
	}

	private replaceContent(contentEl: HTMLElement): void {
		const blockReplacementEl: HTMLElement = this.getBlockReplacementEl();
		blockReplacementEl.replaceWith(contentEl);
	}

	private getBlockReplacementEl(): HTMLElement {
		if (this.rootEl.classList.contains(this.CLASS_BLOCK_REPLACEMENT)) {
			return this.rootEl;
		}
		return this.rootEl.querySelector(`.${this.CLASS_BLOCK_REPLACEMENT}`);
	}

	private updateVisibilityElementsOnLoading(visibility: EVisibility): void {
		const items: Array<HTMLElement> = this.getVisibilityElements();
		if (visibility === EVisibility.VISIBLE) {
			this.visibility.show(items);
		} else {
			this.visibility.hide(items);
		}
	}

	private getVisibilityElements(): Array<HTMLElement> {
		return Array.from(
			this.rootEl.getElementsByClassName(this.CLASS_HIDDEN_ELEMENT_IS_LOADING)
		) as Array<HTMLElement>;
	}

	private isRequestOnStart(): boolean {
		return strToBoolean(this.rootEl.getAttribute(this.DATA_ATTR_REQUEST_ON_START));
	}
}
