import {fromEvent} from "rxjs";
import {filter} from "rxjs/operators";
import {getDefinitionElement, isDefinitionElementEvent} from "@helpers/index";

export default class ClearInput {
	private CLASS_HANDLER: string = 'js-clearInput'
	private CLASS_FIELD: string = 'js-field';

	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		const selectorHandler: string = `.${this.CLASS_HANDLER}`;
		fromEvent(document, 'click')
			.pipe(
				filter(event => isDefinitionElementEvent(event, selectorHandler)),
			)
			.subscribe(event => {
				event.preventDefault();
				const btn: HTMLButtonElement = getDefinitionElement(event.target as HTMLElement, selectorHandler)
				const field: HTMLElement = btn.closest(`.${this.CLASS_FIELD}`);
				const input: HTMLInputElement = field.querySelector('INPUT');
				input.value = '';
				input.dispatchEvent(new Event('input'));
			})
	}
}
