import {EDropdownState, IDropdown, TDropdownOption} from "@modules/dropdown/model";
import {EAnimationType, IAnimation} from "@modules/animation/model";
import {fromEvent} from "rxjs";
import Animation from "@modules/animation/Animation";
import {getDefinitionElement} from "@helpers/index";
import CreateEvent from "@modules/event/Event";
import GlobalClick from "@modules/global-click/GlobalClick";
import {filter} from "rxjs/operators";

export default class Dropdown implements IDropdown {
	private blockEl: HTMLElement;
	private containerEl: HTMLElement;

	private option: TDropdownOption;

	private animation: IAnimation;

	constructor(handlerEl: HTMLElement, options?: TDropdownOption) {
		this.blockEl = handlerEl;
		this.option = {
			...this.getDefaultOption(),
			...options,
		};
		this.init();
	}

	private init(): void {
		this.initElements();
		this.initAnimation();
		this.attachEvent();
	}

	public open(): void {
		this.animation.show();
	}

	public close(): void {
		this.animation.hide();
	}

	public getState(): EDropdownState {
		const hasOpenClass: boolean = this.blockEl.classList.contains(this.option.stateClasses.open);
		return hasOpenClass ? EDropdownState.OPEN : EDropdownState.CLOSED;
	}

	public toggle(): void {
		if (this.getState() == EDropdownState.OPEN) {
			this.close();
		} else {
			this.open();
		}
	}

	private getDefaultOption(): TDropdownOption {
		return {
			closeAnyClick: true,
			animation: {
				name: 'o-animation-fade-down',
				type: EAnimationType.ANIMATION,
			},
			classes: {
				block: 'js-dropdown',
				handler: 'js-dropdownHandler',
				container: 'js-dropdownContainer',
				item: 'js-dropdownItem',
			},
			stateClasses: {
				open: 'is-open',
				opens: 'is-opens',
				close: 'is-close',
				closes: 'is-closes',
			},
		}
	}

	private attachEvent(): void {
		fromEvent(this.blockEl, 'click')
			.subscribe(event => {
				const handler: HTMLElement = getDefinitionElement(event.target as HTMLElement, `.${this.option.classes.handler}`);
				const item: HTMLElement = getDefinitionElement(event.target as HTMLElement, `.${this.option.classes.item}`);
				if (handler) {
					this.toggle();
				}

				if (item) {
					if (this.option?.hooks?.onItemClick) {
						this.option?.hooks?.onItemClick(item);
					}
				}
			})

		GlobalClick
			.getInstance()
			.getStreamOnClick()
			.pipe(
				filter(event => getDefinitionElement(event.target as HTMLElement, `.${this.option.classes.block}`) !== this.blockEl),
			)
			.subscribe(() => {
				if (!this.option.closeAnyClick) {
					return;
				}

				if (this.getState() === EDropdownState.OPEN) {
					this.toggle();
				}
			})
	}

	private initAnimation(): void {
		this.animation = new Animation(this.containerEl, {
			...this.option.animation,
			beforeEnter: () => {
				this.clearClassEntering();
				this.setClassEntering(EDropdownState.OPEN);
			},
			beforeLeave: () => {
				this.clearClassEntering();
			},
			afterEnter: () => {
				this.clearClassEntering();
				this.clearClassState();
				this.dispatchEvent(EDropdownState.OPEN);
				this.setClassState(EDropdownState.OPEN);
				this.setClassEntering(EDropdownState.OPEN);

				if (this.option?.hooks?.afterOpen) {
					this.option.hooks.afterOpen();
				}
			},
			afterLeave: () => {
				this.clearClassEntering();
				this.clearClassState();
				this.dispatchEvent(EDropdownState.CLOSED);
				this.setClassState(EDropdownState.CLOSED);

				if (this.option?.hooks?.afterClose) {
					this.option.hooks.afterClose();
				}
			}
		})
	}

	private initElements(): void {
		this.findElements();
	}

	private findElements(): void {
		this.containerEl = this.blockEl.querySelector(`.${this.option.classes.container}`);
	}

	private clearClassState(): void {
		this.blockEl.classList.remove(this.option.stateClasses.open);
		this.blockEl.classList.remove(this.option.stateClasses.close);
	}

	private clearClassEntering(): void {
		this.blockEl.classList.remove(this.option.stateClasses.opens);
		this.blockEl.classList.remove(this.option.stateClasses.closes);
	}

	private setClassState(state: EDropdownState): void {
		switch (state) {
			case EDropdownState.OPEN:
				this.blockEl.classList.add(this.option.stateClasses.open);
				return;
			case EDropdownState.CLOSED:
				this.blockEl.classList.add(this.option.stateClasses.close);
				return;
		}
	}

	private setClassEntering(state: EDropdownState): void {
		switch (state) {
			case EDropdownState.OPEN:
				this.blockEl.classList.add(this.option.stateClasses.opens);
				return;
			case EDropdownState.CLOSED:
				this.blockEl.classList.add(this.option.stateClasses.closes);
				return;
		}
	}

	private dispatchEvent(state: EDropdownState): boolean {
		let event: Event;

		if (state == EDropdownState.OPEN) {
			event = new CreateEvent('open').createCustom();
		} else {
			event = new CreateEvent('closed').createCustom();
		}

		const canceled: boolean = !this.blockEl.dispatchEvent(event);
		return !canceled;
	}
}
