import {IBtn, TBtnOption} from "@modules/btn/model";

export default class Btn implements IBtn {
	constructor(private btnEl: HTMLButtonElement, private options?: TBtnOption) {
		const defaultOptions: TBtnOption = {
			classes: {
				loading: 'is-loading',
				disabled: 'is-disabled',
			}
		};
		this.options = {
			...defaultOptions,
			...this.options,
		};
	}

	public addLoader(): this {
		this.btnEl.classList.add(this.options.classes.loading);
		return this;
	}

	public removeLoader(): this {
		this.btnEl.classList.remove(this.options.classes.loading);
		return this;
	}

	public disable(): this {
		this.btnEl.disabled = true;
		return this;
	}

	public fakeDisable(): this {
		this.btnEl.classList.add(this.options.classes.disabled);
		return this;
	}

	public enable(): this {
		this.btnEl.disabled = false;
		return this;
	}

	public fakeEnable(): this {
		this.btnEl.classList.remove(this.options.classes.disabled);
		return this;
	}

	public setText(text: string): this {
		const textEl: HTMLElement = this.btnEl.querySelector(`.c-btn__text`);
		if (textEl) {
			textEl.innerText = text;
		}
		return this;
	}

	public setPrependIcon(iconClass: string): this {
		this.createPrepend(iconClass);
		return this;
	}

	public removePrependIcon(): this {
		this.getPrepend().remove();
		return this;
	}

	public setAppendIcon(iconClass: string): this {
		this.createAppend(iconClass);
		return this;
	}

	public removeAppendIcon(): this {
		this.getAppend().remove();
		return this;
	}

	public isDisabled(): boolean {
		return this.btnEl.disabled;
	}

	public isFakeDisabled(): boolean {
		return this.btnEl.classList.contains(this.options.classes.disabled);
	}

	public isLoading(): boolean {
		return this.btnEl.classList.contains(this.options.classes.loading);
	}

	public getBtn(): HTMLButtonElement {
		return this.btnEl;
	}

	private createPrepend(iconClass: string): HTMLElement {
		const current: HTMLElement = this.getPrepend();
		if (current) {
			current.remove();
		}
		this.btnEl.insertAdjacentHTML("afterbegin", this.getPrependTemplate(iconClass));
		return this.getPrepend();
	}

	private createAppend(iconClass: string): HTMLElement {
		const current: HTMLElement = this.getAppend();
		if (current) {
			current.remove();
		}
		this.btnEl.insertAdjacentHTML("beforeend", this.getAppendTemplate(iconClass));
		return this.getPrepend();
	}

	private getPrepend(): HTMLElement {
		return this.btnEl.querySelector(`.c-btn__prepend`);
	}

	private getAppend(): HTMLElement {
		return this.btnEl.querySelector(`.c-btn__append`);
	}

	private getPrependTemplate(iconClass: string): string {
		return `
			<div class="c-btn__prepend o-icon-box">
				<i class="o-icon ${iconClass}"></i>
			</div>
		`;
	}

	private getAppendTemplate(iconClass: string): string {
		return `
			<div class="c-btn__append o-icon-box">
				<i class="o-icon ${iconClass}"></i>
			</div>
		`;
	}
}
