import Carousel from "@modules/carousel/Carousel";
import ImageViewer from "@modules/image-viewer/ImageViewer";

const productImageViewer: HTMLElement = document.querySelector(`.js-imageViewerProduct`);
if (productImageViewer) {
	new ImageViewer(
		productImageViewer,
		new Carousel(productImageViewer, {
			configName: 'imageViewer',
		}),
		{
			classes: {
				classHandler: 'js-imageViewerProductHandler',
				classBlock: 'js-imageViewerProduct',
				classClose: 'js-imageViewerClose',
			}
		}
	)
}
