import CartCostLine from "@modules/cart/CartCostLine";
import {fromEvent} from "rxjs";
import {MoneyFormatter} from "@modules/formatter/MoneyFormatter";
import {EMoneyFormatterTypes} from "@modules/formatter/model";
import {ECartCostLineNames} from "@modules/cart/model";
import SelectInsuranceComponent from "@components/select-insurance/SelectInsuranceComponent";
import GlobalLoader from "@modules/loader/GlobalLoader";
import HttpRequest from "@modules/http-request/HttpRequest";
import {Method} from "axios";

export type TUpdateCartCostOnInsuranceStepGetCostResponse = {
	pricePerMonth?: string
	price?: string
}

export default class UpdateCartCostOnInsuranceStep {
	private CLASS_COST_LINE: string = 'js-cartCostLine';
	private CLASS_SELECT_INSURANCE: string = 'js-cartSelectInsurance';
	private CLASS_CART_FORM: string = 'js-cartForm';

	private DATA_ATTR_URL_GET_PRICE: string = 'data-url-cart-price';
	private DATA_ATTR_METHOD_GET_PRICE: string = 'data-method-cart-price';

	private selectInsuranceEl: SelectInsuranceComponent;
	private costLineEl: HTMLElement;
	private formEl: HTMLFormElement;

	private cartCostLine: CartCostLine;
	private globalLoader: GlobalLoader;
	private httpService: HttpRequest;

	public init(): void {
		this.selectInsuranceEl = document.querySelector(`.${this.CLASS_SELECT_INSURANCE}`);
		this.costLineEl = document.querySelector(`.${this.CLASS_COST_LINE}`);
		this.formEl = document.querySelector(`.${this.CLASS_CART_FORM}`);

		if (!this.selectInsuranceEl || !this.costLineEl) {
			return;
		}
		this.httpService = new HttpRequest();
		this.globalLoader = GlobalLoader.getInstance();
		this.cartCostLine = new CartCostLine(this.costLineEl);
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(this.selectInsuranceEl, 'on-change')
			.subscribe(async () => {
				await this.selectInsuranceEl.updateComplete;

				//Очень плохо так делать
				// ожидаю что внутренние компоненты selectInsuranceEl будут отрендерены за 60 ms
				setTimeout(() => {
					this.updateCostLine();
				}, 60);
			});

		fromEvent(this.selectInsuranceEl, 'first-updated')
			.subscribe(async () => {
				await this.selectInsuranceEl.updateComplete;
				//Очень плохо так делать
				// ожидаю что внутренние компоненты selectInsuranceEl будут отрендерены за 60 ms
				setTimeout(() => {
					this.updateCostLine();
				}, 60);
			});
	}

	private updateCostLine(): void {
		this.globalLoader.show();
		const formData = new FormData(this.formEl);
		const url = this.formEl.getAttribute(this.DATA_ATTR_URL_GET_PRICE);
		const method = this.formEl.getAttribute(this.DATA_ATTR_METHOD_GET_PRICE);
		this.httpService
			.request<TUpdateCartCostOnInsuranceStepGetCostResponse>({
				url: url,
				method: method as Method,
				data: formData,
			})
			.then(response => {
				this.updatePricePerMonth(response.data.data.pricePerMonth);
				this.updateFullPrice(response.data.data.price);
			})
			.catch(reject => {

			})
			.finally(() => {
				this.globalLoader.hide();
			});
	}

	private updatePricePerMonth(value: string): void {
		const pricePerMonth = MoneyFormatter.format(
			value,
			EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT,
		);
		this.cartCostLine.updateItemValue(ECartCostLineNames.PRICE_PER_MONTH, pricePerMonth);
	}

	private updateFullPrice(value: string): void {
		const price = MoneyFormatter.format(
			value,
			EMoneyFormatterTypes.THOUSAND_MONTH_SUPERSCRIPT,
		);
		this.cartCostLine.updateItemValue(ECartCostLineNames.FULL_PRICE, price);
	}
}
