import {fromEvent, merge} from "rxjs";
import {debounceTime} from "rxjs/operators";

export default class FullHeightIframe {
	private CLASS_HANDLER: string = 'js-fullHeightIframe';

	private CLASS_MOD_PARENT_CONTENT_HEIGHT: string = 'u-iframe-full_paren-content-height';

	public init(): void {
		this.iframesOnLoad();
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(window, 'resize')
			.pipe(
				debounceTime(60),
			)
			.subscribe(() => {
				this.getIframes().forEach(iframe => {
					this.updateHeight(iframe);
				});
			});
	}

	private updateHeight(iframe: HTMLIFrameElement): void {
		const iframeDocument: Document = this.getIframeDocument(iframe);
		if (iframeDocument) {
			iframe.height = (this.getIframeDocument(iframe)?.body?.scrollHeight || 0) + 'px';
			iframe.classList.remove(this.CLASS_MOD_PARENT_CONTENT_HEIGHT);
		} else {
			iframe.classList.add(this.CLASS_MOD_PARENT_CONTENT_HEIGHT);
		}
	}

	private setOverflowHidden(iframe: HTMLIFrameElement): void {
		this.getIframeDocument(iframe)?.head?.appendChild(
			this.getStyle()
		);
	}

	private getStyle(): HTMLStyleElement {
		const styleEl: HTMLStyleElement = document.createElement('style');
		styleEl.setAttribute('type', 'text/css');
		styleEl.innerHTML = `
		  body {
			overflow: hidden !important;
		  }
		`;
		return styleEl;
	}

	private getIframeDocument(iframe: HTMLIFrameElement): HTMLDocument {
		try {
			return iframe.contentDocument || iframe.contentWindow?.document;
		} catch (e) {
			console?.info(`FullHeightIframe: ${e}`);
		}
		return null;
	}

	public getIframeWindow(iframe: HTMLIFrameElement): Window {
		try {
			return iframe.contentWindow;
		} catch (e) {
			console?.info(`FullHeightIframe: ${e}`);
		}
		return window;
	}

	private iframesOnLoad(): void {
		this.getIframes().forEach(iframe => {
			this.updateHeight(iframe);
			merge(
				fromEvent(window, 'load'),
				fromEvent(this.getIframeWindow.apply(this, iframe), 'load'),
				fromEvent(iframe, 'load'),
			)
				.subscribe(() => {
					this.setOverflowHidden(iframe);
					this.updateHeight(iframe);
				});
		})
	}

	private getIframes(): Array<HTMLIFrameElement> {
		return Array.from(
			document.getElementsByClassName(this.CLASS_HANDLER)
		) as Array<HTMLIFrameElement>;
	}
}
