import {formDataToObject} from "@helpers/index";
import CSRFToken from "@modules/csrf-token/CSRFToken";
import {ECSRFTokenKeys, ICSRFToken} from "@modules/csrf-token/model";
import Environment from "@modules/env/Environment";
import {EEnvironmentType} from "@modules/env/model";
import {TRequestResponse, TRequestResponseMetadata} from "@modules/http-request/model";
import {EToastType} from "@modules/toast/model";
import Toast from "@modules/toast/Toast";
import axios, {AxiosError, AxiosInstance, AxiosRequestConfig, AxiosResponse} from 'axios';

const defaultHeaders: any = {
	'X-Requested-With': 'XMLHttpRequest'
}

export default class HttpRequest {
	private env: EEnvironmentType = Environment.getEnv();

	constructor() {

	}

	public request<TData, TMeta extends TRequestResponseMetadata = TRequestResponseMetadata>(options: AxiosRequestConfig): Promise<AxiosResponse<TRequestResponse<TData, TMeta>>> {
		const instance: AxiosInstance = axios.create();
		this.setInterceptors(instance);
		return instance.request({
			...options,
			url: this.getURL(options.url),
			headers: {
				...defaultHeaders
			},
		});
	}

	public requestHTML(options: AxiosRequestConfig): Promise<AxiosResponse<string>> {
		const instance: AxiosInstance = axios.create();
		this.setInterceptors(instance);
		return instance.request({
			...options,
			url: this.getURL(options.url),
			headers: {
				...defaultHeaders
			},
		});
	}

	static isNetworkError(reject: AxiosError): boolean {
		return reject.request.status === 0;
	}

	private setInterceptors(instance: AxiosInstance): void {
		instance.interceptors.response.use(
			(response) => {
				this.toastInterceptor(response?.data);
				this.redirectURLInterceptor(response?.data);
				return response;
			},
			(error) => {
				this.toastInterceptor(error?.response?.data);
				this.redirectURLInterceptor(error?.response?.data);
				this.networkErrorInterceptor(error);
				return Promise.reject(error);
			}
		);
		instance.interceptors.request.use(
			(config) => {
				this.convertDataToQuery(config);
				this.addCSRFToken(config);
				return config;
			},
		);
	}

	private getURL(url: string): string {
		const hostName: string = window.location.hostname;
		const devURL: string = `${window.location.protocol}//${hostName}:8181`;
		if (this.env === EEnvironmentType.DEVELOPMENT) {
			let finalUrl: string;
			try {
				const objectURL: URL = new URL(url);
				finalUrl = devURL + objectURL.pathname + objectURL.search;
			} catch (e) {
				finalUrl = devURL + url;
			}
			return finalUrl
		}
		return url;
	}

	private toastInterceptor(response: TRequestResponse<any, TRequestResponseMetadata>): void {
		if (response?.metadata?.toasts && Array.isArray(response?.metadata?.toasts)) {
			const toast = Toast.getInstance();
			response?.metadata?.toasts.forEach(toastOption => {
				toast.add(toastOption);
			})
		}
	}

	private redirectURLInterceptor(response: TRequestResponse<any, TRequestResponseMetadata>): void {
		const redirectURL: string = response?.metadata?.redirectURL;
		if (redirectURL) {
			window.location.href = redirectURL;
		}
	}

	private networkErrorInterceptor(reject: AxiosError): void {
		if (HttpRequest.isNetworkError(reject)) {
			Toast
				.getInstance()
				.add({
					text: `
					При загрузке данных произошла ошибка !<br>
					Проверьте подключение к интернету
					`,
					type: EToastType.ERROR,
					iconClass: 'o-icon_close-filled',
				});
		}
	}

	private addCSRFToken(config: AxiosRequestConfig): AxiosRequestConfig {
		let newConfig: AxiosRequestConfig = config;
		if (config.method.toUpperCase() !== "GET") {
			try {
				const csrfToken: ICSRFToken = CSRFToken.getInstance();
				const csrfTokenBodyKey = ECSRFTokenKeys.REQUEST_BODY;
				const csrfTokenHeaderKey = ECSRFTokenKeys.REQUEST_HEADERS;
				const csrfTokenValue = csrfToken.getToken();
				if (config.data instanceof FormData) {
					newConfig.data.append(csrfTokenBodyKey, csrfTokenValue);
				} else if (typeof config.data === 'object') {
					newConfig.data = {
						...config.data || {},
						[csrfTokenBodyKey]: csrfTokenValue,
					}
				}
				newConfig.headers[csrfTokenHeaderKey] = csrfTokenValue;
			} catch (e) {
				console.error(e);
				return config;
			}
		}
		return newConfig;
	}

	private convertDataToQuery(config: AxiosRequestConfig): AxiosRequestConfig {
		let newConfig: AxiosRequestConfig = config;
		if (config.method.toUpperCase() === "GET") {
			let params: any = config.data || {};
			if (config.data instanceof FormData) {
				params = formDataToObject(config.data);
			}
			newConfig.params = {
				...config.params,
				...params,
			};
			newConfig.paramsSerializer = (params: any) => {
				const urlParams: URLSearchParams = new URLSearchParams();
				if (typeof params !== 'object' || params === null) {
					return config.paramsSerializer(params);
				}
				Object
					.entries(params)
					.forEach(([key, value]) => {
						if (Array.isArray(value)) {
							value.forEach(arrValue => {
								urlParams.append(key, arrValue)
							})
						} else {
							urlParams.append(key, String(value))
						}
					})
				return urlParams.toString();
			}
		}
		return newConfig;
	}

	private isLocalhost(): boolean {
		const hostName: string = window.location.hostname;
		return hostName === '127.0.0.1' || hostName === 'localhost';
	}
}
