import {DynamicInsertionElements} from "@modules/dynamic-element/DynamicInsertionElements";
import {isHTMLElement, strToBoolean} from "@helpers/index";
import {ECollapseType, ICollapseFactory} from "@modules/collapse/model";
import CollapseFactory from "@modules/collapse/CollapseFactory";

export default class CollapseAutoActivation extends DynamicInsertionElements {
	private collapseFactory: ICollapseFactory = new CollapseFactory();

	constructor(
		private handler: string,
		private name: string,
		private type: ECollapseType = ECollapseType.DEFAULT,
	) {
		super();
	}

	public init(): void {
		this.activate(document);
		this.attachEventDynamicInsertion();
	}

	protected dynamicInsertHandler(nodeModules: Node) {
		if (isHTMLElement(nodeModules)) {
			this.activate(nodeModules as HTMLElement);
		}
	}

	private activate(scope: HTMLElement | Document): void {
		const handlers: Array<HTMLElement> = Array.from(scope.getElementsByClassName(this.handler)) as Array<HTMLElement>;
		handlers.forEach(handler => {
			if (strToBoolean(handler.getAttribute(this.getDataActivatedName()))) {
				return
			}
			handler.setAttribute(this.getDataActivatedName(), 'true');

			if (this.type === ECollapseType.DEFAULT) {
				this.collapseFactory.createCollapse(handler);
			} else if (this.type === ECollapseType.ANIMATED) {
				this.collapseFactory.createCollapseAnimated(handler);
			}
		});
	}

	private getDataActivatedName(): string {
		return `data-${this.name}-activated`
	}
}
