import {fromEvent} from "rxjs";
import Btn from "@modules/btn/Btn";
import {IBtn} from "@modules/btn/model";
import {AxiosError, Method} from "axios";
import HttpRequest from "@modules/http-request/HttpRequest";
import {strToBoolean} from "@helpers/index";
import {TRequestResponse} from "@modules/http-request/model";
import FormValidator from "@modules/form/Validator";
import {IFormValidator} from "@modules/form/model";

export default class FormSubmit {
	private DATA_ATTR_AJAX: string = 'data-form-submit-ajax';

	private httpRequest: HttpRequest = new HttpRequest();

	public init(): void {
		this.attachEvent();
	}

	private attachEvent(): void {
		fromEvent(window, 'submit')
			.subscribe(event => {
				event.preventDefault();
				const btnEl: HTMLButtonElement = this.getSubmitter(event);
				const formEl: HTMLFormElement = event.target as HTMLFormElement;
				const btn: IBtn = new Btn(btnEl);

				if (this.isAjax(formEl)) {
					this.request(btn, formEl)
				} else {
					formEl.submit();
					btn
						.disable()
						.addLoader()
				}
			});
	}

	private isAjax(form: HTMLFormElement): boolean {
		return strToBoolean(form.getAttribute(this.DATA_ATTR_AJAX));
	}

	private request(btn: IBtn, form: HTMLFormElement): void {
		if (btn.isDisabled()) {
			return;
		}
		const formValidator: IFormValidator = new FormValidator(form);
		btn
			.addLoader()
			.disable();
		this.httpRequest.request<string, object>({
			method: form.method as Method,
			url: form.action,
			data: new FormData(form)
		})
			.then(() => {
				formValidator.clearMessages();
			})
			.catch((reject: AxiosError<TRequestResponse>) => {
				formValidator.clearMessages();
				if (reject.response.status === 400) {
					formValidator.showErrors(reject.response.data.metadata.errorFields);
				}
			})
			.finally(() => {
				btn
					.removeLoader()
					.enable();
			});
	}

	private getSubmitter(event: Event): HTMLButtonElement {
		const form: HTMLFormElement = event.target as HTMLFormElement;
		const formID: string = form.getAttribute('id');
		let submitter: HTMLButtonElement;
		const eventSubmitter: any = event['submitter'];
		if (eventSubmitter && eventSubmitter.tagName === 'BUTTON') {
			submitter = eventSubmitter;
		} else {
			submitter = form.querySelector(`BUTTON[type='submit']`)
		}
		if (!submitter && formID) {
			submitter = document.querySelector(`BUTTON[form='${formID}']`);
		}
		return submitter;
	}
}
