import ToggleChange from "@modules/toggle/ToggleChange";
import {TToggleChangeStreamOnChange} from "@modules/toggle/model";

export default class ToggleSetClass extends ToggleChange {
	private DATA_ATTR_CONNECTED_CLASS: string = 'data-toggle-set-class-connected-class';
	private DATA_ATTR_ACTIVE_CLASS: string = 'data-toggle-set-class-active-class';
	private DATA_ATTR_UNACTIVE_CLASS: string = 'data-toggle-set-class-unactive-class';

	constructor() {
		super({
			classHandler: 'js-toggleSetClassHandler',
		});
		this.subscribeChanges();
	}

	private subscribeChanges(): void {
		this.getStreamOnChange()
			.subscribe(streamOnChange => {
				this.onChange(streamOnChange);
			})
	}

	private onChange(streamOnChange: TToggleChangeStreamOnChange) {
		const toggle: HTMLInputElement = streamOnChange.input;
		const state: boolean = streamOnChange.state;
		const connectedClass: string = toggle.getAttribute(this.DATA_ATTR_CONNECTED_CLASS);
		const activeClass: Array<string> = toggle.getAttribute(this.DATA_ATTR_ACTIVE_CLASS).split(' ').filter(Boolean);
		const unactiveClass: Array<string> = toggle.getAttribute(this.DATA_ATTR_UNACTIVE_CLASS).split(' ').filter(Boolean);
		if (!connectedClass) return;

		const connectedElements: Array<HTMLElement> = Array.from(document.getElementsByClassName(connectedClass)) as Array<HTMLElement>;
		connectedElements.forEach(connectedElement => {
			if (state) {
				if (activeClass.length) {
					connectedElement.classList.add(...activeClass);
				}
				if (unactiveClass.length) {
					connectedElement.classList.remove(...unactiveClass);
				}
			} else {
				if (unactiveClass.length) {
					connectedElement.classList.add(...unactiveClass);
				}
				if (activeClass.length) {
					connectedElement.classList.remove(...activeClass);
				}
			}
		})
	}
}
