import HandleEmptyContent from "@modules/handle-empty-content/HandleEmptyContent";
import {IVisibility} from "@modules/visibility/model";
import Visibility from "@modules/visibility/Visibility";

export default class HandleEmptyContentVisibility extends HandleEmptyContent {
	private DATA_ATTR_CLASS_VISIBLE_EMPTY: string = 'data-class-visible-empty';
	private DATA_ATTR_CLASS_VISIBLE_NOT_EMPTY: string = 'data-class-visible-not-empty';

	private classVisibleEmpty: string;
	private classVisibleNotEmpty: string;

	private visibility: IVisibility = new Visibility();

	constructor(handlerEl: HTMLElement) {
		super(handlerEl);
		this.getClasses();
	}

	private getClasses(): void {
		this.classVisibleEmpty = this.handlerEl.getAttribute(this.DATA_ATTR_CLASS_VISIBLE_EMPTY);
		this.classVisibleNotEmpty = this.handlerEl.getAttribute(this.DATA_ATTR_CLASS_VISIBLE_NOT_EMPTY);
	}

	private getVisibleEmptyItems(): Array<HTMLElement> {
		return Array.from(
			document.getElementsByClassName(this.classVisibleEmpty)
		) as Array<HTMLElement>;
	}

	private getVisibleNotEmptyItems(): Array<HTMLElement> {
		return Array.from(
			document.getElementsByClassName(this.classVisibleNotEmpty)
		) as Array<HTMLElement>;
	}

	protected update(): void {
		const length: number = this.handlerEl.children.length;

		this.getVisibleEmptyItems().forEach(item => {
			length ? this.visibility.hide(item) : this.visibility.show(item);
		});
		this.getVisibleNotEmptyItems().forEach(item => {
			length ? this.visibility.show(item) : this.visibility.hide(item);
		});
	}

}
