import {EAnimationType, IAnimation, TAnimationClasses, TAnimationOption} from "@modules/animation/model";

export default class Animation implements IAnimation {
	private frames: Array<any>
	private framesRun: boolean
	private canAnimate: boolean
	private raf: (callback: FrameRequestCallback) => number;
	private element: HTMLElement;

	private option: TAnimationOption;

	constructor(element: HTMLElement, options?: TAnimationOption) {
		this.element = element;
		this.raf = window.requestAnimationFrame;

		let divTest = document.createElement("div");

		/* checking browser for necessary opportunities */
		this.canAnimate = (typeof this.raf === "function") &&
			("classList" in divTest) &&
			typeof divTest.style.transition !== undefined;

		if (this.canAnimate) {
			this.raf = this.raf.bind(window);
		}

		/* requestAnimationFrame queue */
		this.frames = [];
		this.framesRun = false;

		this.option = this.calcOptions(options);
	}

	public show(): void {
		if (!this.canAnimate) {
			this.showVisuality(this.element);
		}

		if (!this.isHidden(this.element)) {
			return;
		}
		/* set handler on animation finish */
		this.setFinishHandler(() => {
			this.removeClasses(this.element, this.option.enterClassNames.enterActive);
			this.removeClasses(this.element, this.option.enterClassNames.enterTo);
			this.option.afterEnter(this.element);
		});

		this.showVisuality(this.element);
		this.addClasses(this.element, this.option.enterClassNames.enter);
		this.option.beforeEnter(this.element);

		this.addFrame(() => {
			this.addClasses(this.element, this.option.enterClassNames.enterActive);
		});

		this.addFrame(() => {
			this.removeClasses(this.element, this.option.enterClassNames.enter);
			this.addClasses(this.element, this.option.enterClassNames.enterTo);
		});
	}

	public hide(): void {
		if (!this.canAnimate) {
			this.hideVisuality(this.element);
		}

		if (this.isHidden(this.element)) {
			return;
		}

		this.setFinishHandler(() => {
			this.hideVisuality(this.element);
			this.removeClasses(this.element, this.option.leaveClassNames.leaveActive);
			this.removeClasses(this.element, this.option.leaveClassNames.leaveTo);
			// this.option.systemOnEnd && this.option.systemOnEnd();
			this.option.afterLeave(this.element);
		});

		this.addClasses(this.element, this.option.leaveClassNames.leave);

		this.option.beforeLeave(this.element);

		this.addFrame(() => {
			this.addClasses(this.element, this.option.leaveClassNames.leaveActive);
		});

		this.addFrame(() => {
			this.addClasses(this.element, this.option.leaveClassNames.leaveTo);
			this.removeClasses(this.element, this.option.leaveClassNames.leave);
		});
	}

	public insert(target: HTMLElement | Document, before: Node = null): void {
		this.hideVisuality(this.element);
		target.insertBefore(this.element, before);
		this.show();
	}

	public remove(): void {
		this.option.systemDoneCallback = function () {
			this.element.parentNode.removeChild(this.element);
		}

		this.hide();
	}

	private setFinishHandler(fn: () => void): void {
		let eventName: string;
		let isCssTrack: boolean = true;

		if (this.option.type === 'transition') {
			eventName = 'transitionend';
		} else if (this.option.type === 'animation') {
			eventName = 'animationend';
		} else {
			isCssTrack = false;
		}

		if (isCssTrack) {
			let handler = () => {
				this.element.removeEventListener(eventName, handler);
				fn();
			};

			this.element.addEventListener(eventName, handler);
		} else {
			setTimeout(fn, this.option.duration);
		}
	}

	private calcOptions(options?: TAnimationOption): TAnimationOption {
		return {
			...this.getDefaultOption(options),
			...options
		};
	}

	private getDefaultOption(options?: TAnimationOption): Partial<TAnimationOption> {
		const name: string = 'animation-class';
		return {
			name: name,
			enterName: options.enterName || options.name || name,
			leaveName: options.leaveName || options.name || name,
			type: EAnimationType.TRANSITION,
			duration: null,
			enterClassNames: this.getClassNames(options.enterName || options.name || name),
			leaveClassNames: this.getClassNames(options.leaveName || options.name || name),
			displayProperty: 'block',
			beforeEnter: (el) => {
			},
			afterEnter: (el) => {
			},
			beforeLeave: (el) => {
			},
			afterLeave: (el) => {
			},
			systemDoneCallback: (el) => {
			}
		}
	}

	private getClassNames(name: string): TAnimationClasses {
		return {
			enter: name + '-enter',
			enterActive: name + '-enter-active',
			enterTo: name + '-enter-to',
			leave: name + '-leave',
			leaveActive: name + '-leave-active',
			leaveTo: name + '-leave-to'
		}
	}

	private addFrame(fn: () => void): void {
		this.frames.push(fn);

		if (!this.framesRun) {
			this.nextFrame();
		}
	}

	private nextFrame(): void {
		if (this.frames.length === 0) {
			this.framesRun = false;
			return;
		}

		const frame: () => void = this.frames.shift();

		this.raf(() => {
			this.raf(() => {
				frame();
				this.nextFrame();
			});
		});
	}

	private addClasses(el: HTMLElement, str: string): void {
		str
			.split(' ')
			.forEach(value => {
				el.classList.add(value);
			});
	}

	private removeClasses(el: HTMLElement, str: string): void {
		str
			.split(' ')
			.forEach(value => {
				el.classList.remove(value);
			});
	}

	private hideVisuality(el: HTMLElement): void {
		el.style.display = 'none';
	}

	private showVisuality(el: HTMLElement): void {
		el.style.removeProperty('display');

		if (this.isHidden(el)) {
			el.style.display = this.option.displayProperty;
		}
	}

	private isHidden(el): boolean {
		return this.getStyle(el, 'display') === 'none';
	}

	private getStyle(el, prop): string {
		return getComputedStyle(el)[prop];
	}
}
