import ProductCostLineComponent from "@components/product-cost-line/ProductCostLineComponent";
import Btn from "@modules/btn/Btn";

export enum EProductCartHandlerStatus {
	ADD,
	REMOVE
}

export default class ProductCartHandler {
	private CLASS_HANDLER: string = 'js-addToCartHandler';
	private CLASS_ACTIVE: string = 'is-active';
	private CLASS_LOADING: string = 'is-loading';
	private CLASS_HANDLER_ON_PRODUCT_PAGE: string = 'js-addToCartHandlerOnProductPage';

	private DATA_ADDED_TEXT: string = 'data-add-to-cart-added-text';
	private DATA_ADDED_ICON: string = 'data-add-to-cart-added-icon';
	private DATA_REMOVED_TEXT: string = 'data-add-to-cart-removed-text';
	private DATA_REMOVED_ICON: string = 'data-add-to-cart-removed-icon';

	private productCostLine: ProductCostLineComponent;

	private static instance: ProductCartHandler;

	constructor() {
		if (ProductCartHandler.instance) {
			throw new Error("Error: Instantiation failed: Use ProductCartIdentical.getInstance() instead of new.");
		}
		ProductCartHandler.instance = this;
		this.productCostLine = document.querySelector(`c-product-cost-line`);
	}

	public static getInstance(): ProductCartHandler {
		if (!ProductCartHandler.instance) {
			ProductCartHandler.instance = new ProductCartHandler();
		}
		return this.instance;
	}

	public setActive(sku: string, handler?: HTMLElement,): void {
		this.updateStatusesBySky(sku, EProductCartHandlerStatus.ADD);
		this.updateStatusOnProductPage(sku, EProductCartHandlerStatus.ADD, handler);
	}

	public setUnactive(sku: string, handler?: HTMLElement): void {
		this.updateStatusesBySky(sku, EProductCartHandlerStatus.REMOVE);
		this.updateStatusOnProductPage(sku, EProductCartHandlerStatus.REMOVE, handler);
	}

	public setLoader(handler: HTMLElement): void {
		handler.classList.add(this.CLASS_LOADING);

		if (this.isProductPageHandler(handler)) {
			if (this.productCostLine) {
				this.productCostLine
					.updateComplete
					.then(() => {
						this.productCostLine.isLoadingBtn = true;
					});
			}
		}
	}

	public removeLoader(handler: HTMLElement): void {
		handler.classList.remove(this.CLASS_LOADING);

		if (this.isProductPageHandler(handler)) {
			if (this.productCostLine) {
				this.productCostLine
					.updateComplete
					.then(() => {
						this.productCostLine.isLoadingBtn = false;
					});
			}

		}
	}

	public setDisabled(handler: HTMLElement): void {
		if (handler instanceof HTMLButtonElement) {
			handler.disabled = true;
		}

		if (this.isProductPageHandler(handler)) {
			if (this.productCostLine) {
				this.productCostLine
					.updateComplete
					.then(() => {
						this.productCostLine.isDisableBtn = true;
					});
			}
		}
	}

	public removeDisabled(handler: HTMLElement): void {
		if (handler instanceof HTMLButtonElement) {
			handler.disabled = false;
		}

		if (this.isProductPageHandler(handler)) {
			if (this.productCostLine) {
				this.productCostLine
					.updateComplete
					.then(() => {
						this.productCostLine.isDisableBtn = false;
					});
			}
		}
	}

	private getIdenticalProductCardHandlers(sku: string): Array<HTMLElement> {
		const inputs: Array<HTMLInputElement> = Array.from(
			document.querySelectorAll(`.${this.CLASS_HANDLER} input[name="sku"][value="${sku}"]`)
		);
		return inputs
			.map(input => input.closest(`.${this.CLASS_HANDLER}`));
	}

	private updateStatusesBySky(sku: string, status: EProductCartHandlerStatus): void {
		const productCardHandlers = this.getIdenticalProductCardHandlers(sku);
		productCardHandlers.forEach(handler => {
			this.updateStatusByHandler(handler, status);
		});
	}

	private updateStatusByHandler(handler: HTMLElement, status: EProductCartHandlerStatus): void {
		switch (status) {
			case EProductCartHandlerStatus.ADD:
				handler.classList.add(this.CLASS_ACTIVE);
				break;
			case EProductCartHandlerStatus.REMOVE:
				handler.classList.remove(this.CLASS_ACTIVE);
				break;
			default:
				break;
		}
	}

	private updateStatusOnProductPage(sku: string, status: EProductCartHandlerStatus, handler?: HTMLElement): void {
		if (!handler) {
			handler = this.getProductPageHandlerBySku(sku);
		}
		if (!handler) {
			return;
		}
		const isProductPageHandler: boolean = this.isProductPageHandler(handler);
		if (!isProductPageHandler) {
			return;
		}
		this.updateStatusByHandler(handler, status);
		this.updateBtnText(handler, status);
	}

	private isProductPageHandler(handler: HTMLElement): boolean {
		return handler.classList.contains(this.CLASS_HANDLER_ON_PRODUCT_PAGE);
	}

	private updateBtnText(handler: HTMLElement, status: EProductCartHandlerStatus): void {
		const addedText: string = handler.getAttribute(this.DATA_ADDED_TEXT);
		const addedIcon: string = handler.getAttribute(this.DATA_ADDED_ICON);
		const removedText: string = handler.getAttribute(this.DATA_REMOVED_TEXT);
		const removedIcon: string = handler.getAttribute(this.DATA_REMOVED_ICON);

		let btn = addedText && new Btn(handler as HTMLButtonElement);

		switch (status) {
			case EProductCartHandlerStatus.ADD:
				btn.setText(addedText);
				if (addedIcon) {
					btn.setPrependIcon(addedIcon);
				}
				break;
			case EProductCartHandlerStatus.REMOVE:
				btn.setText(removedText);
				if (removedIcon) {
					btn.setPrependIcon(removedIcon);
				} else {
					btn.removePrependIcon();
				}
				break;
			default:
				break;
		}

		if (this.productCostLine) {
			this.productCostLine
				.updateComplete
				.then(() => {
					switch (status) {
						case EProductCartHandlerStatus.ADD:
							this.productCostLine.btnText = addedText;
							break;
						case EProductCartHandlerStatus.REMOVE:
							this.productCostLine.btnText = removedText;
							break;
						default:
							break;
					}
				});
		}
	}

	private getProductPageHandlerBySku(sku: string): HTMLElement {
		const handler: HTMLElement = document.querySelector(`.${this.CLASS_HANDLER}.${this.CLASS_HANDLER_ON_PRODUCT_PAGE}`);
		const skuInput = handler?.querySelector(`input[name="sku"][value="${sku}"]`);
		if (!handler || !skuInput) {
			return;
		}

		return handler;
	}
}
