import {EScreenOrientation, IScreenOrientation} from "@modules/screen/model";
import {BehaviorSubject, fromEvent, Observable} from "rxjs";
import {debounceTime, filter} from "rxjs/operators";

export default class ScreenOrientationService implements IScreenOrientation {
	private static instance: IScreenOrientation;

	private stream: BehaviorSubject<EScreenOrientation> = new BehaviorSubject<EScreenOrientation>(null);

	private lastOrientation: EScreenOrientation;
	private lastViewWidth: number;

	constructor() {
		if (ScreenOrientationService.instance) {
			throw new Error("Error: Instantiation failed: Use ScreenOrientationService.getInstance() instead of new.");
		}
		ScreenOrientationService.instance = this;
		this.lastViewWidth = document.documentElement.clientWidth;
		this.attachEvent();
		this.lastOrientation = this.getOrientation();
	}

	public static getInstance(): IScreenOrientation {
		if (!ScreenOrientationService.instance) {
			ScreenOrientationService.instance = new ScreenOrientationService();
		}
		return this.instance;
	}

	public getStream(): Observable<EScreenOrientation> {
		return this.stream
			.asObservable()
			.pipe(
				filter(stream => Boolean(stream))
			)
	}

	public getOrientation(): EScreenOrientation {
		if (window.matchMedia("(orientation: portrait)").matches) {
			return EScreenOrientation.PORTRAIT;
		} else if (window.matchMedia("(orientation: landscape)").matches) {
			return EScreenOrientation.LANDSCAPE;
		} else {
			return undefined;
		}
	}

	private attachEvent(): void {
		fromEvent(window, 'resize')
			.pipe(
				debounceTime(30),
			)
			.subscribe(() => {
				const currentOrientation: EScreenOrientation = this.getOrientation();
				const currentWidth: number = document.documentElement.clientWidth;
				if (currentOrientation !== this.lastOrientation && currentWidth !== this.lastViewWidth) {
					this.lastViewWidth = currentWidth;
					this.lastOrientation = currentOrientation;
					this.stream.next(currentOrientation);
				}
			})
	}
}
