import BtnComponent from "@components/btn/BtnComponent";
import {EProductBasketBundlerOptionControlSignals} from "@components/product-basket-bundler/product-basket-bundler-option-controls/model";
import ProductBasketBundlerOptionControls
	from "@components/product-basket-bundler/product-basket-bundler-option-controls/ProductBasketBundlerOptionControls";
import {EProductBasketBundlerOptionStates} from "@components/product-basket-bundler/product-basket-bundler-option-states/model";
import ProductBasketBundlerOptionStates
	from "@components/product-basket-bundler/product-basket-bundler-option-states/ProductBasketBundlerOptionStates";
import {EProductBasketBundlerStoreTypes} from "@components/product-basket-bundler/product-basket-bundler-store/model";
import ProductBasketBundlerStore
	from "@components/product-basket-bundler/product-basket-bundler-store/ProductBasketBundlerStore";
import {
	EProductBundlerOptionPaymentMethodTypes,
	TProductBundlerOptionPaymentMethod
} from "@components/product-basket-bundler/product-bundler-option-payment-method/model";
import ProductBundlerOptionComponent
	from "@components/product-basket-bundler/product-bundler-option/ProductBundlerOptionComponent";
import {TProductCostLineOnBtnClickEventDetail} from "@components/product-cost-line/model";
import {TSelectContractTariffSelected} from "@components/select-tariff/model";
import {TAccessory} from "@models/Accessory";
import {TInsurance} from "@models/Insurance";
import HttpRequest from "@modules/http-request/HttpRequest";
import SmoothScroll from "@modules/scroll/SmoothScroll";
import {EToastType, IToast} from "@modules/toast/model";
import Toast from "@modules/toast/Toast";
import WebComponentLite from "@modules/web-component/WebComponentLite";
import {
	customElement,
	html,
	state,
	property,
	PropertyValues,
	queryAll,
	queryAsync,
	TemplateResult
} from "lit-element";
import 'reflect-metadata';
import {from, fromEvent, of} from "rxjs";
import {filter} from "rxjs/internal/operators";
import {map, switchMap} from "rxjs/operators";
import {EBtnMods, EBtnSize, EBtnState, EBtnType} from "../btn/model";
import {ELoaderSize, ELoaderType} from "../loader/model";
import ProductCostLineComponent from "../product-cost-line/ProductCostLineComponent";
import {
	EProductBasketBundlerControl,
	EProductBasketBundlerOptions,
	TProductBasketBundlerAddToCardRequest,
	TProductBasketBundlerGetOptionsRequest,
	TProductBasketBundlerGetOptionsResponse
} from "./model";

@customElement('c-product-basket-bundler')
export default class ProductBasketBundlerComponent extends WebComponentLite {
	@property({
		type: String,
		attribute: 'sku-id',
	})
	public skuId: string;

	@property({
		type: String,
		attribute: 'confirmed-link',
	})
	public confirmedLink: string = '';

	@state()
	public currentStepIndex: number = 0;

	@state()
	private optionTypes: Array<EProductBasketBundlerOptions> = [];

	@queryAll('[product-basket-bundler-option]')
	private options: NodeListOf<ProductBundlerOptionComponent>

	@queryAsync('[product-basket-bundler-next-btn]')
	private btnNext: Promise<BtnComponent>;

	@queryAsync('[product-basket-bundler-prev-btn]')
	private btnPrev: Promise<BtnComponent>;

	@state()
	private isLoading: boolean = false;

	@state()
	private error: unknown;

	@state()
	private isAddToCartRequesting: boolean = false;

	@state()
	private prevControl: EProductBasketBundlerControl = {
		disabled: false,
		loading: false,
		visible: false,
	};

	@state()
	private nextControl: EProductBasketBundlerControl = {
		disabled: false,
		loading: false,
		visible: true,
	};

	@state()
	private optionIsReady: boolean = false;

	protected httpService: HttpRequest = new HttpRequest();
	private readonly store: ProductBasketBundlerStore;
	private readonly optionStates: ProductBasketBundlerOptionStates;
	private readonly controls: ProductBasketBundlerOptionControls;
	private readonly productCostLine: ProductCostLineComponent;
	private smoothScroll: SmoothScroll = new SmoothScroll({
		block: 'start',
	});
	private smoothScrollController: SmoothScroll = new SmoothScroll({
		block: "center",
	});
	private toast: IToast = Toast.getInstance();

	constructor() {
		super();
		this.store = new ProductBasketBundlerStore();
		this.optionStates = new ProductBasketBundlerOptionStates();
		this.controls = new ProductBasketBundlerOptionControls();
		this.productCostLine = document.querySelector(`c-product-cost-line`);
		this.attachEvent();
	}

	protected firstUpdated(_changedProperties: PropertyValues): void {
		super.firstUpdated(_changedProperties);
		this.getOptions();
		this.updateBtnAvailableInProductCostLine();
		this.store.skuId = this.skuId;
	}

	private attachEvent(): void {
		this.optionStates
			.onChangeState
			.asObservable()
			.pipe(
				filter(state => state.key === this.getCurrentOptionType())
			)
			.subscribe((state) => {
				this.updateControlsState(state.key, state.state);

				const isFirst = this.isFirstOption(state.key);
				if (!isFirst) {
					this.scrollToTopContainer();
				}
			});

		this.store
			.getStreamChangeByType(EProductBasketBundlerStoreTypes.SELECTED_OPTION)
			.subscribe(() => {
				this.updateCostInProductCostLine();
				this.updateBtnAvailableInProductCostLine();
			})

		fromEvent(this.productCostLine, 'on-btn-click')
			.subscribe((event: CustomEvent<TProductCostLineOnBtnClickEventDetail>) => {
				this.handleClickBtnAddToCart(event);
			});

		this.controls
			.signal
			.asObservable()
			.subscribe(signal => {
				switch (signal) {
					case EProductBasketBundlerOptionControlSignals.NEXT:
						this.handleNextSignal();
						break;
					case EProductBasketBundlerOptionControlSignals.PREV:
						this.handlePrevSignal();
						break;
				}
			});
	}

	private handlePrevSignal(): void {
		this.prevStep();
	}

	private handleNextSignal(): void {
		if (this.currentStepIndex >= this.optionTypes.length - 1) {
			this.addToCart();
		} else {
			this.nextStep();
		}
	}

	private handleClickBtnAddToCart(event: CustomEvent<TProductCostLineOnBtnClickEventDetail>): void {
		if (event.detail.states.includes(EBtnState.DISABLED)) {
			this.notifyAboutInactiveBtnAddToCart();
			return;
		}
		this.addToCart();
	}

	private notifyAboutInactiveBtnAddToCart(): void {
		this.toast.add({
			type: EToastType.INFO,
			text: 'Для формирования Корзины пройдите на Следующий шаг',
		});
		this.scrollToNextBtn();
	}

	private scrollToNextBtn(): void {
		from(this.btnNext)
			.pipe(
				switchMap(component => {
					if (!component) {
						console?.error('ProductBasketBundlerComponent: btn next is not defined !');
						return of(null);
					}
					return from(component.updateComplete)
						.pipe(
							map(() => component)
						)
				})
			)
			.pipe(
				filter(value => Boolean(value))
			)
			.subscribe(component => {
				this.smoothScrollController.scrollTo(component);
			});
	}

	private updateControlsState(option: EProductBasketBundlerOptions, state: EProductBasketBundlerOptionStates): void {
		const isFirst = this.isFirstOption(option);
		const isLast = this.isLastOption(option);

		switch (state) {
			case EProductBasketBundlerOptionStates.LOADING:
				this.prevControl = {
					visible: !isFirst,
					disabled: true,
				}
				this.nextControl = {
					visible: !isLast,
					disabled: true,
				}
				break;
			case EProductBasketBundlerOptionStates.ERROR:
				this.prevControl = {
					visible: !isFirst,
					disabled: false,
				}
				this.nextControl = {
					visible: !isLast,
					disabled: true,
				}
				break;
			case EProductBasketBundlerOptionStates.WAITING:
				this.prevControl = {
					visible: !isFirst,
					disabled: false,
				}
				this.nextControl = {
					visible: false,
					disabled: true,
				}
				break;
			case EProductBasketBundlerOptionStates.READY:
				this.prevControl = {
					visible: !isFirst,
					disabled: false,
				}
				this.nextControl = {
					visible: !isLast,
					disabled: false,
				}
				break;
		}
	}

	private scrollToTopContainer(): void {
		this.smoothScroll.scrollTo(this.parentElement);
	}

	private addToCart(): void {
		if (this.isAddToCartRequesting) {
			return;
		}
		this.productCostLine.isLoadingBtn = true;
		this.productCostLine.isDisableBtn = true;
		this.isAddToCartRequesting = true;
		const data: TProductBasketBundlerAddToCardRequest = {
			skuId: this.skuId,
			paymentMethod: this.store.selectedPaymentMethod.getValue() || null,
			period: this.store.selectedContractTariff.getValue()?.period?.value || null,
			tariff: this.store.selectedContractTariff.getValue()?.tariff || null,
			phoneNumber: this.store.phoneInfo.getValue()?.number || null,
			operatorName: this.store.phoneInfo.getValue()?.operator?.name || null,
			insurance: this.store.selectedInsurance.getValue() || null,
			accessory: this.store.selectedAccessory.getValue() || null,
		};

		this.httpService
			.request({
				url: '/products/add_to_basket/',
				method: 'POST',
				data: data,
			})
			.then((response) => {
				//TODO: handle response
			})
			.catch((reject) => {
				//TODO: handle error
			})
			.finally(() => {
				this.isAddToCartRequesting = false;
				this.productCostLine.isLoadingBtn = false;
				this.productCostLine.isDisableBtn = false;
			})
	}

	private getOptions(): void {
		this.isLoading = true;
		const data: TProductBasketBundlerGetOptionsRequest = {
			skuId: this.skuId,
		};
		this.httpService
			.request<TProductBasketBundlerGetOptionsResponse>({
				url: '/products/basket-bundler-options/',
				method: "GET",
				data: data,
			})
			.then(response => {
				this.optionTypes = response.data.data;
			})
			.catch(reject => {
				this.error = reject;
			})
			.finally(() => {
				this.isLoading = false;
			})
	}

	private getCurrentOptionType(): EProductBasketBundlerOptions {
		return this.options.item(this.currentStepIndex).type;
	}

	private isFirstOption(type: EProductBasketBundlerOptions): boolean {
		return this.optionTypes[0] === type;
	}

	private isLastOption(type: EProductBasketBundlerOptions): boolean {
		return this.optionTypes[this.optionTypes.length - 1] === type;
	}

	public nextStep(): void {
		if (this.currentStepIndex >= this.optionTypes.length - 1) {
			return;
		}
		const prevStep = this.options.item(this.currentStepIndex);
		this.currentStepIndex++
		const currentStep = this.options.item(this.currentStepIndex);
		currentStep.requestData();
		this.updateBtnAvailableInProductCostLine();
		prevStep.isActive = false;
		currentStep.isActive = true;
	}

	public prevStep(): void {
		if (this.currentStepIndex <= 0) {
			return;
		}
		const prevStep = this.options.item(this.currentStepIndex);
		prevStep.resetData();
		this.currentStepIndex--;
		const currentStep = this.options.item(this.currentStepIndex);
		this.updateBtnAvailableInProductCostLine();
		prevStep.isActive = false;
		currentStep.isActive = true;

		this.prevControl = {
			visible: this.currentStepIndex != 0,
			disabled: false,
		}
		this.nextControl = {
			visible: true,
			disabled: false,
		}
		this.scrollToTopContainer();
	}

	protected nextBtnHandler(event: Event): void {
		event.preventDefault();
		this.nextStep();
	}

	protected prevBtnHandler(event: Event): void {
		event.preventDefault();
		this.prevStep();
	}

	protected render(): TemplateResult {
		if (this.isLoading) {
			return this.getLoaderTemplate();
		}

		if (this.error) {
			return this.getErrorTemplate();
		}

		return this.getContentTemplate();
	}

	private getContentTemplate(): TemplateResult {
		return html`
			<div class="c-product-basket-bundler">
				${this.getHeaderTemplate()}

				<div class="c-product-basket-bundler__content">
					${this.getOptionTemplates(this.optionTypes).map((step, index) => {
						const activeClass: string = this.currentStepIndex === index ? 'c-product-basket-bundler__item_active' : '';
						return html`
							<div class="c-product-basket-bundler__item ${activeClass}">
								${step}
							</div>
						`;
					})}
				</div>

				<footer class="c-product-basket-bundler__footer">
					${this.getPrevBtnTemplate()}
					${this.getNextBtnTemplate()}
				</footer>
			</div>
		`;
	}

	private getHeaderTemplate(): TemplateResult {
		if (this.optionTypes.length > 1) {
			return html`
				<header class="c-product-basket-bundler__header">
					<p class="c-product-basket-bundler__progress">
						${this.currentStepIndex + 1} шаг из ${this.optionTypes.length}
					</p>
				</header>
			`;
		}
		return null;
	}

	private getLoaderTemplate(): TemplateResult {
		return html`
			<c-loader active
					  type="${ELoaderType.PRIMARY}"
					  size="${ELoaderSize.MD}"
					  class="c-product-basket-bundler__loader">
			</c-loader>
		`;
	}

	private getErrorTemplate(): TemplateResult {
		return html`
			<div class="u-text-center">
				<h4>При загрузке данных произошла ошибка !</h4>
				<p>Проверьте подключение к интернету</p>
			</div>
		`;
	}

	private getPrevBtnTemplate(): TemplateResult {
		if (!this.prevControl.visible) {
			return null;
		}

		return html`
			<c-btn product-basket-bundler-prev-btn
				   class="c-product-basket-bundler__btn c-product-basket-bundler__btn-prev"
				   type="${EBtnType.LINK}"
				   size="${EBtnSize.LG}"
				   .mods="${[EBtnMods.WITHOUT_P, EBtnMods.ADAPTIVE_TO_MD]}"
				   prependIconClass="o-icon_angle-left-filled"
				   @click="${this.prevBtnHandler}"
				   ?loading="${this.prevControl.loading}"
				   ?disabled="${this.prevControl.disabled}">
				<span class="u-d-none u-d-sm-inline">Предыдущий шаг</span>
				<span class="u-d-sm-none">Назад</span>
			</c-btn>
		`;
	}

	private getNextBtnTemplate(): TemplateResult {
		if (!this.nextControl.visible) {
			return null;
		}

		return html`
			<c-btn product-basket-bundler-next-btn
				   class="c-product-basket-bundler__btn c-product-basket-bundler__btn-next"
				   type="${EBtnType.PRIMARY}"
				   size="${EBtnSize.MD}"
				   .mods="${[EBtnMods.ADAPTIVE_TO_MD]}"
				   @click="${this.nextBtnHandler}"
				   ?loading="${this.nextControl.loading}"
				   ?disabled="${this.nextControl.disabled}">
				<span class="u-d-none u-d-sm-inline">Следующий шаг</span>
				<span class="u-d-sm-none">Далее</span>
			</c-btn>
		`;
	}

	private getOptionTemplates(types: Array<EProductBasketBundlerOptions>): Array<TemplateResult> {
		return types.map((type, index) => this.getOptionByTypeTemplate(type, index))
	}

	private getOptionByTypeTemplate(type: EProductBasketBundlerOptions, index: number): TemplateResult {
		switch (type) {
			case EProductBasketBundlerOptions.PAYMENT_METHOD:
				return html`
					<c-product-bundler-option-payment-method product-basket-bundler-option
															 .isActive="${index === 0}"
															 .store="${this.store}"
															 .controls="${this.controls}"
															 .states="${this.optionStates}"/>
				`;
			case EProductBasketBundlerOptions.CONTRACT_TARIFF:
				return html`
					<c-product-bundler-option-contract-tariff product-basket-bundler-option
															  .isActive="${index === 0}"
															  .store="${this.store}"
															  .controls="${this.controls}"
															  .states="${this.optionStates}"
															  .confirmedLink="${this.confirmedLink}">
				`;
			case EProductBasketBundlerOptions.INSURANCE:
				return html`
					<c-product-bundler-option-insurance product-basket-bundler-option
														.isActive="${index === 0}"
														.store="${this.store}"
														.controls="${this.controls}"
														.states="${this.optionStates}">
				`;
			case EProductBasketBundlerOptions.ACCESSORY:
				return html`
					<c-product-bundler-option-accessory product-basket-bundler-option
														isActive="${index === 0}"
														.store="${this.store}"
														.controls="${this.controls}"
														.states="${this.optionStates}">
				`;
		}
	}

	private updateCostInProductCostLine(): void {
		if (!this.productCostLine) {
			throw new Error('ProductCostLine is not defined');
		}
		let price: number = 0;
		let pricePerMonth: number = 0;
		const paymentMethod: TProductBundlerOptionPaymentMethod = this.store.selectedPaymentMethod.getValue();
		if (paymentMethod) {
			const tariffPrice = this.getContractTariffPrice(paymentMethod.type);
			const paymentMethodPrice = this.getPaymentMethodPrice(tariffPrice);
			const insurancePrice = this.getInsurancePrice(paymentMethod.type);
			const accessoryPrice = this.getAccessoryPrice(paymentMethod.type);

			const optionsPrice: number = paymentMethodPrice + tariffPrice + insurancePrice + accessoryPrice;
			if (paymentMethod.type === EProductBundlerOptionPaymentMethodTypes.FULL_PRICE) {
				price = optionsPrice;
				pricePerMonth = 0;
			} else if (paymentMethod.type === EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF) {
				price = 0;
				pricePerMonth = optionsPrice;
			}
		}

		this.productCostLine.price = price.toString();
		this.productCostLine.pricePerMonth = pricePerMonth.toString();
	}

	private getPaymentMethodPrice(tariffPrice: number): number {
		const paymentMethod: TProductBundlerOptionPaymentMethod = this.store.selectedPaymentMethod.getValue();

		if (!tariffPrice) {
			if (paymentMethod.type === EProductBundlerOptionPaymentMethodTypes.FULL_PRICE) {
				return parseFloat(paymentMethod.price) || 0;
			} else if (paymentMethod.type === EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF) {
				return parseFloat(paymentMethod.contractTariff?.pricePerMonth) || 0;
			}
		}
		return 0;
	}

	private getContractTariffPrice(paymentType: EProductBundlerOptionPaymentMethodTypes): number {
		const contractTariff: TSelectContractTariffSelected = this.store.selectedContractTariff.getValue();
		if (!contractTariff) {
			return 0;
		}
		switch (paymentType) {
			case EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF:
				return parseFloat(contractTariff.tariff?.pricePerMonth || contractTariff.period?.pricePerMonth) || 0;
			case EProductBundlerOptionPaymentMethodTypes.FULL_PRICE:
				return parseFloat(contractTariff.tariff?.price || contractTariff.period?.pricePerMonth) || 0;
			default:
				return 0;
		}
	}

	private getInsurancePrice(paymentType: EProductBundlerOptionPaymentMethodTypes): number {
		const insurance: Array<TInsurance> = this.store.selectedInsurance.getValue() || [];
		if (!insurance.length) {
			return 0;
		}
		switch (paymentType) {
			case EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF:
				return insurance.map(item => parseFloat(item.pricePerMonth)).reduce((prev, current) => prev + current, 0);
			case EProductBundlerOptionPaymentMethodTypes.FULL_PRICE:
				return insurance.map(item => parseFloat(item.price)).reduce((prev, current) => prev + current, 0);
			default:
				return 0;
		}
	}

	private getAccessoryPrice(paymentType: EProductBundlerOptionPaymentMethodTypes): number {
		const accessory: Array<TAccessory> = this.store.selectedAccessory.getValue() || [];
		if (!accessory.length) {
			return 0;
		}
		switch (paymentType) {
			case EProductBundlerOptionPaymentMethodTypes.CONTRACT_TARIFF:
				return accessory.map(item => parseFloat(item.pricePerMonth)).reduce((prev, current) => prev + current, 0);
			case EProductBundlerOptionPaymentMethodTypes.FULL_PRICE:
				return accessory.map(item => parseFloat(item.price)).reduce((prev, current) => prev + current, 0)
			default:
				return 0;
		}
	}

	private updateBtnAvailableInProductCostLine(): void {
		if (this.currentStepIndex < this.optionTypes.length - 1) {
			this.productCostLine.isDisableBtn = true;
		} else {
			const option: ProductBundlerOptionComponent = this.options.item(this.currentStepIndex);
			if (!option) {
				return;
			}
			const value: Object | Array<Object> = this.store.getSelectedValueByType(
				option.type
			);
			const required: boolean = this.store.selectedIsRequired(option.type);
			if (required) {
				this.productCostLine.isDisableBtn = !Boolean(value);
			} else {
				this.productCostLine.isDisableBtn = false;
			}
		}
	}
}
