import {ITimer, TTimerValue} from "@modules/timer/model";
import {BehaviorSubject, interval, Observable, Subscription} from "rxjs";
import {filter, map, takeWhile, tap} from "rxjs/operators";

export default class Timer implements ITimer {
	private streamTimer: BehaviorSubject<TTimerValue> = new BehaviorSubject<TTimerValue>(null);
	private isStopped: boolean = false;
	private lastSec: number;

	private intervalStream: Subscription;

	public getStream(): Observable<any> {
		return this.streamTimer
			.asObservable()
			.pipe(
				filter(value => Boolean(value))
			)
	}

	public start(ms: number): void {
		if (this.intervalStream) {
			this.stop();
		}
		this.isStopped = false;
		this.intervalStream = interval(1000)
			.pipe(
				map(value => (ms / 1000) - value),
				takeWhile(value => {
					return !this.isStopped && value > -1;
				}),
				tap(sec => {
					this.lastSec = sec;
				})
			)
			.subscribe(value => {
				this.streamTimer.next({
					second: value,
					fillTime: this.secondToFullTime(value),
				})
			})
	}

	public stop(): void {
		this.isStopped = true;
		if (this.intervalStream) {

		this.intervalStream.unsubscribe();
		}
	}

	private secondToFullTime(sec: number) {
		const hours = Math.floor(sec / 3600);
		const minutes = Math.floor(sec / 60) % 60;
		const seconds = sec % 60;

		return [hours, minutes, seconds]
			.map((v) => v < 10 ? '0' + v : v)
			.filter((v, i) => v !== '00' || i > 0)
			.join(':');
	}
}
